import axios from "axios";
import { message } from "antd";
import { BASE_URL } from "../../constants/constants";

const getAllRecoveryRegisterData = async (searchText, page, limit) => {
  try {
    const response = searchText
      ? await axios.get(
          `${BASE_URL}get-all-recovery-details?searchDN=${searchText}&page=${page}&limit=${limit}`
        )
      : await axios.get(
          `${BASE_URL}get-all-recovery-details?page=${page}&limit=${limit}`
        );

    const responseData = response.data || "";

    return responseData;
  } catch (err) {
    console.log("recovery_error",err)
    // message.error("Network Failed, Please Try Again");
  }
};

const updateRecoveryValue = async (data) => {
  try {
    const response = await axios.put(
      `${BASE_URL}update-recovered-value/${data.parentId}/${data._id}`,
      {
        recovery: +data.recovery,
      }
    );
    // console.log(response);
    const { recoverResult } = response.data || "";

    return recoverResult;
  } catch (error) {
    const { response } = error;
    const { data } = response;
    const { message: msg } = data;
    if (msg) {
      message.error(msg);
    } else {
      message.error("Network Failed, Please Try Again");
    }
  }
};

const updateRecoveredData = async (id, data) => {
  try {
    const response = await axios.put(
      `${BASE_URL}update-recovered-data/${id}`,
      data
    );
    // console.log(response);
    return response.data;
  } catch (error) {
    const { response } = error;
    const { data } = response;
    const { message: msg } = data;
    if (msg) {
      message.error(msg);
    } else {
      message.error("Network Failed, Please Try Again");
    }
  }
};

const getRecoveryRegisterDataById = async (registerId) => {
  try {
    const response = await axios.get(
      `${BASE_URL}get-recovery-challan-data/${registerId}`
    );
    // console.log(response);
    const { deliveryDetails } = response.data || "";

    return deliveryDetails;
  } catch (err) {
    message.error("Network Failed, Please Try Again");
  }
};

const getAllDeliveryNumbers = async (searchText, page) => {
  try {
    if (searchText && page) {
      const response = await axios.get(
        `${BASE_URL}get-all-recovery-delivery?page=${searchText}&limit=${page}` 
      );

      const { deliveryData } = response.data || "";

      return deliveryData;
    } else if (searchText) {
      const response = await axios.get(
        `${BASE_URL}get-all-recovery-delivery?searchDNo=${searchText}`
      );
      // console.log(response);
      const { deliveryData } = response.data || "";

      return deliveryData;
    } else {
      const response = await axios.get(
        `${BASE_URL}get-all-recovery-delivery?page=1&limit=300`
      );
      // console.log(response);
      const { deliveryData } = response.data || "";

      return deliveryData;
    }
  } catch (err) {
    message.error("Network Failed, Please Try Again");
  }
};

const createRecoveryCode = async (payload) => {
  try {
    const response = await axios.post(
      `${BASE_URL}create-recovery-register`,
      payload
    );

    const { data, message } = response.data || {};

    return {
      data,
      message,
    };
  } catch (err) {
    // console.log(err.response);
    const errMessage =
      (err.response && err.response.data && err.response.data.message) ||
      "Network Failed, Please Try Again";
    message.error(errMessage);
  }
};

const deleteRecoveryCode = async (recoveryId) => {
  try {
    const response = await axios.delete(
      `${BASE_URL}delete-recovered-data/${recoveryId}`
    );
    const { data, message } = response.data || "";
    return response.data || {};
  } catch (err) {
    message.error("Network Failed, Please Try Again");
  }
};

export {
  getAllRecoveryRegisterData,
  getRecoveryRegisterDataById,
  updateRecoveryValue,
  getAllDeliveryNumbers,
  createRecoveryCode,
  deleteRecoveryCode,
  updateRecoveredData,
};
