/* eslint-disable no-unused-vars */
import React, {Component} from "react";
import {Row, Col, Form, Input, Button, Alert, message, Spin, Modal} from "antd";
import BgImage from "../../assests/images/truklink-b-img.png";
import Logo from "../../assests/images/trucklink_logo.png";
import auth from "../../utils/auth";
import axios from "axios";
import moment from "moment";
import {BASE_URL} from "../../constants/constants";
import "./login.css";
const publicIp = require("public-ip");

const navigator = window.navigator;
const hostreff = window.location.host;
const nowStr = new Date();
let momentObj = moment(new Date());
var nVer = navigator.appVersion;

var nAgt = navigator.userAgent;
var browserName = navigator.appName;
var fullVersion = "" + parseFloat(navigator.appVersion);
var majorVersion = parseInt(navigator.appVersion, 10);
var nameOffset, verOffset, ix;

// In Opera 15+, the true version is after "OPR/"
if ((verOffset = nAgt.indexOf("OPR/")) != -1) {
  browserName = "Opera";
  fullVersion = nAgt.substring(verOffset + 4);
}
// In older Opera, the true version is after "Opera" or after "Version"
else if ((verOffset = nAgt.indexOf("Opera")) != -1) {
  browserName = "Opera";
  fullVersion = nAgt.substring(verOffset + 6);
  if ((verOffset = nAgt.indexOf("Version")) != -1)
    fullVersion = nAgt.substring(verOffset + 8);
}
// In MSIE, the true version is after "MSIE" in userAgent
else if ((verOffset = nAgt.indexOf("MSIE")) != -1) {
  browserName = "Microsoft Internet Explorer";
  fullVersion = nAgt.substring(verOffset + 5);
}
// In Chrome, the true version is after "Chrome"
else if ((verOffset = nAgt.indexOf("Chrome")) != -1) {
  browserName = "Chrome";
  fullVersion = nAgt.substring(verOffset + 7);
}
// In Safari, the true version is after "Safari" or after "Version"
else if ((verOffset = nAgt.indexOf("Safari")) != -1) {
  browserName = "Safari";
  fullVersion = nAgt.substring(verOffset + 7);
  if ((verOffset = nAgt.indexOf("Version")) != -1)
    fullVersion = nAgt.substring(verOffset + 8);
}
// In Firefox, the true version is after "Firefox"
else if ((verOffset = nAgt.indexOf("Firefox")) != -1) {
  browserName = "Firefox";
  fullVersion = nAgt.substring(verOffset + 8);
}
// In most other browsers, "name/version" is at the end of userAgent
else if (
  (nameOffset = nAgt.lastIndexOf(" ") + 1) < (verOffset = nAgt.lastIndexOf("/"))
) {
  browserName = nAgt.substring(nameOffset, verOffset);
  fullVersion = nAgt.substring(verOffset + 1);
  if (browserName.toLowerCase() == browserName.toUpperCase()) {
    browserName = navigator.appName;
  }
}
// trim the fullVersion string at semicolon/space if present
if ((ix = fullVersion.indexOf(";")) != -1)
  fullVersion = fullVersion.substring(0, ix);
if ((ix = fullVersion.indexOf(" ")) != -1)
  fullVersion = fullVersion.substring(0, ix);

majorVersion = parseInt("" + fullVersion, 10);
if (isNaN(majorVersion)) {
  fullVersion = "" + parseFloat(navigator.appVersion);
  majorVersion = parseInt(navigator.appVersion, 10);
}

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      msg: "",
      visible: true,
      loading: false,
      mvisible: false,
      locationAgent: browserName,
      refferrer: hostreff,
      loginTime: momentObj._d,
      systemIp: "",
    };
  }
  onFinish = async (values) => {
    const {username, password} = values;
    let curDate = moment(new Date());
    const {locationAgent, refferrer, loginTime, systemIp} = this.state;

    try {
      this.setState({loading: true});
      const response = await axios.post(`${BASE_URL}login`, {
        email: username,
        password: password,
        browserAgent: locationAgent,
        referrer: refferrer,
        userEmail: username,
        loginTime: curDate,
        ipAddress: systemIp,
      });
      const userDetails = response.data || null;
      if (userDetails) {
        auth.set(userDetails, {
          path: "/",
          expires: new Date(Date.now() + 60 * 60 * 1000),
        });
        auth.setUserData(userDetails.logData);
        this.props.history.push({
          pathname: "/vehicle-onwer-details",
          state: {
            role_id: userDetails.logData._id,
          },
        });
        message.success("Welcome");
        this.setState({loading: false});
      } else {
        message.error("Login Failed", 3);
        this.setState({loading: false});
      }
    } catch (error) {
      console.log(error);
      this.setState({
        msg: "Invalid username and Password",
        loading: false,
      });
    }
  };
  onFinishReset = async (changepasswordinfo) => {
    const {email} = changepasswordinfo;
    try {
      await axios
        .post(`${BASE_URL}forget-password`, {
          email,
        })
        .then((data) => {
          message.success(`Email has been sent to ${email}`);
          this.setState({
            mvisible: false,
          });
          setTimeout(() => {
            window.location.replace("/");
          }, 200);
        })
        .catch((error) => {
          const {response} = error;
          const {data} = response;
          const {message: msg} = data;
          message.error(msg);
        });
    } catch (err) {
      console.log(err);
      // message.error("Unable to Reset Password");
    }
  };
  handleClose = () => {
    this.setState({visible: false});
  };
  iploc = async () => {
    try {
      this.setState({
        systemIp: await publicIp.v4(),
      });
    } catch (err) {
      console.log(err);
    }
  };
  componentDidMount() {
    auth.removeToken();
    this.iploc();
  }
  showModal = () => {
    this.setState({mvisible: true});
  };
  handleCancel = () => {
    this.setState({mvisible: false});
  };
  render() {
    return (
      <div className="loginarea">
        <Modal
          title={
            <div>
              <h3 style={{fontSize: "20px", margin: "0px"}}>Forgot Password</h3>
              <p
                style={{
                  fontSize: "12px",
                  fontFamily: "MyriadProSemiBold",
                }}
              >
                Enter Details
              </p>
            </div>
          }
          className="ModalLook"
          visible={this.state.mvisible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          destroyOnClose={true}
          footer={null}
        >
          <Form
            ref={this.formRef}
            name="changepasswordinfo"
            className="vehicleinfoForm"
            onFinish={this.onFinishReset}
            initialValues={{
              amount: 0,
            }}
            autoComplete="off"
          >
            {/* <h2 style={{margin: "25px 30px 15px 30px", fontSize: "14px"}}>
              User Details
            </h2> */}
            <Row style={{margin: "10px 22px"}}>
              <Col xs={24} sm={24} md={10} lg={10} xl={10}>
                <h3 style={{marginLeft: "5px"}}>Email</h3>
                <div>
                  <Form.Item
                    name="email"
                    rules={[
                      {
                        type: "email",
                        message: "The input is not valid E-mail!",
                      },
                      {
                        required: true,
                        message: "Please input your E-mail!",
                      },
                    ]}
                  >
                    <Input />
                  </Form.Item>
                </div>
              </Col>
            </Row>
            <Row
              style={{
                border: "0.5px dashed #979797",
                opacity: "0.40",
                margin: "20px 0 20px 0px",
              }}
            />
            <Row>
              <Col lg={17}></Col>
              <Col lg={7}>
                <div
                  style={{
                    padding: "10px",
                    marginRight: "20px",
                    float: "right",
                  }}
                >
                  <Button className="loGinBtn" type="primary" htmlType="submit">
                    SUBMIT
                  </Button>
                </div>
                <div style={{padding: "10px", float: "right"}}>
                  <Button
                    type="primary"
                    className="cancelBtn"
                    onClick={() => {
                      document
                        .getElementsByClassName("ant-modal-close")[0]
                        .click();
                      // this.onReset();
                    }}
                  >
                    CANCEL
                  </Button>
                </div>
              </Col>
            </Row>
          </Form>
        </Modal>
        <Spin spinning={this.state.loading}>
          <Row>
            <Col xs={24} sm={24} md={24} lg={12}>
              <div className="leftloginarea">
                <div className="loginbg">
                  <div>
                    <img src={BgImage} style={{width: "260px"}} alt="bgimage" />
                  </div>
                  <div className="leftitlearea">
                    <h1 className="headdesc">Welcome to</h1>
                    <p className="headtitle ">TruckLink</p>
                  </div>
                </div>
                <div className="loginfooter">support@trucklink.in</div>
              </div>
            </Col>
            <Col xs={24} sm={24} md={24} lg={12} className="RightLogin">
              <div className="Rightloginarea">
                <div className="logo">
                  <img src={Logo} alt="logo" />
                </div>
                <div className="rightlogintxtarea">
                  <h1 className="rightlogintitle">Please log in</h1>
                  <p>Log in to continue to your account</p>
                </div>
                <div className="loginForm">
                  <div className="errorform">
                    {this.state.msg ? (
                      <Alert
                        message={this.state.msg || ""}
                        type="error"
                        // closable
                        // afterClose={this.handleClose}
                      />
                    ) : null}
                  </div>
                  <Form
                    name="basic"
                    initialValues={{remember: true}}
                    onFinish={this.onFinish}
                  >
                    <Form.Item
                      name="username"
                      place
                      rules={[
                        {
                          required: true,
                          message: "Please input your username!",
                        },
                      ]}
                    >
                      <Input
                        placeholder="Username"
                        className="loginuserfield"
                      />
                    </Form.Item>

                    <Form.Item
                      name="password"
                      rules={[
                        {
                          required: true,
                          message: "Please input your password!",
                        },
                      ]}
                    >
                      <Input.Password
                        placeholder="Enter Password"
                        className="loginuserfield"
                      />
                    </Form.Item>

                    <Form.Item>
                      <Button
                        type="primary"
                        htmlType="submit"
                        className="loginbtn"
                      >
                        Login
                      </Button>
                    </Form.Item>
                  </Form>
                  <a onClick={this.showModal}>Forgot Password?</a>
                  <div className="rightloginfooter">
                    Privacy Policy | Terms of use
                  </div>
                  <div className="rightfooter">
                    <p>
                      Privacy Policy | Terms of use
                      <br />
                      support@trucklink.in
                    </p>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Spin>
      </div>
    );
  }
}

export default Login;
