import React, {Component} from "react";
import {Button, message, DatePicker} from "antd";
import jsPDF from "jspdf";
import "jspdf-autotable";
import Print from "../../assests/images/print.svg";

class PrintBillRegister extends Component {
  constructor(props) {
    super(props);
  }
  handlePrint = (e) => {
    var totalPagesExp = "{total_pages_count_string}";
    try {
      const doc = new jsPDF("l", "mm", "a4");
      const items = [];
      this.props.data.forEach((d, index) => {
        items.push([
          index + 1,
          d.billNumber || "-",
          d.billType || 0,
          d.valueRaised || 0,
          d.valueReceived || 0,
          d.tax || 0,
          d.difference || 0,
          d.remarks || "NA",
        ]);
      });
      if (items.length === 0) {
        message.error("Please select bill(s) to download");
      } else {
        doc.setFontSize(10);
        const d = new Date();
        const m = d.getMonth() + 1;
        const day = d.getDate();
        const year = d.getFullYear();
        doc.autoTable({
          head: [
            [
              "Sl No",
              "Bill No",
              "Bill Type",
              "Value Raised",
              "Value Recieved",
              "Tax",
              "Difference",
              "Remarks",
            ],
          ],
          body: items,
          startY: 10,
          headStyles: {fontSize: 8, fontStyle: "normal", fillColor: "#44495b"},
          bodyStyles: {fontSize: 8, textAlign: "center"},
          didDrawPage: function (data) {
            // Header
            doc.setFontSize(10);
            doc.text("Bill Register", data.settings.margin.left + 0, 5);
            doc.text("Date:-", data.settings.margin.left + 155, 5);
            doc.text(
              day + "/" + m + "/" + year,
              data.settings.margin.left + 170,
              5
            );

            // Footer
            var str = "Page " + doc.internal.getNumberOfPages();
            // Total page number plugin only available in jspdf v1.0+
            if (typeof doc.putTotalPages === "function") {
              str = str + " of " + totalPagesExp;
            }
            doc.setFontSize(10);

            // jsPDF 1.4+ uses getWidth, <1.4 uses .width
            var pageSize = doc.internal.pageSize;
            var pageHeight = pageSize.height
              ? pageSize.height
              : pageSize.getHeight();
            doc.text(str, data.settings.margin.left, pageHeight - 10);
          },
          margin: {top: 10},
        });
        if (typeof doc.putTotalPages === "function") {
          doc.putTotalPages(totalPagesExp);
        }
        doc.save("bill-register.pdf");
      }
    } catch (err) {
      message.error("Unable to Print");
    }
  };
  render() {
    return (
      <Button
        className="button"
        onClick={this.handlePrint}
        icon={<img src={Print} style={{paddingRight: "10px"}} />}
      >
        PRINT
      </Button>
    );
  }
}

export default PrintBillRegister;
