const {Search} = Input;
const {Text} = Typography;
const EditableContext = React.createContext();
const numInput = (event) => {
  const val = event.target.value;
  var digit = /^[0-9]*\.?[0-9]*$/.test(val);
  if (/^[0-9]*\.?[0-9]*$/.test(val) && event.key !== "Backspace") {
    var with2Decimals = val.toString().match(/^\d+(?:\.\d{1,2})?$/);
    if (with2Decimals) {
      event.target.value = with2Decimals[0];
    }
  } else {
    return true;
  }
};
const expandIcon = {
  color: "#01a6d0",
  fontSize: "14px",
  width: "24px",
  height: "24px",
  paddingTop: "6px",
};
const decimals = (n, d = 2) => {
  if (typeof n !== "number" || typeof d !== "number") return n;
  let isDecimalNum = n % 1 !== 0;
  n = isDecimalNum ? parseFloat(n) : parseInt(n);
  return isDecimalNum ? n.toFixed(d) : n;
};

const EditableRow = ({index, ...props}) => {
  const [form] = Form.useForm();
  return (
    <Form form={form} component={false}>
      <EditableContext.Provider value={form}>
        <tr {...props} />
      </EditableContext.Provider>
    </Form>
  );
};

const EditableCell = ({
  title,
  editable,
  children,
  dataIndex,
  record,
  handleSave,
  ...restProps
}) => {
  const [editing, setEditing] = useState(false);
  const inputRef = useRef();
  const form = useContext(EditableContext);
  useEffect(() => {
    if (editing) {
      inputRef.current.focus();
    }
  }, [editing]);

  const toggleEdit = () => {
    setEditing(!editing);
    form.setFieldsValue({
      [dataIndex]: record[dataIndex],
    });
  };

  const save = async (e) => {
    try {
      const values = await form.validateFields(); 
      toggleEdit();
      handleSave({...record, ...values});
      // toggleEdit(); 
    } catch (errInfo) {
      console.log("Save failed:", errInfo);
    } 
  };

  const inputSave = async (e) => {
    try {
      const values = await form.validateFields();
      // toggleEdit();
      handleSave({...record, ...values});
    } catch (errInfo) {
      console.log("Save failed:---------------", errInfo);
    }
  };

  let childNode = children;

  if (editable) {
    childNode = editing ? (
      <Form.Item
        style={{
          margin: 0,
        }}
        rules={[
          {
            required: true,
          },
          {
            pattern: new RegExp(
              /^\d{1,8}(\.\d{1,2})?$/
            ),
            message: "Only 8 digit with upto 2 decimal allowed"
          },
        ]}
        name={dataIndex}
      >
        <Input
          type="number"
          ref={inputRef}
          onChange={inputSave}
          onPressEnter={save}
          onBlur={save}
          // onKeyPress={(event) => {
          //   if (
          //     !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
          //     event.key !== "Backspace"
          //   ) {
          //     event.preventDefault();
          //   }
          // }}
          // maxLength={8}
          onKeyPress={(event) => {
            if (numInput(event)) {
              event.preventDefault();
            }
          }}
        />
      </Form.Item>
    ) : (
      <div className="editable-cell-value-wrap" onClick={toggleEdit}>
        {children}
      </div>
    );
  }
  return <td {...restProps}>{childNode}</td>;
};

