import React, {Component} from "react";
import "antd/dist/antd.css";
import {Layout} from "antd";
import {MenuUnfoldOutlined, MenuFoldOutlined} from "@ant-design/icons";
import Logo from "../../../assests/images/trucklink.png";
import ReceiveAcknowlegement from "./receiveregisterdetails";
import SideMenu from "../../../components/menus/sidemenu/sidemenu";
import HeaderMenu from "../../../components/menus/headermenu/headermenu";
import ReceiveHeaderBreedCrumb from "../../../components/receivemenubreedcrumb/receivemenubreedcrumb";
const {Header, Sider, Content} = Layout;
const today = new Date();
const months = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "Jun",
  "Jul",
  "Aug",
  "Sep",
  "Oct",
  "Nov",
  "Dec",
];
const days = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];

const todaydate = `${days[today.getDay()]} ${today.getDate()}, ${
  months[today.getMonth()]
} ${today.getFullYear()}`;
const d = new Date();
const h = (d.getHours() % 12 < 10 ? "0" : "") + (d.getHours() % 12);
const m = (d.getMinutes() < 10 ? "0" : "") + d.getMinutes();
const todaytime = `${h}:${m}:${d.getHours() > 12 ? "PM" : "AM"}`;
class ReceiveRegister extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: false,
      vidible: false,
      collaspedwidth: "",
      sidebarWidth: "200",
    };
  }

  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  render() {
    return (
      <Layout>
        <Sider
          trigger={null}
          className="siderbar"
          collapsible
          collapsed={this.state.collapsed}
          breakpoint="sm"
          width={this.state.sidebarWidth}
          collapsedWidth={this.state.collapsedWidth}
          onBreakpoint={(broken) => {
            if (broken === true) {
              this.setState({
                collapsed: true,
                collapsedWidth: "0",
                sidebarWidth: "80",
              });
            } else {
              this.setState({
                collapsedWidth: "80",
                sidebarWidth: "200",
              });
            }
          }}
        >
          <div className="layoutlogo">
            <div>
              <img src={Logo} alt="logo" />
            </div>
            <div
              style={{
                fontFamily: "AvenirBold",
                fontSize: "22px",
                color: "#fff",
                padding: "5px",
              }}
            >
              <span>TRUCK</span>
              <span style={{fontFamily: "Avenir"}}>LINK</span>
            </div>
          </div>
          <SideMenu history={this.props.history} />
        </Sider>
        <Layout className="site-layout">
          <Header
            className="site-layout-background"
            style={{padding: 0, display: "flex"}}
          >
            {React.createElement(
              this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
              {
                className: "trigger",
                onClick: this.toggle,
              }
            )}
            <HeaderMenu />
          </Header>
          <Content>
            <div
              style={{
                display: "flex",
                background: "#fff",
                height: "60px",
                marginTop: "2px",
                padding: "4px 10px 10px 30px",
              }}
            >
              <div style={{marginTop: "4px"}}>
                <h3
                  style={{
                    fontSize: "18px",
                    fontFamily: "AvenirBlack",
                    margin: "0px",
                  }}
                >
                  Receive Acknowledgement
                </h3>
                <p style={{fontSize: "12px", fontFamily: "AvenirBold"}}>
                  Review/Update Achnowledgement
                </p>
              </div>
              <div style={{position: "absolute", right: 40}}>
                <p
                  style={{
                    marginTop: "10px",
                    fontSize: "12px",
                  }}
                >
                  <span
                    style={{
                      fontFamily: "AvenirBlack",
                    }}
                  >
                    {todaydate}
                  </span>
                  <br />
                  <span style={{fontSize: "10px", fontFamily: "AvenirBold"}}>
                    {todaytime}
                  </span>
                </p>
              </div>
            </div>
            <div>
              <ReceiveHeaderBreedCrumb
                path="receiveregister"
                history={this.props.history}
              />
            </div>
            <div
              className="site-layout-background"
              style={{
                margin: "20px",
                minHeight: 280,
              }}
            >
              <ReceiveAcknowlegement />
            </div>
          </Content>
        </Layout>
      </Layout>
    );
  }
}

export default ReceiveRegister;
