import React, {Component} from "react";
import {Input, Row, Col, Form, Select, Button, message, Spin} from "antd";

import {CloseCircleOutlined} from "@ant-design/icons";
import axios from "axios";
import {BASE_URL} from "../../../../constants/constants";
import "antd/dist/antd.css";
import "./vehicleOwnerDetail.css";
const {Option} = Select;

class EditvehicleiOwnerDetail extends Component {
  constructor(props) {
    super(props);
    const {ownerdetail} = this.props;
    const {accountIds} = ownerdetail;
    this.state = {
      loading: false,
      visible: false,
      bankInfoForEdit: accountIds,
      ownerdetails: ownerdetail,
      states: [],
    };
  }

  formRef = React.createRef();

  onFinish = async (values) => {
    const bankDetails = values.bankDetails || null;
    const newBankData = [];
    const oldAccounts = [];
    bankDetails.filter((data) => {
      if (data["_id"] === undefined) {
        newBankData.push(data);
      } else {
        oldAccounts.push(data);
      }
    });

    const BankDetails = [];
    try {
      this.setState({
        loading: true,
      });
      await axios.put(
        `${BASE_URL}update-owner-details/${this.props.ownerdetail._id}`,
        {
          name: values.name,
          email: values.email,
          phoneNumber: values.phoneNumber,
          panNumber: values.panNumber,
          address: values.address,
          district: values.district,
          state: values.state,
        }
      );
      await axios.put(`${BASE_URL}update-owner-accounts`, oldAccounts);
      if (newBankData.length > 0) {
        newBankData.forEach((value) => {
          BankDetails.push({
            accountNumber: value.accountNumber,
            accountHolderName: value.accountHolderName,
            ifscCode: value.ifscCode,
            bankName: value.bankName,
            branchName: value.branchName,
            ownerId: this.props.ownerdetail._id,
            phoneNumber: values.phoneNumber,
          });
        });
        await axios.post(
          `${BASE_URL}create-accounts-owner/${this.props.ownerdetail._id}`,
          BankDetails
        );
      }
      this.setState({
        loading: false,
      });
      if(this.props.onSearch()){
         this.props.onSearch();
      }
      message.success("Successfully updated details");
      this.props.onUpdate();
      
      document.getElementsByClassName("ant-modal-close")[0].click();
    } catch (err) {
      console.log(err);
      this.setState({
        loading: false,
      });
      message.error("Network Error, Please Try Again");
    }
  };

  onReset = () => {
    this.formRef.current.resetFields();
  };

  getallStates = async (e) => {
    try {
      const response = await axios.get(`${BASE_URL}get-all-states`);
      const states = response.data.states || "";
      const allStates = states.map(({code, name}) => {
        return (
          <Option key={code} value={name}>
            {name}
          </Option>
        );
      });
      this.setState({states: allStates});
    } catch (err) {
      console.log(err);
    }
  };

  getOwnerDetail = async (e) => {
    try {
      const response = await axios.get(
        `${BASE_URL}get-owner-details/5eb95ef7aa78e70011ddf4c2`
      );
      const ownerDetails = response.data.ownerDetails[0] || "";
      this.setState({
        ownerdetails: ownerDetails,
      });
    } catch (err) {
      console.log(err);
    }
  };

  componentDidMount() {
    this.getallStates();
    this.getOwnerDetail();
  }

  render() {
    const {ownerdetails, bankInfoForEdit} = this.state;
    const {
      address,
      countryCode,
      createdAt,
      district,
      email,
      modifiedAt,
      name,
      panNumber,
      phoneNumber,
      state,
    } = ownerdetails;
    return (
      <div>
        <Spin spinning={this.state.loading} delay={500}>
          <Form
            ref={this.formRef}
            name="vehicleinfo"
            className="vehicleinfoForm"
            onFinish={this.onFinish}
            // autoComplete="off"
            initialValues={{
              address,
              countryCode,
              createdAt,
              district,
              email,
              modifiedAt,
              name,
              panNumber,
              phoneNumber,
              state,
              bankDetails: bankInfoForEdit,
            }}
          >
            <h2 style={{margin: "15px 30px"}}>Vehicle Owner Details</h2>
            <Row style={{margin: "10px 22px"}}>
              <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Owner Name
                </h3>
                <div>
                  <Form.Item
                    name="name"
                    rules={[
                      {required: true, message: "This feild is required"},
                    ]}
                  >
                    <Input />
                  </Form.Item>
                </div>
              </Col>

              <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                <h3 style={{marginLeft: "5px"}}>Address</h3>
                <div>
                  <Form.Item name="address">
                    <Input />
                  </Form.Item>
                </div>
              </Col>

              <Col xs={12} sm={12} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}}>District</h3>
                <div>
                  <Form.Item name="district">
                    <Input />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={12} sm={12} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}}>State</h3>
                <div>
                  <Form.Item name="state">
                    <Select
                      showSearch
                      placeholder="Select States"
                      optionFilterProp="children"
                      filterOption={(input, option) =>
                        option.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {this.state.states}
                    </Select>
                  </Form.Item>
                </div>
              </Col>

              <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                <h3 style={{marginLeft: "5px"}}>PAN Card Number</h3>
                <div>
                  <Form.Item name="panNumber">
                    <Input
                      onKeyUp={(e) => {
                        var start = e.target.selectionStart;
                        var end = e.target.selectionEnd;
                        e.target.value = e.target.value.toUpperCase();
                        e.target.setSelectionRange(start, end);
                      }}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Mobile Number
                </h3>
                <div>
                  <Form.Item
                    name="phoneNumber"
                    rules={[
                      {
                        required: true,
                        message: "invalid mobile number",
                      },
                      {min: 10, message: "Mobile No not less than 10 digit"},
                    ]}
                  >
                    <Input
                      onKeyPress={(event) => {
                        if (
                          !/^[0-9]+$/.test(event.key) &&
                          event.key !== "Backspace"
                        ) {
                          event.preventDefault();
                        }
                      }}
                      maxLength="10"
                    />
                  </Form.Item>
                </div>
              </Col>

              <Col
                xs={12}
                sm={12}
                md={8}
                lg={8}
                xl={8}
                style={{marginRight: "-5px"}}
              >
                <h3 style={{marginLeft: "5px"}}>Email ID</h3>
                <div>
                  <Form.Item name="email">
                    <Input />
                  </Form.Item>
                </div>
              </Col>
            </Row>

            <Row
              style={{
                border: "0.5px dashed #979797",
                opacity: "0.40",
                margin: "10px 0 10px 0px",
              }}
            />

            <h2 style={{margin: "15px 30px"}}>Bank Details</h2>
            <Row style={{margin: "10px 17px"}}>
              <Form.List name="bankDetails">
                {(bankDetails, {add, remove, index}) => {
                  return (
                    <>
                      {bankDetails.map((field, index) => (
                        <div key={field.key}>
                          <Row>
                            <Col xs={24} sm={24} md={21} lg={21} xl={21}>
                              <Row>
                                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                                  <h3
                                    style={{marginLeft: "5px"}}
                                    className="require"
                                  >
                                    Account Holders Name
                                  </h3>
                                  <div>
                                    <Form.Item
                                      name={[index, "accountHolderName"]}
                                      rules={[
                                        {
                                          required: true,
                                          message: "This feild is required",
                                        },
                                      ]}
                                    >
                                      <Input />
                                    </Form.Item>
                                  </div>
                                </Col>
                                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                                  <h3
                                    style={{marginLeft: "5px"}}
                                    className="require"
                                  >
                                    Bank Name
                                  </h3>
                                  <div>
                                    <Form.Item
                                      name={[index, "bankName"]}
                                      rules={[
                                        {
                                          required: true,
                                          message: "This feild is required",
                                        },
                                      ]}
                                    >
                                      <Input />
                                    </Form.Item>
                                  </div>
                                </Col>
                                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                                  <h3
                                    style={{marginLeft: "5px"}}
                                    className="require"
                                  >
                                    Account Number
                                  </h3>
                                  <div>
                                    <Form.Item
                                      name={[index, "accountNumber"]}
                                      rules={[
                                        {
                                          required: true,
                                          message: "This feild is required",
                                        },
                                      ]}
                                    >
                                      <Input
                                        onKeyPress={(event) => {
                                          if (
                                            !/^[0-9]+$/.test(event.key) &&
                                            event.key !== "Backspace"
                                          ) {
                                            event.preventDefault();
                                          }
                                        }}
                                      />
                                    </Form.Item>
                                  </div>
                                </Col>
                                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                                  <h3
                                    style={{marginLeft: "5px"}}
                                    className="require"
                                  >
                                    Bank Branch
                                  </h3>
                                  <div>
                                    <Form.Item
                                      name={[index, "branchName"]}
                                      rules={[
                                        {
                                          required: true,
                                          message: "This feild is required",
                                        },
                                      ]}
                                    >
                                      <Input />
                                    </Form.Item>
                                  </div>
                                </Col>
                                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                                  <h3
                                    style={{marginLeft: "5px"}}
                                    className="require"
                                  >
                                    IFSC Code
                                  </h3>
                                  <div>
                                    <Form.Item
                                      name={[index, "ifscCode"]}
                                      rules={[
                                        {
                                          required: true,
                                          message: "This feild is required",
                                        },
                                      ]}
                                    >
                                      <Input
                                        onKeyUp={(e) => {
                                          var start = e.target.selectionStart;
                                          var end = e.target.selectionEnd;
                                          e.target.value = e.target.value.toUpperCase();
                                          e.target.setSelectionRange(
                                            start,
                                            end
                                          );
                                        }}
                                      />
                                    </Form.Item>
                                  </div>
                                </Col>
                              </Row>
                            </Col>
                            <Col xs={24} sm={24} md={3} lg={3} xl={3}>
                              {index >=
                                this.props.ownerdetail.accountIds.length && (
                                // <Button
                                //   type="secondary"
                                //   className="addbankBtn"
                                //   onClick={() => remove(index)}
                                // >
                                <CloseCircleOutlined
                                  onClick={() => remove(index)}
                                  style={{fontSize: "28px", color: "#01a6d0"}}
                                />
                                // </Button>
                              )}
                            </Col>
                          </Row>
                          <Row
                            style={{
                              border: "0.5px dashed #979797",
                              opacity: "0.40",
                              margin: "20px 0 20px 0px",
                            }}
                          />
                        </div>
                      ))}
                      <Row>
                        <Col xs={12} sm={12} md={8} lg={7} xl={7}>
                          <Button
                            type="primary"
                            className="addbankBtn"
                            onClick={() => add()}
                          >
                            + Add Bank
                          </Button>
                        </Col>
                      </Row>
                    </>
                  );
                }}
              </Form.List>
            </Row>

            <Row
              style={{
                border: "0.5px dashed #979797",
                opacity: "0.40",
                margin: "10px 0 10px 0px",
              }}
            />
            <Row>
              <Col lg={24} style={{margin: "10px 0px"}}>
                <div
                  style={{
                    padding: "10px",
                    float: "right",
                    margin: "0px 23px 0px 10px",
                  }}
                >
                  <Button className="save" type="primary" htmlType="submit">
                    SAVE
                  </Button>
                </div>
                <div style={{padding: "10px", float: "right"}}>
                  <Button
                    type="primary"
                    className="cancelBtn"
                    onClick={() => {
                      this.onReset();
                      document
                        .getElementsByClassName("ant-modal-close")[0]
                        .click();
                    }}
                  >
                    CANCEL
                  </Button>
                </div>
              </Col>
            </Row>
          </Form>
        </Spin>
      </div>
    );
  }
}

export default EditvehicleiOwnerDetail;
