/* eslint-disable no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable no-unused-expressions */
import React, {Component} from "react";
import {
  Input,
  Row,
  Col,
  Form,
  Select,
  Upload,
  Button,
  InputNumber,
  message,
  Spin,
  DatePicker,
} from "antd";
import axios from "axios";
import moment from "moment";
import {PaperClipOutlined} from "@ant-design/icons";
import Logo from "../../../assests/images/trucklink.png";
import {BASE_URL} from "../../../constants/constants";
import dateicon from "../../../assests/images/dateicon.svg";
const {Option} = Select;
const addFile = {fontWeight: "600", color: " #01a6d0"};
const {Dragger} = Upload;
const dateFormatList = ["DD/MM/YYYY"];
const numInput = (event) => {
  const val = event.target.value;
  var digit = /^[0-9]*\.?[0-9]*$/.test(val);
  if (/^[0-9]*\.?[0-9]*$/.test(val) && event.key !== "Backspace") {
    var with2Decimals = val.toString().match(/^-?\d+(?:\.\d{0,1})?/);
    if (with2Decimals) {
      event.target.value = with2Decimals[0];
    }
  } else {
    return true;
  }
};
class Editvoucherbook extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      visible: false,
      fullrecord: "",
      AllVehicleNumber: [],
      VehicleArray: [],
      Invoice: "",
      ownerName: "",
      commission: "",
      grDate: "",
      vehicleId: "",
      ownerId: "",
      materialDataSelect: "",
      materialdataDetails: "",
      ownerDetails: "",
      materialDetails: "",
    };
  }

  formRef = React.createRef();

  getAllVehicleNumber = async () => {
    try {
      const response = await axios.get(`${BASE_URL}get-voucher-vehicles-info`);
      const vehicleData = response.data.vehicle || "";
      const vehicleNo = vehicleData.map(({_id, registrationNumber}) => {
        return (
          <Option key={_id} value={registrationNumber}>
            {registrationNumber}
          </Option>
        );
      });

      this.setState({
        AllVehicleNumber: vehicleNo,
        VehicleArray: vehicleData,
      });
    } catch (err) {
      console.log(err);
    }
  };
  getMaterialData = async () => {
    try {
      const response = await axios.get(`${BASE_URL}get-material`);
      const materialData = response.data.materials || "";
      const materialtypes = materialData.map(({_id, materialType}) => {
        const val = materialType.trimEnd();
        return (
          <Option key={_id} value={_id}>
            {materialType}
          </Option>
        );
      });
      this.setState({
        materialDataSelect: materialtypes,
        materialdataDetails: materialData,
      });
    } catch (err) {
      message.error("Unable to fetch material data");
      console.log(err);
    }
  };
  componentDidMount() {
    this.getAllVehicleNumber();
    this.getMaterialData();
  }

  onFinish = async (voucherDeatils) => {
    const {ownerDetails, materialDetails} = this.state;
    const {
      voucherNumber,
      amount,
      modeOfPayment,
      narration,
      vehicleNumber,
      voucherDate,
      materialType,
    } = voucherDeatils;
    var DateString = moment(voucherDate).format("DD/MM/YYYY");
    var startDate = moment(DateString, "DD/MM/YYYY");
    var vDate = startDate._i;
    try {
      await axios.post(`${BASE_URL}create-voucher`, {
        voucherNumber: voucherNumber,
        voucherDate: vDate,
        vehicleNumber: vehicleNumber,
        materialType: materialDetails ? materialDetails.materialType : "",
        materialId: materialType ? materialType : "",
        vehicleId: ownerDetails[0]._id,
        vehicleBank: ownerDetails[0].accountId,
        ownerName: ownerDetails[0].ownerId.name,
        ownerId: ownerDetails[0].ownerId._id,
        ownerPhone: ownerDetails[0].ownerId.phoneNumber,
        narration: narration,
        amount: amount,
        modeOfPayment: modeOfPayment,
      });
      message.success("Successfully updated voucher details");
      this.props.parentFn();
      this.setState({loading: false});
    } catch (err) {
      console.log(err);
      message.error("Voucher Number already exist");
    }
  };

  onReset = () => {
    this.formRef.current.resetFields();
  };

  onChange = (e) => {
    setTimeout(() => {
      const {VehicleArray} = this.state;
      const filteredTableData = this.state.VehicleArray.filter(
        ({registrationNumber}) => registrationNumber === e
      );

      const ownerName = filteredTableData[0].ownerId.name;
      this.setState({ownerDetails: filteredTableData, ownerName});
    }, 100);
  };
  getMaterial = (e) => {
    try {
      const {materialdataDetails} = this.state;
      const filteredTableData = materialdataDetails.filter(
        ({_id}) => _id === e
      );
      this.setState({materialDetails: filteredTableData[0]});
    } catch (err) {
      console.log(err);
    }
  };
  render() {
    const {AllVehicleNumber, ownerName, materialDataSelect} = this.state;
    return (
      <div>
        <Spin spinning={this.state.loading} delay={500}>
          <Form
            ref={this.formRef}
            name="vehicleinfo"
            className="vehicleinfoForm"
            onFinish={this.onFinish}
            initialValues={{
              amount: 0,
            }}
            autoComplete="off"
          >
            <h2 style={{margin: "25px 30px 15px 30px", fontSize: "14px"}}>
              Voucher Details
            </h2>
            <Row style={{margin: "10px 22px"}}>
              <Col xs={24} sm={24} md={6} lg={6} xl={6}>
                <h3 style={{marginLeft: "5px"}}>Voucher Number</h3>
                <div>
                  <Form.Item name="voucherNumber">
                    <Input />
                  </Form.Item>
                </div>
              </Col>

              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Voucher Date
                </h3>
                <div>
                  <Form.Item
                    name="voucherDate"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <DatePicker
                      format={dateFormatList}
                      suffixIcon={<img src={dateicon} alt="dateIcon" />}
                    />
                  </Form.Item>
                </div>
              </Col>

              <Col xs={24} sm={24} md={5} lg={5} xl={5}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Truck Number
                </h3>
                <div>
                  <Form.Item
                    name="vehicleNumber"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Select
                      showSearch
                      placeholder="Select Vehicle Number"
                      optionFilterProp="children"
                      showArrow
                      onChange={this.onChange}
                      // onFocus={onFocus}
                      // onBlur={onBlur}
                      // onSearch={onSearch}
                      filterOption={(input, option) =>
                        option.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {AllVehicleNumber}
                    </Select>
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={8} lg={8} xl={8}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Owner Name
                </h3>
                <div>
                  {/* <Form.Item
                    name="ownerName"
                  > */}
                  <Input disabled value={ownerName} />
                  {/* </Form.Item> */}
                </div>
              </Col>
              <Col xs={24} sm={24} md={10} lg={10} xl={10}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Narration
                </h3>
                <div>
                  <Form.Item
                    name="narration"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Amount
                </h3>
                <div>
                  <Form.Item
                    name="amount"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input
                      onKeyPress={(event) => {
                        if (
                          numInput(event) &&
                          !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
                          event.key !== "Backspace"
                        ) {
                          event.preventDefault();
                        }
                      }}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Mode of Payment
                </h3>
                <div>
                  <Form.Item
                    name="modeOfPayment"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Select
                      placeholder="Select"
                      style={{width: "100%"}}
                      showArrow
                    >
                      <option value="Bank Transfer">Bank Transfer</option>
                      <option value="Cash">Cash</option>
                    </Select>
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}}>Material Type</h3>
                <div>
                  <Form.Item name="materialType">
                    <Select
                      placeholder="Select"
                      style={{width: "100%"}}
                      onChange={this.getMaterial}
                      showArrow
                    >
                      {materialDataSelect}
                    </Select>
                  </Form.Item>
                </div>
              </Col>
            </Row>

            <Row
              style={{
                border: "0.5px dashed #979797",
                opacity: "0.40",
                margin: "20px 0 20px 0px",
              }}
            />
            <Row>
              <Col lg={17}></Col>
              <Col lg={7}>
                <div
                  style={{
                    padding: "10px",
                    marginRight: "20px",
                    float: "right",
                  }}
                >
                  <Button className="savebtn" type="primary" htmlType="submit">
                    SAVE
                  </Button>
                </div>
                <div style={{padding: "10px", float: "right"}}>
                  <Button
                    type="primary"
                    className="cancelBtn"
                    onClick={() => {
                      document
                        .getElementsByClassName("ant-modal-close")[0]
                        .click();
                      this.onReset();
                    }}
                  >
                    CANCEL
                  </Button>
                </div>
              </Col>
            </Row>
          </Form>
        </Spin>
      </div>
    );
  }
}

export default Editvoucherbook;
