import React, {useState, useEffect} from "react";
import "antd/dist/antd.css";
import moment from "moment";
import {
  Table,
  Input,
  Form,
  Select,
  DatePicker,
  Button,
  message,
  Upload,
  Modal,
} from "antd";
import axios from "axios";
import {BASE_URL} from "../../../constants/constants";
import Editicon from "../../../assests/images/edit.svg";
import ViewDocument from "../../../assests/images/documentview.svg";
import PdfViewer from "../../../components/pdfviewer/pdfviewer";
import {CloseCircleOutlined, UploadOutlined} from "@ant-design/icons";
import EditReceiveAcknowledgement from "./editreceiveacknowledgement";
import auth from "../../../utils/auth";
import "./receiveregister.css";
const dateFormatList = ["DD/MM/YYYY"];
const userDetails = auth.getAuthToken().userDetails;
const NullData = (tableText) => <span>{tableText || <p>&#8212;</p>}</span>;
let grDateValueof = "";
let invoiceDateValueof = "";
let invoicefilelink = "";

function ongrDateChange(date, dateString) {
  grDateValueof = dateString;
}
function onInvoiceDateChange(date, dateString) {
  invoiceDateValueof = dateString;
}
const imageStyle = {
  height: "auto",
  maxWidth: "480px",
  minWidth: "200px",
  paddingBottom: 25,
};
const axiosFileUploadRequestInvoice = async (file) => {
  try {
    const formData = new FormData();
    formData.append("file", file.file);
    const config = {
      headers: {
        "content-type": "multipart/form-data",
      },
    };
    const response = await axios.post(`${BASE_URL}rc-upload`, formData, config);
    const {rcBookProof} = response.data;
    invoicefilelink = rcBookProof;
    message.success("File uploaded successfully ");
  } catch (err) {
    message.error("Network Failed, Please Try Again");
  }
};

const getGrDateField = (text, record) => {
  return (
    <Form.Item name="grDateMom">
      <DatePicker onChange={ongrDateChange} format={dateFormatList} />
    </Form.Item>
  );
};
const getInvoiceDateField = (text, record) => {
  return (
    <Form.Item name="invoiceDateMoM">
      <DatePicker onChange={onInvoiceDateChange} format={dateFormatList} />
    </Form.Item>
  );
};
const invoiceuploadFileFiled = (text, record) => {
  return (
    <Upload customRequest={axiosFileUploadRequestInvoice} fileList="">
      <Button style={{fontSize: "12px", height: "28px"}}>
        <UploadOutlined /> Upload
      </Button>
    </Upload>
  );
};

const getHTMLElement = (inputNode) => {
  switch (inputNode) {
    case "Grno":
      return (
        <Form.Item name="grNumber">
          <Input />
        </Form.Item>
      );
    case "vehicleNumber":
      return (
        <Form.Item name="vehicleNumber">
          <Input />
        </Form.Item>
      );
    case "deliveryNumber":
      return (
        <Form.Item name="deliveryNumber">
          <Input />
        </Form.Item>
      );
    case "rate":
      return (
        <Form.Item name="rate">
          <Input />
        </Form.Item>
      );
    case "totalExpense":
      return (
        <Form.Item name="totalExpense">
          <Input />
        </Form.Item>
      );
    case "commissionRate":
      return (
        <Form.Item name="commisionDate">
          <Input />
        </Form.Item>
      );
    case "commissionTotal":
      return (
        <Form.Item name="commisionTotal">
          <Input />
        </Form.Item>
      );
    case "diesel":
      return (
        <Form.Item name="diesel">
          <Input />
        </Form.Item>
      );
    case "cash":
      return (
        <Form.Item name="cash">
          <Input />
        </Form.Item>
      );
    case "bankTransfer":
      return (
        <Form.Item name="bankTransfer">
          <Input />
        </Form.Item>
      );
    case "shortage":
      return (
        <Form.Item name="shortage">
          <Input />
        </Form.Item>
      );
    case "text":
      return (
        <Form.Item name="quantityInMetricTons">
          <Input />
        </Form.Item>
      );
    case "grDate":
      return getGrDateField();

    case "invoiceuploadFile":
      return invoiceuploadFileFiled();
    case "dateinvoice":
      return getInvoiceDateField();
  }
};
const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = getHTMLElement(inputType);
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};
const EditableTable = (TableData, DataValue, FindFilter) => {
  const [form] = Form.useForm();
  const [data, setData] = useState(TableData.TableData);
  const [editingKey, setEditingKey] = useState("");
  const [invoiceDate, setInvoiceDate] = useState("");
  const [ShortageValue, setShortageValue] = useState("0");
  const [PostValue, setPostValue] = useState(false);
  const [InvoieFileUrl, setInvoiceFileUrl] = useState("");
  const [FileType, SetFileType] = useState("");
  const [PreviewFileUrl, SetPreviewFileUrl] = useState("");
  const [editableRecieveDetails, SeteditableRecieveDetails] = useState("");
  const [DoucmentVisible, SetDoucmentVisible] = useState(false);
  const [EditReciveACKVisible, SetEditReciveACKVisible] = useState(false);
  const isEditing = (record) => record._id === editingKey;
  const edit = (record) => {
    SetEditReciveACKVisible(true);
    SeteditableRecieveDetails(record);
    setEditingKey("");
  };
  const cancel = () => {
    setEditingKey("");
  };
  const gettableData = async () => {
    try {
      const response = await axios.get(`${BASE_URL}get-receive-register`);
      const dispatchData = response.data.disptachData || "";

      if (dispatchData && dispatchData.length > 0) {
        const dataSource = dispatchData.map((data, index) => {
          const date = data.grDate;
          var GrDate = moment(date, "DD-MM-YYYY");
          if (data.invoiceDate) {
            const date = data.invoiceDate;
            var InvoiceDate = moment(date, "DD-MM-YYYY");
          }
          return {
            ...data,
            slno: index + 1,
            commissionPercent: "@" + data.commisionRate + "%",
            grDateMom: GrDate,
            invoiceDateMoM: InvoiceDate,
          };
        });
        return dataSource;
      }
    } catch (err) {
      message.error("Error fetching data. Please try again later", 2);
    }
  };
  function viewDocments(e) {
    if (e.invoiceProof !== null && e.invoiceProof !== "") {
      let url = e.invoiceProof;
      let type = url ? url.split(".") : "";
      type = type[type.length - 1];
      SetDoucmentVisible(true);
      SetFileType(type);
      SetPreviewFileUrl(url);
    } else {
      message.error("No Invoice Available", [0]);
    }
  }
  useEffect(() => {
    if (
      TableData.FindFilter === "" ||
      TableData.FindFilter === undefined ||
      TableData.FindFilter === null
    ) {
      if (TableData) {
        if (TableData.TableData.length > 0) {
          if (!data && PostValue !== true) {
            setData(TableData.TableData);
          }
          if (data && data.length === 0) {
            setData(TableData.TableData);
          }
          if (PostValue === true && TableData.DataValue.length === 0) {
            gettableData().then(function (response) {
              setData(response);
            });
          }
          // if (PostValue === false && TableData.DataValue.length === 0) {
          //   gettableData().then(function (response) {
          //     setData(response);
          //   });
          // }
          // setPostValue(false);
        }
      }
    } else {
      setData(TableData.DataValue);
    }
  }, [data, DataValue, TableData, FindFilter, PostValue]);
  const save = async (key) => {
    try {
      const {
        _id,
        commisionRate,
        materialType,
        vehicleNumber,
        vehicleType,
        invoiceProof,
      } = key;
      const row = await form.validateFields();
      setEditingKey("");
      const {
        bankTransfer,
        cash,
        deliveryLocation,
        deliveryNumber,
        diesel,
        grDate,
        grNumber,
        invoiceDate,
        loadLocation,
        quantityInMetricTons,
        rate,
        shortage,
      } = row;

      const totalExpenses = quantityInMetricTons * rate;
      const commissionTotals = (totalExpenses * commisionRate) / 100;
      const balances =
        totalExpenses -
        commissionTotals -
        diesel -
        cash -
        bankTransfer -
        shortage;
      let GrDateValue = "";
      let InvoiceDateValue = "";
      if (grDateValueof === "") {
        GrDateValue = grDate;
      } else {
        GrDateValue = grDateValueof;
      }
      if (invoiceDateValueof === "") {
        InvoiceDateValue = invoiceDate;
      } else {
        InvoiceDateValue = invoiceDateValueof;
      }
      let inoviceLink = "";
      if (invoicefilelink === "") {
        inoviceLink = invoiceProof;
      } else {
        inoviceLink = invoicefilelink;
      }

      try {
        await axios.put(`${BASE_URL}update-dispatch-challan/${_id}`, {
          invoiceDate: InvoiceDateValue,
          balance: balances,
          materialType: materialType,
          grNumber: grNumber,
          deliveryLocation: deliveryLocation,
          bankTransfer: bankTransfer,
          cash: cash,
          deliveryNumber: deliveryNumber,
          diesel: diesel,
          loadLocation: loadLocation,
          quantityInMetricTons: quantityInMetricTons,
          rate: rate,
          vehicleNumber: vehicleNumber,
          grDate: GrDateValue,
          commisionRate: commisionRate,
          commisionTotal: commissionTotals,
          totalExpense: totalExpenses,
          shortage: shortage,
          vehicleType: vehicleType,
          invoiceProof: inoviceLink,
        });
        message.success("Successfully Updated Acknowledgment");
        gettableData().then(function (response) {
          setData(response);
        });
        grDateValueof = "";
        invoiceDateValueof = "";
        invoicefilelink = "";
      } catch (err) {
        console.log(err);
      }
    } catch (errInfo) {
      message.error("Enter Required Field");
    }
  };
  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {},
    onSelect: (record, selected, selectedRows) => {},
    onSelectAll: (selected, selectedRows, changeRows) => {},
  };

  function onChange(date, dateString) {
    setInvoiceDate(dateString);
  }
  function handlechange(e) {
    setShortageValue(e.target.value);
  }
  const post = async (key) => {
    const {_id, invoiceDate} = key;
    if (invoiceDate !== null) {
      try {
        await axios.put(`${BASE_URL}update-challan-status/${_id}/ACK`);
        message.success("Successfully Moved to Recieve Register");
        gettableData().then(function (response) {
          setData(response);
        });
        setPostValue(true);
      } catch (err) {
        console.log(err);
      }
    } else {
      message.error("Enter required field");
    }
  };
  const invoiceDateSave = async (key) => {
    const {
      _id,
      totalExpense,
      bankTransfer,
      cash,
      commisionRate,
      commisionTotal,
      deliveryLocation,
      deliveryNumber,
      diesel,
      grNumber,
      loadLocation,
      materialType,
      quantityInMetricTons,
      rate,
      shortage,
      vehicleNumber,
      grDate,
      vehicleType,
      invoiceProof,
      balance,
    } = key;
    const balances = balance - ShortageValue;
    var DateString = moment(invoiceDate).format("DD/MM/YYYY");
    var startDate = moment(DateString, "DD/MM/YYYY");

    var invoiceDates = startDate._i;
    if (invoiceDate === "") {
      message.error("Enter Invoice Date");
    } else {
      try {
        await axios.put(`${BASE_URL}update-dispatch-challan/${_id}`, {
          invoiceDate: invoiceDates,
          balance: balances,
          materialType: materialType,
          grNumber: grNumber,
          deliveryLocation: deliveryLocation,
          bankTransfer: bankTransfer,
          cash: cash,
          deliveryNumber: deliveryNumber,
          diesel: diesel,
          loadLocation: loadLocation,
          quantityInMetricTons: quantityInMetricTons,
          rate: rate,
          vehicleNumber: vehicleNumber,
          grDate: grDate,
          commisionRate: commisionRate,
          commisionTotal: commisionTotal,
          totalExpense: totalExpense,
          shortage: ShortageValue,
          vehicleType: vehicleType,
          invoiceProof: InvoieFileUrl,
        });
        setPostValue(true);
        gettableData().then(function (response) {
          setData(response);
        });
        message.success("successfully saved");
      } catch (err) {
        console.log(err);
      }
    }
  };
  const columns = [
    {
      title: "Sl No",
      dataIndex: "slno",
      key: "name",
      render: NullData,
      fixed: "left",
      width: "40px",
    },
    {
      title: "GR No",
      dataIndex: "grNumber",
      key: "grNumber",
      inputTypeValue: "Grno",
      width: "80px",
      render: (text, record) => (
        <span>{!record.grNumber ? <p>&#8212;</p> : record.grNumber}</span>
      ),
    },
    {
      title: "GR Date",
      dataIndex: "grDate",
      key: "grDate",
      inputTypeValue: "grDate",
      width: "80px",
    },
    {
      title: "Truck No",
      dataIndex: "vehicleNumber",
      key: "vehicleNumber",
      width: "100px",

      inputTypeValue: "vehicleNumber",
    },
    {
      title: "Invoice Date",
      dataIndex: "invoiceDate",
      key: "invoiceDate",
      width: "100px",
      sorter: (a, b) => `${a.invoiceDate}`.localeCompare(b.invoiceDate),
    },
    {
      title: "Invoice",
      dataIndex: "invoiceUpload",
      inputTypeValue: "invoiceuploadFile",
      key: "invoiceUpload",
      width: "100px",
      render: (text, record) => (
        <span>
          {!record.invoiceDate ? (
            <p>&#8212;</p>
          ) : (
            <span style={{textAlign: "left"}}>
              {record.invoiceProof ? (
                <a>
                  <div style={{display: "flex"}}>
                    <div style={{margin: "5px"}}>
                      {record.invoiceProof && (
                        <a
                          download={`Rc-${record.registrationNumber}`}
                          href={record.invoiceProof}
                          target="_blank"
                        >
                          <img src={ViewDocument} width="18px" />
                        </a>
                      )}
                    </div>
                    {/* {!record.invoiceProof && <p>&#8212;</p>} */}
                    {record.invoiceProof && (
                      <div
                        style={{margin: "5px", color: "#595959"}}
                        onClick={() => viewDocments(record)}
                      >
                        View
                      </div>
                    )}
                  </div>
                </a>
              ) : (
                <p>&#8212;</p>
              )}
            </span>
          )}
        </span>
      ),
    },
    {
      title: "From",
      dataIndex: "loadLocation",
      key: "loadLocation",
      inputTypeValue: "loadLocation",
      width: "80px",
    },
    {
      title: "Destination",
      dataIndex: "deliveryLocation",
      key: "deliveryLocation",
      inputTypeValue: "deliveryLocation",
      width: "80px",
    },
    {
      title: "Delivery No",
      dataIndex: "deliveryNumber",
      key: "deliveyNumber",
      render: NullData,
      width: "100px",
    },
    {
      title: "Qty (MT)",
      dataIndex: "quantityInMetricTons",
      key: "qunantityInMetricTons",
      width: "80px",
      render: (text, record) => (
        <span>
          {!record.quantityInMetricTons
            ? 0
            : record.quantityInMetricTons.toFixed(2)}
        </span>
      ),
    },
    {
      title: "Rate",
      dataIndex: "rate",
      key: "rate",
      width: "80px",
      render: (text, record) => (
        <span>{!record.rate ? 0 : record.rate.toFixed(2)}</span>
      ),
    },
    {
      title: "Total",
      dataIndex: "totalExpense",
      key: "total",
      width: "80px",
      render: (text, record) => (
        <span>{!record.totalExpense ? 0 : record.totalExpense.toFixed(2)}</span>
      ),
    },
    {
      title: "Commission",
      dataIndex: "commissionPercent",
      key: "commissionRate",
      inputTypeValue: "commissionRate",
      render: NullData,
      colSpan: 2,
      width: "60px",
    },
    {
      title: "Commission Total",
      dataIndex: "commisionTotal",
      key: "commissionTotal",
      inputTypeValue: "commissionTotal",
      width: "80px",
      render: (text, record) => (
        <span>
          {!record.commisionTotal ? 0 : record.commisionTotal.toFixed(2)}
        </span>
      ),
      colSpan: 0,
    },
    {
      title: "Diesel",
      dataIndex: "diesel",
      key: "diesel",
      width: "80px",
      render: (text, record) => (
        <span>{!record.diesel ? 0 : record.diesel.toFixed(2)}</span>
      ),
      inputTypeValue: "diesel",
    },
    {
      title: "Cash",
      dataIndex: "cash",
      key: "cash",
      width: "80px",
      render: (text, record) => (
        <span>{!record.cash ? 0 : record.cash.toFixed(2)}</span>
      ),
    },
    {
      title: "Bank Transfer",
      dataIndex: "bankTransfer",
      key: "bankTransfer",
      inputTypeValue: "bankTransfer",
      width: "100px",
      render: (text, record) => (
        <span>{!record.bankTransfer ? 0 : record.bankTransfer.toFixed(2)}</span>
      ),
    },
    {
      title: "Shortage",
      dataIndex: "shortage",
      key: "shortage",
      inputTypeValue: "shortage",
      width: "80px",
      editable: true,
      render: (text, record) => (
        <span>{!record.invoiceDate ? "0" : record.shortage.toFixed(2)}</span>
      ),
    },
    {
      title: "Balance",
      dataIndex: "balance",
      key: "balance",
      width: "100px",
      render: (text, record) => (
        <span>
          {record.balance > 0 ? (
            record.balance.toFixed(2)
          ) : (
            <span style={{color: "red"}}>{record.balance.toFixed(2)}</span>
          )}
        </span>
      ),
    },
    {
      title: "Action",
      dataIndex: "operation",
      fixed: "right",
      width: "80px",
      render: (_, record) => {
        const editable = isEditing(record);

        return editable ? (
          <div></div>
        ) : (
          <div style={{display: "flex"}}>
            <div style={{margin: "5px"}}>
              {userDetails && userDetails.roleName === "Admin" ? (
                <img
                  src={Editicon}
                  style={{cursor: "pointer"}}
                  disabled={editingKey !== ""}
                  onClick={() => edit(record)}
                  width="20px"
                />
              ) : (
                <img src={Editicon} style={{cursor: "pointer"}} width="20px" />
              )}
            </div>
          </div>
        );
      },
    },
  ];
  const deliveryDropDownMenu = (text, record) => {
    return <Input onChange={handlechange} />;
  };
  const axiosFileUploadRequest = async (file) => {
    try {
      const formData = new FormData();
      formData.append("file", file.file);
      const config = {
        headers: {
          "content-type": "multipart/form-data",
        },
      };
      const response = await axios.post(
        `${BASE_URL}rc-upload`,
        formData,
        config
      );
      const {rcBookProof} = response.data;
      setInvoiceFileUrl(rcBookProof);
      message.success("File uploaded successfully ");
    } catch (err) {
      message.error("Network Failed, Please Try Again");
    }
  };
  const getgrDate = (text, record) => {
    return <span>{record.grDateMom._i}</span>;
  };
  const InvoiceDateField = (text, record) => {
    return <DatePicker onChange={onChange} />;
  };
  const UploadInvoice = (text, reord) => {
    return (
      <Upload customRequest={axiosFileUploadRequest} fileList="">
        <Button
          style={{
            fontSize: "12px",
            height: "28px",
            borderRadius: "4px",
            padding: "0px 6px",
            color: "#646464",
          }}
        >
          <UploadOutlined style={{color: "#4ebddc"}} /> Upload
        </Button>
      </Upload>
    );
  };

  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }
    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: col.inputTypeValue,
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });
  const reloadAfterModalClose = () => {
    gettableData().then(function (response) {
      setData(response);
    });
    SetEditReciveACKVisible(false);
    setPostValue(true);
    setEditingKey("");
    SeteditableRecieveDetails("");
  };
  const editRecieveCancel = () => {
    SetEditReciveACKVisible(false);
    setEditingKey("");

    SeteditableRecieveDetails("");
  };
  return (
    <div>
      <Modal
        visible={DoucmentVisible}
        title={
          <div>
            View Document
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Invoice View
            </p>
          </div>
        }
        onCancel={() => SetDoucmentVisible(false)}
        className={`ant${FileType === "pdf" ? "PdfModal" : "ImageModal"}`}
        style={{width: "900px"}}
        footer={null}
        // okButtonProps={{style: {display: "none"}}}
      >
        {FileType === "pdf" && <PdfViewer url={PreviewFileUrl || ""} />}
        {FileType !== "pdf" && (
          <img src={PreviewFileUrl} alt="img" style={imageStyle} />
        )}
      </Modal>
      <Modal
        visible={EditReciveACKVisible}
        className="ModalLook"
        title={
          <div>
            Receive Register
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Update Register
            </p>
          </div>
        }
        onCancel={editRecieveCancel}
        footer={null}
        // okButtonProps={{style: {display: "none"}}}
      >
        {editableRecieveDetails ? (
          <EditReceiveAcknowledgement
            EditRecieveDetails={editableRecieveDetails}
            parentFn={() => reloadAfterModalClose()}
          />
        ) : null}
      </Modal>
      <Form form={form} component={false}>
        <Table
          components={{
            body: {
              cell: EditableCell,
            },
          }}
          bordered
          dataSource={data}
          columns={mergedColumns}
          rowClassName="editable-row"
          pagination={{
            onChange: cancel,
          }}
          scroll={{x: 1500}}
          // rowSelection={rowSelection}
          rowKey="_id"
        />
      </Form>
    </div>
  );
};
export default EditableTable;
