import React, {Component} from "react";
import {Menu, Avatar} from "antd";
import Onboarding from "../../../assests/images/onboarding.svg";
import Despatch from "../../../assests/images/depatchicon.svg";
import Receive from "../../../assests/images/receiveicon.svg";
import Accounting from "../../../assests/images/accountingicon.svg";
import Reports from "../../../assests/images/reporticon.svg";
import Settings from "../../../assests/images/settingicon.svg";
import UserImage from "../../../assests/images/user.jpeg";
import Styles from "./sidemenu.module.css";
import auth from "./../../../utils/auth";

class sidemenu extends Component {
  constructor(props, a) {
    super(props);
    // this.userDetails = auth.getAuthToken().userDetails;
    const Details = auth.getAuthToken().userDetails;

    this.state = {
      current: null,
      defaultValue: "1",
      userDetails: Details,
    };
  }
  componentWillMount() {
    if (this.props.history.location.pathname === "/dispatch-challan") {
      this.setState({
        defaultValue: "5",
      });
    }
    if (this.props.history.location.pathname === "/vehicle-onwer-details") {
      this.setState({
        defaultValue: "1",
      });
    }
    if (this.props.history.location.pathname === "/receive-acknowledgement") {
      this.setState({
        defaultValue: "6",
      });
    }
    if (this.props.history.location.pathname === "/receive-register") {
      this.setState({
        defaultValue: "6",
      });
    }
    if (
      [
        "/owner-advance",
        "/voucher-book",
        "/daily-cash-book",
        "/recovery-register",
      ].includes(this.props.history.location.pathname)
    ) {
      this.setState({
        defaultValue: "7",
      });
    }

    if (
      this.props.history.location.pathname === "/report" ||
      this.props.history.location.pathname === "/report-dashboard"
    ) {
      this.setState({
        defaultValue: "8",
      });
    }

    if (
      this.props.history.location.pathname === "/your-accout" ||
      this.props.history.location.pathname === "/user-onboarding"
    ) {
      this.setState({
        defaultValue: "9",
      });
    }
  }
  handleClick = (e) => {
    if (e.key === "1") {
      this.props.history.push("./vehicle-onwer-details");
    } else if (e.key === "5") {
      this.props.history.push("./dispatch-challan");
    } else if (e.key === "6") {
      this.props.history.push("./receive-acknowledgement");
    } else if (e.key === "7") {
      this.props.history.push("./owner-advance");
    } else if (e.key === "8") {
      if (this.state.userDetails.roleName == "Admin") {
        this.props.history.push("./report-dashboard");
      } else {
        this.props.history.push("./report");
      }
    } else if (e.key === "9") {
      this.props.history.push("./your-account");
    }
  };
  render() {
    // const {firstName: fn, name: nm} = this.userDetails;
    // const firstName = nm || fn || "";

    return (
      <Menu
        theme="dark"
        onClick={this.handleClick}
        defaultSelectedKeys={this.state.defaultValue}
        mode="inline"
        className="SideMenu"
      >
        <Menu.Item
          key="1"
          icon={
            <object
              data={Onboarding}
              type="image/svg+xml"
              className="svgicon"
            ></object>
          }
          style={{padding: 0}}
        >
          Onboarding
        </Menu.Item>
        <Menu.Item
          key="5"
          icon={
            <object
              data={Despatch}
              type="image/svg+xml"
              className="svgicon"
            ></object>
          }
        >
          Dispatch
        </Menu.Item>
        <Menu.Item
          key="6"
          icon={
            <object
              data={Receive}
              type="image/svg+xml"
              className="svgicon"
            ></object>
          }
        >
          Receive
        </Menu.Item>
        <Menu.Item
          key="7"
          icon={
            <object
              data={Accounting}
              type="image/svg+xml"
              className="svgicon"
            ></object>
          }
        >
          <div>Accounting</div>
        </Menu.Item>
        <Menu.Item
          key="8"
          icon={
            <object
              data={Reports}
              type="image/svg+xml"
              className="svgicon"
            ></object>
          }
        >
          Reports Dashboard
        </Menu.Item>
        <Menu.Item
          key="9"
          icon={
            <object
              data={Settings}
              type="image/svg+xml"
              className="svgicon"
            ></object>
          }
        >
          Settings
        </Menu.Item>
      </Menu>
    );
  }
}
export default sidemenu;
