import React, {Component} from "react";

const today = new Date();
const months = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "Jun",
  "Jul",
  "Aug",
  "Sep",
  "Oct",
  "Nov",
  "Dec",
];
const days = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
// var myVar = setInterval(myTimer, 1000);
var dateTime = "";
// function myTimer() {
//   var d = new Date();
// }
function getDateTime() {
  var now = new Date();
  var year = now.getFullYear();
  var month = now.getMonth() + 1;
  var day = now.getDate();
  var hour = (now.getHours() % 12 < 10 ? "0" : "") + (d.getHours() % 12);
  var minute = now.getMinutes();
  var second = now.getSeconds();
  if (month.toString().length == 1) {
    month = "0" + month;
  }
  if (day.toString().length == 1) {
    day = "0" + day;
  }
  if (hour.toString().length == 1) {
    hour = "0" + hour;
  }
  if (minute.toString().length == 1) {
    minute = "0" + minute;
  }
  if (second.toString().length == 1) {
    second = "0" + second;
  }
  // var AP = now.toLocaleString("en-US", {hour12: true});
  var dateTime =
    hour + ":" + minute + " " + `${now.getHours() > 12 ? "PM" : "AM"}`;
  return dateTime;
}

const todaydate = `${days[today.getDay()]} ${today.getDate()}, ${
  months[today.getMonth()]
} ${today.getFullYear()}`;
const d = new Date();
const h = (d.getHours() % 12 < 10 ? "0" : "") + (d.getHours() % 12);
const m = (d.getMinutes() < 10 ? "0" : "") + d.getMinutes();
const todaytime = `${h}:${m} ${d.getHours() > 12 ? "PM" : "AM"}`;
class CurrentTime extends Component {
  state = {
    CurrTime: "",
  };
  componentDidMount() {
    setInterval(function () {
      const currentTime = getDateTime();
      dateTime = currentTime;
    }, 1000);
    this.setState({CurrTime: dateTime});
  }

  render() {
    return (
      <p
        style={{
          marginTop: "10px",
          fontSize: "13px",
        }}
      >
        <span
          style={{
            fontFamily: "MyriadProBold",
          }}
        >
          {todaydate}
        </span>
        <br />
        <span style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
          {this.state.CurrTime ? this.state.CurrTime : todaytime}
          {/* {dateTime} */}
        </span>
      </p>
    );
  }
}
export default CurrentTime;
