import React, { Component } from "react";
import {
  Table,
  Button,
  Modal,
  message,
  Spin,
  Row,
  Select,
  DatePicker,
  Col,
} from "antd";
import moment from "moment";
import axios from "axios";
import { BASE_URL } from "../../constants/constants";
import PrintDownloadReport from "./printDownloadReport";
import DownloadIcon from "../../assests/images/download.svg";
import CurrentTime from "../../components/currenttime/currenttime";
import auth from "../../utils/auth";
import * as XLSX from "xlsx";
import "./report.css";
import PrintIcon from "../../assests/images/print.svg";
import dateicon from "../../assests/images/dateicon.svg";
import PrintAll from "./printdetails";
import ReportDashboard from "../../components/reportbreedcrumb/reportbreedcrumb";

const { RangePicker } = DatePicker;

const dateFormatList = ["DD/MM/YYYY"];

const truckType = [
  {
    label: "All",
    value: "",
  },
  {
    label: "Bag",
    value: "Bag",
  },
  {
    label: "Bulk",
    value: "Bulk",
  },
];
function sortByKey(array, key) {
  return array.sort((a, b) => a[key].localeCompare(b[key]));
}

// TODO: Remove this function as this
// is a simulation function for API
const wait = async (ms) =>
  new Promise((resolve) => {
    setTimeout(resolve, ms);
  });

// TODO: Actual API call for material type
const getMaterialTypeData = async () => {
  try {
    const response = await axios.get(`${BASE_URL}get-material`);
    const { materials: materialData } = response.data || "";
    const defaultOption = {
      label: "All",
      value: "",
    };

    if (materialData && materialData.length > 0) {
      const materialTypes = materialData.map(({ _id, materialType }) => {
        const val = materialType ? materialType.trimEnd() : null;
        return {
          label: val,
          value: val,
        };
      });
      return [defaultOption, ...materialTypes];
    }
    return null;
  } catch (err) {
    message.error("Network Failed, Please Try Again");
  }
};

const getOwnerDetailsData = async (ownerId, matType) => {
  try {
    let responseData = {};
    let queryParams = buildQueryParams(matType);
    const tripAggregateValues = new Promise(async (resolve, reject) => {
      try {
        if (matType !== "") {
          const response = await axios.get(
            `${BASE_URL}trip-register-aggregate-values/${ownerId}${queryParams}`
          );

          resolve(response);
        } else {
          const response = await axios.get(
            `${BASE_URL}trip-register-aggregate-values/${ownerId}`,
            {}
          );
          resolve(response);
        }
      } catch (e) {
        reject({ error: e });
      }
    });

    const getOwnerAdvanceData = new Promise(async (resolve, reject) => {
      try {
        if (matType !== "") {
          const response = await axios.get(
            `${BASE_URL}get-ledger-data-owner/${ownerId}${queryParams}`
          );
          resolve(response);
        } else {
          const response = await axios.get(
            `${BASE_URL}get-ledger-data-owner/${ownerId}`
          );
          resolve(response);
        }
      } catch (e) {
        reject({ error: e });
      }
    });
    const getOwnersVoucher = new Promise(async (resolve, reject) => {
      try {
        if (matType !== "") {
          const response = await axios.get(
            `${BASE_URL}get-owner-voucher/${ownerId}${queryParams}`
          );
          resolve(response);
        } else {
          const response = await axios.get(
            `${BASE_URL}get-owner-voucher/${ownerId}`
          );
          resolve(response);
        }
      } catch (e) {
        reject({ error: e });
      }
    });
    const getOwnerBankAccounts = new Promise(async (resolve, reject) => {
      try {
        const response = await axios.get(
          `${BASE_URL}get-owner-accounts/${ownerId}`
        );
        resolve(response);
      } catch (e) {
        reject({ error: e });
      }
    });

    await Promise.allSettled([
      tripAggregateValues,
      getOwnerAdvanceData,
      getOwnersVoucher,
      getOwnerBankAccounts,
    ]).then((response) => {
      const [
        challanData,
        ownersAdvance,
        ownersVoucher,
        ownersBankAccount,
      ] = response;

      responseData = {
        tripRegister: {
          value:
            challanData.value &&
            challanData.value.data &&
            sortByKey(challanData.value.data.tripAggregates, "invoiceISODate"),
          error: challanData.reason && challanData.reason.error,
        },
        ownersAdvance: {
          value:
            ownersAdvance.value &&
            ownersAdvance.value.data &&
            ownersAdvance.value.data.ownersAdavance,
          error: ownersAdvance.reason && ownersAdvance.reason.error,
        },
        advanceVoucher: {
          value:
            ownersVoucher.value &&
            ownersVoucher.value.data &&
            ownersVoucher.value.data.voucher,
          error: ownersVoucher.reason && ownersVoucher.reason.error,
        },
        bankDetails: {
          value:
            ownersBankAccount.value &&
            ownersBankAccount.value.data &&
            ownersBankAccount.value.data.bankDetails,
          error: ownersBankAccount.reason && ownersBankAccount.reason.error,
        },
      };
    });
    return responseData;
  } catch (err) {
    message.error("Network Failed, Please Try Again");
  }
};

// TODO: Actual API call for truck type
const getTruckTypeData = async () => {
  await wait(300);
  return truckType;
};

const buildQueryParams = (params) => {
  let queryParams = [];
  for (const param in params) {
    if (params[param]) {
      queryParams.push(`${param}=${params[param]}`);
    }
  }
  return queryParams.length ? `?${queryParams.join("&")}` : "";
};

const getSearchResult = async (params, page, limit) => {
  const pages = page ? page : 1;

  const limitData = limit ? limit : 10;
  let queryParams = buildQueryParams(params.params) || null;

  const response = queryParams
    ? await axios.get(
        `${BASE_URL}report-table-data${queryParams}&page=${pages}&limit=${limitData}`
      )
    : await axios.get(
        `${BASE_URL}report-table-data?page=${pages}&limit=${limitData}`
      );

  if (response.data.tripAggregates.length > 0) {
    const countData = response.data.tripAggregates[0].count || 0;
    const data =
      (Object.keys(response.data).length > 0 &&
        response.data.tripAggregates.length > 0 &&
        response.data.tripAggregates[0].data.map((trip, index) => ({
          key: trip._id,
          slno: ` ${index + 1}`,
          ownerName: trip.ownerDetails[0] && trip.ownerDetails[0].ownerName,
          ownerPhone: trip.ownerDetails[0] && trip.ownerDetails[0].ownerPhone,
          numberOfTrips: trip.noOfTrips,
          shortage: (trip.totalShortage && trip.totalShortage.toFixed(2)) || (
            <span>{<p>&#8212;</p>}</span>
          ),
          bankTransfer:
            (trip.totalBankTransfer && trip.totalBankTransfer.toFixed(2)) ||
            "0.00",
          cash: (trip.totalCash && trip.totalCash.toFixed(2)) || "0.00",
          diesel: (trip.totalDiesel && trip.totalDiesel.toFixed(2)) || "0.00",
          commission:
            (trip.totalCommisionTotal && trip.totalCommisionTotal.toFixed(2)) ||
            "0.00",
          amount: (trip.totalAmount && trip.totalAmount.toFixed(2)) || "0.00",
          totalCount: countData || 0,
          quantity:
            (trip.totalQuantity && trip.totalQuantity.toFixed(2)) || "0.00",
        }))) ||
      [];
    data.sort((a, b) =>
      a.ownerName > b.ownerName ? 1 : b.ownerName > a.ownerName ? -1 : 0
    );

    return data;
  } else {
    const data = response.data.tripAggregates;

    return data;
  }
};

class Reportresult extends Component {
  constructor(props) {
    super(props);
    const Det = auth.getUserData();
    const Details = auth.getAuthToken().userDetails;
    this.state = {
      materialType: [],
      truckType: [],
      filters: {
        material: null,
        vehicle: null,
        startDate: null,
        endDate: null,
      },
      insidefilters: {
        material: null,
        vehicle: null,
      },
      isMaterialTypeLoading: false,
      isTruckTypeLoading: false,
      searchResult: [],
      isSearching: false,
      data: [],
      vidible: false,
      printreport: false,
      ownersData: {},
      selectRowArray: [],
      TypeofMaterial: "",
      loadingModal: false,
      logId: Det._id,
      userDetails: Details,
      page: 1,
      limit: 10,
      totalCount: "",
    };
  }

  async showmodals(e) {
    this.setState({
      printreport: true,
      ownerDetails: e,
    });
  }
  doSomethingBeforeUnload = async () => {
    await axios.put(`${BASE_URL}logout/${this.state.logId}`);
  };
  setupBeforeUnloadListener = async () => {
    window.addEventListener("beforeunload", async (ev) => {
      ev.preventDefault();
      return this.doSomethingBeforeUnload();
    });
  };
  async componentDidMount() {
    this.setState({
      isMaterialTypeLoading: true,
    });
    const data = await getMaterialTypeData();
    this.setState({
      materialType: data,
      isMaterialTypeLoading: false,
    });
    this.setupBeforeUnloadListener();
  }

  async handleMaterialChange(value) {
    const filters = { ...this.state.filters };
    const insidefilters = { ...this.state.insidefilters };
    if (value != "") {
      filters.material = "(" + value + ")";
      insidefilters.material = "(" + value + ")";
    } else {
      filters.material = value;
      insidefilters.material = value;
    }

    this.setState({
      isTruckTypeLoading: true,
      filters,
      insidefilters,
    });

    const res = await getTruckTypeData();
    this.setState({
      truckType: res,
      isTruckTypeLoading: false,
    });
  }

  handleTruckChange(value) {
    const filters = { ...this.state.filters };
    const insidefilters = { ...this.state.insidefilters };
    filters.vehicle = value;
    insidefilters.vehicle = value;
    this.setState({ filters, insidefilters });
  }

  // getOwnerDetails = async() => {

  // }

  async onSearchClick() {
    const { filters } = this.state;
    // if (filters) {
    //   if (
    //     filters.material === "" ||
    //     filters.vehicle === "" ||
    //     filters.startDate === ""
    //   ) {
    //     message.error("Apply Filters");
    //   } else {
    this.setState({
      isSearching: true,
    });
    const data = await getSearchResult({
      params: {
        ...this.state.filters,
      },
    });

    if (!data.length > 0) {
      message.error("Data Not Found");
    }

    this.setState({
      isSearching: false,
      data,
      totalCount: data.length > 0 ? data[0].totalCount || 0 : 0,
      page: 1,
      limit: 10,
    });
    //   }
    // }
  }
  changePagination = async (e) => {
    try {
      this.setState({ loadingModal: true, page: e });

      const { filters } = this.state;

      const data = await getSearchResult(
        {
          params: {
            ...this.state.filters,
          },
        },
        e,
        this.state.limit
      );

      this.setState({ loadingModal: false, data });
    } catch (err) {
      // console.log(err);
    }
  };

  changePaginationAll = async (currentpage, pageSize) => {
    try {
      this.setState({ loadingModal: true, page: currentpage, limit: pageSize });

      const data = await getSearchResult(
        {
          params: {
            ...this.state.filters,
          },
        },
        currentpage,
        pageSize
      );

      this.setState({ loadingModal: false, data });
      // this.setState({loading: false});
    } catch (err) {
      // console.log(err);
    }
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };
  handleCancelEdit = () => {
    this.setState({
      printreport: false,
      visible: false,
    });
  };

  handleLoadOwnerReport = async (row) => {
    await this.setState({
      visible: true,
      selectedOwnwer: row,
      loadingModal: true,
    });
    const insideParams = { ...this.state.filters };
    let ownersData = await getOwnerDetailsData(row.key, insideParams);
    await this.setState({ ownersData: ownersData, loadingModal: false });
  };

  handleDateChange = (dates = ["", ""]) => {
    const filters = { ...this.state.filters };

    filters.startDate =
      (Array.isArray(dates) && new Date(dates[0]).getTime()) || "";
    // console.log(dates[0]);
    // (Array.isArray(dates) &&
    //   moment(new Date(dates[0])).format("DD/MM/YYYY")) ||
    // "";
    filters.endDate =
      (Array.isArray(dates) && dates[1] && new Date(dates[1]).getTime()) || "";
    this.setState({
      filters,
    });
  };

  onSelectChange = (selectedRowKeys, selectedRows) => {
    this.setState({ selectedRowKeys });
    this.setState({
      selectRowArray: selectedRows,
    });
  };
  onDownload = () => {
    const selectedData = this.state.selectRowArray.map((d, index) => {
      return {
        "S.No": index + 1,
        "Owner Name": d.ownerName || "-",
        "Number of Trips": d.numberOfTrips || "-",
        Quantity: d.quantity || "-",
        Amount: d.amount || "0",
        Commission: d.commission || "-",
        Diesel: d.diesel || "0",
        Cash: d.cash || "0",
        "Bank Transfer": d.bankTransfer || "0",
        Shotage: d.shortage || "0",
      };
    });
    if (selectedData.length === 0) {
      message.error("Please select User to download", [0]);
    } else {
      const ws = XLSX.utils.json_to_sheet(selectedData);
      const wb = {
        Sheets: { reports: ws },
        SheetNames: ["reports"],
      };
      const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" });
      const data = new Blob([excelBuffer], {
        type:
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8",
      });
      var url = window.URL.createObjectURL(data);
      window.open(url, "_blank");
    }
  };

  disableSearch = () => {
    const { material, vehicle } = this.state.filters;
    return !((material || material === "") && (vehicle || vehicle === ""));
  };

  render() {
    const { selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
      hideDefaultSelections: true,
      columnWidth: 10,
    };
    const columns = [
      {
        title: "Sl No",
        dataIndex: "slno",
        key: "name",
        render: (text, record, index) =>
          (this.state.page - 1) * this.state.limit + index + 1,
        // (
        //   <span>{this.state.data.indexOf(record) + 1}</span>
        // ),
      },
      {
        title: "Owner Name",
        dataIndex: "ownerName",
        key: "ownerName",
        align: "left",
        render: (text, row, index) => {
          {
            return (
              <a onClick={() => this.handleLoadOwnerReport(row)}>{text}</a>
            );
          }
        },
      },
      {
        title: "Number of Trips",
        dataIndex: "numberOfTrips",
        key: "numberOfTrips",
      },
      {
        title: "Quantity",
        dataIndex: "quantity",
        key: "quantity",
      },
      {
        title: "Amount",
        dataIndex: "amount",
        key: "amount",
      },
      {
        title: "Commission",
        dataIndex: "commission",
        key: "commission",
      },
      {
        title: "Diesel",
        dataIndex: "diesel",
        key: "diesel",
      },
      {
        title: "Cash",
        dataIndex: "cash",
        key: "cash",
      },
      {
        title: "Bank Transfer",
        dataIndex: "bankTransfer",
        key: "bankTransfer",
        align: "left",
      },
      {
        title: "Shortage",
        dataIndex: "shortage",
        key: "shortage",
      },
    ];

    const {
      materialType,
      isMaterialTypeLoading,
      truckType,
      isTruckTypeLoading,
      isSearching,
      data,
      selectedOwnwer,
      ownersData,
      loadingModal,
    } = this.state;
    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{ marginTop: "4px" }}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Report Dashboard
            </h3>
            <p style={{ fontSize: "12px", fontFamily: "MyriadProBold" }}>
              View All Repots
            </p>
          </div>
          <div style={{ position: "absolute", right: 40 }}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <ReportDashboard
            path="home"
            history={this.props.history}
            userData={this.state.userDetails}
          />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <div className="report">
            <Modal
              title={
                <div>
                  <h3 style={{ fontSize: "20px", margin: "0px" }}>
                    {(selectedOwnwer &&
                      selectedOwnwer.ownerName &&
                      `${selectedOwnwer.ownerName}'s`) ||
                      ""}{" "}
                    Performance Report
                  </h3>
                  <p
                    style={{
                      fontSize: "12px",
                      fontFamily: "MyriadProBold",
                    }}
                  >
                    Print and Download the Report
                  </p>
                </div>
              }
              className="ModalLook reportModal"
              visible={this.state.visible}
              onOk={this.handleOk}
              onCancel={this.handleCancelEdit}
              destroyOnClose
              footer={null}
            >
              <Spin spinning={this.state.loadingModal}>
                <PrintDownloadReport
                  ownerDetails={selectedOwnwer}
                  dataSource={ownersData}
                />
              </Spin>
            </Modal>
            <div style={{ padding: "0px 0px 0px 20px" }}>
              <Row>
                {/* <Col
                  xs={12}
                  sm={12}
                  md={12}
                  lg={12}
                  xl={12}
                  style={{padding: "10px", marginTop: "10px"}}
                >
                  <h3 className="reporttitle">Apply Filters</h3>
                </Col> */}
                <Col
                  xs={12}
                  sm={12}
                  md={3}
                  lg={3}
                  xl={3}
                  style={{ padding: "10px" }}
                >
                  <h3 className="filterLabel">Material Type</h3>
                  <div>
                    <Select
                      placeholder="Select"
                      style={{
                        width: "100%",
                        overflowY: "auto tabindex=0",
                      }}
                      onChange={(e) => this.handleMaterialChange(e)}
                      loading={isMaterialTypeLoading}
                      options={materialType}
                      disabled={
                        isMaterialTypeLoading ||
                        (materialType && materialType.length === 0)
                      }
                    />
                  </div>
                </Col>

                <Col
                  xs={12}
                  sm={12}
                  md={3}
                  lg={3}
                  xl={3}
                  style={{ padding: "10px" }}
                >
                  <h3 className="filterLabel">Truck Type</h3>
                  <div>
                    <Select
                      placeholder="Select"
                      style={{
                        width: "100%",
                        overflowY: "auto tabindex=0",
                      }}
                      onChange={(e) => this.handleTruckChange(e)}
                      options={truckType}
                      disabled={isTruckTypeLoading || truckType.length === 0}
                    />
                  </div>
                </Col>
                <Col
                  xs={12}
                  sm={12}
                  md={5}
                  lg={5}
                  xl={5}
                  style={{ padding: "10px" }}
                >
                  <h3
                    className="filterLabel"
                    style={{
                      borderRadius: "10px",
                    }}
                  >
                    Date Range
                  </h3>
                  <div style={{ display: "flex" }}>
                    <RangePicker
                      onChange={(dates) => this.handleDateChange(dates)}
                      format={dateFormatList}
                      allowEmpty={[true, true]}
                      suffixIcon={<img src={dateicon} alt="dateIcon" />}
                    />
                  </div>
                </Col>
                <Col
                  xs={12}
                  sm={12}
                  md={4}
                  lg={4}
                  xl={4}
                  style={{ padding: "24px 10px 0px" }}
                >
                  <div style={{ padding: "10px" }}>
                    <Button
                      type="primary"
                      onClick={() => this.onSearchClick()}
                      disabled={this.disableSearch()}
                    >
                      SEARCH
                    </Button>
                  </div>
                </Col>
                <Col
                  xs={12}
                  sm={12}
                  md={9}
                  lg={9}
                  xl={9}
                  style={{ padding: "10px", marginTop: "15px" }}
                >
                  <div style={{ display: "flex", float: "right" }}>
                    <div style={{ padding: "5px" }}>
                      <Button
                        className="outlineBtn"
                        onClick={this.onDownload}
                        icon={
                          <img
                            src={DownloadIcon}
                            style={{ paddingRight: "7px" }}
                          />
                        }
                      >
                        Download
                      </Button>
                    </div>
                    <div style={{ padding: "5px" }}>
                      <PrintAll Data={this.state.selectRowArray} />
                    </div>
                  </div>
                </Col>
              </Row>
            </div>

            <div className="reportcontainer">
              <Row></Row>
              <Row
                style={{
                  border: "0.5px dashed #979797",
                  opacity: "0.40",
                }}
              />
            </div>
            <Spin spinning={this.state.loadingModal}>
              <div className="reportdetails">
                {isSearching ? (
                  <div className="loader">
                    <Spin />
                  </div>
                ) : (
                  // data.length === 0 || (
                  <Table
                    className="reportTable"
                    columns={columns}
                    rowKey="key"
                    dataSource={data}
                    rowSelection={rowSelection}
                    pagination={{
                      current: this.state.page,
                      defaultPageSize: 10,
                      showSizeChanger: true,
                      onShowSizeChange: this.changePaginationAll,
                      total: this.state.totalCount,
                      onChange: this.changePagination,
                    }}
                  />
                  // )
                )}
              </div>
            </Spin>
          </div>
        </div>
      </div>
    );
  }
}

export default Reportresult;
