/* eslint-disable no-unused-vars */
import React, {Component} from "react";
import {Row, Col, Form, Input, Button, Alert, message, Spin, Modal} from "antd";
import BgImage from "../../assests/images/truklink-b-img.png";
import Logo from "../../assests/images/trucklink_logo.png";
import auth from "../../utils/auth";
import axios from "axios";
import {BASE_URL} from "../../constants/constants";
import "./reset.css";

class Reset extends Component {
  constructor(props) {
    super(props);
    const url = this.props.history.location.search;
    const loc = url ? url.split("key=") : null;

    this.state = {
      msg: "",
      visible: true,
      loading: false,
      mvisible: false,
      location: loc,
    };
  }
  onFinish = async (values) => {
    const {password, confirmPassword} = values;
    const {location} = this.state;
    try {
      if (location && location.length > 1) {
        const token = location[1];
        await axios
          .put(`${BASE_URL}reset-password`, {
            resetPasswordToken: token,
            password,
          })
          .then((data) => {
            message.success("successfully Reset Password");
            this.props.parentFn();
            setTimeout(() => {
              // utils.removeToken();
              window.location.replace("/");
            }, 200);
          })
          .catch((error) => {
            const {response} = error;
            const {data} = response;
            const {message: msg} = data;
            message.error(msg);
          });
      }
    } catch (error) {
      console.log(error);
    }
  };

  componentDidMount() {
    auth.removeToken();
  }
  login = () => {
    window.location.replace("/");
  };
  render() {
    return (
      <div className="loginarea">
        <Spin spinning={this.state.loading}>
          <Row>
            <Col xs={24} sm={24} md={24} lg={12}>
              <div className="leftloginarea">
                <div className="loginbg">
                  <div>
                    <img src={BgImage} style={{width: "260px"}} alt="bgimage" />
                  </div>
                  <div className="leftitlearea">
                    <h1 className="headdesc">Welcome to</h1>
                    <p className="headtitle ">TruckLink</p>
                  </div>
                </div>
                <div className="loginfooter">support@trucklink.in</div>
              </div>
            </Col>
            <Col xs={24} sm={24} md={24} lg={12} className="RightLogin">
              <div className="Rightloginarea">
                <div className="logo">
                  <img src={Logo} alt="logo" />
                </div>
                <div className="rightlogintxtarea">
                  <h1 className="rightlogintitle">Please Enter Password</h1>
                  <p>Reset Password to continue to your account</p>
                </div>
                <div className="loginForm">
                  {/* <div className="errorform">
                    {this.state.msg ? (
                      <Alert
                        message={this.state.msg || ""}
                        type="error"
                      />
                    ) : null}
                  </div> */}
                  <Form
                    name="basic"
                    initialValues={{remember: true}}
                    onFinish={this.onFinish}
                  >
                    <Form.Item
                      name="password"
                      place
                      rules={[
                        {
                          required: true,
                          message: "Please input password!",
                        },
                      ]}
                    >
                      <Input.Password
                        placeholder="Enter Password"
                        className="loginuserfield"
                      />
                    </Form.Item>

                    <Form.Item
                      name="confirmPassword"
                      dependencies={["password"]}
                      hasFeedback
                      rules={[
                        {
                          required: true,
                          message: "Please confirm your password!",
                        },
                        ({getFieldValue}) => ({
                          validator(rule, value) {
                            if (!value || getFieldValue("password") === value) {
                              return Promise.resolve();
                            }

                            return Promise.reject(
                              "The two passwords that you entered do not match!"
                            );
                          },
                        }),
                      ]}
                      hasFeedback
                    >
                      <Input.Password
                        placeholder="Enter Password"
                        className="loginuserfield"
                      />
                    </Form.Item>
                    <Form.Item>
                      <Button
                        type="primary"
                        htmlType="submit"
                        className="loginbtn"
                      >
                        Reset
                      </Button>
                    </Form.Item>
                  </Form>
                  <a onClick={this.login}>Login</a>
                  <div className="rightloginfooter">
                    Privacy Policy | Terms of use
                  </div>
                  <div className="rightfooter">
                    <p>
                      Privacy Policy | Terms of use
                      <br />
                      support@trucklink.in
                    </p>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Spin>
      </div>
    );
  }
}

export default Reset;
