import React, {Component} from "react";
import {Row, Col, Menu} from "antd";
import "./receivemenubreedcrumb.css";

class MenuBreedCrumb extends Component {
  constructor(props, a) {
    super(props);
    this.state = {
      current: null,
    };
  }

  handleConsigmentClick = (e) => {
    this.setState({
      current: e.key,
      activeItem: 1,
      activeState: false,
    });
  };

  acknowledgement = () => {
    this.props.history.push("/receive-acknowledgement");
  };

  receiveregister = () => {
    this.props.history.push("/receive-register");
  };

  render() {
    const url = this.props.history.location.pathname;
    return (
      <div>
        <Row className="consignmentList">
          <Col span={20}>
            <Menu
              onClick={this.handleConsigmentClick}
              selectedKeys={[this.state.current]}
              mode="horizontal"
            >
              <Menu.Item
                key="ownermaster"
                className={
                  url === "/receive-acknowledgement"
                    ? "consigmentActiveItem"
                    : "consigmentItem"
                }
                onClick={this.acknowledgement}
              >
                Acknowledgement
              </Menu.Item>
              <Menu.Item
                key="truckmaster"
                className={
                  url === "/receive-register"
                    ? "consigmentActiveItem"
                    : "consigmentItem"
                }
                onClick={this.receiveregister}
              >
                Receive Register
              </Menu.Item>
            </Menu>
          </Col>
        </Row>
      </div>
    );
  }
}

export default MenuBreedCrumb;
