/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from "react";
import {
  Table,
  Input,
  Button,
  Modal,
  Popconfirm,
  Checkbox,
  message,
  Spin,
  Row,
  Col,
  Tooltip,
} from "antd";
import {RightOutlined, DownOutlined, RetweetOutlined} from "@ant-design/icons";
import VehicleInformation from "./vehicleinformation/vehicleinformation";
import EditVehicleInformation from "./vehicleinformation/edit-vehicle-info";
import OwnerTransfer from "./vehicleinformation/owner-transfer";
import HeaderBreedCrumb from "../../../components/menubreedcrumb/menubreedcrumb";
import CurrentTime from "../../../components/currenttime/currenttime";
import axios from "axios";
import {BASE_URL} from "../../../constants/constants";
import Onboarding from "../../../assests/images/onboarding.svg";
import Editicon from "../../../assests/images/edit.svg";
import Deleteicon from "../../../assests/images/delete.svg";
import ViewDocument from "../../../assests/images/documentview.svg";
import "./onboarding.css";
import auth from "../../../utils/auth";
import PdfViewer from "../../../components/pdfviewer/pdfviewer";
const {Search} = Input;

const imageStyle = {
  height: "auto",
  maxWidth: "480px",
  minWidth: "200px",
  paddingBottom: 25,
};

const iconStyle = {
  color: "#01a6d0",
  fontSize: "14px",
  width: "24px",
  height: "24px",
};
const NullData = (tableText) => <span>{tableText || <p>&#8212;</p>}</span>;
const AccountMasking = (text, reord) => {
  const length = text.toString().length;
  const res = text.substr(-4);
  const val = length - 4;
  const ress = text.substring(0, val);
  const value = ress + "XXXX";
  return <p>{value}</p>;
};

let timeout = null;
const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    // console.log(
    //   `selectedRowKeys: ${selectedRowKeys}`,
    //   "selectedRows: ",
    //   selectedRows
    // );
  },
  columnWidth: "10px",
  getCheckboxProps: (record) => ({
    disabled: record.name === "Disabled User",
    // Column configuration not to be checked
    name: record.name,
  }),
};
class TruckMaster extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const Det = auth.getUserData();
    this.state = {
      vidible: false,
      loading: false,
      doucmentvisible: false,
      editableVehicleDetails: null,
      fileurl: "",
      searchValue: "",
      searchResultArray: "",
      editvisible: false,
      selectionType: Checkbox,
      expandedRowKeys: "null",
      rowExpandable: false,
      rowId: "",
      dataSource: [],
      fileType: "",
      previewUrl: "",
      userDetails: Details,
      logId: Det._id,
      page:1,
      limit:50,
      totalCount:""
    };
  }
  customExpandIcon(props) {
    if (props.expanded) {
      return (
        <a
          style={{color: "black"}}
          onClick={(e) => {
            props.onExpand(props.record, e);
          }}
        >
          <DownOutlined style={iconStyle} className="customIcon" />
        </a>
      );
    }
    return (
      <a
        style={{color: "black"}}
        onClick={(e) => {
          props.onExpand(props.record, e);
        }}
      >
        <RightOutlined style={iconStyle} className="customIcon" />
      </a>
    );
  }
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleOkowner = (e) => {
    this.setState({
      editownertvisible: false,
    });
  };

  handleCancelowner = (e) => {
    this.setState({
      editownertvisible: false,
    });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancelEdit = () => {
    this.setState({
      editvisible: false,
    });
  };

  handledocumentCancel = () => {
    this.setState({
      doucmentvisible: false,
    });
  };

  gettableData = async (searchValue,page,limit) => {
    try {
      const pages = page ? page : 1;
      const limitData = limit ? limit : this.state.limit;
      const searchData = searchValue ? searchValue : null;

      this.setState({loading: true});
      const response = searchData ?  await axios.get(`${BASE_URL}get-vehicle-details?searchVehicleNumber=${searchData}&page=${pages}&limit=${limitData}`)
      : await axios.get(`${BASE_URL}get-vehicle-details?page=${pages}&limit=${limitData}`);  

      const truck = response.data.truck[0].data || "";
      const totalCount = response.data.truck[0].count || "";
      const newData = [];
      if (truck && truck.length > 0) {
        truck.forEach((item, index) => {
          
          newData.push({
            ...item,
            phoneNumber: item.ownerId[0] ? item.ownerId[0].phoneNumber : null,
            key: item._id,
          });
        });
        this.setState({
          datasource: newData,
          totalCount: totalCount,
          loading: false,
        });
        return newData;
      }
     
      // const searchValue = this.state.searchValue;
      // if (
      //   searchValue !== "" &&
      //   searchValue !== null &&
      //   searchValue !== undefined
      // ) {
      //   const searchVal = searchValue.toString().toUpperCase().trim();

      //   const newSearchedTableData = this.state.datasource.filter(
      //     ({registrationNumber}) => {
      //       const vehno = registrationNumber || "";
      //       return vehno.toString().toUpperCase().includes(searchVal);
      //     }
      //   );
      //   this.setState({searchResultArray: newSearchedTableData});
      // } else {
      //   this.setState({
      //     searchResultArray: [],
      //     printableArray: [],
      //     selectedRowKeys: [],
      //   });
      // }
    } catch (err) {
      // console.log(err);
      this.setState({loading:false})
    }
  };

  changePagination = async (e) => {
    try {
      this.setState({loading: true, page: e});

      if(this.state.searchValue){
        const data = await this.gettableData(this.state.searchValue,e);
        this.setState({searchResultArray :data})
        }


      this.gettableData(this.state.searchValue,e);

      // this.setState({loading: false});
    } catch (err) {
      console.log(err);
    }
  };

  changePaginationAll = async(currentpage,pageSize) =>{
    try{
      
      this.setState({loading: true, page: currentpage,limit: pageSize });

      if(this.state.searchValue){
        const data = await this.gettableData(this.state.searchValue,currentpage,pageSize);
        this.setState({searchResultArray:data})
        }


      this.gettableData(this.state.searchValue,currentpage,pageSize);

      // this.setState({loading: false});
    }catch(err){
      console.log(err)
    }
  }

  doSomethingBeforeUnload = async () => {
    await axios.put(`${BASE_URL}logout/${this.state.logId}`);
  };
  setupBeforeUnloadListener = async () => {
    
    window.addEventListener("beforeunload", async (ev) => {
      ev.preventDefault();
      return this.doSomethingBeforeUnload();
    });
  };

  componentDidMount() {
    this.gettableData();
    this.setupBeforeUnloadListener();
  }

  showmodals(e) {
    this.setState({
      editvisible: true,
      editableVehicleDetails: e,
    });
  }

  showownermodals(e) {
    this.setState({
      editownertvisible: true,
      editableVehicleDetails: e,
    });
  }
  viewDocments(e) {
    if (e.rcBookProof != null) {
      let url = e.rcBookProof;
      let type = url ? url.split(".") : "";
      type = type[type.length - 1];
      this.setState({
        doucmentvisible: true,
        fileType: type,
        previewUrl: url,
      });
    } else {
      message.error("No rc book Available", [0]);
    }
  }

  deleterecord(e) {}

  confirm = async (e) => {
     
    try {
      
      await axios.delete(
        `${BASE_URL}delete-vehicle-details/${e._id}/${
          e.ownerId ? e.ownerId[0]._id : null
        }`
      );
      if(this.state.searchValue){
          this.searchResult(this.state.searchValue)
      }
      this.gettableData();
      message.success("Successfully deleted vehicle");
    } catch (err) {
      message.error("Unable to delete");
    }
  };

  searchResult = async(e) => {
    // clearTimeout(timeout);
    let searchValue = e;
    // timeout = setTimeout(() => {
    //   this.setState({searchValue});
    //   if (
    //     searchValue !== "" &&
    //     searchValue !== null &&
    //     searchValue !== undefined
    //   ) {
    //     searchValue = searchValue.toString().toUpperCase().trim();

    //     const newSearchedTableData = this.state.datasource.filter(
    //       ({registrationNumber}) => {
    //         const vehno = registrationNumber || "";
    //         return vehno.toString().toUpperCase().includes(searchValue);
    //       }
    //     );
        
    //     this.setState({searchResultArray: newSearchedTableData});
    //   } else {
    //     this.setState({
    //       searchResultArray: [],
    //       printableArray: [],
    //       selectedRowKeys: [],
    //     });
    //   }
    // }, 500);

    if (searchValue != "") {
      this.setState({searchValue:searchValue,page:1})
    } else {
      this.setState({searchValue:"",page:1})
    }

    
    if ( searchValue !== "" && searchValue !== null && searchValue !== undefined ) {
          // searchValue = searchValue.toString().toUpperCase().trim();
            
          const newSearchedTableData = await this.gettableData(searchValue,1);
         
          
          this.setState({searchResultArray: newSearchedTableData});
        } else if(searchValue === "" || searchValue === null ) {
          const newSearchedTableData = await this.gettableData(searchValue,1);
        
          this.setState({searchResultArray: newSearchedTableData});
        }else{
          this.setState({
            searchResultArray: [],
            printableArray: [],
            selectedRowKeys: [],
          });
        }

  };

  onTableRowExpand(expanded, record) {
    var keys = [];
    if (expanded) {
      keys.push(record._id); // I have set my record.id as row key. Check the documentation for more details.
      this.setState({
        rowId: record._id,
      });
    }
    this.setState({expandedRowKeys: keys});
  }

  setRowClassName = (record) => {
    return record._id === this.state.rowId ? "expandparent" : "";
  };

  reloadAfterModalClose = () => {
    this.gettableData();
    this.setState({
      visible: false,
      editvisible: false,
      doucmentvisible: false,
      editownertvisible: false,
    });
  };

  render() {
    const {userDetails} = this.state;
    const columns = [
      {
        title: "Sl No",
        dataIndex: "slno",
        key: "name",
        render: (text, record,index) => (
          
          <span>
            {/* {this.state.searchValue === "" || this.state.searchValue === null
              ? this.state.datasource.indexOf(record) + 1
              : this.state.searchResultArray.indexOf(record) + 1}
               */}
               {this.state.searchValue === "" || this.state.searchValue === null
              ?  (this.state.page - 1) * this.state.limit + index + 1
              : (this.state.page - 1) * this.state.limit + index + 1}
              
              
          </span>
        ),
      },
      {
        title: "Vehicle Number",
        dataIndex: "registrationNumber",
        key: "registrationNumber",
        sorter: (a, b) =>
          `${a.registrationNumber}`.localeCompare(b.registrationNumber),
        render: (text) => (
          <span style={{textTransform: "uppercase"}}>{text}</span>
        ),
      },
      {
        title: "Vehicle Type",
        dataIndex: "truckType",
        key: "truckType",
        render: NullData,
      },
      {
        title: "Owners Number",
        dataIndex: "phoneNumber",
        key: "phoneNumber",
        sorter: (a, b) => `${a.phoneNumber}`.localeCompare(b.phoneNumber),
        render: NullData,
      },
      {
        title: "Driver Name",
        dataIndex: "driverName",
        key: "driverName",
        render: NullData,
      },
      {
        title: "Driver Number",
        dataIndex: "driverPhoneNumber",
        key: "driverPhoneNumber",
        render: NullData,
      },
      {
        title: "RC Book",
        dataIndex: "rcbook",
        width: 150,
        key: "rcbook",
        render: (text, record) => (
          // <a>
          <div style={{display: "flex"}}>
            <div style={{margin: "5px"}}>
              {record.rcBookProof && (
                <a
                  download={`Rc-${record.registrationNumber}`}
                  href={record.rcBookProof}
                  target="_blank"
                >
                  <img src={ViewDocument} width="18px" />
                </a>
              )}
              {!record.rcBookProof && <span>&#8212;</span>}
            </div>
            {record.rcBookProof && (
              <div
                style={{margin: "5px", color: "#01a6d0", cursor: "pointer"}}
                onClick={() => this.viewDocments(record)}
              >
                View Document
              </div>
            )}
          </div>
          // </a>
        ),
      },
      {
        title: "Commission %",
        dataIndex: "commission",
        key: "commission",
        align: "center",
      },
      {
        title: "Action",
        dataIndex: "",
        key: "x",
        render: (text, record) => (
          <div style={{display: "flex"}}>
            {userDetails && userDetails.roleName === "Admin" ? (
              <div style={{margin: "5px"}}>
                <Tooltip title="Owner Transfer">
                  <RetweetOutlined
                    style={{
                      cursor: "pointer",
                      fontSize: "14px",
                      color: "#01a6c5",
                      paddingTop: "3px",
                    }}
                    onClick={() => this.showownermodals(record)}
                  />
                  {/* <img
                  src={Editicon}
                  style={{cursor: "pointer"}}
                  onClick={() => this.showownermodals(record)}
                  width="15px"
                /> */}
                </Tooltip>
              </div>
            ) : (
              ""
            )}
            <div style={{margin: "5px"}}>
              
              <Tooltip title="Edit Truck Details">
                
                <img
                  src={Editicon}
                  style={{cursor: "pointer"}}
                  onClick={() => this.showmodals(record)}
                  
                  width="15px"
                />
              </Tooltip>
            </div>
            {userDetails && userDetails.roleName === "Admin" ? (
              <div style={{margin: "5px"}}>
                <Popconfirm
                  title="Are you sure?"
                  onConfirm={() => this.confirm(record)}
                  onCancel={this.cancel}
                  okText="Yes"
                  cancelText="No"
                >
                  <a href="#">
                    <img
                      src={Deleteicon}
                      style={{cursor: "pointer"}}
                      width="15px"
                    />
                  </a>
                </Popconfirm>
              </div>
            ) : (
              ""
            )}
          </div>
        ),
      },
    ];
    const insidecolumns = [
      {
        title: "Account Holders Name",
        dataIndex: "accountHolderName",
        key: "accountHolderName",
        width: "16%",
        render: NullData,
      },
      {
        title: "Bank Name",
        dataIndex: "bankName",
        key: "bankName",
        width: "13%",
        render: NullData,
      },

      {
        title: "Bank Branch",
        dataIndex: "branchName",
        key: "branchName",
        width: "13%",
        render: NullData,
      },
      {
        title: "IFSC Code",
        dataIndex: "ifscCode",
        key: "ifscCode",
        width: "13%",
        render: NullData,
      },
      {
        title: "A/C NO",
        dataIndex: "accountNumber",
        key: "accountNumber",
        width: "13%",
        render: (text, record) => (
          <span>
            {userDetails && userDetails.roleName === "Admin"
              ? text
              : AccountMasking(text, record)}
          </span>
        ),
      },
    ];

    const {
      editableVehicleDetails,
      fileType,
      previewUrl,
      ownerDetails,
    } = this.state;
    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Truck Details
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Enter Truck registration and Owner Details
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <HeaderBreedCrumb
            path="home"
            history={this.props.history}
            userData={this.state.userDetails}
          />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <Spin spinning={this.state.loading}>
            <div>
              <Modal
                visible={this.state.doucmentvisible}
                title={
                  <div>
                    View Document
                    <p style={{fontSize: "12px", fontFamily: "AvenirMedium"}}>
                      RC Book View{" "}
                    </p>
                  </div>
                }
                onCancel={() => this.setState({doucmentvisible: false})}
                className={`ant${
                  fileType === "pdf" ? "PdfModal" : "ImageModal"
                }`}
                style={{width: "900px"}}
                okButtonProps={{style: {display: "none"}}}
              >
                {fileType === "pdf" && <PdfViewer url={previewUrl || ""} />}
                {fileType !== "pdf" && (
                  <img src={previewUrl} alt="img" style={imageStyle} />
                )}
              </Modal>

              <Modal
                title={
                  <div>
                    <h3 style={{fontSize: "20px", margin: "0px"}}>
                      {" "}
                      Truck Details
                    </h3>
                    <p
                      style={{
                        fontSize: "12px",
                        fontFamily: "AvenirMedium",
                      }}
                    >
                      Enter Truck Details
                    </p>
                  </div>
                }
                className="ModalLook"
                visible={this.state.editvisible}
                onOk={this.handleOk}
                onCancel={this.handleCancelEdit}
                destroyOnClose={true}
                footer={null}
              >
                <div>
                  {editableVehicleDetails && (
                    <EditVehicleInformation
                      record={editableVehicleDetails}
                      parentFn={() => this.reloadAfterModalClose()}
                      onUpdate={() => this.gettableData(this.state.searchValue,this.state.page,this.state.limit)}
                      onSearch={() => this.searchResult(this.state.searchValue)}
                    />
                  )}
                </div>
              </Modal>

              <Modal
                title={
                  <div>
                    <h3 style={{fontSize: "20px", margin: "0px"}}>
                      {" "}
                      Owner Transfer
                    </h3>
                    <p
                      style={{
                        fontSize: "12px",
                        fontFamily: "AvenirMedium",
                      }}
                    >
                      View Truck Details
                    </p>
                  </div>
                }
                className="ModalLook"
                visible={this.state.editownertvisible}
                onOk={this.handleOkowner}
                onCancel={this.handleCancelowner}
                destroyOnClose={true}
                footer={null}
              >
                <div>
                  {editableVehicleDetails && (
                    <OwnerTransfer
                      record={editableVehicleDetails}
                      parentFn={() => this.reloadAfterModalClose()}
                    />
                  )}
                </div>
              </Modal>

              <div style={{padding: "20px"}}>
                <div className="search">
                  <Row>
                    <Col xs={24} sm={24} md={6} lg={6} xl={6}>
                      <h3 className="filterLabel">Search By Vehicle Number</h3>
                      <Search
                        className="searchbtn"
                        style={{width: "250px"}}
                        // onChange={this.searchResult}
                        onChange={(e) => this.searchResult(e.target.value)}
                      />
                    </Col>
                    <Col xs={24} sm={24} md={18} lg={18} xl={18}>
                      <div style={{float: "right", marginTop: "20px"}}>
                        {" "}
                        <Button
                          type="primary"
                          onClick={this.showModal}
                          icon={
                            <img
                              src={Onboarding}
                              style={{paddingRight: "5px", width: "30px"}}
                            />
                          }
                        >
                          ADD TRUCK
                        </Button>
                      </div>
                    </Col>
                  </Row>
                </div>
                <div style={{position: "absolute", right: 30, padding: "10px"}}>
                  <Modal
                    title={
                      <div>
                        <h3 style={{fontSize: "20px", margin: "0px"}}>
                          Vehicle Information
                        </h3>
                        <p
                          style={{
                            fontSize: "12px",
                            fontFamily: "AvenirMedium",
                          }}
                        >
                          Enter Truck registration and Owner Details
                        </p>
                      </div>
                    }
                    className="ModalLook"
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    destroyOnClose={true}
                    onCancel={this.handleCancel}
                    footer={null}
                  >
                    <div>
                      <VehicleInformation
                        parentFn={() => this.reloadAfterModalClose()}
                      />
                    </div>
                  </Modal>
                </div>
              </div>

              <div
                className="trucktable"
                style={{overflowX: "auto", margin: "10px 10px 0px 10px"}}
              >
                <Table
                  columns={columns}
                  rowKey="_id"
                  onRow={this.onClickRow}
                  rowClassName={this.setRowClassName}
                  expandedRowKeys={this.state.expandedRowKeys}
                  onExpand={(expand, record) =>
                    this.onTableRowExpand(expand, record)
                  }
                  expandedRowRender={(record) => (
                    <Table
                      size="small"
                      className="insideTable"
                      columns={insidecolumns}
                      dataSource={[record.accountId[0] ? record.accountId[0] : {}]}
                      rowKey="insidetable"
                      pagination={false}
                    />
                  )}
                  dataSource={
                    this.state.searchValue === "" ||
                    this.state.searchValue === null
                      ? this.state.datasource
                      : this.state.searchResultArray
                  }
                  // rowSelection={{
                  //   type: this.state.selectionType,
                  //   ...rowSelection,
                  // }}
                  expandIcon={(props) => this.customExpandIcon(props)}
                  style={{width: "100%"}}
                  // pagination={{
                  //   pageSizeOptions: ["10", "20", "50", "100"],
                  //   defaultPageSize: 50,
                  // }}
                  pagination={{
                    current: this.state.page,
                    defaultPageSize: 50,
                    showSizeChanger: true, 
                      onShowSizeChange: this.changePaginationAll,
                    total: this.state.totalCount,
                    onChange: this.changePagination,
                  }}
                />
              </div>
            </div>
          </Spin>
        </div>
      </div>
    );
  }
}

export default TruckMaster;
