class ReportDashboard extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    this.state = {
      visible: false,
      HeaderTotal: [],
      totalExpenses: [],
      recoveryRatioData: [],
      monthlyEarning: [],
      materialLoadData: [],
      userDetails: Details,
      loading:false
    };
  }
  getTotalHeader = async (date) => {
     this.setState({loading:true})
    const dateValue = date ? date : CurrentYear;
    const response = await axios.get(
      `${BASE_URL}get-all-header-visualtion?entryYear=${dateValue}`
    );
    if (response.data) {
      const HeaderTotal = [
        {
          label: "Total Trips",
          value: response.data.totalTrip
            ? response.data.totalTrip.toFixed()
            : "0",
        },
        {
          label: "Revenue",
          value: response.data.totalRevenue
            ? response.data.totalRevenue.toFixed()
            : "0",
          prefix: "",
        },
        {
          label: "Commission",
          value: response.data.totalCommission
            ? response.data.totalCommission.toFixed()
            : "0",
          prefix: "",
        },
        {
          label: "Total Outstanding",
          value: response.data.outStandingAmount
            ? response.data.outStandingAmount.toFixed()
            : "0",
          prefix: "",
        },
      ];
      this.setState({
        HeaderTotal,
        loading:false
        
      });
    }
  };

  getTotalExpresnses = async (date) => {
    this.setState({loading:true})
    const dateValue = date ? date : CurrentYear;
    const response = await axios.get(
      `${BASE_URL}get-expenses-visualtion?entryYear=${dateValue}`
    );
    if (response.data.totalTrip) {
      const totalExpenses = response.data.totalTrip.map((data, index) => {
        const monthName = moment()
          .month(data._id.month - 1)
          .format("MMM");

        return {
          name: monthName,
          Diesel: data.totalDiesel,
          Cash: data.totalCash,
          "Bank Transfer": data.totalBankTransfer,
          Shortage: data.totalShortage,
          amt: 1000,
        };
      });

       this.setState({
        totalExpenses,
        loading:false
      });
    }
  };
  onChange = (date, dateString) => {
    this.getTotalExpresnses(dateString);
    this.getMonthlyEarning(dateString);
    this.getMaterialLoad(dateString);
    this.getTotalHeader(dateString);
  };
  getTotalRecoveryRatio = async () => {
    this.setState({loading:true})
    const response = await axios.get(`${BASE_URL}get-all-recovery-details`);

    if (response.data) {
      const recoveryRatioData = [
        {
          name: "Value",
          value: response.data.totalValue ? response.data.totalValue : 0,
        },
        {
          name: "Recovered",
          value: response.data.totalRecovered
            ? response.data.totalRecovered
            : 0,
        },
      ];

       this.setState({
        recoveryRatioData,
        loading:false
      });
    }
  };

  getMonthlyEarning = async (date) => {
    this.setState({loading:true})
    const dateValue = date ? date : CurrentYear;
    const response = await axios.get(
      `${BASE_URL}get-earning-visualtion?entryYear=${dateValue}&entryMonth=${CurrentMonth}`
    );
    const currentEarningData = response.data.currentEarningData;
    const previousEarningData = response.data.previousEarningData;
    if (response.data) {
      const monthlyEarning = [
        {
          name: "Last Month",
          Earning:
            previousEarningData && previousEarningData.length > 0
              ? previousEarningData[0].totalEarning
              : "0",
        },
        {
          name: "This Month",
          Earning:
            currentEarningData && currentEarningData.length > 0
              ? currentEarningData[0].totalEarning
              : "0",
        },
      ];

       this.setState({
        monthlyEarning,
        loading:false
      });
    }
  };

  getMaterialLoad = async (date) => {
    this.setState({loading:true})
    const dateValue = date ? date : CurrentYear;
    const response = await axios.get(
      `${BASE_URL}get-material-type-chart?entryYear=${dateValue}`
    );
    const materialType = response.data.materialType;
    if (materialType) {
      const materialLoaddata = materialType.map((data, index) => {
        const monthName = moment()
          .month(data._id.month - 1)
          .format("MMM");

        return {
          ...data,
          name: monthName,
          amt: data.amount,
        };
      });
      this.setState({
        materialLoaddata,
        loading:false
      });
    }
  };

  componentDidMount() {
    
    
   this.getTotalHeader();
   this.getTotalExpresnses();
   this.getTotalRecoveryRatio();
   this.getMonthlyEarning();
   this.getMaterialLoad();
    
   
  }
  render() {
    const {
      HeaderTotal,
      totalExpenses,
      recoveryRatioData,
      monthlyEarning,
      materialLoaddata,
    } = this.state;
    return (
      <Spin spinning={this.state.loading}>
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Visual Dashboard
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              View All Repots
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <Row>
            <Col
              span={12}
              xs={12}
              sm={12}
              xl={12}
              // style={{padding: "0 0 10px 20px"}}
            >
              <ReportBreedCrumb
                path="home"
                history={this.props.history}
                userData={this.state.userDetails}
              />
            </Col>
            <Col
              span={12}
              xs={12}
              sm={12}
              xl={12}
              style={{textAlign: "right", padding: "25px 25px 0px 0px"}}
            >
              Select Year:{" "}
              <DatePicker
                onChange={this.onChange}
                picker="year"
                defaultValue={moment(CurrentYear, "YYYY")}
              />
            </Col>
          </Row>
        </div>
        {/* className="site-card-border-less-wrapper" */}
        <div className="site-card-border-less-wrapper">
          <Row gutter={[16, 16]}>
            {HeaderTotal.map((item) => (
              <Col span={6} xs={24} sm={12} xl={6}>
                <StatisticCard data={item} />
              </Col>
            ))}
          </Row>
          <Row gutter={[16, 16]}>
            <Col span={8} xs={24} sm={24} xl={8}>
              <MonthlyEarning
                chartData={
                  monthlyEarning && monthlyEarning.length > 0
                    ? monthlyEarning
                    : monthlyEarningData
                }
              />
            </Col>
            <Col span={16} xs={24} sm={24} xl={16}>
              <Expenses chartData={totalExpenses} />
            </Col>
          </Row>
          <Row gutter={[16, 16]}>
            <Col span={8} xs={24} sm={24} xl={8}>
              <RecoveryRatio chartData={recoveryRatioData} />
            </Col>
            <Col span={16} xs={24} sm={24} xl={16}>
              <MaterialLoadTrend chartData={materialLoaddata} />
            </Col>
          </Row>
        </div>
      </div>
      </Spin>
    );
  }
}

export default ReportDashboard;
