/* eslint-disable react/jsx-no-duplicate-props */
/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from "react";
import {Table, Input, message, Spin, Row, Col} from "antd";
import HeaderBreedCrumb from "../../../components/menubreedcrumb/menubreedcrumb";
import CurrentTime from "../../../components/currenttime/currenttime";
import axios from "axios";
import {BASE_URL} from "../../../constants/constants";
// import {BASE_URL} from "./browsername";
import auth from "../../../utils/auth";
import "./activitylog.css";
import moment from "moment";

const {Search} = Input;

const NullData = (tableText) => <span>{tableText || <p>&#8212;</p>}</span>;

let timeout = null;

class ActivityLog extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const Det = auth.getUserData();
    this.state = {
      vidible: false,
      loading: false,
      doucmentvisible: false,
      fileurl: "",
      datasource: [],
      searchValue: "",
      searchResultArray: [],
      editvisible: false,
      selectedRowKeys: [],
      ownerDetails: "",
      key: "",
      expandedRowKeys: "null",
      rowExpandable: false,
      rowId: "",
      userDetails: Details,
      TableHeaderClass: "maintable",
      logId: Det._id,
    };
  }

  getTableData = async () => {
    try {
      this.setState({loading: true});
      const response = await axios.get(`${BASE_URL}get-all-logs`);
      const ownerDetails = response.data.logsDetails || "";
      if (ownerDetails && ownerDetails.length > 0) {
        const data = ownerDetails.map((data, index) => {
          // var dateObj = new Date(dateString);
          let momentObj = moment(new Date(data.loginTime));
          let momentString = momentObj.format("MMMM Do YYYY, h:mm:ss A");

          let momentObjlog = moment(new Date(data.logoutTime));
          let momentLogout = momentObjlog.format("MMMM Do YYYY, h:mm:ss A");
          const sTime = momentObj.format("LTS");
          const lTime = momentObjlog.format("LTS");
          const startTime = moment(sTime, "HH:mm:ss a");
          const endTime = moment(lTime, "HH:mm:ss a");
          // calculate total duration
          var duration = moment.duration(endTime.diff(startTime));

          const hours = parseInt(duration.asHours());
          const minutes = parseInt(duration.asMinutes()) % 60;
          const seconds = parseInt(duration.asSeconds()) % 60;

          const durations = hours
            ? hours + " Hours " + minutes + " Minutes " + seconds + " Seconds "
            : minutes
            ? minutes + " Minutes " + seconds + " Seconds"
            : seconds + " Seconds";

          return {
            ...data,
            loginTime: momentString,
            logoutTime: data && data.logoutTime ? momentLogout : "",
            timeDuration: minutes || seconds ? durations : "",
          };
        });
        
        this.setState({
          datasource: data,
        });
      }

      this.setState({loading: false});
    } catch (err) {
      this.setState({loading: false});
      message.error("Error fetching data. Please try again later", 2);
    }
  };
  doSomethingBeforeUnload = async () => {
    await axios.put(`${BASE_URL}logout/${this.state.logId}`);
  };
  setupBeforeUnloadListener = async () => {
    console.log(this.state.userDetails, "confiff");
    window.addEventListener("beforeunload", async (ev) => {
      ev.preventDefault();
      return this.doSomethingBeforeUnload();
    });
  };

  componentDidMount() {
    this.getTableData();
    this.setupBeforeUnloadListener();
  }

  searchResult = (e) => {
    clearTimeout(timeout);
    let searchValue = e.target.value;
    timeout = setTimeout(() => {
      this.setState({searchValue});
      if (
        searchValue !== "" &&
        searchValue !== null &&
        searchValue !== undefined
      ) {
        searchValue = searchValue.toString().toUpperCase().trim();

        const newSearchedTableData = this.state.datasource.filter(
          ({userEmail}) => {
            const vehno = userEmail || "";
            return vehno.toString().toUpperCase().includes(searchValue);
          }
        );
        this.setState({searchResultArray: newSearchedTableData});
      } else {
        this.setState({
          searchResultArray: [],
          printableArray: [],
          selectedRowKeys: [],
        });
      }
    }, 500);
  };

  render() {
    const {userDetails} = this.state;

    const columns = [
      {
        title: "Sl No",
        dataIndex: "slno",
        key: "name",
        width: 80,
        render: (text, record) => (
          <span>
            {this.state.searchValue === "" || this.state.searchValue === null
              ? this.state.datasource.indexOf(record) + 1
              : this.state.searchResultArray.indexOf(record) + 1}
          </span>
        ),
      },
      {
        title: "Login ID",
        dataIndex: "userEmail",
        key: "userEmail",
        render: NullData,
        width: "20%",
      },
      {
        title: "IP Address",
        dataIndex: "ipAddress",
        key: "ipAddress",
        render: NullData,
      },
      {
        title: "Login Time",
        dataIndex: "loginTime",
        key: "loginTime",
        render: NullData,
      },

      {
        title: "Agent",
        dataIndex: "browserAgent",
        key: "browserAgent",
        ellipsis: true,
        render: NullData,
      },
      {
        title: "Logout Time",
        dataIndex: "logoutTime",
        key: "logoutTime",
        render: NullData,
      },
      {
        title: "Duration",
        dataIndex: "timeDuration",
        key: "Duration",
        render: NullData,
      },
    ];

    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Activity Log
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              View Activity Log Details
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <HeaderBreedCrumb
            path="home"
            history={this.props.history}
            userData={this.state.userDetails}
          />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <div className="ownermaster">
            <Spin spinning={this.state.loading}>
              <div>
                <div style={{padding: "20px"}}>
                  <div className="search">
                    <Row>
                      <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                        <h3 className="filterLabel">Search By Login ID</h3>
                        <Search
                          className="searchbtn"
                          style={{width: "250px"}}
                          onChange={this.searchResult}
                        />
                      </Col>
                    </Row>
                  </div>
                </div>
                <div
                  className={this.state.TableHeaderClass}
                  style={{
                    overflowX: "auto",
                    margin: "10px 10px 0px 10px",
                  }}
                  id="content"
                >
                  <Table
                    columns={columns}
                    rowKey="_id"
                    onRow={this.onClickRow}
                    rowClassName={this.setRowClassName}
                    dataSource={
                      this.state.searchValue === "" ||
                      this.state.searchValue === null
                        ? this.state.datasource
                        : this.state.searchResultArray
                    }
                    pagination={{
                      pageSizeOptions: ["10", "20", "50", "100"],
                      defaultPageSize: 20,
                    }}
                  />
                </div>
                <div id="afterTable"></div>
              </div>
            </Spin>
          </div>
        </div>
      </div>
    );
  }
}

export default ActivityLog;
