const { hostname, protocol } = window.location;

const DEV = `/trucklink-dev/v1/`;
const STAGE = `/trucklink-stage/v1/`;
const LOCAL = `:4000/api/v1/`;
const BASEURL = `https://products.hutechsolutions.in/services/`;
const getApiURL = () => {
  if (hostname === "products.hutechsolutions.in") {
    return BASEURL + DEV;
  } else if (hostname === "staging.hutechsolutions.in") {
    return BASEURL + STAGE;
  } else if (hostname === "localhost") {
    // return `${protocol}//${hostname}${LOCAL}`;
    return BASEURL + STAGE;
  } else {
    return "https://trucklink.in/prod/v1/";
  }
};

const BASE_URL = getApiURL();

export { BASE_URL };
