import React from "react";
import {
  Input,
  Button,
  Form,
  Table,
  Row,
  Col,
  message,
  Spin,
  List,
} from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import {
  getAllDeliveryNumbers,
  createRecoveryCode,
  updateRecoveredData,
  getRecoveryRegisterDataById,
  updateRecoveryValue,
} from "./recoveryRegisterAPI";

import InfiniteScroll from "react-infinite-scroller";

import "./recoveryRegister.css";

const { Search } = Input;
const numInput = (event) => {
  const val = event.target.value;
  var digit = /^[0-9]*\.?[0-9]*$/.test(val);
  if (/^[0-9]*\.?[0-9]*$/.test(val) && event.key !== "Backspace") {
    var with2Decimals = val.toString().match(/^-?\d+(?:\.\d{0,1})?/);
    if (with2Decimals) {
      event.target.value = with2Decimals[0];
    }
  } else {
    return true;
  }
};
class RecoveryForm extends React.Component {
  constructor(props) {
    super(props);
    console.log(props);
  }
  state = {
    allDeliveryNumbers: [],
    deliveryNumbersList: [],
    allDerivedDOList: [],
    derivedDOList: [],
    removedDoList: [],
    originalSearchText: "",
    derivedSearchText: "",
    derivedSearch: [],
    loadingoutside: false,
    loading: false,
    hasMore: true,
    page: 1,
    limit: 300,
    data: [],
  };

  materialtype = [
    {
      title: "",
      dataIndex: "deliveryNumber",
      width: "70%",
      editable: true,
    },

    {
      title: "",
      dataIndex: "operation",
      width: "30%",
      render: (text, record) => {
        return (
          <div>
            <Button
              type="primary"
              className="addBtn"
              onClick={() => this.addDerivedDoList(record)}
            >
              ADD
            </Button>
          </div>
        );
      },
    },
  ];
  blockedFromRemove = [];

  loadLocation = [
    {
      title: "",
      dataIndex: "deliveryNumber",
      width: "70%",
    },
    {
      title: "",
      dataIndex: "operation",
      width: "30%",
      render: (text, record) => {
        return (
          <div>
            <Button
              type="primary"
              className="rmoveBtn"
              disabled={this.blockedFromRemove.includes(record._id)}
              onClick={() => this.removeDerivedDoList(record)}
            >
              REMOVE
            </Button>
          </div>
        );
      },
    },
  ];

  addDerivedDoList = (data) => {
    const allDerivedDOList = [...this.state.derivedDOList, data];
    const deliveryNumbersList = this.state.deliveryNumbersList.filter(
      (item) => item._id !== data._id
    );
    const { derivedSearchText, originalSearchText } = this.state;

    this.setState({
      deliveryNumbersList: deliveryNumbersList,
      allDerivedDOList,
      derivedDOList: allDerivedDOList,
      removedDoList: [
        ...this.state.removedDoList.filter((id) => id !== data._id),
      ],
    });
  };

  removeDerivedDoList = (data) => {
    const { derivedSearchText, originalSearchText } = this.state;
    const allDerivedDOList = this.state.derivedDOList.filter(
      (item) => item._id !== data._id
    );
    const deliveryNumbersList = [...this.state.deliveryNumbersList, data];

    this.setState({
      deliveryNumbersList: deliveryNumbersList,
      allDerivedDOList,
      derivedDOList: allDerivedDOList,
      removedDoList: [...this.state.removedDoList, data._id.toString()],
    });
  };

  onFinish = async (values) => {
    try {
      let payload = {};
      if (this.props.editMode) {
        payload = {
          recoveryCode: values.recoveryCode,
          value: values.value,
          challan:
            this.state.allDerivedDOList.length > 0
              ? this.state.allDerivedDOList
              : [
                  ...this.props.data.challan.filter(
                    (item) => !this.state.removedDoList.includes(item)
                  ),
                ],
        };
      } else {
        payload = {
          ...values,
          challan: this.state.allDerivedDOList.map((item) => item._id),
        };
      }

      this.setState({
        loadingoutside: true,
      });

      const response = this.props.editMode
        ? await updateRecoveredData(
            this.props.data && this.props.data._id,
            payload
          )
        : await createRecoveryCode(payload);
      if (response && response.data) {
        if (this.props.onUpdate()) {
          this.props.onUpdate();
        }

        message.success(
          `Recovery code ${
            this.props.editMode ? "updated" : "created"
          } successfully`
        );
        this.props.onSave();
      }

      this.setState({
        loadingoutside: false,
      });
    } catch (err) {
      console.log(err);
    }
  };

  handleGetAllDeliveryNumbers = async () => {
    try {
      await this.setState({
        loading: true,
        loadingoutside: true,
      });
      const response = await getAllDeliveryNumbers();
      const slicedResponse = response.filter(
        (item) => this.props.linkedChallans.indexOf(item._id) === -1
      );

      const allDeliveryNumbers = this.props.editMode
        ? slicedResponse.filter(
            (item) => !this.props.data.challan.includes(item._id)
          )
        : slicedResponse;
      const allDerivedDOList = this.props.editMode
        ? response.filter((item) => this.props.data.challan.includes(item._id))
        : [];

      this.setState({
        allDeliveryNumbers: allDeliveryNumbers,
        deliveryNumbersList: allDeliveryNumbers,
        allDerivedDOList: allDerivedDOList,
        // derivedDOList: allDerivedDOList,
        loading: false,
        loadingoutside: false,
      });
    } catch (err) {
      console.log(err);
    }
  };

  async getRecoveryRegisterById() {
    this.setState({ loadingoutside: true });
    let response;
    if (this.props.editMode) {
      response = await getRecoveryRegisterDataById(this.props.data._id);
    }

    let doList = [];
    response.challan.map((item) => {
      doList.push({ _id: item._id, deliveryNumber: item.deliveryNumber });
    });

    const response_check = await this.setState({
      derivedDOList: doList,
      derivedSearch: doList,
    });
    if (response_check) {
      this.setState({ loadingoutside: false });
    }
  }

  componentDidMount() {
    this.handleGetAllDeliveryNumbers();
    if (this.props.editMode) {
      this.getRecoveryRegisterById();
    }
  }

  handleSaveRecoveryCode = () => {};

  searchResult = async (searchText, type) => {
    await this.setState({
      loading: true,
    });

    getAllDeliveryNumbers(searchText).then((res) => {
      res = res.filter(
        (ar) => !this.state.derivedDOList.find((rm) => rm._id === ar._id)
      );

      this.setState({
        deliveryNumbersList: res,
        loading: false,
      });
    });
    this.setState({
      [type === "original"
        ? "originalSearchText"
        : "derivedSearchText"]: searchText,
    });
  };

  searchResultDoList = async (searchText, type) => {
    await this.setState({
      loading: true,
    });

    const { derivedDOList, derivedSearch } = this.state;
    const searchData = derivedSearch.filter((item) =>
      item.deliveryNumber.match(searchText)
    );

    await this.setState({
      derivedDOList:
        searchData.length == 0
          ? []
          : searchData == ""
          ? derivedDOList
          : searchData,
      loading: false,
    });
  };

  handleInfiniteOnLoad = async () => {
    await this.setState({
      loading: true,
    });
    let { page, limit } = this.state;
    page = page + 1;
    limit = limit + 300;
    let { deliveryNumbersList } = this.state;
    getAllDeliveryNumbers(page, limit).then((res) => {
      deliveryNumbersList = deliveryNumbersList.concat(res);
      this.setState({
        deliveryNumbersList,
        loading: false,
        page,
        limit,
      });
    });
  };

  render() {
    const {
      deliveryNumbersList,
      originalSearchText,
      derivedSearchText,
      derivedDOList,
      loadingoutside,
      loading,
    } = this.state;
    this.blockedFromRemove = this.props.recoveryData
      .filter((item) => +item.recovery > 0)
      .map((item) => item._id.toString());
    const validateMessages = {
      required: "This field is required!",
      types: {
        email: "Not validate email!",
        number: "Not a valid number!",
      },
      number: {
        range: "Value must be between ${min} and ${max}",
      },
    };

    const locale = {
      emptyText: (
        <div className="ant-empty ant-empty-normal">
          <span>
            <ExclamationCircleOutlined style={{ color: "red" }} />
            <span
              style={{
                padding: "5px",
                textTransform: "initial",
                marginTop: "50px",
              }}
            >
              Please, add DO number.
            </span>
          </span>
        </div>
      ),
    };
    return (
      <Spin spinning={loadingoutside} delay={100}>
        <div style={{ padding: "30px 10px 50px 30px" }}>
          <h2 style={{ fontWeight: "bold" }}>Link Code</h2>

          <Form
            name="recoveryForm"
            onFinish={this.onFinish}
            initialValues={this.props.data}
            validateMessages={validateMessages}
            autoComplete="off"
          >
            <div style={{ marginTop: "20px" }}>
              <Row>
                <Col xs={24} sm={24} md={7} lg={7} xl={7}>
                  <div>
                    <h3 className="title">Recovery Code</h3>
                    <Form.Item
                      name="recoveryCode"
                      rules={[
                        {
                          pattern: new RegExp(
                            /^(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]+)$/
                          ),
                          message: "Invalid Recovery Code",
                        },
                        {
                          required: true,
                        },
                      ]}
                    >
                      <Input maxLength={14} onKeyPress={function (event) {
                        var test = event.target.value
                        if (/[^a-zA-Z0-9]/.test(test)) {
                          event.preventDefault()
                        }
                      }}/>
                    </Form.Item>
                  </div>
                </Col>
                <Col xs={24} sm={24} md={6} lg={6} xl={6} offset={1}>
                  <div>
                    <h3 className="title">Value</h3>
                    <Form.Item
                      name="value"
                      rules={[
                        {
                          required: true,
                          message: "This filed is required!",
                        },
                        {
                          pattern: new RegExp(/^\d{1,8}(\.\d{1,2})?$/),
                          message: "Only 8 digit with 2 decimal allowed",
                        },
                      ]}
                    >
                      <Input
                        type="number"
                        onKeyPress={(event) => {
                          if (numInput(event)) {
                            event.preventDefault();
                          }
                        }}
                      />
                    </Form.Item>
                  </div>
                </Col>
              </Row>
              <Row style={{ margin: "0 0 30px 0" }}>
                <Col xs={24} sm={24} md={10} lg={10} xl={10}>
                  <h3 className="title">Add DO number to Recovery Code</h3>
                  <div className="master-data-maindiv delivery-number-drag-area">
                    <div>
                      <div className="searchdiv">
                        <Search
                          className="searchdata"
                          onChange={(e) =>
                            this.searchResult(e.target.value, "original")
                          }
                          placeholder="Search"
                          // value={originalSearchText}
                        />
                      </div>

                      <div className="materialTable">
                        <div className="demo-infinite-container">
                          <InfiniteScroll
                            initialLoad={false}
                            pageStart={0}
                            loadMore={this.handleInfiniteOnLoad}
                            hasMore={!this.state.loading && this.state.hasMore}
                            useWindow={false}
                          >
                            <List
                              dataSource={deliveryNumbersList.filter((item) =>
                                item.deliveryNumber.includes(originalSearchText)
                              )}
                              renderItem={(item) => (
                                <List.Item
                                  actions={[
                                    <Button
                                      type="primary"
                                      className="addBtn"
                                      onClick={() =>
                                        this.addDerivedDoList(item)
                                      }
                                    >
                                      ADD
                                    </Button>,
                                  ]}
                                >
                                  <List.Item.Meta title={item.deliveryNumber} />
                                </List.Item>
                              )}
                            >
                              {this.state.loading && this.state.hasMore && (
                                <div className="demo-loading-container">
                                  <Spin />
                                </div>
                              )}
                            </List>
                          </InfiniteScroll>
                        </div>
                        {/* <div className="demo-infinite-container">
                          <Spin spinning={loading}>
                            <InfiniteScroll
                              initialLoad={false}
                              pageStart={0}
                              loadMore={this.handleInfiniteOnLoad}
                              hasMore={
                                !this.state.loading && this.state.hasMore
                              }
                              useWindow={false}
                            >
                              <List
                                dataSource={deliveryNumbersList.filter((item) =>
                                  item.deliveryNumber.includes(
                                    originalSearchText
                                  )
                                )}
                                renderItem={(item) => (
                                  <List.Item
                                    actions={[
                                      <Button
                                        type="primary"
                                        className="addBtn"
                                        onClick={() =>
                                          this.addDerivedDoList(item)
                                        }
                                      >
                                        ADD
                                      </Button>,
                                    ]}
                                  >
                                    <List.Item.Meta
                                      title={item.deliveryNumber}
                                    />
                                  </List.Item>
                                )}
                              >
                                {this.state.loading && this.state.hasMore && (
                                  <div className="demo-loading-container">
                                    <Spin />
                                  </div>
                                )}
                              </List>
                              {/* <Table
                              rowClassName={() => "editable-row"}
                              dataSource={deliveryNumbersList.filter((item) =>
                                item.deliveryNumber.includes(originalSearchText)
                              )}
                              columns={this.materialtype}
                              pagination={false}
                              // scroll={{y: 280}}
                              rowKey="_id"
                            /> *
                            </InfiniteScroll>
                          </Spin>
                        </div> */}
                      </div>
                    </div>
                  </div>
                </Col>
                <Col xs={24} sm={24} md={10} lg={10} xl={10} offset={2}>
                  <h3 className="title">DO List</h3>
                  <div className="master-data-maindiv delivery-number-drag-area">
                    <div>
                      <div className="searchdiv">
                        <Search
                          className="searchdata"
                          onChange={(e) =>
                            this.searchResultDoList(e.target.value, "derived")
                          }
                          placeholder="Search"
                          // value={derivedSearchText}
                        />
                      </div>
                      <div className="materialTable">
                        <Table
                          rowClassName={() => "editable-row"}
                          dataSource={derivedDOList.filter((item) =>
                            item.deliveryNumber.includes(derivedSearchText)
                          )}
                          columns={this.loadLocation}
                          pagination={false}
                          scroll={{ y: 240 }}
                          rowKey="_id"
                          locale={locale}
                        />
                      </div>
                    </div>
                  </div>
                  {/* <div className="demo-infinite-container">
                    <InfiniteScroll
                      initialLoad={false}
                      pageStart={2}
                      loadMore={this.handleInfiniteOnLoad}
                      hasMore={!this.state.loading && this.state.hasMore}
                      useWindow={false}
                    >
                      <List
                        dataSource={this.state.data}
                        renderItem={(item) => (
                          <List.Item key={item.id}>
                            <List.Item.Meta
                              avatar={
                                <Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />
                              }
                              // title={<a href="https://ant.design">{item.name.last}</a>}
                              description={item.email}
                            />
                            <div>Content</div>
                          </List.Item>
                        )}
                      >
                        {this.state.loading && this.state.hasMore && (
                          <div className="demo-loading-container">
                            <Spin />
                          </div>
                        )}
                      </List>
                    </InfiniteScroll>
                  </div> */}
                </Col>
              </Row>
              <Row>
                <Col lg={24} style={{ margin: "10px 0px" }}>
                  <div
                    style={{
                      padding: "10px",
                      float: "right",
                      margin: "0px 23px 0px 10px",
                    }}
                  >
                    <Button className="save" type="primary" htmlType="submit">
                      {this.props.editMode ? "UPDATE" : "SAVE"}
                    </Button>
                  </div>
                  <div style={{ padding: "10px", float: "right" }}>
                    <Button
                      type="primary"
                      className="cancelBtn"
                      onClick={() => this.props.onCancel()}
                    >
                      CANCEL
                    </Button>
                  </div>
                </Col>
              </Row>
            </div>
          </Form>
        </div>
      </Spin>
    );
  }
}

export default RecoveryForm;
