/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from "react";
import {
  Table,
  Input,
  Button,
  Modal,
  Popconfirm,
  message,
  Spin,
  Row,
  Upload,
  Col,
  Form,
  Select,
} from "antd";
import {RightOutlined, DownOutlined} from "@ant-design/icons";
import AddvehicleiOwnerDetail from "../addtruckdetails/vehicleOwnerDetail";
import HeaderBreedCrumb from "../../../../components/menubreedcrumb/menubreedcrumb";
import CurrentTime from "../../../../components/currenttime/currenttime";
import axios from "axios";
import {BASE_URL} from "../../../../constants/constants";
import EditvehicleiOwnerDetail from "../edit/vehicleOwnerDetail";
import Editicon from "../../../../assests/images/edit.svg";
import Deleteicon from "../../../../assests/images/delete.svg";
import Download from "../../../../assests/images/download.svg";
import Print from "../../../../assests/images/print.svg";
import Addtruckicon from "../../../../assests/images/addtruckicon.svg";
import PrintOwnerDetail from "./printownerdetails";
import auth from "../../../../utils/auth";
import Uploadicon from "../../../../assests/images/upload.svg";
import XlsFile from "../../../../assests/file/onwercreation.xlsx";
import "./ownerMaster.css";
import * as XLSX from "xlsx";
const {Search} = Input;
// const userDetails = auth.getAuthToken().userDetails;

const iconStyle = {
  color: "#01a6d0",
  fontSize: "14px",
  width: "24px",
  height: "24px",
};
const NullData = (tableText) => <span>{tableText || <p>&#8212;</p>}</span>;

let timeout = null;
const AccountMasking = (text, reord) => {
  const length = text.toString().length;
  const res = text.substr(-4);
  const val = length - 4;
  const ress = text.substring(0, val);
  const value = ress + "XXXX";
  return <p>{value}</p>;
};
class OwnerMaster extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const Det = auth.getUserData();
    this.state = {
      vidible: false,
      loading: false,
      doucmentvisible: false,
      fileurl: "",
      datasource: [],
      searchValue: "",
      searchResultArray: [],
      editvisible: false,
      selectedRowKeys: [],
      ownerDetails: "",
      key: "",
      expandedRowKeys: "null",
      rowExpandable: false,
      rowId: "",
      userDetails: Details,
      TableHeaderClass: "maintable",
      downloadVisible: false,
      uploadVisible: false,
      logId: Det._id,
      page:1,
      limit:50,
      totalCount:""
    };
  }
  handleModalCancel = (e) => {
    this.setState({
      downloadVisible: false,
    });
  };
  handleModalOk = (e) => {
    this.setState({
      downloadVisible: false,
    });
  };
  handleUploadCancel = (e) => {
    this.setState({
      uploadVisible: false,
    });
  };
  handleUploadOk = (e) => {
    this.setState({
      uploadVisible: false,
    });
  };
  onSelectChange = (selectedRowKeys) => {
    this.setState({selectedRowKeys});
  };

  customExpandIcon(props) {
    if (props.expanded) {
      return (
        <a
          style={{color: "black"}}
          onClick={(e) => {
            props.onExpand(props.record, e);
          }}
        >
          <DownOutlined style={iconStyle} className="customIcon" />
        </a>
      );
    }
    return (
      <a
        style={{color: "black"}}
        onClick={(e) => {
          props.onExpand(props.record, e);
        }}
      >
        <RightOutlined style={iconStyle} className="customIcon" />
      </a>
    );
  }

  getInsideData = (record) => {
    const childData = [];
    if (record) {
      record.accountIds.forEach((item) => {
        childData.push({
          ...item,
          phoneNumber: record.phoneNumber,
          state: record.state,
        });
      });
    }
    return childData;
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancelEdit = () => {
    this.setState({
      editvisible: false,
    });
  };

  handledocumentCancel = () => {
    this.setState({
      doucmentvisible: false,
    });
  };

  getTableData = async (searchValue ,page,limit) => {
    try {
  
      const pages = page ? page : 1;
      const limitData = limit ? limit : this.state.limit;
      
      const searchData = searchValue ? searchValue : null;
      
      this.setState({loading: true});
      const response = searchData ? await axios.get(`${BASE_URL}get-owner-bank-details?searchOwnerName=${searchData}&page=${pages}&limit=${limitData}`) 
      : await axios.get(`${BASE_URL}get-owner-bank-details?page=${pages}&limit=${limitData}`)
      // const response = await axios.get(`${BASE_URL}get-owner-bank-details?page=${pages}&limit=50`) 
    
      const ownerDetails = response.data.ownerDetails[0].data || "";

      if (ownerDetails && ownerDetails.length > 0) {
        const arrRes = ownerDetails.sort(function (a, b) {
          a = a.name.toLowerCase();
          b = b.name.toLowerCase();

          return a < b ? -1 : a > b ? 1 : 0;
        });
       await this.setState({
          datasource: arrRes,
          totalCount: response.data.ownerDetails[0].count || "",
          loading: false
        });
        
        return arrRes;
      }
      

      // const {searchValue} = this.state;
      // if (
      //   searchValue !== "" &&
      //   searchValue !== null &&
      //   searchValue !== undefined
      // ) {
      //   const searchVal = searchValue.toString().toUpperCase().trim();

      //   const newSearchedTableData = this.state.datasource.filter(({name}) => {
      //     const vehno = name || "";
      //     return vehno.toString().toUpperCase().includes(searchVal);
      //   });
      //   this.setState({searchResultArray: newSearchedTableData});
      // }
      
    } catch (err) {
      this.setState({loading: false});
      // message.error("Error fetching data. Please try again later", 2);
    }
  };

  changePagination = async (e) => {
    try {
      this.setState({loading: true, page: e});
     
       if(this.state.searchValue){
       const data = await this.getTableData(this.state.searchValue,e);
       this.setState({searchResultArray:data})
       }

     this.getTableData(this.state.searchValue,e);

      // this.setState({loading: false});
    } catch (err) {
      console.log(err);
    }
  };

  changePaginationAll = async(currentpage,pageSize) =>{
    try{
      this.setState({loading: true, page: currentpage,limit: pageSize });

      if(this.state.searchValue){
        const data = await this.getTableData(this.state.searchValue,currentpage,pageSize);
        this.setState({searchResultArray:data})
        }


      this.getTableData(this.state.searchValue,currentpage,pageSize);

      // this.setState({loading: false});
    }catch(err){
      console.log(err)
    }
  }

  componentDidMount() {
    this.getTableData(this.state.searchValue,this.state.page);
    this.setupBeforeUnloadListener();
  }
  async showmodals(e) {
    this.setState({
      editvisible: true,
      ownerDetails: e,
    });
  }

  viewDocments(e) {
    this.setState({
      doucmentvisible: true,
    });
  }

  deleterecord(e) {}

  confirm = async (e) => {
    try {
      this.setState({loading: true});
      await axios.delete(`${BASE_URL}delete-owner-details/${e._id}`);
      if(this.state.searchValue){
         this.searchResult(this.state.searchValue)
      }
      message.success("Deleted owner.");
      this.getTableData();
      this.setState({loading: false});
    } catch (err) {
      
      this.setState({loading: false});
      message.error("Network Error, Please Try Again");
    }
  };

  onModalFinish = (e) => {
    const {challanType} = e;
    const {userDetails} = this.state;
    if (challanType == "selectedpage") {
      const {datasource} = this.state;
      let accoutDetails = [];
      const selectedData = this.state.datasource
        .filter((d) => {
          if (this.state.selectedRowKeys.includes(d._id)) {
            return d;
          }
        })
        .map((d, index) => {
          d.accountIds.map((Ac, index) => {
            accoutDetails.push(Ac);
          });
          return {
            "S.No": index + 1,
            id: d._id,
            name: d.name,
            Address: d.address,
            District: d.district,
            State: d.state,
            Phone: d.phoneNumber,
            PAN: d.panNumber,
            Email: d.email,
          };
        });

      const accountData = accoutDetails.map((data) => {
        return {
          id: data._id,
          ownerId: data.ownerId,
          accountHolderName: data.accountHolderName,
          accountNumber: data.accountNumber,
          ifscCode: data.ifscCode,
          bankName: data.bankName,
          branchName: data.branchName,
        };
        // });
      });
      // console.log(accountData, "accountda");
      if (selectedData.length === 0) {
        message.error("Please select owners to download");
      } else {
        const ws = XLSX.utils.json_to_sheet(selectedData);
        const ws2 = XLSX.utils.json_to_sheet(accountData);
        // const wb = {
        let wb;
        if (userDetails && userDetails.roleName === "Admin") {
          wb = {
            Sheets: {
              owner_data_sheet: ws,
              account_data_sheet: ws2,
            },
            SheetNames: ["owner_data_sheet", "account_data_sheet"],
          };
        } else {
          wb = {
            Sheets: {
              owner_data_sheet: ws,
            },
            SheetNames: ["owner_data_sheet"],
          };
        }

        const excelBuffer = XLSX.write(wb, {bookType: "xlsx", type: "array"});
        const data = new Blob([excelBuffer], {
          type:
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8",
        });
        var url = window.URL.createObjectURL(data);
        window.open(url, "_blank");
      }
      this.setState({
        downloadVisible: false,
      });
    }
    if (challanType == "allchallans") {
      const {datasource} = this.state;
      let accoutDetails = [];
      const selectedData = this.state.datasource.map((d, index) => {
        d.accountIds.map((Ac, index) => {
          accoutDetails.push(Ac);
        });
        return {
          "S.No": index + 1,
          id: d._id,
          name: d.name,
          Address: d.address,
          District: d.district,
          State: d.state,
          Phone: d.phoneNumber,
          PAN: d.panNumber,
          Email: d.email,
        };
      });

      const accountData = accoutDetails.map((data) => {
        return {
          id: data._id,
          ownerId: data.ownerId,
          accountHolderName: data.accountHolderName,
          accountNumber: data.accountNumber,
          ifscCode: data.ifscCode,
          bankName: data.bankName,
          branchName: data.branchName,
        };
        // });
      });
      // console.log(accountData, "accountda");
      if (selectedData.length === 0) {
        message.error("Please select users to download");
      } else {
        const ws = XLSX.utils.json_to_sheet(selectedData);
        const ws2 = XLSX.utils.json_to_sheet(accountData);
        // const wb = {
        let wb;
        if (userDetails && userDetails.roleName === "Admin") {
          wb = {
            Sheets: {
              owner_data_sheet: ws,
              account_data_sheet: ws2,
            },
            SheetNames: ["owner_data_sheet", "account_data_sheet"],
          };
        } else {
          wb = {
            Sheets: {
              owner_data_sheet: ws,
              account_data_sheet: ws2,
            },
            SheetNames: ["owner_data_sheet"],
          };
        }

        const excelBuffer = XLSX.write(wb, {bookType: "xlsx", type: "array"});
        const data = new Blob([excelBuffer], {
          type:
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8",
        });
        var url = window.URL.createObjectURL(data);
        window.open(url, "_blank");
      }
      this.setState({
        downloadVisible: false,
      });
    }
    if (challanType == "createOwner") {
      // console.log("hihi");

      // var url = window.URL.createObjectURL();
      window.open(XlsFile, "_blank");
    }
    this.setState({
      downloadVisible: false,
    });
  };

  searchResult = async(e) => {
    // clearTimeout(timeout);
    let searchValue = e;
    
    // timeout = setTimeout(() => {
    //   this.setState({searchValue});
    //   if (
    //     searchValue !== "" &&
    //     searchValue !== null &&
    //     searchValue !== undefined
    //   ) {
    //     searchValue = searchValue.toString().toUpperCase().trim();

    //     const newSearchedTableData = this.state.datasource.filter(({name}) => {
    //       const vehno = name || "";
    //       return vehno.toString().toUpperCase().includes(searchValue);
    //     });
        
    //     this.setState({searchResultArray: newSearchedTableData});
    //   } else {
    //     this.setState({
    //       searchResultArray: [],
    //       printableArray: [],
    //       selectedRowKeys: [],
    //     });
    //   }
    // }, 500);
    if (searchValue != "") {
      this.setState({searchValue:searchValue ,page:1})
    } else {
      this.setState({searchValue:"",page:1})
    }

    
    if ( searchValue !== "" && searchValue !== null && searchValue !== undefined ) {
          // searchValue = searchValue.toString().toUpperCase().trim();
            
          const newSearchedTableData = await this.getTableData(searchValue,1);
          
          this.setState({searchResultArray: newSearchedTableData});
        } else if(searchValue === "" || searchValue === null ) {
          const newSearchedTableData = await this.getTableData(searchValue,1);
          
          this.setState({searchResultArray: newSearchedTableData});
          
        }else{
          this.setState({
            searchResultArray: [],
            printableArray: [], 
            selectedRowKeys: [],
          });
        }


  };
  doSomethingBeforeUnload = async () => {
    await axios.put(`${BASE_URL}logout/${this.state.logId}`);
  };
  setupBeforeUnloadListener = async () => {
    
    window.addEventListener("beforeunload", async (ev) => {
      ev.preventDefault();
      return this.doSomethingBeforeUnload();
    });
  };

  onModalDownload = () => {
    this.setState({
      downloadVisible: true,
    });
  };
  onModalUpload = () => {
    this.setState({
      uploadVisible: true,
    });
  };
  axiosFileUploadRequestUpdate = async (file) => {
    this.setState({loading: true});
    try {
      const formData = new FormData();
      formData.append("file", file.file);
      const config = {
        headers: {
          "content-type": "multipart/form-data",
        },
      };
      await axios
        .post(`${BASE_URL}update-owner-by-xsl`, formData, config)
        .then((data) => {
          message.success("Sucessfully Uploaded");

          this.setState({
            uploadVisible: false,
            loading: false,
          });
        })
        .catch((error) => {
          const {response} = error;
          const {data} = response;
          const {message: msg} = data;
          message.error(msg);

          this.setState({
            loading: false,
          });
        });

      this.setState({loading: false});
      try {
        const response = await axios.get(`${BASE_URL}get-owner-bank-details`);
        const ownerDetails = response.data.ownerDetails || "";
        if (ownerDetails && ownerDetails.length > 0) {
          const arrRes = ownerDetails.sort(function (a, b) {
            a = a.name.toLowerCase();
            b = b.name.toLowerCase();

            return a < b ? -1 : a > b ? 1 : 0;
          });
          this.setState({
            datasource: arrRes,
          });
        }
      } catch (err) {
        console.log(err);
      }
    } catch (err) {
      // message.error("Network Failed, Please Try Again");
    }
  };

  axiosFileUploadRequest = async (file) => {
    this.setState({loading: true});
    try {
      const formData = new FormData();
      formData.append("file", file.file);
      const config = {
        headers: {
          "content-type": "multipart/form-data",
        },
      };
      await axios
        .post(`${BASE_URL}create-owner-by-xsl`, formData, config)
        .then((data) => {
          message.success("Sucessfully Uploaded");
          this.setState({
            uploadVisible: false,
            loading: false,
          });
        })
        .catch((error) => {
          const {response} = error;
          const {data} = response;
          const {message: msg} = data;
          message.error(msg);
          this.setState({loading: false});
        });
      try {
        const response = await axios.get(`${BASE_URL}get-owner-bank-details?page=1&limit=50`);
        const ownerDetails = response.data.ownerDetails[0].data || "";
        
        if (ownerDetails && ownerDetails.length > 0) {
          const arrRes = ownerDetails.sort(function (a, b) {
            a = a.name.toLowerCase();
            b = b.name.toLowerCase();

            return a < b ? -1 : a > b ? 1 : 0;
          });
          await this.setState({
            datasource: arrRes,
            totalCount: response.data.ownerDetails[0].count || "",
            page: 1,
            loading:false
          });
          // this.setState({loading: false});
        }
      } catch (err) {
        console.log(err);
        this.setState({loading: false});
      }
    } catch (err) {
      // message.error("Network Failed, Please Try Again");
    }
  };


  onTableRowExpand(expanded, record) {
    var keys = [];
    if (expanded) {
      keys.push(record._id);
      this.setState({
        rowId: record._id,
      });
    }
    this.setState({expandedRowKeys: keys});
  }

  setRowClassName = (record) => {
    return record._id === this.state.rowId ? "expandparent" : "";
  };
  render() {
    const {selectedRowKeys, ownerDetails, userDetails} = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
      hideDefaultSelections: true,
      columnWidth: 10,
    };

    const columns = [
      {
        title: "Sl No",
        dataIndex: "slno",
        key: "name",
        fixed: "top",
        render: (text, record,index) => (
          <span>
            {this.state.searchValue === "" || this.state.searchValue === null
              ? (this.state.page - 1) * this.state.limit + index + 1 
              : (this.state.page - 1) * this.state.limit + index + 1}
          </span>
          // this.state.datasource.indexOf(record) + 1
        ),   
      },
      {
        title: "Owner Name",
        dataIndex: "name",
        key: "name",
      },
      {
        title: "Address",
        dataIndex: "address",
        key: "address",
        render: NullData,
      },
      {
        title: "District",
        dataIndex: "district",
        key: "district",
        render: NullData,
      },
      {
        title: "State",
        dataIndex: "state",
        key: "state",
        render: NullData,
      },
      {
        title: "PAN Card",
        dataIndex: "panNumber",
        key: "panNumber",
        render: NullData,
      },
      {
        title: "Email ID",
        dataIndex: "email",
        key: "email",
        render: NullData,
      },
      {
        title: "Action",
        dataIndex: "",
        key: "x",
        render: (text, record) => (
          <div style={{display: "flex"}}>
            <div style={{margin: "5px"}}>
              <img
                src={Editicon}
                style={{cursor: "pointer"}}
                onClick={() => this.showmodals(record)}
              />
            </div>
            {userDetails && userDetails.roleName === "Admin" ? (
              <div style={{margin: "5px"}}>
                <Popconfirm
                  title="Are you sure?"
                  onConfirm={() => this.confirm(record)}
                  onCancel={this.cancel}
                  okText="Yes"
                  cancelText="No"
                >
                  <a href="#">
                    <img src={Deleteicon} style={{cursor: "pointer"}} />
                  </a>
                </Popconfirm>
              </div>
            ) : (
              ""
            )}
          </div>
        ),
      },
    ];
    const insidecolumns = [
      {
        title: "Account Holders Name",
        dataIndex: "accountHolderName",
        key: "accountHolderName",
        width: "18%",
        render: NullData,
      },
      {
        title: "Bank Name",
        dataIndex: "bankName",
        key: "bankName",
        width: "10%",
        render: NullData,
      },
      {
        title: "Bank Branch",
        dataIndex: "branchName",
        key: "branchName",
        width: "10%",
        render: NullData,
      },
      {
        title: "IFSC Code",
        dataIndex: "ifscCode",
        key: "ifscCode",
        width: "10%",
        render: NullData,
      },
      {
        title: "A/C NO",
        dataIndex: "accountNumber",
        key: "accountNumber",
        width: "10%",
        render: (text, record) => (
          <span>
            {userDetails && userDetails.roleName === "Admin"
              ? text
              : AccountMasking(text, record)}
          </span>
        ),
      },
      {
        title: "Phone No",
        dataIndex: "phoneNumber",
        key: "phoneNumber",
        width: "10%",
        render: NullData,
      },
    ];
    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Vehicle Owner Details
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Enter Truck Owner Details
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <HeaderBreedCrumb
            path="home"
            history={this.props.history}
            userData={this.state.userDetails}
          />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <Modal
            title={
              <div>
                <h3 style={{fontSize: "20px", margin: "0px"}}>
                  Upload Owner Master
                </h3>
                <p
                  style={{
                    fontSize: "12px",
                    fontFamily: "MyriadProBold",
                  }}
                >
                  Upload Owner Master Details
                </p>
              </div>
            }
            visible={this.state.uploadVisible}
            onOk={this.handleUploadOk}
            destroyOnClose={true}
            onCancel={this.handleUploadCancel}
            footer={null}
          >
            <div>
              <Form
                ref={this.formRef}
                name="material"
                onFinish={this.onModalFinish}
                autoCompleter="off"
                style={{margin: "30px 30px 60px 30px"}}
              >
                <Row>
                  <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                    <h3 className="typeTitle">Upload Type</h3>
                  </Col>

                  <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                    <div
                      className="material-type"
                      style={{paddingBottom: "30px"}}
                    >
                      <Upload
                        name="file"
                        customRequest={this.axiosFileUploadRequest}
                        accept=".xlsx"
                        showUploadList="false"
                        fileList=""
                      >
                        <Button
                          className="button"
                          icon={
                            <img
                              src={Uploadicon}
                              style={{paddingRight: "10px"}}
                            />
                          }
                        >
                          CREATE OWNER UPLOAD
                        </Button>
                      </Upload>
                    </div>
                  </Col>
                  <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                    <div className="material-type">
                      <Upload
                        name="file"
                        customRequest={this.axiosFileUploadRequestUpdate}
                        accept=".xlsx"
                        showUploadList="false"
                        fileList=""
                      >
                        <Button
                          className="button"
                          icon={
                            <img
                              src={Uploadicon}
                              style={{paddingRight: "10px"}}
                            />
                          }
                        >
                          UPDATE OWNER UPLOAD
                        </Button>
                      </Upload>
                    </div>
                  </Col>
                </Row>
              </Form>
            </div>
          </Modal>
          <Modal
            title={
              <div>
                <h3 style={{fontSize: "20px", margin: "0px"}}>
                  Download Owner Master
                </h3>
                <p
                  style={{
                    fontSize: "12px",
                    fontFamily: "MyriadProBold",
                  }}
                >
                  Download Owner Master Details
                </p>
              </div>
            }
            visible={this.state.downloadVisible}
            onOk={this.handleModalOk}
            destroyOnClose={true}
            onCancel={this.handleModalCancel}
            footer={null}
          >
            <div>
              <Form
                ref={this.formRef}
                name="material"
                onFinish={this.onModalFinish}
                autoCompleter="off"
                style={{margin: "30px 30px 60px 30px"}}
              >
                <Row>
                  <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                    <h3 className="typeTitle">Download Type</h3>
                  </Col>

                  <Col xs={24} sm={24} md={17} lg={17} xl={17}>
                    <div className="material-type">
                      <Form.Item
                        name="challanType"
                        rules={[
                          {
                            required: true,
                            message: "This feild is required",
                          },
                        ]}
                      >
                        <Select placeholder="Select owners to download">
                          <Select.Option value="createOwner">
                            Create Owner Template
                          </Select.Option>
                          <Select.Option value="selectedpage">
                            Selected Page
                          </Select.Option>
                          <Select.Option value="allchallans">
                            All Owner
                          </Select.Option>
                        </Select>
                      </Form.Item>
                    </div>
                  </Col>
                  <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                    <Button className="addbtn" htmlType="submit">
                      Download
                    </Button>
                  </Col>
                </Row>
              </Form>
            </div>
          </Modal>
          <div className="ownermaster">
            <Spin spinning={this.state.loading}>
              <div>
                <Modal
                  title={
                    <div>
                      View Document
                      <p
                        style={{
                          fontSize: "12px",
                          fontFamily: "AvenirMedium",
                        }}
                      >
                        RC Book View{" "}
                      </p>
                    </div>
                  }
                  visible={this.state.doucmentvisible}
                  onOk={this.handleOk}
                  onCancel={this.handledocumentCancel}
                  footer={null}
                >
                  <img src="https://i.stack.imgur.com/oCFF7.png" width="100%" />
                </Modal>
                <Modal
                  title={
                    <div>
                      <h3 style={{fontSize: "20px", margin: "0px"}}>
                        Vehicle Owner Details
                      </h3>
                      <p
                        style={{
                          fontSize: "12px",
                          fontFamily: "AvenirMedium",
                        }}
                      >
                        Enter Truck Owner Details
                      </p>
                    </div>
                  }
                  className="ModalLook"
                  visible={this.state.editvisible}
                  onOk={this.handleOk}
                  onCancel={this.handleCancelEdit}
                  destroyOnClose
                  footer={null}
                >
                  {ownerDetails && (
                    <EditvehicleiOwnerDetail
                      ownerdetail={ownerDetails}
                      onUpdate={this.getTableData}
                      onSearch={() => this.searchResult(this.state.searchValue)}
                    />
                  )}
                </Modal>
                <div style={{padding: "20px"}}>
                  <div className="search">
                    <Row>
                      <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                        <h3 className="filterLabel">Search By Owner Name</h3>
                        <Search
                          className="searchbtn"
                          style={{width: "250px"}}
                          // onChange={this.searchResult}
                          onChange={(e) =>
                            this.searchResult(e.target.value)
                          }
                        />
                      </Col>

                      <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                        <div
                          style={{
                            float: "right",
                            display: "flex",
                          }}
                        >
                          <div
                            style={{
                              marginTop: "20px",
                            }}
                          >
                            {/* <Upload
                              name="file"
                              customRequest={this.axiosFileUploadRequest}
                              accept=".xlsx"
                              showUploadList="false"
                              fileList=""
                              // className={Styles.growerUpload}
                            > */}
                            <Button
                              className="button"
                              icon={
                                <img
                                  src={Uploadicon}
                                  style={{paddingRight: "10px"}}
                                />
                              }
                              onClick={() => {
                                this.onModalUpload();
                              }}
                            >
                              {/* <UploadOutlined
                              style={{fontSize: "20px", color: "#01a6d0"}}
                            /> */}
                              UPLOAD
                            </Button>
                            {/* </Upload> */}
                          </div>
                          <div
                            style={{
                              marginTop: "20px",
                            }}
                          >
                            <Button
                              className="button"
                              icon={
                                <img
                                  src={Download}
                                  style={{paddingRight: "10px"}}
                                />
                              }
                              onClick={() => {
                                this.onModalDownload();
                              }}
                            >
                              DOWNLOAD
                            </Button>
                          </div>
                          <div
                            style={{
                              marginTop: "20px",
                            }}
                          >
                            <PrintOwnerDetail
                              printData={selectedRowKeys}
                              Data={this.state.datasource}
                            />
                            {/* <Button
                              className="button"
                              icon={
                                <img
                                  src={Print}
                                  style={{paddingRight: "10px"}}
                                />
                              }
                            >
                              PRINT
                            </Button> */}
                          </div>
                          <div
                            style={{
                              marginTop: "20px",
                            }}
                          >
                            <Button
                              type="primary"
                              onClick={this.showModal}
                              className="addbtn"
                              icon={
                                <img
                                  src={Addtruckicon}
                                  style={{marginRight: "6px"}}
                                />
                              }
                              onClick={() => this.setState({visible: true})}
                            >
                              ADD TRUCK OWNER DATA
                            </Button>
                          </div>
                        </div>
                      </Col>
                    </Row>
                  </div>
                  <div
                    style={{position: "absolute", right: 30, padding: "10px"}}
                  >
                    <Modal
                      title={
                        <div>
                          <h3 style={{fontSize: "20px", margin: "0px"}}>
                            Vehicle Owner Information
                          </h3>
                          <p
                            style={{
                              fontSize: "12px",
                              fontFamily: "AvenirMedium",
                              wordWrap: "break-word",
                            }}
                          >
                            Enter Truck registration and Owner Details
                          </p>
                        </div>
                      }
                      className="ModalLook"
                      visible={this.state.visible}
                      destroyOnClose
                      onOk={this.handleOk}
                      onCancel={this.handleCancel}
                      footer={null}
                    >
                      <div>
                        <AddvehicleiOwnerDetail onUpdate={this.getTableData} />
                      </div>
                    </Modal>
                  </div>
                </div>
                <div
                  className={this.state.TableHeaderClass}
                  style={{
                    overflowX: "auto",
                    margin: "10px 10px 0px 10px",
                  }}
                  id="content"
                >
                  <Table
                    columns={columns}
                    rowSelection={rowSelection}
                    rowKey="_id"
                    onRow={this.onClickRow}
                    // scroll={{y: 480}}
                    rowClassName={this.setRowClassName}
                    expandedRowKeys={this.state.expandedRowKeys}
                    onExpand={(expand, record) =>
                      this.onTableRowExpand(expand, record)
                    }
                    expandedRowRender={(record) => (
                      <Table
                        size="small"
                        className="insideTable"
                        columns={insidecolumns}
                        dataSource={this.getInsideData(record)}
                        rowKey="insidetable"
                        pagination={false}
                      />
                    )}
                    dataSource={
                      this.state.searchValue === "" ||
                      this.state.searchValue === null
                        ? this.state.datasource
                        : this.state.searchResultArray
                    }
                    expandIcon={(props) => this.customExpandIcon(props)}
                    style={{width: "100%"}}
                    // pagination={{
                    //   pageSizeOptions: ["10", "20", "50", "100"],
                    //   defaultPageSize: 50,
                    // }}
                    pagination={{
                      current: this.state.page,
                      defaultPageSize: 50,
                      showSizeChanger: true, 
                      onShowSizeChange: this.changePaginationAll,
                      total: this.state.totalCount,
                      onChange: this.changePagination,
                    }}
                  />
                </div>
                <div id="afterTable"></div>
              </div>
            </Spin>
          </div>
        </div>
      </div>
    );
  }
}

export default OwnerMaster;
