/* eslint-disable no-unused-vars */
/* eslint-disable no-const-assign */
/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from "react";
import {
  Table,
  Input,
  Button,
  Modal,
  Popconfirm,
  Checkbox,
  message,
  Spin,
  Row,
  Select,
  DatePicker,
  Col,
  Form,
  InputNumber,
} from "antd";
import moment from "moment";
import {RightOutlined, DownOutlined} from "@ant-design/icons";
import EditReceiveAcknowledgement from "./editreceiveacknowledgement";
import axios from "axios";
import HeaderBreedCrumb from "../../../components/receivemenubreedcrumb/receivemenubreedcrumb";
import CurrentTime from "../../../components/currenttime/currenttime";
import {BASE_URL} from "../../../constants/constants";
import Editicon from "../../../assests/images/edit.svg";
import ChallanIcon from "../../../assests/images/challan.svg";
import DownloadIcon from "../../../assests/images/download.svg";
import PrintIcon from "../../../assests/images/print.svg";
import Deleteicon from "../../../assests/images/delete.svg";
import ViewDocument from "../../../assests/images/documentview.svg";
import dateicon from "../../../assests/images/dateicon.svg";
import PdfViewer from "../../../components/pdfviewer/pdfviewer";
import EditDataTable from "./editabledata";
import "./receive.css";
const {Search} = Input;
const {RangePicker} = DatePicker;

const imageStyle = {
  height: "auto",
  maxWidth: "480px",
  minWidth: "200px",
  paddingBottom: 25,
};
const SELECTED_THRESHOLD = 0;
const iconStyle = {
  color: "#01a6d0",
  fontSize: "14px",
  width: "24px",
  height: "24px",
};
const dateFormatList = ["DD/MM/YYYY"];

const currentDate = moment(new Date(), dateFormatList[1]);

let timeout = null;
const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {},
  columnWidth: "10px",
  getCheckboxProps: (record) => ({
    disabled: record.name === "Disabled User",
    // Column configuration not to be checked
    name: record.name,
  }),
};
const EditableContext = React.createContext();

const EditableRow = ({index, ...props}) => {
  const [form] = Form.useForm();
  return (
    <Form form={form} component={false}>
      <EditableContext.Provider value={form}>
        <tr {...props} />
      </EditableContext.Provider>
    </Form>
  );
};
const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === "number" ? <InputNumber /> : <Input />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Please Input ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

class RecieveAcknowledgement extends Component {
  state = {
    vidible: false,
    loading: false,
    doucmentvisible: false,
    editableVehicleDetails: null,
    fileurl: "",
    searchValue: "",
    searchResultArray: "",
    selectionType: Checkbox,
    dataSource: [],
    fileType: "",
    previewUrl: "",
    materialSelect: [],
    truckTypeStatus: [],
    startDate: "",
    endDate: "",
    editingKey: "",
    data: [],
    FindFilter: "",
    searchFilterData: [],
  };
  customExpandIcon(props) {
    if (props.expanded) {
      return (
        <a
          style={{color: "black"}}
          onClick={(e) => {
            props.onExpand(props.record, e);
          }}
        >
          <DownOutlined style={iconStyle} className="customIcon" />
        </a>
      );
    }
    return (
      <a
        style={{color: "black"}}
        onClick={(e) => {
          props.onExpand(props.record, e);
        }}
      >
        <RightOutlined style={iconStyle} className="customIcon" />
      </a>
    );
  }
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancelEdit = () => {
    this.setState({
      editvisible: false,
    });
  };

  gettableData = async () => {
    try {
      this.setState({loading: true});
      const response = await axios.get(
        `${BASE_URL}get-acknowledgement-register`
      );
      const dispatchData = response.data.disptachData || "";
      if (dispatchData && dispatchData.length > 0) {
        this.setState({
          dataSource: dispatchData.map((data, index) => {
            const date = data.grDate;
            var GrDate = moment(date, "DD-MM-YYYY");
            if (data.invoiceDate) {
              const date = data.invoiceDate;
              var InvoiceDate = moment(date, "DD-MM-YYYY");
            }
            return {
              ...data,
              slno: index + 1,
              commissionPercent: data.commisionRate
                ? "@" + data.commisionRate + "%"
                : 0,
              grDateMom: GrDate,
              invoiceDateMoM: InvoiceDate,
            };
          }),
        });
      }
      this.setState({loading: false});
    } catch (err) {
      this.setState({loading: false});
      message.error("Error fetching data. Please try again later", 2);
    }
  };

  componentDidMount() {
    this.gettableData();
  }

  showmodals(e) {
    this.setState({
      editvisible: true,
      editableVehicleDetails: e,
    });
  }

  deleterecord(e) {}

  confirm = async (e) => {
    try {
      await axios.delete(
        `${BASE_URL}delete-vehicle-details/${e._id}/${
          e.ownerId ? e.ownerId._id : null
        }`
      );
      this.gettableData();
      message.success("Successfully deleted vehicle");
    } catch (err) {
      message.error("Unable to delete");
    }
  };

  searchResult = (e) => {
    try {
      clearTimeout(timeout);
      let searchValue = e.target.value;

      timeout = setTimeout(() => {
        this.setState({searchValue});
        if (
          searchValue !== "" &&
          searchValue !== null &&
          searchValue !== undefined
        ) {
          const searchData = this.state.searchFilterData;
          searchValue = searchValue.toString().toUpperCase().trim();
          if (searchData.length > 0) {
            const newSearchedTableData = searchData.filter(
              ({vehicleNumber, deliveryNumber}) => {
                return (
                  this.searchFn(searchValue, deliveryNumber) ||
                  this.searchFn(searchValue, vehicleNumber)
                );
              }
            );
            this.setState({
              searchResultArray: newSearchedTableData,
              searchFilterData: newSearchedTableData,
            });
          } else {
            const newSearchedTableData = this.state.dataSource.filter(
              ({vehicleNumber, deliveryNumber}) => {
                return (
                  this.searchFn(searchValue, deliveryNumber) ||
                  this.searchFn(searchValue, vehicleNumber)
                );
              }
            );
            this.setState({
              searchResultArray: newSearchedTableData,
            });
          }
        } else {
          this.setState({
            searchResultArray: [],
            selectedRowKeys: [],
          });
        }
      }, 500);
    } catch (err) {
      console.error({err});
    }
  };

  searchFn = (search, value) => {
    if (value && search) {
      return value.toString().toUpperCase().includes(search);
    } else {
      return false;
    }
  };

  reloadAfterModalClose = () => {
    this.gettableData();
    this.setState({
      visible: false,
      editvisible: false,
      doucmentvisible: false,
    });
  };
  handleMaterialType = async (materialValue) => {
    // await this.getTableData();
    const obj = materialValue.toString();
    const matValue =
      Object.keys(materialValue).length > 0 ? obj.split(",") : [];
    this.setState({materialSelect: matValue});
    const {dataSource} = this.state;
    materialValue = materialValue.toString().toUpperCase();
    if (matValue.length > 0 && dataSource.length > 0) {
      const filteredTableData = dataSource.filter(({materialType}) =>
        matValue.includes(materialType.toString().toUpperCase())
      );
      this.setState({dataSource: filteredTableData});
    }
  };
  handleTruckType = (truckValue) => {
    // await this.getTableData();
    const obj = truckValue.toString().toLowerCase();
    const vehicleValue =
      Object.keys(truckValue).length > 0 ? obj.split(",") : [];
    this.setState({truckTypeStatus: vehicleValue});
    const {dataSource} = this.state;
    truckValue = vehicleValue.toString().toLowerCase().trim();
    if (vehicleValue.length > 0 && dataSource.length > 0) {
      const filteredTableData = dataSource.filter(({vehicleType}) =>
        vehicleValue.includes(vehicleType.toString().toLowerCase())
      );
      this.setState({dataSource: filteredTableData});
    }
  };

  onDateChange = async (dates) => {
    await this.gettableData();
    const {dataSource} = this.state;
    const filterDateFormat = "YYYY-MM-DD";
    if (dates) {
      try {
        const [a, b] = dates;
        var st = (a && moment(a || "").format(filterDateFormat)) || "";
        var et = (b && moment(b || "").format(filterDateFormat)) || "";
        const filteredData = dataSource.filter((ackData) => {
          const {grISODate} = ackData;
          var dt = moment(new Date(grISODate)).format(filterDateFormat);
          if (st && et) {
            return (
              moment(dt).isSameOrAfter(st, "day") &&
              moment(dt).isSameOrBefore(et, "day")
            );
          }
          if (st) {
            return moment(dt).isSameOrAfter(st, "day");
          }
          if (et) {
            return moment(dt).isSameOrBefore(et, "day");
          }
        });
        await this.setState({
          dataSource: filteredData,
          searchResultArray: filteredData,
          searchValue: st || et,
          searchFilterData: filteredData,
        });
      } catch (error) {
        console.log({error});
        message.error("Unable to fetch data");
      }
    } else {
      await this.setState({dataSource: dataSource, searchValue: ""});
    }
  };

  render() {
    const {
      dataSource,
      searchResultArray,
      searchValue,
      SetSearchData,
    } = this.state;

    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Receive Acknowledgement
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Review/Update Acknowledgement
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <HeaderBreedCrumb path="home" history={this.props.history} />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <Spin spinning={this.state.loading} delay={500}>
            <div className="recive">
              <div style={{padding: "0px 0px 0px 20px"}}>
                <Row>
                  <Col
                    xs={12}
                    sm={12}
                    md={5}
                    lg={5}
                    xl={5}
                    style={{padding: "10px"}}
                  >
                    <h3
                      className="filterLabel"
                      style={{
                        borderRadius: "10px",
                      }}
                    >
                      Sort by Date Range
                    </h3>
                    <div style={{display: "flex"}}>
                      <RangePicker
                        format={dateFormatList}
                        onChange={(dates) => this.onDateChange(dates)}
                        allowEmpty={[true, true]}
                        suffixIcon={<img src={dateicon} alt="dateIcon" />}
                      />
                    </div>
                  </Col>
                  <Col
                    xs={12}
                    sm={12}
                    md={5}
                    lg={5}
                    xl={5}
                    style={{padding: "10px"}}
                  >
                    <h3 className="filterLabel">
                      Search By Delivery No or Truck No
                    </h3>
                    <div className="challansearch">
                      <Search
                        className="searchbtn"
                        onChange={this.searchResult}
                        style={{height: "30px !important"}}
                      />
                    </div>
                  </Col>
                  <Col xs={24} sm={24} md={14} lg={14} xl={14}>
                    <div
                      style={{
                        float: "right",
                        marginTop: "20px",
                        display: "flex",
                      }}
                    >
                      <div>
                        <Button
                          className="button"
                          icon={
                            <img
                              src={DownloadIcon}
                              style={{paddingRight: "10px"}}
                            />
                          }
                          // onClick={() => {
                          //   this.onDownload();
                          // }}
                        >
                          DOWNLOAD
                        </Button>
                      </div>
                    </div>
                  </Col>
                </Row>
              </div>
              <div className="recieveackTable">
                <EditDataTable
                  TableData={dataSource || searchResultArray}
                  DataValue={searchResultArray}
                  FindFilter={searchValue}
                />
              </div>
            </div>
          </Spin>
        </div>
      </div>
    );
  }
}

export default RecieveAcknowledgement;
