/* eslint-disable react/jsx-no-duplicate-props */
/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from "react";
import {Table, Input, message, Spin, Row, Col} from "antd";
import HeaderBreedCrumb from "../../../components/menubreedcrumb/menubreedcrumb";
import CurrentTime from "../../../components/currenttime/currenttime";
import axios from "axios";
import {BASE_URL} from "../../../constants/constants";

import auth from "../../../utils/auth";
import "./ownertransferlog.css";
import moment from "moment";

const {Search} = Input;

const NullData = (tableText) => <span>{tableText || <p>&#8212;</p>}</span>;

let timeout = null;

class OwnerTransferLog extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const Det = auth.getUserData();
    this.state = {
      vidible: false,
      loading: false,
      doucmentvisible: false,
      fileurl: "",
      datasource: [],
      searchValue: "",
      searchResultArray: [],
      editvisible: false,
      selectedRowKeys: [],
      ownerDetails: "",
      key: "",
      expandedRowKeys: "null",
      rowExpandable: false,
      rowId: "",
      userDetails: Details,
      TableHeaderClass: "maintable",
      logId: Det._id,
    };
  }

  getTableData = async () => {
    try {
      this.setState({loading: true});
      const response = await axios.get(`${BASE_URL}get-all-users-logs`);
      const ownerDetails = response.data.ownerDetails || "";
      if (ownerDetails && ownerDetails.length > 0) {
        const data = ownerDetails.map((data, index) => {
          // var dateObj = new Date(dateString);
          let momentString = "";
          if (data.ownerTransferDate) {
            let momentObj = moment(new Date(data.ownerTransferDate));
            momentString = momentObj.format("MMMM Do YYYY, h:mm:ss A");
          } else {
            let momentObj = moment(new Date(data.createdAt));
            momentString = momentObj.format("MMMM Do YYYY, h:mm:ss A");
          }

          return {
            ...data,
            transferFrom: data.oldOwnerId ? data.oldOwnerId.name : "",
            transferTo: data.newOwnerId ? data.newOwnerId.name : "",
            transferDate: momentString,
          };
        });

        this.setState({
          datasource: data,
        });
      }

      this.setState({loading: false});
    } catch (err) {
      this.setState({loading: false});
      message.error("Error fetching data. Please try again later", 2);
    }
  };
  doSomethingBeforeUnload = async () => {
    await axios.put(`${BASE_URL}logout/${this.state.logId}`);
  };
  setupBeforeUnloadListener = async () => {
    console.log(this.state.userDetails, "confiff");
    window.addEventListener("beforeunload", async (ev) => {
      ev.preventDefault();
      return this.doSomethingBeforeUnload();
    });
  };

  componentDidMount() {
    this.getTableData();
    this.setupBeforeUnloadListener();
  }

  searchResult = (e) => {
    clearTimeout(timeout);
    let searchValue = e.target.value;
    timeout = setTimeout(() => {
      this.setState({searchValue});
      if (
        searchValue !== "" &&
        searchValue !== null &&
        searchValue !== undefined
      ) {
        searchValue = searchValue.toString().toUpperCase().trim();

        const newSearchedTableData = this.state.datasource.filter(
          ({vehicleNumber}) => {
            const vehno = vehicleNumber || "";
            console.log(vehno);
            return vehno.toString().toUpperCase().includes(searchValue);
          }
        );
        this.setState({searchResultArray: newSearchedTableData});
      } else {
        this.setState({
          searchResultArray: [],
          printableArray: [],
          selectedRowKeys: [],
        });
      }
    }, 500);
  };

  render() {
    const {userDetails} = this.state;

    const columns = [
      {
        title: "Sl No",
        dataIndex: "slno",
        key: "name",
        fixed: "top",
        render: (text, record) => (
          <span>
            {this.state.searchValue === "" || this.state.searchValue === null
              ? this.state.datasource.indexOf(record) + 1
              : this.state.searchResultArray.indexOf(record) + 1}
          </span>
        ),
      },
      {
        title: "Vehicle Number",
        dataIndex: "vehicleNumber",
        key: "vehicleNumber",
        render: NullData,
      },
      {
        title: "Transfer From (Owner)",
        dataIndex: "transferFrom",
        key: "transferFrom",
        render: NullData,
      },
      {
        title: "Transfer To (Owner)",
        dataIndex: "transferTo",
        key: "transferTo",
        render: NullData,
      },
      {
        title: "Transfer Date and Time",
        dataIndex: "transferDate",
        key: "state",
        render: NullData,
      },
    ];

    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Owner Transfer Log
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              View Owner Transfer Details
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <HeaderBreedCrumb
            path="home"
            history={this.props.history}
            userData={this.state.userDetails}
          />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <div className="ownermaster">
            <Spin spinning={this.state.loading}>
              <div>
                <div style={{padding: "20px"}}>
                  <div className="search">
                    <Row>
                      <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                        <h3 className="filterLabel">
                          Search By Vehicle Number
                        </h3>
                        <Search
                          className="searchbtn"
                          style={{width: "250px"}}
                          onChange={this.searchResult}
                        />
                      </Col>
                    </Row>
                  </div>
                </div>
                <div
                  className={this.state.TableHeaderClass}
                  style={{
                    overflowX: "auto",
                    margin: "10px 10px 0px 10px",
                  }}
                  id="content"
                >
                  <Table
                    columns={columns}
                    rowKey="_id"
                    onRow={this.onClickRow}
                    rowClassName={this.setRowClassName}
                    dataSource={
                      this.state.searchValue === "" ||
                      this.state.searchValue === null
                        ? this.state.datasource
                        : this.state.searchResultArray
                    }
                    pagination={{
                      pageSizeOptions: ["10", "20", "50", "100"],
                      defaultPageSize: 20,
                    }}
                  />
                </div>
                <div id="afterTable"></div>
              </div>
            </Spin>
          </div>
        </div>
      </div>
    );
  }
}

export default OwnerTransferLog;
