import React, {Component} from "react";
import {Row, Col, Menu} from "antd";
import "./menubreedcrumb.css";

class MenuBreedCrumb extends Component {
  constructor(props, a) {
    super(props);
    const {userData} = this.props;
    console.log(userData, "userData");
    this.state = {
      current: null,
      userdetails: userData,
    };
  }

  handleConsigmentClick = (e) => {
    this.setState({
      current: e.key,
      activeItem: 1,
      activeState: false,
    });
  };

  ownerdetail = () => {
    this.props.history.push("/vehicle-onwer-details");
  };

  truckdeails = () => {
    this.props.history.push("/truck-details");
  };

  masterDetails = () => {
    this.props.history.push("/master-data");
  };
  ownerTransferLog = () => {
    this.props.history.push("/owner-transfer-log");
  };
  activityLog = () => {
    this.props.history.push("/activity-log");
  };

  render() {
    const url = this.props.history.location.pathname;
    const {userdetails} = this.state;
    return (
      <div>
        <Row className="consignmentList">
          <Col span={20}>
            <Menu
              onClick={this.handleConsigmentClick}
              selectedKeys={[this.state.current]}
              mode="horizontal"
            >
              <Menu.Item
                key="ownermaster"
                className={
                  url === "/vehicle-onwer-details"
                    ? "consigmentActiveItem"
                    : "consigmentItem"
                }
                onClick={this.ownerdetail}
              >
                Owner Master
              </Menu.Item>
              <Menu.Item
                key="truckmaster"
                className={
                  url === "/truck-details"
                    ? "consigmentActiveItem"
                    : "consigmentItem"
                }
                onClick={this.truckdeails}
              >
                Truck Master
              </Menu.Item>
              <Menu.Item
                key="truckmaster"
                className={
                  url === "/master-data"
                    ? "consigmentActiveItem"
                    : "consigmentItem"
                }
                onClick={this.masterDetails}
              >
                Master Data
              </Menu.Item>
              {userdetails && userdetails.roleName === "Admin" ? (
                <Menu.Item
                  key="truckmaster"
                  className={
                    url === "/owner-transfer-log"
                      ? "consigmentActiveItem"
                      : "consigmentItem"
                  }
                  onClick={this.ownerTransferLog}
                >
                  Owner Transfer Log
                </Menu.Item>
              ) : (
                ""
              )}
              {userdetails && userdetails.roleName === "Admin" ? (
                <Menu.Item
                  key="truckmaster"
                  className={
                    url === "/activity-log"
                      ? "consigmentActiveItem"
                      : "consigmentItem"
                  }
                  onClick={this.activityLog}
                >
                  Activity Log
                </Menu.Item>
              ) : (
                ""
              )}
            </Menu>
          </Col>
        </Row>
      </div>
    );
  }
}

export default MenuBreedCrumb;
