import React, {Component} from "react";
import {
  Button,
  Spin,
  Table,
  Popconfirm,
  Row,
  Col,
  Modal,
  Input,
  Switch,
  message,
} from "antd";
import {RightOutlined, DownOutlined} from "@ant-design/icons";
import HeaderBreedCrumb from "../../../../components/settingbreedcrumb/settingbreedcrumb";
import CurrentTime from "../../../../components/currenttime/currenttime";
import Editicon from "../../../../assests/images/edit.svg";
import Deleteicon from "../../../../assests/images/delete.svg";
import Adduser from "../addUser";
import Edituser from "../editUser";
import axios from "axios";
import {BASE_URL} from "../../../../constants/constants";
import "./useronboarding.css";
import auth from "../../../../utils/auth";
const {Search} = Input;
let timeout = null;

class UserOnboarding extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const {_id} = Details;
    const Det = auth.getUserData();
    this.state = {
      visible: false,
      loading: false,
      dataSource: [],
      searchValue: "",
      searchResultArray: [],
      userDetails: Details,
      editvisible: false,
      editUserDetails: [],
      logId: Det._id,
    };
  }
  gettableData = async () => {
    try {
      this.setState({loading: true});
      const response = await axios.get(`${BASE_URL}users`);
      const userData = response.data.user || "";
      if (userData && userData.length > 0) {
        this.setState({
          dataSource: userData,
        });
      } else {
        this.setState({
          dataSource: [],
        });
      }
      this.setState({loading: false});
    } catch (err) {
      this.setState({loading: false});
      message.error("Error fetching data. Please try again later", 2);
    }
  };
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };
  showmodals = (e) => {
    this.setState({
      editvisible: true,
      editUserDetails: e,
    });
  };
  confirm = async (id) => {
    try {
      const response = await axios.delete(`${BASE_URL}delete-user/${id}`);
      message.success("Record Deleted Successfully");
      this.gettableData();
    } catch (error) {
      message.error("unable to delete");
    }
  };
  handleCancelEdit = (e) => {
    this.setState({
      editvisible: false,
    });
  };
  handleStatusChange = async (e) => {
    const {_id, isActive} = e;
    try {
      const response = await axios.put(`${BASE_URL}user-status`, {
        userId: _id,
        status: isActive === true ? false : true,
      });
      message.success("Updated user status successfully");
      this.gettableData();
    } catch (error) {
      message.error("Unable to Update users status");
    }
  };
  reloadAfterModalClose = () => {
    this.gettableData();
    this.setState({
      visible: false,
      editvisible: false,
    });
  };
  searchResult = (e) => {
    try {
      clearTimeout(timeout);
      let searchValue = e.target.value;
      timeout = setTimeout(() => {
        this.setState({searchValue});
        if (
          searchValue !== "" &&
          searchValue !== null &&
          searchValue !== undefined
        ) {
          searchValue = searchValue.toString().toUpperCase().trim();

          const newSearchedTableData = this.state.dataSource.filter(
            ({name}) => {
              return this.searchFn(searchValue, name);
            }
          );
          this.setState({searchResultArray: newSearchedTableData});
        } else {
          this.setState({
            searchResultArray: [],
          });
        }
      }, 500);
    } catch (err) {
      console.error({err});
    }
  };
  searchFn = (search, value) => {
    if (value && search) {
      return value.toString().toUpperCase().includes(search);
    } else {
      return false;
    }
  };
  doSomethingBeforeUnload = async () => {
    await axios.put(`${BASE_URL}logout/${this.state.logId}`);
  };
  setupBeforeUnloadListener = async () => {
    console.log(this.state.userDetails, "confiff");
    window.addEventListener("beforeunload", async (ev) => {
      ev.preventDefault();
      return this.doSomethingBeforeUnload();
    });
  };

  componentDidMount() {
    this.gettableData();
    this.setupBeforeUnloadListener();
  }
  render() {
    const {editUserDetails, userDetails} = this.state;
    const columns = [
      {
        title: "Sl No",
        dataIndex: "slno",
        key: "slno",
        render: (text, record) => (
          <span>
            {this.state.searchValue === "" || this.state.searchValue === null
              ? this.state.dataSource.indexOf(record) + 1
              : this.state.searchResultArray.indexOf(record) + 1}
          </span>
        ),
      },
      {
        title: "Name",
        dataIndex: "name",
        key: "name",
        render: (text, record) => (
          <span style={{textTransform: "capitalize"}}>
            {record ? record.name : "-"}
          </span>
        ),
      },
      {
        title: "Email ID",
        dataIndex: "email",
        key: "email",
      },
      {
        title: "Phone Number",
        dataIndex: "phoneNumber",
        key: "phoneNumber",
      },
      {
        title: "Role",
        dataIndex: "roleName",
        key: "roleName",
      },
      {
        title: "Action",
        dataIndex: "",
        key: "x",
        // fixed: "right",
        render: (text, record) => (
          <div style={{display: "flex"}}>
            <div style={{margin: "5px"}}>
              <Button
                onClick={() => this.handleStatusChange(record)}
                className="toggleBtn"
              >
                <Switch
                  size="small"
                  defaultChecked={record.isActive ? true : false}
                />
              </Button>
            </div>
            <div style={{margin: "5px"}}>
              <img
                src={Editicon}
                style={{cursor: "pointer"}}
                onClick={() => this.showmodals(record)}
                width="15px"
              />
            </div>
            <div style={{margin: "5px"}}>
              <Popconfirm
                title="Are you sure?"
                onConfirm={() => this.confirm(record._id)}
                onCancel={this.cancel}
                okText="Yes"
                cancelText="No"
              >
                <a href="#">
                  <img
                    src={Deleteicon}
                    style={{cursor: "pointer"}}
                    width="15px"
                  />
                </a>
              </Popconfirm>
            </div>
          </div>
        ),
      },
    ];
    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Account and Settings
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Change Your Settings
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <HeaderBreedCrumb
            path="home"
            history={this.props.history}
            userData={userDetails}
          />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <div className="useronboarding">
            <Spin spinning={this.state.loading}>
              <div>
                <Modal
                  title={
                    <div>
                      <h3 style={{fontSize: "20px", margin: "0px"}}>
                        User Onboarding
                      </h3>
                      <p
                        style={{
                          fontSize: "12px",
                          fontFamily: "AvenirMedium",
                        }}
                      >
                        Enter User Details
                      </p>
                    </div>
                  }
                  className="ModalLook"
                  visible={this.state.visible}
                  onOk={this.handleOk}
                  destroyOnClose={true}
                  onCancel={this.handleCancel}
                  footer={null}
                >
                  <div>
                    <Adduser parentFn={() => this.reloadAfterModalClose()} />
                  </div>
                </Modal>
                <Modal
                  title={
                    <div>
                      <h3 style={{fontSize: "20px", margin: "0px"}}>
                        User Onboarding
                      </h3>
                      <p
                        style={{
                          fontSize: "12px",
                          fontFamily: "AvenirMedium",
                        }}
                      >
                        Edit User Details
                      </p>
                    </div>
                  }
                  className="ModalLook"
                  visible={this.state.editvisible}
                  onOk={this.handleOk}
                  onCancel={this.handleCancelEdit}
                  destroyOnClose={true}
                  footer={null}
                >
                  <div>
                    {editUserDetails && (
                      <Edituser
                        record={editUserDetails}
                        parentFn={() => this.reloadAfterModalClose()}
                      />
                    )}
                  </div>
                </Modal>{" "}
                <div style={{padding: "0px 0px 0px 20px"}}>
                  <Row>
                    <Col
                      xs={12}
                      sm={12}
                      md={5}
                      lg={5}
                      xl={5}
                      style={{padding: "10px"}}
                    >
                      <h3
                        style={{
                          fontSize: "10px",
                          fontWeight: "bold",
                        }}
                      >
                        Search by User Name
                      </h3>
                      <div>
                        <Search
                          className="searchbtn"
                          onChange={this.searchResult}
                          style={{height: "20px !important"}}
                        />
                      </div>
                    </Col>
                    <Col
                      xs={12}
                      sm={12}
                      md={19}
                      lg={19}
                      xl={19}
                      style={{padding: "10px", marginTop: "10px"}}
                    >
                      <div style={{display: "flex", float: "right"}}>
                        <div style={{padding: "10px"}}>
                          <Button type="primary" onClick={this.showModal}>
                            CREATE USER
                          </Button>
                        </div>
                      </div>
                    </Col>
                  </Row>
                </div>
                <div className="useronboardingtable">
                  <Table
                    columns={columns}
                    rowKey="_id"
                    dataSource={
                      this.state.searchValue === "" ||
                      this.state.searchValue === null
                        ? this.state.dataSource
                        : this.state.searchResultArray
                    }
                  />
                </div>
              </div>
            </Spin>
          </div>
        </div>
      </div>
    );
  }
}

export default UserOnboarding;
