import axios from "axios";
import {message} from "antd";
import {BASE_URL} from "../../constants/constants";

const createBillRegister = async (payload) => {
  try {
    const response = await axios.post(
      `${BASE_URL}create-bill-register`,
      payload
    );

    const {Material, message} = response.data || {};

    return {
      Material,
      message,
    };
  } catch (err) {
    // console.log(err.response);
    const errMessage =
      (err.response && err.response.data && err.response.data.message) ||
      "Network Failed, Please Try Again";
    message.error(errMessage);
  }
};

const getAllBillRegisterData = async (searchText,page,limit) => {
  try {
    const response = searchText ? await axios.get(`${BASE_URL}get-all-bill-register?searchBNT=${searchText}&page=${page}&limit=${limit}`) :
    await axios.get(`${BASE_URL}get-all-bill-register?page=${page}&limit=${limit}`);
    // console.log(response);
    const allbillData = response.data || "";
      
    return allbillData;
  } catch (err) {
    
    message.error("Network Failed, Please Try Again");
  }
};

const getBillRegisterDataById = async (registerId) => {
  try {
    const response = await axios.get(
      `${BASE_URL}get-delivery-data/${registerId}`
    );
    // console.log(response);
    const {deliveryDetails} = response.data || "";

    return deliveryDetails;
  } catch (err) {
    message.error("Network Failed, Please Try Again");
  }
};

const getAllDeliveryNumbers = async (searchText, page) => {
  try {
    if (searchText && page) {
      console.log(page);
      const response = await axios.get(
        `${BASE_URL}get-all-bill-delivery?page=${searchText}&limit=${page}`
      );
      console.log(response);
      const {deliveryData} = response.data || "";

      return deliveryData;
    } else if (searchText) {
      const response = await axios.get(
        `${BASE_URL}get-all-bill-delivery?searchDNo=${searchText}`
      );
      // console.log(response);
      const {deliveryData} = response.data || "";

      return deliveryData;
    } else {
      const response = await axios.get(
        `${BASE_URL}get-all-bill-delivery?page=1&limit=300`
      );
      // console.log(response);
      const {deliveryData} = response.data || "";

      return deliveryData;
    }
  } catch (err) {
    message.error("Network Failed, Please Try Again");
  }
};

const updateBillDifference = async (data) => {
  try {
    const response = await axios.put(
      `${BASE_URL}update-difference-data/${data._id}`,
      {
        valueRaised: +data.valueRaised,
        valueReceived: +data.valueReceived,
      }
    );
    // console.log(response);
    const {deliveryDetails} = response.data || "";

    return deliveryDetails;
  } catch (err) {
    message.error("Network Failed, Please Try Again");
  }
};

const updateBillRegister = async (id, data) => {
  console.log(data);
  try {
    const response = await axios.put(
      `${BASE_URL}update-bill-register-data/${id}`,
      data
    );
    console.log(response);
    return response.data;
  } catch (err) {
    message.error("Network Failed, Please Try Again");
  }
};

const deleteBillRegister = async (billId) => {
  try {
    const response = await axios.delete(
      `${BASE_URL}delete-bill-register-data/${billId}`
    );
    // console.log(response);
    const {data, message} = response.data || "";
    // message.success(message);
    return response.data || {};
  } catch (err) {
    message.error("Network Failed, Please Try Again");
  }
};

export {
  getAllBillRegisterData,
  getBillRegisterDataById,
  updateBillDifference,
  getAllDeliveryNumbers,
  createBillRegister,
  deleteBillRegister,
  updateBillRegister,
};
