import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
import Home from "./page/login/login";
import Basic from "./page/onboarding/truckmaster/onboarding";
import OwnerDetail from "./page/onboarding/ownermaster/mainlayout/ownerMaster";
import masterdata from "./page/onboarding/masterdata/addmasterdata";
import Dispatch from "./page/dispatch/dispatchchallan";
import Receive from "./page/recieve/recieveacknowledgement/receiveacknowledgement";
import ReceiveRegister from "./page/recieve/recieveregister/receiveregisterdetails";
import VoucherBook from "./page/accounting-module/voucher/voucherBook";
import OwnerAdvance from "./page/accounting-module/ownerAdvance/ownerAdvance";
import DailyCashBook from "./page/accounting-module/cashBook/dailycashbook";
import Report from "./page/report/ownerReport";
import Accounts from "./page/settings/account/mainlayout/account";
import UserOnboarding from "./page/settings/useronboarding/mainlayout/useronboarding";
import Layout from "./components/layouts/layouts";
import NotFound from "./page/error-handling/not-found";
import Reset from "./page/resetpassword/reset";
import auth from "./utils/auth";
import RecieveReg from "./page/recieveregister/recieveregister";
import RecieveAck from "./page/recieveacknowledgment/recieveacknowledgment";
import Master from "./page/master/addmasterdata";
import ReportDashboard from "./page/reportDashboard/reportDashboard";
import RecoveryRegister from "./page/recoveryRegister/recoveryRegister";
import BillRegister from "./page/billRegister/billRegister";
import OwnerTransferlog from "./page/log/ownertransferlog/ownertransferlog";
import Activitylog from "./page/log/activitylog/activitylog";
import { BASE_URL } from "../src/constants/constants";
import "antd/dist/antd.css";
import axios from "axios";
import "./App.css";

import * as jwt from "jsonwebtoken";

const userDetails = auth.getAuthToken().userDetails;
const userData = auth.getUserData();

const logoutdat = async () => {
  try {
    const { _id } = userData.logData;
    const data = {};
    auth.setUserData({});
    console.log(_id);
    const response = await axios.put(`${BASE_URL}logout/${_id}`);
    // console.log(response, "respone");
  } catch (err) {}
};
const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={(props) => {
      const token = auth.getAuthToken();
      return jwt.verify(token.token, "TruckLink_JWT_KEY", function (
        err,
        decoded
      ) {
        if (err === null) {
          return (
            <>
              <Layout component={Component} {...props} />
            </>
          );
        } else {
          // if (userData) {
          //   logoutdat();
          // }
          return <Redirect to="/" />;
        }
      });
    }}
  />
);
function App() {
  return (
    <div>
      <Router>
        <Switch>
          <Route exact path="/" component={Home}></Route>
          <PrivateRoute exact path="/truck-details" component={Basic} />
          <PrivateRoute
            exact
            path="/vehicle-onwer-details"
            component={OwnerDetail}
          />
          <PrivateRoute exact path="/dispatch-challan" component={Dispatch} />
          {/* <PrivateRoute exact path="/master-data" component={masterdata} /> */}
          {/* <PrivateRoute
            exact
            path="/receive-acknowledgement"
            component={Receive}
          /> */}
          {/* <PrivateRoute
            exact
            path="/receive-register"
            component={ReceiveRegister}
          /> */}
          <PrivateRoute exact path="/voucher-book" component={VoucherBook} />
          <PrivateRoute exact path="/owner-advance" component={OwnerAdvance} />

          <PrivateRoute
            exact
            path="/daily-cash-book"
            component={DailyCashBook}
          />
          <PrivateRoute
            exact
            path="/report-dashboard"
            component={ReportDashboard}
          />
          <PrivateRoute exact path="/report" component={Report} />

          <PrivateRoute exact path="/your-account" component={Accounts} />
          <PrivateRoute
            exact
            path="/user-onboarding"
            component={UserOnboarding}
          />

          <PrivateRoute exact path="/receive-register" component={RecieveReg} />
          <PrivateRoute
            exact
            path="/recovery-register"
            component={RecoveryRegister}
          />
          <PrivateRoute exact path="/bill-register" component={BillRegister} />

          <PrivateRoute
            exact
            path="/receive-acknowledgement"
            component={RecieveAck}
          />

          <PrivateRoute exact path="/master-data" component={Master} />
          <Route exact path="/reset-password" component={Reset}></Route>

          <PrivateRoute
            exact
            path="/owner-transfer-log"
            component={OwnerTransferlog}
          ></PrivateRoute>

          <PrivateRoute
            exact
            path="/activity-log"
            component={Activitylog}
          ></PrivateRoute>
          <Route path="/*" component={NotFound} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
