/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import {
  Table,
  Input,
  Button,
  Form,
  Row,
  Col,
  InputNumber,
  DatePicker,
  Select,
  message,
  Popconfirm,
  Spin,
} from "antd";
import axios from "axios";
import {BASE_URL} from "../../../constants/constants";
import OwnerAdvanceNestedTable from "./ownerAdvanceNestedTable";
import "../cashBook/cashBook.css";
import HeaderBreedCrumb from "../../../components/accountingmenubreedcrumb/accountingmenubreedcrumb";
import CurrentTime from "../../../components/currenttime/currenttime";
import Addtruckicon from "../../../assests/images/addtruckicon.svg";
import dateicon from "../../../assests/images/dateicon.svg";
import {RightOutlined, DownOutlined} from "@ant-design/icons";
import auth from "../../../utils/auth";
import "./ownerAdvance.css";
import moment from "moment";
const {RangePicker} = DatePicker;
const EditableContext = React.createContext();
const {Option} = Select;
const dateFormat = "DD/MM/YYYY";
const expandIcon = {
  color: "#01a6d0",
  fontSize: "14px",
  width: "24px",
  height: "24px",
  paddingTop: "6px",
};

const numInput = (event) => {
  const val = event.target.value;
  // var digit = /^[0-9]*\.?[0-9]*$/.test(val);
  if (/^[0-9]*\.?[0-9]*$/.test(val) && event.key !== "Backspace") {
    var with2Decimals = val.toString().match(/^-?\d+(?:\.\d{0,1})?/);
    if (with2Decimals) {
      event.target.value = with2Decimals[0];
    }
  } else {
    return true;
  }
};

const EditableRow = ({index, ...props}) => {
  const [form] = Form.useForm();
  return (
    <Form form={form} component={false}>
      <EditableContext.Provider value={form}>
        <tr {...props} />
      </EditableContext.Provider>
    </Form>
  );
};

class EditableOwnerAdvanceTable extends React.Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const Det = auth.getUserData();
    this.state = {
      dataSource: [],
      defaultDataSource: [],
      userdetailsData: "",
      userdetailsAllData: [],
      ownerName: "",
      editingKey: "",
      TotalOutstanding: "",
      expandedRowKeys: "",
      ledgerEntries: [],
      nameOfOwner: "",
      ownerDetails: [],
      startDate: "",
      endDate: "",
      userDetails: Details,
      LoadingState: "",
      logId: Det._id,
    };

    this.columns = [
      {
        title: "Date",
        dataIndex: "intDate",
        key: "intDate",
        width: "20%",
        editable: true,
        render: (index, record) => (
          <span>{record ? record.initialDate : ""}</span>
        ),
      },
      {
        title: "Owner Name",
        dataIndex: "ownerName",
        editable: true,
        width: "25%",
        render: (text, record) => (
          <span style={{textTransform: "capitalize"}}>
            {record ? record.ownerName : ""}
          </span>
        ),
      },
      {
        title: "Outstanding Amount ",
        dataIndex: "IntAmount",
        editable: true,
        width: "20%",
        render: (index, record) => (
          <span
            style={
              record && record.IntAmount >= 0
                ? record.IntAmount == 0
                  ? {color: "#000"}
                  : {color: "#38bc00"}
                : {color: "red"}
            }
          >
            {record ? record.IntAmount : ""}
          </span>
        ),
      },
      {
        title: "",
        dataIndex: "operation",
        width: "35%",
        align: "center",
        render: (text, record) => {
          const editable = this.isEditing(record);
          return (
            <div>
              {editable ? (
                <span>
                  <EditableContext.Consumer>
                    {(form) => (
                      <div>
                        {record._id === 1 ? (
                          <div className="editablebtn">
                            <div>
                              <Button
                                type="primary"
                                className="savebtn"
                                onClick={() =>
                                  this.createOwnerAdvance(form, record.key)
                                }
                                style={{
                                  height: "26px",
                                  padding: "4px",
                                  width: "60px",
                                }}
                              >
                                SAVE
                              </Button>
                            </div>
                            <div>
                              <Button
                                type="primary"
                                className="cancelCashbook"
                                onClick={() => this.handleCancel(record._id)}
                              >
                                CANCEL
                              </Button>
                            </div>
                          </div>
                        ) : (
                          <div style={{display: "flex"}}>
                            <div>
                              <Button
                                type="primary"
                                className="savebtn"
                                onClick={() =>
                                  this.handleSave(form, record.key)
                                }
                                style={{
                                  height: "27px",
                                  padding: "5px",
                                  width: "60px",
                                }}
                              >
                                SAVE
                              </Button>
                            </div>
                            <div>
                              <Button
                                type="primary"
                                className="cancelCashbook"
                                onClick={() => this.handleCancel(record._id)}
                              >
                                CANCEL
                              </Button>
                            </div>
                          </div>
                        )}
                      </div>
                    )}
                  </EditableContext.Consumer>
                </span>
              ) : (
                <span>
                  {this.state.userDetails &&
                  this.state.userDetails.roleName === "Admin" ? (
                    <Popconfirm
                      title="Are you sure?"
                      onConfirm={() => this.handleOwnerDelete(record._id)}
                      okText="Yes"
                      cancelText="No"
                      disabled={this.state.editingKey !== ""}
                    >
                      <Button
                        type="primary"
                        className="cancelCashbook"
                        style={{
                          height: "27px",
                          padding: "4px",
                          width: "60px",
                        }}
                      >
                        DELETE
                      </Button>
                    </Popconfirm>
                  ) : (
                    ""
                  )}
                </span>
              )}
            </div>
          );
        },
      },
    ];
  }
  setRowClassName = (record) => {
    return record._id === this.state.rowId ? "expandparent" : "";
  };
  onTableRowExpand = async (expanded, record) => {
    const {_id} = record;
    var keys = [];
    if (expanded) {
      keys.push(record._id);
      this.setState({
        rowId: record._id,
      });
      try {
        if (record && _id !== "") {
          const response = await axios.get(`${BASE_URL}get-ledger-data/${_id}`);
          const ledgerEntries = response.data.ownersAdavance[0].ledgerEntries;
          const dataSource = ledgerEntries.map((data, index) => {
            const date = data.entryDate;
            var eDate = moment(date, "DD/MM/YYYY");
            return {
              ...data,
              entDate: eDate,
              IdofOwner: _id,
            };
          });
          const TableData = record.ownerDetails.map((data, index) => {
            return {
              ...data,
              ledgerId: _id,
            };
          });
          this.setState({
            ledgerEntries: dataSource,
            ownerDetails: TableData,
          });
        }
      } catch (err) {
        console.log(err);
      }
    }

    this.setState({expandedRowKeys: keys});
  };

  handleOwnerDelete = async (id) => {
    try {
      const response = await axios.delete(
        `${BASE_URL}delete-owner-record/${id}`
      );
      message.success("Record Deleted Successfully");
      this.gettableData();
      this.getoutstandingData();
    } catch (error) {
      console.log(error);
    }
  };

  getAllOwnerDetails = async () => {
    try {
      const response = await axios.get(`${BASE_URL}get-owner-name`);

      const userDetails = response.data.ownerDetails || "";
      if (userDetails && userDetails.length > 0) {
        const userTypes = userDetails.map(({_id, name}) => {
          return (
            <Option key={_id} value={_id}>
              {name}
            </Option>
          );
        });
        this.setState({
          userdetailsData: userTypes,
          userdetailsAllData: userDetails,
        });
      }
    } catch (err) {
      console.log(err);
    }
  };
  customExpandIcon(props) {
    if (props.expanded) {
      return (
        <a
          style={{color: "black"}}
          onClick={(e) => {
            props.onExpand(props.record, e);
          }}
        >
          <DownOutlined style={expandIcon} className="customIcon" />
        </a>
      );
    }
    return (
      <a
        style={{color: "black"}}
        onClick={(e) => {
          props.onExpand(props.record, e);
        }}
      >
        <RightOutlined style={expandIcon} className="customIcon" />
      </a>
    );
  }
  isEditing = (record) => {
    return record._id === this.state.editingKey;
  };

  handleEdit = (key) => {
    this.setState({editingKey: key});
  };

  doSomethingBeforeUnload = async () => {
    await axios.put(`${BASE_URL}logout/${this.state.logId}`);
  };
  setupBeforeUnloadListener = async () => {
    console.log(this.state.userDetails, "confiff");
    window.addEventListener("beforeunload", async (ev) => {
      ev.preventDefault();
      return this.doSomethingBeforeUnload();
    });
  };

  componentDidMount() {
    this.getAllOwnerDetails();
    this.gettableData();
    this.getoutstandingData();
    this.setupBeforeUnloadListener();
  }
  gettableData = async () => {
    try {
      this.setState({loading: true});
      const response = await axios.get(`${BASE_URL}get-advance-data`);
      const {ownersAdavance} = response.data || [];
      if (ownersAdavance && ownersAdavance.length > 0) {
        const dataSource = ownersAdavance.map((data, index) => {
          const {ownerDetails, outStandingAmount} = data;
          const {initialDate, ownerName} = ownerDetails[0];
          const intDate = moment(initialDate, "DD/MM/YYYY");
          // const IntAmount = data.initialAmount.toFixed(2);
          return {
            ...data,
            ownerName,
            initialDate,
            slno: index + 1,
            intDate: intDate,
            IntAmount: outStandingAmount,
          };
        });
        this.setState({loading: false});
        await this.setState({dataSource, defaultDataSource: dataSource});
      } else {
        this.setState({
          dataSource: [],
          defaultDataSource: [],
        });
      }
      this.setState({loading: false});
    } catch (err) {
      this.setState({loading: false});
      message.error("Error fetching data. Please try again later", 2);
    }
  };
  getoutstandingData = async () => {
    try {
      const response = await axios.get(
        `${BASE_URL}get-owner-advance-outstanding-details`
      );
      const outstandingEntries = response.data.amountData || "";
      if (outstandingEntries && outstandingEntries.length > 0) {
        this.setState({
          TotalOutstanding: outstandingEntries[0].outStandingAmount.toFixed(2),
        });
      } else {
        this.setState({
          TotalOutstanding: "0.00",
        });
      }
    } catch (err) {
      console.log(err);
    }
  };
  createOwnerAdvance = async (form, key) => {
    const row = await form.validateFields();
    const {date, intDate, IntAmount, ownerName} = row;
    const {nameOfOwner} = this.state;
    var DateString = moment(intDate).format("DD/MM/YYYY");
    var startDate = moment(DateString, "DD/MM/YYYY");
    var vDate = startDate._i;
    try {
      await axios
        .post(`${BASE_URL}create-owner-advance`, {
          ownerId: ownerName,
          ownerName: nameOfOwner,
          entryDate: vDate,
          credit: Number(IntAmount),
          narration: "Vehicle Advance",
        })
        .then((data) => {
          message.success("Sucessfully Added Onwer Advance Outstanding");
          this.setState({editingKey: ""});
          this.gettableData();
          this.getoutstandingData();
        })
        .catch((error) => {
          const {response} = error;
          const {data} = response;
          const {message: msg} = data;
          // window.alert(JSON.stringify(data))
          message.error(msg);
        });
    } catch (err) {
      console.log(err);
      // message.error("Something went wrong");
    }
  };
  handleSave = async (form, key) => {
    this.setState({editingKey: ""});
  };

  handleCancel = (key) => {
    const dataSource = [...this.state.dataSource];
    const dataof = dataSource.filter((item) => item._id !== key);
    this.setState({editingKey: "", dataSource: dataof});
  };

  handleAdd = () => {
    const {count, dataSource} = this.state;
    const newData = {
      _id: 1,
      ownerName: "",
      initialDate: "",
      IntAmount: "0",
    };
    if (dataSource.length > 0) {
      this.setState({
        dataSource: [newData, ...dataSource],
        count: count + 1,
        editingKey: 1,
      });
    } else {
      this.setState({
        dataSource: [newData],
        editingKey: 1,
      });
    }
  };
  onChangeuserdata = (e) => {
    try {
      const {userdetailsAllData} = this.state;
      const filteredTableData = this.state.userdetailsAllData.filter(
        ({_id}) => _id === e
      );
      if (filteredTableData) {
        const owner = filteredTableData[0].name;
        this.setState({nameOfOwner: owner});
      }
    } catch (err) {
      console.log(err);
    }
  };

  onDateChange = async (dates) => {
    const {defaultDataSource} = this.state;
    const filterDateFormat = "YYYY-MM-DD";
    if (dates) {
      try {
        const [a, b] = dates;
        var st = (a && moment(a || "").format(filterDateFormat)) || "";
        var et = (b && moment(b || "").format(filterDateFormat)) || "";
        const dataSource = defaultDataSource.filter(({ownerDetails}) => {
          const {initialISODate} = ownerDetails[0];
          var dt = moment(new Date(initialISODate)).format(filterDateFormat);
          if (st && et) {
            return (
              moment(dt).isSameOrAfter(st) && moment(dt).isSameOrBefore(et)
            );
          }
          if (st) {
            return moment(dt).isSameOrAfter(st);
          }
          if (et) {
            return moment(dt).isSameOrBefore(et);
          }
        });
        await this.setState({dataSource});
      } catch (error) {
        console.log({error});
        message.error("Unable to fetch data");
      }
    } else {
      await this.setState({dataSource: defaultDataSource});
    }
  };
  reloadAfterEntry = async () => {
    this.gettableData();
    this.getoutstandingData();
    const {rowId} = this.state;
    try {
      if (rowId !== "") {
        const response = await axios.get(`${BASE_URL}get-ledger-data/${rowId}`);
        const ledgerEntries = response.data.ownersAdavance[0].ledgerEntries;
        const dataSource = ledgerEntries.map((data, index) => {
          const date = data.entryDate;
          var eDate = moment(date, "DD/MM/YYYY");
          return {
            ...data,
            entDate: eDate,
            IdofOwner: rowId,
          };
        });
        // const TableData = record.ownerDetails.map((data, index) => {
        //   return {
        //     ...data,
        //     ledgerId: rowId,
        //   };
        // });
        this.setState({
          ledgerEntries: dataSource,
          // ownerDetails: TableData,
        });
      }
    } catch (err) {
      console.log(err);
    }
  };
  render() {
    const EditableCell = ({
      editing,
      dataIndex,
      title,
      inputType,
      record,
      index,
      children,
      ...restProps
    }) => {
      let inputNode =
        inputType === "number" ? (
          <InputNumber />
        ) : (
          <Input
            onKeyPress={(event) => {
              if (
                !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
                event.key !== "Backspace"
              ) {
                event.preventDefault();
              }
            }}
            maxLength={10}
          />
        );
      if (dataIndex === "intDate") {
        inputNode = (
          <DatePicker
            format={dateFormat}
            suffixIcon={<img src={dateicon} alt="dateIcon" />}
            allowClear={false}
            autoComplete="off"
          />
        );
      }
      if (dataIndex === "ownerName") {
        inputNode = (
          <Select
            showSearch
            placeholder="Select Owner Name"
            optionFilterProp="children"
            showArrow
            onChange={this.onChangeuserdata}
            filterOption={(input, option) =>
              option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
            width="200px"
          >
            {this.state.userdetailsData}
          </Select>
        );
      }
      if (dataIndex === "IntAmount") {
        inputNode = (
          <Input
            type="number"
            onKeyPress={(event) => {
              if (
                numInput(event)
                // &&
                // !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
                // event.key !== "Backspace"
              ) {
                event.preventDefault();
              }
            }}
          />
        );
      }
      return (
        <td {...restProps}>
          {editing ? (
            <Form.Item
              name={dataIndex === "intDate" ? "intDate" : dataIndex}
              style={{
                margin: 0,
                width: "90%",
              }}
              rules={[
                {
                  required: true,
                },
              ]}
              initialValue={record[dataIndex]}
            >
              {inputNode}
            </Form.Item>
          ) : (
            children
          )}
        </td>
      );
    };

    const {dataSource} = this.state;
    const components = {
      body: {
        row: EditableRow,
        cell: EditableCell,
      },
    };

    const columns = this.columns.map((col) => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: (record) => ({
          record,
          inputType: col.dataIndex === "serialnumber" ? "number" : "text",
          dataIndex: col.dataIndex,
          title: col.title,
          editing: this.isEditing(record),
        }),
      };
    });
    const {editingKey, ledgerEntries, ownerDetails, loading} = this.state;
    return (
      <Spin spinning={loading}>
        <div>
          <div
            style={{
              display: "flex",
              background: "#fff",
              height: "60px",
              marginTop: "2px",
              padding: "4px 10px 10px 30px",
            }}
          >
            <div style={{marginTop: "4px"}}>
              <h3
                style={{
                  fontSize: "18px",
                  fontFamily: "MyriadProBold",
                  margin: "0px",
                }}
              >
                Owner Advance Outstanding
              </h3>
              <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
                Owner Advance Outstanding Entry
              </p>
            </div>
            <div style={{position: "absolute", right: 40}}>
              <CurrentTime />
            </div>
          </div>
          <div>
            <HeaderBreedCrumb
              path="home"
              history={this.props.history}
              userData={this.state.userDetails}
            />
          </div>
          <div
            className="site-layout-background"
            style={{
              margin: "20px",
              minHeight: 280,
            }}
          >
            <div className="cashBookContainer">
              <div className="cashBookHeader">
                <h2>Owner Advance Outstanding</h2>

                <Col xs={12} sm={12} md={15} lg={15} xl={15}>
                  <div style={{display: "flex", float: "right"}}>
                    <div>
                      <h3
                        className="filterLabel"
                        style={{
                          borderRadius: "10px",
                        }}
                      >
                        Sort by Date Range
                      </h3>
                      <div style={{display: "flex"}}>
                        <div>
                          <RangePicker
                            format={dateFormat}
                            onChange={(dates) => this.onDateChange(dates)}
                            allowEmpty={[true, true]}
                            suffixIcon={<img src={dateicon} alt="dateIcon" />}
                          />
                        </div>
                      </div>
                    </div>
                    <div style={{padding: "10px", margin: "14px 0 0 10px"}}>
                      <Button
                        type="primary"
                        onClick={this.handleAdd}
                        icon={
                          <img
                            src={Addtruckicon}
                            style={{marginRight: "6px"}}
                          />
                        }
                        disabled={editingKey !== ""}
                      >
                        ADD OWNER ADVANCE
                      </Button>
                    </div>
                  </div>
                </Col>
              </div>
              <Table
                components={components}
                className="ownerAdvanceTable"
                dataSource={dataSource}
                scroll={{y: 600}}
                columns={columns}
                onRow={this.onClickRow}
                rowClassName={this.setRowClassName}
                expandedRowKeys={this.state.expandedRowKeys}
                onExpand={(expand, record) =>
                  this.onTableRowExpand(expand, record)
                }
                expandedRowRender={(record) =>
                  editingKey ? (
                    ""
                  ) : (
                    <OwnerAdvanceNestedTable
                      TableData={ledgerEntries ? ledgerEntries : ""}
                      OwnerDetails={ownerDetails ? ownerDetails : ""}
                      parentFn={() => this.reloadAfterEntry()}
                    />
                  )
                }
                expandIcon={(props) => this.customExpandIcon(props)}
                pagination={false}
                rowKey="_id"
              />

              <Row className="outstanding">
                <Col className="outstandingColOne"></Col>

                <Col className="outstandingColTwo">Total Outstanding</Col>
                <Col className="outstandingColthree">
                  <span>
                    {this.state.TotalOutstanding
                      ? this.state.TotalOutstanding
                      : "0.00"}
                  </span>
                </Col>
              </Row>
            </div>
          </div>
        </div>
      </Spin>
    );
  }
}

export default EditableOwnerAdvanceTable;
