import React, {Component} from "react";
import {
  Table,
  Input,
  Button,
  Modal,
  Popconfirm,
  Checkbox,
  message,
  Spin,
  Row,
  Select,
  DatePicker,
  Col,
  Typography,
} from "antd";
import moment from "moment";
import Editvoucherbook from "./editVoucherBook";
import AddVoucherBook from "./addVoucherBook";
import HeaderBreedCrumb from "../../../components/accountingmenubreedcrumb/accountingmenubreedcrumb";
import CurrentTime from "../../../components/currenttime/currenttime";
import VoucherPrint from "./printvoucherdetails";
import axios from "axios";
import {BASE_URL} from "../../../constants/constants";
import Editicon from "../../../assests/images/edit.svg";
import ChallanIcon from "../../../assests/images/challan.svg";
import DownloadIcon from "../../../assests/images/download.svg";
import PrintIcon from "../../../assests/images/print.svg";
import Deleteicon from "../../../assests/images/delete.svg";
import dateicon from "../../../assests/images/dateicon.svg";
import auth from "../../../utils/auth";
import "./voucher.css";
import * as XLSX from "xlsx";
const {Text} = Typography;
const userDetails = auth.getAuthToken().userDetails;
const {Search} = Input;
const {RangePicker} = DatePicker;
const dateFormat = "DD/MM/YYYY";
const currentYear = moment().year();
const currentMonth = moment().month() + 1;
const imageStyle = {
  height: "auto",
  maxWidth: "480px",
  minWidth: "200px",
  paddingBottom: 25,
};
const SELECTED_THRESHOLD = 0;
const iconStyle = {
  color: "#01a6d0",
  fontSize: "14px",
  width: "24px",
  height: "24px",
};
const dateFormatList = ["DD/MM/YYYY"];

const {Option} = Select;
const currentDate = moment(new Date(), dateFormatList[1]);

let timeout = null;

class VoucherBook extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const Det = auth.getUserData();
    this.state = {
      vidible: false,
      loading: false,
      doucmentvisible: false,
      editableVehicleDetails: null,
      fileurl: "",
      searchValue: "",
      searchResultArray: "",
      selectionType: Checkbox,
      dataSource: [],
      fileType: "",
      previewUrl: "",
      materialSelect: [],
      truckTypeStatus: [],
      startDate: "",
      endDate: "",
      materialDataSelect: [],
      truckDataSelect: [],
      selectedRowKeys: [],
      dateSearchdata: [],
      selectRowArray: [],
      selectedYear: currentYear,
      selectedMonth: currentMonth,
      totalAmount: "",
      userDetails: Details,
      logId: Det._id,
    };
  }
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancelEdit = () => {
    this.setState({
      editvisible: false,
    });
  };

  gettableData = async () => {
    try {
      this.setState({loading: true});
      const {selectedYear, selectedMonth} = this.state;
      const response = await axios.get(
        `${BASE_URL}get-vouchers-by-month/${selectedMonth}/${selectedYear}`
      );
      const dispatchData = response.data.vaucharEntries || "";
      const amounts = response.data.amounts.monthlyTotalAmount || "";
      if (dispatchData && dispatchData.length > 0) {
        this.setState({
          dataSource: dispatchData.map((data, index) => {
            return {...data, slno: index + 1};
          }),
          totalAmount: amounts,
        });
      } else {
        this.setState({
          dataSource: [],
          totalAmount: "0.00",
        });
      }

      // if (selectedMonth !== "" && selectedYear !== "") {
      //   var filteredData = this.state.dataSource.filter(function (a) {
      //     var aDate = moment(a.voucherDate, "DD/MM/YYYY");
      //     var aYear = new Date(aDate._d).getFullYear();
      //     var aMon = new Date(aDate._d).getMonth() + 1;
      //     if (aMon < 10) {
      //       aMon = "0" + aMon;
      //     }
      //     return aYear == selectedYear && aMon == selectedMonth;
      //   });
      //   this.setState({
      //     dataSource: filteredData,
      //   });
      // } else {
      //   var d = new Date();
      //   var Months = d.getMonth() + 1;
      //   var Years = d.getFullYear();
      //   var filteredData = this.state.dataSource.filter(function (a) {
      //     var aDate = moment(a.voucherDate, "DD/MM/YYYY");
      //     var aYear = new Date(aDate._d).getFullYear();
      //     var aMon = new Date(aDate._d).getMonth() + 1;
      //     return aYear == Years && aMon == Months;
      //   });
      //   this.setState({
      //     dataSource: filteredData,
      //   });
      // }
      const {searchValue} = this.state;
      if (
        searchValue !== "" &&
        searchValue !== null &&
        searchValue !== undefined
      ) {
        const searchVal = searchValue.toString().toUpperCase().trim();

        const newSearchedTableData = this.state.dataSource.filter(
          ({vehicleNumber, ownerName}) => {
            return (
              this.searchFn(searchVal, vehicleNumber) ||
              this.searchFn(searchVal, ownerName)
            );
          }
        );
        this.setState({searchResultArray: newSearchedTableData});
      }
      this.setState({loading: false});
    } catch (err) {
      this.setState({loading: false});
      message.error("Error fetching data. Please try again later", 2);
    }
  };
  onSelectChange = (selectedRowKeys, selectedRows) => {
    this.setState({selectedRowKeys});
    this.setState({
      selectRowArray: selectedRows,
    });
  };

  doSomethingBeforeUnload = async () => {
    await axios.put(`${BASE_URL}logout/${this.state.logId}`);
  };
  setupBeforeUnloadListener = async () => {
    console.log(this.state.userDetails, "confiff");
    window.addEventListener("beforeunload", async (ev) => {
      ev.preventDefault();
      return this.doSomethingBeforeUnload();
    });
  };

  componentDidMount() {
    this.gettableData();
    this.setupBeforeUnloadListener();
  }
  onDownload = () => {
    const selectedData = this.state.dataSource
      .filter((d) => {
        if (this.state.selectedRowKeys.includes(d._id)) {
          return d;
        }
      })
      .map((d, index) => {
        return {
          "S.No": index + 1,
          "Voucher Number": d.voucherNumber,
          "Voucher Date": d.voucherDate,
          "Owner Name": d.ownerName,
          "Truck Number": d.vehicleNumber,
          Narration: d.narration,
          "Mode of Payment": d.modeOfPayment,
          Amount: d.amount,
        };
      });
    if (selectedData.length === 0) {
      message.error("Please select Voucher to download");
    } else {
      const ws = XLSX.utils.json_to_sheet(selectedData);
      const wb = {
        Sheets: {vehicle_owner_details: ws},
        SheetNames: ["vehicle_owner_details"],
      };
      const excelBuffer = XLSX.write(wb, {bookType: "xlsx", type: "array"});
      const data = new Blob([excelBuffer], {
        type:
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8",
      });
      var url = window.URL.createObjectURL(data);
      window.open(url, "_blank");
    }
  };
  showmodals(e) {
    this.setState({
      editvisible: true,
      editableVehicleDetails: e,
    });
  }

  deleterecord(e) {}

  confirm = async (e) => {
    try {
      await axios.delete(`${BASE_URL}/delete-voucher/${e._id}`);
      this.gettableData();
      message.success("Successfully deleted vehicle");
    } catch (err) {
      message.error("Unable to delete");
    }
  };

  searchResult = (e) => {
    try {
      clearTimeout(timeout);
      let searchValue = e.target.value;
      timeout = setTimeout(() => {
        this.setState({searchValue});
        if (
          searchValue !== "" &&
          searchValue !== null &&
          searchValue !== undefined
        ) {
          searchValue = searchValue.toString().toUpperCase().trim();

          const newSearchedTableData = this.state.dataSource.filter(
            ({vehicleNumber, ownerName}) => {
              return (
                this.searchFn(searchValue, vehicleNumber) ||
                this.searchFn(searchValue, ownerName)
              );
            }
          );
          this.setState({searchResultArray: newSearchedTableData});
        } else {
          this.setState({
            searchResultArray: [],
            printableArray: [],
            selectedRowKeys: [],
          });
        }
      }, 500);
    } catch (err) {
      console.error({err});
    }
  };

  searchFn = (search, value) => {
    if (value && search) {
      return value.toString().toUpperCase().includes(search);
    } else {
      return false;
    }
  };

  reloadAfterModalClose = () => {
    this.gettableData();
    this.setState({
      visible: false,
      editvisible: false,
      doucmentvisible: false,
    });
  };
  onMonthChange = async (date, dateString) => {
    const [yr, mnt] = dateString.split("-");
    const defDateRange = moment(`${mnt}/1/${yr}`);
    if (yr && mnt) {
      await this.setState({
        selectedYear: yr,
        selectedMonth: mnt,
      });
    }
    this.gettableData();
  };

  // onDateChange = async (dates) => {
  //   await this.gettableData();
  //   const {dataSource, dateSearchdata} = this.state;
  //   const filterDateFormat = "YYYY-MM-DD";
  //   if (dates) {
  //     try {
  //       const [a, b] = dates;
  //       var st = (a && moment(a || "").format(filterDateFormat)) || "";
  //       var et = (b && moment(b || "").format(filterDateFormat)) || "";
  //       const filteredData = dataSource.filter((voucherData) => {
  //         const {voucherISODate} = voucherData;
  //         var dt = moment(new Date(voucherISODate)).format(filterDateFormat);
  //         if (st && et) {
  //           return (
  //             moment(dt).isSameOrAfter(st, "day") &&
  //             moment(dt).isSameOrBefore(et, "day")
  //           );
  //         }
  //         if (st) {
  //           return moment(dt).isSameOrAfter(st, "day");
  //         }
  //         if (et) {
  //           return moment(dt).isSameOrBefore(et, "day");
  //         }
  //       });
  //       await this.setState({
  //         dataSource: filteredData,
  //         dateSearchdata: filteredData,
  //       });
  //     } catch (error) {
  //       console.log({error});
  //       message.error("Unable to fetch data");
  //     }
  //   } else {
  //     await this.setState({dataSource: dataSource});
  //   }
  // };

  render() {
    const {selectedRowKeys} = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
      hideDefaultSelections: true,
      columnWidth: 10,
    };
    const columns = [
      {
        title: "Sl No",
        dataIndex: "slno",
        key: "name",
        render: (text, record) => (
          <span>
            {this.state.searchValue === "" || this.state.searchValue === null
              ? this.state.dataSource.indexOf(record) + 1
              : this.state.searchResultArray.indexOf(record) + 1}
          </span>
        ),
      },
      {
        title: "Voucher Number",
        dataIndex: "voucherNumber",
        key: "voucherNumber",
      },
      {
        title: "Voucher Date",
        dataIndex: "voucherDate",
        key: "voucherDate",
      },
      {
        title: "Owner Name",
        dataIndex: "ownerName",
        key: "ownerName",
      },
      {
        title: "Truck Number",
        dataIndex: "vehicleNumber",
        key: "vehicleNumber",
      },
      {
        title: "Material Type",
        dataIndex: "materialType",
        key: "materialType",
      },
      {
        title: "Narration",
        dataIndex: "narration",
        key: "narration",
      },
      {
        title: "Mode Of Payment",
        dataIndex: "modeOfPayment",
        key: "modeOfPayment",
      },
      {
        title: "Amount",
        dataIndex: "amount",
        render: (text, record) => (
          <span>{!record.amount ? "0" : record.amount.toFixed(2)}</span>
        ),
      },

      {
        title: "Action",
        dataIndex: "",
        key: "x",
        // fixed: "right",
        render: (text, record) => (
          <div style={{display: "flex"}}>
            <div style={{margin: "5px"}}>
              <img
                src={Editicon}
                style={{cursor: "pointer"}}
                onClick={() => this.showmodals(record)}
                width="15px"
              />
            </div>
            <div style={{margin: "5px"}}>
              {userDetails && userDetails.roleName === "Admin" ? (
                <Popconfirm
                  title="Are you sure?"
                  onConfirm={() => this.confirm(record)}
                  onCancel={this.cancel}
                  okText="Yes"
                  cancelText="No"
                >
                  <a href="#">
                    <img
                      src={Deleteicon}
                      style={{cursor: "pointer"}}
                      width="15px"
                    />
                  </a>
                </Popconfirm>
              ) : (
                ""
              )}
            </div>
          </div>
        ),
      },
    ];

    const disabledDate = (current) => {
      // Can not select days before today and today
      return current && current > moment().endOf("day");
    };
    const {editableVehicleDetails, dataSource} = this.state;

    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Voucher Book
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Create and View Vouchers
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <HeaderBreedCrumb
            path="home"
            history={this.props.history}
            userData={this.state.userDetails}
          />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <Spin spinning={this.state.loading}>
            <div className="dispatchChallan">
              <Modal
                title={
                  <div>
                    <h3 style={{fontSize: "20px", margin: "0px"}}>
                      Voucher Book
                    </h3>
                    <p
                      style={{
                        fontSize: "12px",
                        fontFamily: "AvenirMedium",
                      }}
                    >
                      Enter Voucher Details
                    </p>
                  </div>
                }
                className="ModalLook"
                visible={this.state.visible}
                onOk={this.handleOk}
                destroyOnClose={true}
                onCancel={this.handleCancel}
                footer={null}
              >
                <div>
                  <AddVoucherBook
                    parentFn={() => this.reloadAfterModalClose()}
                  />
                </div>
              </Modal>
              <Modal
                title={
                  <div>
                    <h3 style={{fontSize: "20px", margin: "0px"}}>
                      Voucher Book
                    </h3>
                    <p
                      style={{
                        fontSize: "12px",
                        fontFamily: "AvenirMedium",
                      }}
                    >
                      Enter Voucher Details
                    </p>
                  </div>
                }
                className="ModalLook"
                visible={this.state.editvisible}
                onOk={this.handleOk}
                onCancel={this.handleCancelEdit}
                destroyOnClose={true}
                footer={null}
              >
                <div>
                  {editableVehicleDetails && (
                    <Editvoucherbook
                      record={editableVehicleDetails}
                      parentFn={() => this.reloadAfterModalClose()}
                    />
                  )}
                </div>
              </Modal>
              <div style={{padding: "0px 0px 0px 20px"}}>
                <Row>
                  {/* <Col
                    xs={12}
                    sm={12}
                    md={5}
                    lg={5}
                    xl={5}
                    style={{padding: "10px"}}
                  >
                    <h3
                      className="filterLabel"
                      style={{
                        borderRadius: "10px",
                      }}
                    >
                      Sort by Date Range
                    </h3>
                    <div style={{display: "flex"}}>
                      <RangePicker
                        format={dateFormatList}
                        onChange={(dates) => this.onDateChange(dates)}
                        allowEmpty={[true, true]}
                        suffixIcon={<img src={dateicon} alt="dateIcon" />}
                      />
                    </div>
                  </Col> */}
                  <Col
                    xs={12}
                    sm={12}
                    md={5}
                    lg={5}
                    xl={5}
                    style={{padding: "10px"}}
                  >
                    <h3 className="filterLabel">
                      Search by Truck No / Owner Name
                    </h3>
                    <div>
                      <Search
                        className="searchbtn"
                        onChange={this.searchResult}
                        style={{height: "30px !important", width: "100%"}}
                      />
                    </div>
                  </Col>
                  <Col
                    xs={12}
                    sm={12}
                    md={5}
                    lg={5}
                    xl={5}
                    style={{padding: "10px"}}
                  >
                    <h3 className="filterLabel">Filter By Month</h3>
                    <div>
                      <DatePicker
                        style={{width: "150px", height: "30px"}}
                        onChange={this.onMonthChange}
                        picker="month"
                        allowClear={false}
                        disabledDate={disabledDate}
                        defaultValue={moment()}
                        suffixIcon={<img src={dateicon} alt="dateIcon" />}
                      />
                    </div>
                  </Col>
                  <Col
                    xs={24}
                    sm={24}
                    md={14}
                    lg={14}
                    xl={14}
                    style={{padding: "10px", marginTop: "10px"}}
                  >
                    <div style={{display: "flex", float: "right"}}>
                      <div style={{padding: "10px 5px"}}>
                        <Button
                          className="outlineBtn"
                          onClick={this.onDownload}
                          icon={
                            <img
                              src={DownloadIcon}
                              style={{paddingRight: "7px"}}
                            />
                          }
                        >
                          DOWNLOAD
                        </Button>
                      </div>
                      <div style={{padding: "10px 5px"}}>
                        <VoucherPrint Data={this.state.selectRowArray} />
                        {/* <Button
                        //   className="outlineBtn"
                        //   icon={
                        //     <img
                        //       src={PrintIcon}
                        //       style={{paddingRight: "7px"}}
                        //     />
                        //   }
                        // >
                        //   PRINT
                        // </Button> */}
                      </div>
                      <div style={{padding: "10px 5px"}}>
                        <Button
                          type="primary"
                          onClick={this.showModal}
                          icon={
                            <img
                              src={ChallanIcon}
                              style={{paddingRight: "7px"}}
                            />
                          }
                        >
                          CREATE VOUCHER
                        </Button>
                      </div>
                    </div>
                  </Col>
                </Row>
              </div>

              <div className="voucherbook">
                <Table
                  columns={columns}
                  rowKey="_id"
                  dataSource={
                    this.state.searchValue === "" ||
                    this.state.searchValue === null
                      ? dataSource
                      : this.state.searchResultArray
                  }
                  rowSelection={rowSelection}
                  pagination={false}
                  summary={(pageData) => {
                    // console.log(pageData);
                    // let totalAmount = 0;

                    // pageData.forEach(({amount}) => {
                    //   totalAmount += amount;
                    // });

                    return (
                      <>
                        <Table.Summary.Row>
                          <Table.Summary.Cell
                            colSpan={9}
                            algin="center"
                          ></Table.Summary.Cell>
                        </Table.Summary.Row>
                        <Table.Summary.Row>
                          <Table.Summary.Cell colSpan={7}></Table.Summary.Cell>
                          <Table.Summary.Cell className="monthlysummary"></Table.Summary.Cell>
                          <Table.Summary.Cell className="monthlysummary">
                            Monthly Total:
                          </Table.Summary.Cell>
                          <Table.Summary.Cell className="monthlysummary">
                            {this.state.totalAmount
                              ? this.state.totalAmount
                              : "0.00"}
                          </Table.Summary.Cell>
                          <Table.Summary.Cell
                            colSpan={2}
                            className="monthlysummary"
                          >
                            <Text></Text>
                          </Table.Summary.Cell>
                        </Table.Summary.Row>

                        <Table.Summary.Row>
                          <Table.Summary.Cell
                            colSpan={9}
                            algin="center"
                          ></Table.Summary.Cell>
                        </Table.Summary.Row>
                      </>
                    );
                  }}
                />
              </div>
            </div>
          </Spin>
        </div>
      </div>
    );
  }
}

export default VoucherBook;
