import React from "react";
import {Card} from "antd";
import "./reportDashboard.css";

import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";

// const COLORS = ['#0088FE', '#00C49F', '#FF8042', '#FFBB28'];

const Expenses = ({chartData}) => {
  return (
    <Card
      title="Expenses"
      bordered={false}
      style={{width: "100%", fontSize: 11}}
    >
      <ResponsiveContainer width={"100%"} height={200}>
        <BarChart
          data={chartData}
          margin={{top: 0, bottom: 0, right: 0, left: 0}}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
          <Bar dataKey="Diesel" stackId="a" fill="#8884d8" barSize={20} />
          <Bar dataKey="Cash" stackId="a" fill="#FFBB28" barSize={20} />
          <Bar
            dataKey="Bank Transfer"
            stackId="a"
            fill="#82ca9d"
            barSize={20}
          />
          <Bar dataKey="Shortage" stackId="a" fill="#ff7f42" barSize={20} />
          <Legend />
        </BarChart>
      </ResponsiveContainer>
    </Card>
  );
};

export default Expenses;
