import React from "react";
import {Statistic, Card, Row, Col, Typography} from "antd";
import {ArrowUpOutlined, ArrowDownOutlined} from "@ant-design/icons";
import "./reportDashboard.css";

import {
  ComposedChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

const MonthlyEarning = ({chartData = []}) => {
  console.log(chartData, "chsfdafdas", chartData[1]["Earning"]);
  const monthlyRaise =
    chartData && chartData.length > 0
      ? chartData[1]["Earning"] != 0
        ? chartData[0]["Earning"] != 0
          ? (chartData[1]["Earning"] / chartData[0]["Earning"]) * 100 - 100
          : 100
        : chartData[0]["Earning"] != 0
        ? -100
        : 0
      : 0;
  return (
    <Card
      title="Monthly Earning"
      bordered={false}
      style={{width: "100%", fontSize: 11}}
    >
      <Row>
        <Col span={16} className="monthly-earning-left">
          <Statistic
            title="This Month"
            value={
              chartData && chartData.length > 0 ? chartData[1]["Earning"] : "0"
            }
            prefix=""
          />
          <Typography.Text className="monthly-earning-typo">
            <span
              className={`arrow-wrapper ${
                monthlyRaise >= 0 ? "positive" : "negative"
              }`}
            >
              {Math.abs(monthlyRaise.toFixed(2))}%{""}
              {monthlyRaise >= 0 ? <ArrowUpOutlined /> : <ArrowDownOutlined />}
              {""}
            </span>
            <span> from previous period</span>
          </Typography.Text>
        </Col>
        <Col span={8}>
          <ResponsiveContainer width={"100%"} height={200}>
            <ComposedChart
              data={chartData}
              margin={{top: 0, bottom: 0, right: 0, left: 0}}
            >
              <XAxis dataKey="name" />
              <YAxis hide={true} />
              <Tooltip />
              <Bar dataKey="Earning" fill="#8884d8" barSize={20} />
              {/* <Line type="monotone" dataKey="Earning" stroke="#ff7300" /> */}
            </ComposedChart>
          </ResponsiveContainer>
        </Col>
      </Row>
    </Card>
  );
};

export default MonthlyEarning;
