/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from "react";
import {Menu, Avatar, Dropdown} from "antd";
import {
  QuestionCircleOutlined,
  CaretDownOutlined,
  SettingOutlined,
  UserOutlined,
} from "@ant-design/icons";
import UserImage from "../../../assests/images/user.jpeg";
import utils from "../../../utils/auth";
import Styles from "./headermenu.module.css";
import auth from "./../../../utils/auth";
import axios from "axios";
import {BASE_URL} from "../../../constants/constants";

class headerMenu extends Component {
  constructor(props) {
    super(props);
    this.userDetails = auth.getAuthToken().userDetails;
    this.logData = auth.getAuthToken().logData;
    this.state = {msg: ""};
  }
  Logout = async () => {
    try {
      const {_id} = this.logData;
      console.log(_id);
      await axios.put(`${BASE_URL}logout/${_id}`);
    } catch (err) {}
    const userDetails = {};
    utils.removeToken();
    auth.setUserData(userDetails);
    window.location.replace("/");
  };
  Profile = () => {
    this.props.history.push("./your-account");
  };

  render() {
    const menu = (
      <Menu>
        <Menu.Item>
          <a rel="your-account" onClick={this.Profile}>
            Profile
          </a>
        </Menu.Item>
        <Menu.Item>
          <a rel="noopener noreferrer" onClick={this.Logout}>
            Logout
          </a>
        </Menu.Item>
      </Menu>
    );
    const {firstName: fn, name: nm} = this.userDetails;
    const firstName = nm || fn || "";
    return (
      <div
        className={Styles.headermenu}
        style={{float: "right", position: "absolute", right: "30px"}}
      >
        {/* <span style={{margin: "10px"}}>
          <QuestionCircleOutlined className="sideIcon" />
        </span>
        <span style={{margin: "10px"}}>
          <SettingOutlined style={{fill: "#000"}} />
        </span> */}
        <span style={{margin: "10px"}} className={Styles.headerName}>
          {/* <Avatar src={UserImage} style={{margin: "5px"}} /> */}
          <UserOutlined style={{margin: "5px", fontSize: "18px"}} />
          <Dropdown overlay={menu}>
            <a
              className="ant-dropdown-link"
              onClick={(e) => e.preventDefault()}
              style={{fontFamily: "AvenirBold"}}
            >
              Hello
              {firstName
                ? " " +
                  firstName.split("")[0].toUpperCase() +
                  firstName.substring(1)
                : ""}
              <CaretDownOutlined />
            </a>
          </Dropdown>
        </span>
      </div>
    );
  }
}

export default headerMenu;
