import React, {Component} from "react";
import {Button, message, DatePicker} from "antd";
import jsPDF from "jspdf";
import "jspdf-autotable";
import Print from "../../assests/images/print.svg";

class printownerdetails extends Component {
  constructor(props) {
    super(props);
  }
  handlePrint = (e) => {
    var totalPagesExp = "{total_pages_count_string}";
    try {
      const doc = new jsPDF("l", "mm", "a4");
      const items = [];

      this.props.Data.map((d, index) => {
        items.push([
          index + 1,
          d.ownerName || "-",
          d.numberOfTrips || "-",
          d.quantity || "-",
          d.amount || "0",
          d.commission || "-",
          d.diesel || "0",
          d.cash || "0",
          d.bankTransfer || "0",
          d.shortage || "0",
        ]);
      });
      if (items.length === 0) {
        message.error("Please select users to download");
      } else {
        doc.setFontSize(10);
        const d = new Date();
        const m = d.getMonth() + 1;
        const day = d.getDate();
        const year = d.getFullYear();
        doc.autoTable({
          head: [
            [
              "Sl No",
              "Owner Name",
              "Number of Trips",
              "Quantity",
              "Amount",
              "Commission",
              "Diesel",
              "Cash",
              "Bank Transfer",
              "Shortage",
            ],
          ],
          body: items,
          startY: 10,
          headStyles: {fontSize: 8, fontStyle: "normal", fillColor: "#44495b"},
          bodyStyles: {fontSize: 8, textAlign: "center"},
          didDrawPage: function (data) {
            // Header
            doc.setFontSize(10);
            doc.text("Owner Details", data.settings.margin.left + 0, 5);
            doc.text("Date:-", data.settings.margin.left + 155, 5);
            doc.text(
              day + "/" + m + "/" + year,
              data.settings.margin.left + 170,
              5
            );
            var str = "Page " + doc.internal.getNumberOfPages();
            if (typeof doc.putTotalPages === "function") {
              str = str + " of " + totalPagesExp;
            }
            doc.setFontSize(10);
            var pageSize = doc.internal.pageSize;
            var pageHeight = pageSize.height
              ? pageSize.height
              : pageSize.getHeight();
            doc.text(str, data.settings.margin.left, pageHeight - 10);
          },
          margin: {top: 10},
        });
        if (typeof doc.putTotalPages === "function") {
          doc.putTotalPages(totalPagesExp);
        }
        doc.save("Report.pdf");
      }
    } catch (err) {
      message.error("Unable to Print");
    }
  };
  render() {
    return (
      <div>
        <Button
          className="button"
          onClick={this.handlePrint}
          icon={<img src={Print} style={{paddingRight: "10px"}} />}
        >
          Print
        </Button>
      </div>
    );
  }
}

export default printownerdetails;
