import React, {Component} from "react";
import {
  Button,
  Spin,
  Table,
  Popconfirm,
  Row,
  Col,
  Modal,
  Input,
  message,
} from "antd";
import {FormOutlined} from "@ant-design/icons";
import auth from "../../../utils/auth";
import axios from "axios";
import {BASE_URL} from "../../../constants/constants";
class viewaccout extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const {_id} = Details;
    this.state = {
      visible: false,
      loading: false,
      viewVisible: true,
      userDetails: Details,
      userId: _id,
      userData: "",
    };
  }
  gettableData = async () => {
    try {
      const {userId} = this.state;
      this.setState({loading: true});
      const response = await axios.get(`${BASE_URL}users/${userId}`);
      const userData = response.data.user || "";
      if (userData) {
        this.setState({
          userData: userData,
        });
      } else {
        this.setState({
          useData: "",
        });
      }
      this.setState({loading: false});
    } catch (err) {
      this.setState({loading: false});
      message.error("Error fetching data. Please try again later", 2);
    }
  };
  componentDidMount() {
    this.gettableData();
  }
  render() {
    const {userData} = this.state;
    return (
      <div>
        <Row>
          <Col span={3}>
            <p className="usertitle">Name</p>
          </Col>
          <Col span={1}>
            <span>:</span>
          </Col>
          <Col span={18}>
            <p className="userdesc">{userData && userData.name}</p>
          </Col>
        </Row>

        <Row>
          <Col span={3}>
            <p className="usertitle">Email ID</p>
          </Col>

          <Col span={1}>
            <span>:</span>
          </Col>
          <Col span={18}>
            <p className="userdesc">{userData ? userData.email : ""}</p>
          </Col>
        </Row>

        <Row>
          <Col span={3}>
            <p className="usertitle">Phone Number</p>
          </Col>

          <Col span={1}>
            <span>:</span>
          </Col>
          <Col span={18}>
            <p className="userdesc">{userData ? userData.phoneNumber : ""}</p>
          </Col>
        </Row>

        <Row>
          <Col span={3}>
            <p className="usertitle">Role</p>
          </Col>

          <Col span={1}>
            <span>:</span>
          </Col>
          <Col span={18}>
            <p className="userdesc">{userData ? userData.roleName : ""}</p>
          </Col>
        </Row>
      </div>
    );
  }
}

export default viewaccout;
