/* eslint-disable no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable no-unused-expressions */
import React, {Component} from "react";
import {Input, Row, Col, Form, Select, message, Spin, Button} from "antd";
import axios from "axios";
import moment from "moment";
import {BASE_URL} from "../../../constants/constants";
import utils from "../../../utils/auth";
const {Option} = Select;
class EditUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      visible: false,
      fullrecord: "",
    };
  }

  formRef = React.createRef();

  onFinish = async (changepasswordinfo) => {
    const {oldPassword, newPassword} = changepasswordinfo;
    const {email} = this.props.Data;
    try {
      await axios
        .put(`${BASE_URL}change-password`, {
          email,
          oldPassword,
          newPassword,
        })
        .then((data) => {
          message.success("successfully Reset Password");
          this.props.parentFn();
          setTimeout(() => {
            utils.removeToken();
            window.location.replace("/");
          }, 500);
        })
        .catch((error) => {
          const {response} = error;
          const {data} = response;
          const {message: msg} = data;
          message.error(msg);
        });
    } catch (err) {
      console.log(err);
      message.error("Unable to Reset Password");
    }
  };

  onReset = () => {
    this.formRef.current.resetFields();
  };

  render() {
    return (
      <div>
        <Spin spinning={this.state.loading} delay={500}>
          <Form
            ref={this.formRef}
            name="changepasswordinfo"
            className="vehicleinfoForm"
            onFinish={this.onFinish}
            initialValues={{
              amount: 0,
            }}
            autoComplete="off"
          >
            {/* <h2 style={{margin: "25px 30px 15px 30px", fontSize: "14px"}}>
              User Details
            </h2> */}
            <Row style={{margin: "10px 22px"}}>
              <Col xs={24} sm={24} md={10} lg={10} xl={10}>
                <h3 style={{marginLeft: "5px"}}>Current Password</h3>
                <div>
                  <Form.Item
                    name="oldPassword"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input.Password />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={10} lg={10} xl={10}>
                <h3 style={{marginLeft: "5px"}}>New Password</h3>
                <div>
                  <Form.Item
                    name="newPassword"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input.Password />
                  </Form.Item>
                </div>
              </Col>
            </Row>
            <Row
              style={{
                border: "0.5px dashed #979797",
                opacity: "0.40",
                margin: "20px 0 20px 0px",
              }}
            />
            <Row>
              <Col lg={17}></Col>
              <Col lg={7}>
                <div
                  style={{
                    padding: "10px",
                    marginRight: "20px",
                    float: "right",
                  }}
                >
                  <Button className="savebtn" type="primary" htmlType="submit">
                    SAVE
                  </Button>
                </div>
                <div style={{padding: "10px", float: "right"}}>
                  <Button
                    type="primary"
                    className="cancelBtn"
                    onClick={() => {
                      document
                        .getElementsByClassName("ant-modal-close")[0]
                        .click();
                      this.onReset();
                    }}
                  >
                    CANCEL
                  </Button>
                </div>
              </Col>
            </Row>
          </Form>
        </Spin>
      </div>
    );
  }
}

export default EditUser;
