const formatTripRegisterData = (tripRegisterData, data) => {
  if (tripRegisterData && tripRegisterData[0]) {
    const {tripDetails, ...tripAggrigate} =
      tripRegisterData && tripRegisterData[0];
    console.log(tripDetails, "tripdetails");
    return {
      tripDetails:
        Array.isArray(tripDetails) &&
        tripDetails.map((trip, index) => ({
          // 'key': `${index}-${trip.vehicleNumber}`,
          slNo: index + 1,
          ...trip,
        })),
      tripAggrigate,
    };
  }
  return {};
};

const decimals = (n, d = 2) => {
  if (typeof n !== "number" || typeof d !== "number") return n;
  let isDecimalNum = n % 1 !== 0;
  n = isDecimalNum ? parseFloat(n) : parseInt(n);
  return isDecimalNum ? n.toFixed(d) : n;
};

const convertDataToExcel = (head, body, footer) => {
  const headKeyMap = head.reduce((acc, val) => {
    acc[val.dataIndex] = val.title;
    return acc;
  }, {});

  const headKeys = head.map((item) => item.title);

  let footerObj = {};

  if (Array.isArray(footer)) {
    for (var i = 0, j = 0; i < head.length; j++) {
      footerObj[headKeys[i]] = footer[j] && footer[j].content;
      if (footer[i] && footer[i].colSpan) {
        i += footer[i].colSpan;
      } else {
        i++;
      }
    }
  }

  const bodyArr =
    (Array.isArray(body) &&
      body.map((item) => {
        let changedItem = {};
        for (var i in headKeyMap) {
          changedItem[headKeyMap[i]] = item[i];
        }
        return changedItem;
      })) ||
    [];

  const result = [...(bodyArr || []), footerObj];

  return result;
};

const convertDataToPrintable = (data, headData) => {
  const head = [headData.map((h) => h.title)];
  const columnData = (row) =>
    headData.map((item, index) => {
      if (item.dataIndex === "slno") {
        return index + 1;
      }
      return (
        decimals(row[item.dataIndex]) || (row[item.dataIndex] === 0 ? 0 : "-")
      );
    });
  const body = (Array.isArray(data) &&
    data.map((item) => columnData(item))) || [["Data Not Available"]];

  return {
    head: head,
    body: body,
  };
};

const calculateTotalBalance = (dataExpenses) =>
  dataExpenses.reduce((sum, val) => {
    if (val.particulars === "Total Amount") {
      return sum + +(val.amount || 0);
    } else {
      return sum - +(val.amount || 0);
    }
  }, 0);

export {
  formatTripRegisterData,
  columns,
  columnsAdvance,
  columnsAdvanceVoucher,
  columnsExpenses,
  accountDetailsColumn,
  decimals,
  convertDataToPrintable,
  convertDataToExcel,
  calculateTotalBalance,
};
