/* eslint-disable no-unused-vars */
/* eslint-disable no-const-assign */
/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from "react";
import {Table, Button, Card, Row, Col, message, Spin} from "antd";
import jsPDF from "jspdf";
import axios from "axios";
import {BASE_URL} from "../../constants/constants";
import "jspdf-autotable";
import * as XLSX from "xlsx";
import DownloadIcon from "../../assests/images/download.svg";
import "./report.css";

import PrintIcon from "../../assests/images/print.svg";
import {
  formatTripRegisterData,
  columns,
  columnsAdvance,
  columnsAdvanceVoucher,
  columnsExpenses,
  decimals,
  convertDataToPrintable,
  accountDetailsColumn,
  convertDataToExcel,
  calculateTotalBalance,
} from "./reportUtils";

class PrintDownloadReport extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      ownerTransferLog: [],
    };
  }
  getOwnertransferLog = async () => {
    const response = await axios.get(`${BASE_URL}get-all-users-logs`);
    const ownerDetails = response.data.ownerDetails || "";
    this.setState({
      ownerTransferLog: ownerDetails,
    });
  };
  componentDidMount() {
    this.getOwnertransferLog();
  }
  render() {
    const {ownerDetails, dataSource} = this.props;
    const {ownerTransferLog} = this.state;
    const tripRegisterData =
      formatTripRegisterData(
        dataSource.tripRegister && dataSource.tripRegister.value
      ) || {};

    const bankAccDetails =
      (dataSource.bankDetails && dataSource.bankDetails.value) || [];
    const ownersAdvance =
      (dataSource.ownersAdvance &&
        dataSource.ownersAdvance.value &&
        dataSource.ownersAdvance.value[0]) ||
      {};
    const advanceVoucher =
      (dataSource.advanceVoucher &&
        dataSource.advanceVoucher.value &&
        dataSource.advanceVoucher.value[0]) ||
      {};
    const dataExpenses = [
      {
        particulars: "Total Amount",
        amount:
          tripRegisterData.tripAggrigate &&
          tripRegisterData.tripAggrigate.totalAmount &&
          decimals(tripRegisterData.tripAggrigate.totalAmount),
      },
      {
        particulars: "Commision",
        amount:
          tripRegisterData.tripAggrigate &&
          tripRegisterData.tripAggrigate.totalCommisionTotal &&
          decimals(tripRegisterData.tripAggrigate.totalCommisionTotal),
      },
      {
        particulars: "Diesel",
        amount:
          tripRegisterData.tripAggrigate &&
          tripRegisterData.tripAggrigate.totalDiesel &&
          decimals(tripRegisterData.tripAggrigate.totalDiesel),
      },
      {
        particulars: "Cash",
        amount:
          tripRegisterData.tripAggrigate &&
          tripRegisterData.tripAggrigate.totalCash &&
          decimals(tripRegisterData.tripAggrigate.totalCash),
      },
      {
        particulars: "Bank Transfer",
        amount:
          tripRegisterData.tripAggrigate &&
          tripRegisterData.tripAggrigate.totalBankTransfer &&
          decimals(tripRegisterData.tripAggrigate.totalBankTransfer),
      },
      {
        particulars: "Shortage",
        amount:
          tripRegisterData.tripAggrigate &&
          tripRegisterData.tripAggrigate.totalShortage &&
          decimals(tripRegisterData.tripAggrigate.totalShortage),
      },
      {
        particulars: "Outstanding Debit",
        amount:
          ownersAdvance && ownersAdvance.totalOutstandingDebit
            ? decimals(ownersAdvance.totalOutstandingDebit)
            : "0",
      },
      {
        particulars: "Advance Voucher",
        amount: advanceVoucher.total ? decimals(advanceVoucher.total) : "0",
      },
    ];

    const tripRegisterFooter = [
      [
        {
          content: "Total:",
          colSpan: 5,
        },
        {
          content:
            tripRegisterData.tripAggrigate &&
            tripRegisterData.tripAggrigate.totalQuantity &&
            decimals(tripRegisterData.tripAggrigate.totalQuantity),
        },
        {
          content: "",
        },
        {
          content:
            tripRegisterData.tripAggrigate &&
            tripRegisterData.tripAggrigate.totalAmount &&
            decimals(tripRegisterData.tripAggrigate.totalAmount),
        },
        {
          content:
            tripRegisterData.tripAggrigate &&
            tripRegisterData.tripAggrigate.totalCommisionTotal &&
            decimals(tripRegisterData.tripAggrigate.totalCommisionTotal),
        },
        {
          content:
            tripRegisterData.tripAggrigate &&
            tripRegisterData.tripAggrigate.totalDiesel &&
            decimals(tripRegisterData.tripAggrigate.totalDiesel),
        },
        {
          content:
            tripRegisterData.tripAggrigate &&
            tripRegisterData.tripAggrigate.totalCash &&
            decimals(tripRegisterData.tripAggrigate.totalCash),
        },
        {
          content:
            tripRegisterData.tripAggrigate &&
            tripRegisterData.tripAggrigate.totalBankTransfer &&
            decimals(tripRegisterData.tripAggrigate.totalBankTransfer),
        },
        {
          content:
            tripRegisterData.tripAggrigate &&
            tripRegisterData.tripAggrigate.totalShortage &&
            decimals(tripRegisterData.tripAggrigate.totalShortage),
        },
      ],
    ];

    const ownersAdvanceFooter = [
      [
        {
          content: "",
        },
        {
          content: "New Outstanding:",
          colSpan: 2,
        },
        {
          content: ownersAdvance.outStandingAmount || 0,
          colSpan: 2,
          styles: {
            halign: "center",
          },
        },
      ],
    ];
    // const valueofData = tripRegisterData.tripDetails;
    // console.log(valueofData, "valueofData", ownerTransferLog);
    // const TripData = tripRegisterData.tripDetails
    //   ? tripRegisterData.tripDetails.map((trip, index) => ({
    //       if(ownerTransferLog) {
    //         console.log(ownerTransferLog, "owenrTransfer");
    //         const val = ownerTransferLog.filter((vehicleNumber) => {
    //           return vehicleNumber == trip.vehicleNumber;
    //           console.log(vehicleNumber, "vehicleNumbr");
    //         });
    //         return {
    //           ...trip,
    //           isTransfer: true,
    //         };
    //       },
    //     }))
    //   : "";
    // console.log(TripData, "TripData");
    const advanceVoucherFooter = [
      [
        {
          content: "Total:",
          colSpan: 2,
        },
        {
          content: advanceVoucher.total || 0,
        },
      ],
    ];

    const ownerExpenseFooter = [
      [
        {
          content: "Balance:",
        },
        {
          content: decimals(calculateTotalBalance(dataExpenses)),
          styles: {
            textColor:
              calculateTotalBalance(dataExpenses) <= 0 ? "red" : [0, 136, 170],
          },
        },
      ],
    ];

    const handleDownload = (e) => {
      const ws = XLSX.utils.json_to_sheet(
        convertDataToExcel(
          columns,
          tripRegisterData.tripDetails,
          tripRegisterFooter[0]
        )
      );
      const ws2 = XLSX.utils.json_to_sheet(
        convertDataToExcel(columnsExpenses, dataExpenses, ownerExpenseFooter[0])
      );
      const ws3 = XLSX.utils.json_to_sheet(
        convertDataToExcel(
          columnsAdvance,
          ownersAdvance.ledgerDetails,
          ownersAdvanceFooter[0]
        )
      );
      const ws4 = XLSX.utils.json_to_sheet(
        convertDataToExcel(
          columnsAdvanceVoucher,
          advanceVoucher.voucherDetails,
          advanceVoucherFooter[0]
        )
      );
      const ws5 = XLSX.utils.json_to_sheet(
        convertDataToExcel(
          accountDetailsColumn,
          bankAccDetails.map((item) => ({
            ...item,
            phoneNumber: ownerDetails.ownerPhone,
          }))
        )
      );

      const wb = {
        Sheets: {
          "Trip Register": ws,
          Expenses: ws2,
          Advance: ws3,
          Voucher: ws4,
          "Bank Details": ws5,
        },
        SheetNames: [
          "Trip Register",
          "Expenses",
          "Advance",
          "Voucher",
          "Bank Details",
        ],
      };
      const excelBuffer = XLSX.write(wb, {bookType: "xlsx", type: "array"});
      const data = new Blob([excelBuffer], {
        type:
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8",
      });
      var url = window.URL.createObjectURL(data, "Report.xlsx");
      window.open(url, "_blank");
    };
    const handlePrint = (e) => {
      const {head: expenseHead, body: expenseBody} = convertDataToPrintable(
        dataExpenses,
        columnsExpenses
      );
      const {
        head: ownersAdvanceHead,
        body: ownersAdvanceBody,
      } = convertDataToPrintable(ownersAdvance.ledgerDetails, columnsAdvance);
      const {head: voucherHead, body: voucherBody} = convertDataToPrintable(
        advanceVoucher.voucherDetails,
        columnsAdvanceVoucher
      );
      const {
        head: tripRegisterHead,
        body: tripRegisterBody,
      } = convertDataToPrintable(tripRegisterData.tripDetails, columns);
      const {
        head: bankDetailsHead,
        body: bankDetailsBody,
      } = convertDataToPrintable(
        bankAccDetails.map((item) => ({
          ...item,
          phoneNumber: ownerDetails.ownerPhone,
        })),
        accountDetailsColumn
      );
      try {
        var pageMargin = 14;
        var startX = pageMargin;
        var startYpos = pageMargin;
        const doc = new jsPDF("l", "mm", "a4");
        const commonTableProps = {
          showFoot: "lastPage",
          showHead: "firstPage",
          headStyles: {
            fontSize: 8,
            fontStyle: "normal",
            fillColor: "#44495b",
            linheight: 40,
            minCellHeight: 8,
          },
          footStyles: {
            fontSize: 10,
            fillColor: "#f0f4f6",
            textColor: "#0088aa",
          },
          bodyStyles: {
            fontSize: 10,
            textAlign: "center",
            minCellHeight: 10,
            valign: "middle",
            rowHeight: 10,
          },
          margin: {top: 10},
        };
        const commonTablePropstwo = {
          styles: {
            cellPadding: 2,
            rowHeight: 10,
            // fillStyle: "S",
            // halign: "center",
            valign: "middle",
            // fontStyle: "bold",
            lineWidth: {bottom: 0.01},
            fontSize: 8,
            textColor: 0,
          },
          showFoot: "lastPage",
          showHead: "firstPage",
          // theme: "plain",
          headStyles: {
            fontSize: 10,
            fontStyle: "normal",
            fillColor: "#d1d1d1",
            linheight: 40,
            minCellHeight: 10,
          },
          footStyles: {
            fontSize: 10,
            fillColor: "#f0f4f6",
            textColor: "#0088aa",
          },
          bodyStyles: {
            fontSize: 10,
            textAlign: "center",
            minCellHeight: 10,
            valign: "middle",
            rowHeight: 10,
          },
          margin: {top: 10},
          tableLineColor: 100,
        };
        doc.text(`${ownerDetails.ownerName}'s Performance Report`, 14, 15);
        doc.setFontSize(13);
        doc.text("Trip Register", 14, 25);
        doc.autoTable({
          ...commonTableProps,
          head: tripRegisterHead,
          body: tripRegisterBody,
          startY: 30,
          foot: tripRegisterFooter,
          didDrawPage: function (row, data) {
            if (doc.internal.getNumberOfPages() > 1) {
              return false;
            }
          },
        });
        // window.alert(doc.autoTable.previous.finalY);
        startYpos = 10;
        if (doc.autoTable.previous.finalY <= "80") {
          startYpos = doc.autoTable.previous.finalY + 10;
        } else {
          doc.addPage();
        }

        // console.log(AdvanceVoucherSize, "advaancesize");
        //Owner Expense Table
        doc.text(`${ownerDetails.ownerName}'s Expenses`, 155, startYpos);
        doc.autoTable({
          ...commonTablePropstwo,
          // head: expenseHead,
          head: [["Particulars", "Amount "]],
          body: expenseBody,
          startY: startYpos + 5,
          foot: ownerExpenseFooter,
          margin: {left: 155},
        });
        // if (startYpos >= "100") {
        //   startYpos = -20;
        // }
        //Owner Advance Table
        doc.text(`${ownerDetails.ownerName}'s Advances`, 14, startYpos);
        // window.alert(AdvanceVoucherSize);
        doc.autoTable({
          ...commonTablePropstwo,
          head: ownersAdvanceHead,
          body: ownersAdvanceBody,
          foot: ownersAdvanceFooter,
          startY: startYpos + 5,
          tableWidth: 130,
        });
        // window.alert(doc.autoTable.previous.finalY);
        //Advance voucher Table
        doc.text("Advance Voucher", 14, doc.autoTable.previous.finalY + 10);
        doc.autoTable({
          ...commonTablePropstwo,
          // head: voucherHead,
          head: [["Date", "Narration", "Amount"]],
          body: voucherBody,
          foot: advanceVoucherFooter,
          startY: doc.autoTable.previous.finalY + 15,
          tableWidth: 130,
        });

        //Owner Bank Details Table
        doc.text(
          "Bank Account Details",
          14,
          doc.autoTable.previous.finalY + 30
        );
        doc.autoTable({
          ...commonTableProps,
          head: bankDetailsHead,
          body: bankDetailsBody,
          startY: doc.autoTable.previous.finalY + 35,
        });
        doc.save(`Report-${ownerDetails.ownerName.split(" ").join("-")}.pdf`);
      } catch (err) {
        message.error("Unable to Print");
      }
    };
    const {isLoading} = this.state;
    return (
      // <Spin spining={isLoading} delay={500}>
      <div className="report">
        <div style={{padding: "0px 0px 0px 20px"}}>
          <Row>
            <Col
              xs={12}
              sm={12}
              md={12}
              lg={12}
              xl={12}
              style={{padding: "10px", marginTop: "10px"}}
            >
              <h3 className="reporttitle">Trip Register </h3>
            </Col>
            <Col
              xs={12}
              sm={12}
              md={12}
              lg={12}
              xl={12}
              style={{padding: "10px", marginTop: "10px"}}
            >
              <div style={{display: "flex", float: "right"}}>
                <div style={{padding: "5px"}}>
                  <Button
                    className="outlineBtn"
                    icon={
                      <img src={DownloadIcon} style={{paddingRight: "7px"}} />
                    }
                    onClick={handleDownload}
                  >
                    Download
                  </Button>
                </div>
                <div style={{padding: "5px"}}>
                  <Button
                    className="outlineBtn"
                    onClick={handlePrint}
                    icon={
                      <img src={PrintIcon} style={{paddingRight: "7px"}}></img>
                    }
                  >
                    Print
                  </Button>
                </div>
              </div>
            </Col>
          </Row>
        </div>
        <div className="reportdetails">
          <Table
            className="tripRegister"
            columns={columns}
            rowKey="slNo"
            dataSource={tripRegisterData.tripDetails}
            pagination={false}
            summary={(pageData) => {
              return (
                <>
                  <Table.Summary.Row className="tableSummary">
                    {tripRegisterFooter[0].map((footer) => (
                      <Table.Summary.Cell colSpan={footer.colSpan || 1}>
                        {footer.content}
                      </Table.Summary.Cell>
                    ))}
                  </Table.Summary.Row>
                </>
              );
            }}
          />
        </div>
        <hr className="line"></hr>
        <div className="advanceTables">
          <Row>
            <Col xs={12} sm={12} md={12} lg={12} xl={12}>
              <div>
                <h3>
                  {(ownerDetails &&
                    ownerDetails.ownerName &&
                    `${ownerDetails.ownerName}'s`) ||
                    ""}{" "}
                  Advance
                </h3>
                <Table
                  className="tripRegister"
                  columns={columnsAdvance}
                  rowKey="slno"
                  dataSource={ownersAdvance.ledgerDetails}
                  pagination={false}
                  summary={(pageData) => {
                    return (
                      <>
                        <Table.Summary.Row className="tableSummary">
                          {/* <Table.Summary.Cell></Table.Summary.Cell> */}
                          <Table.Summary.Cell colSpan={3}>
                            New Outstanding:
                          </Table.Summary.Cell>
                          <Table.Summary.Cell colSpan={2}>
                            <div className="totalAmountadvance">
                              <div
                                style={{
                                  color:
                                    decimals(ownersAdvance.outStandingAmount) <=
                                    0
                                      ? decimals(
                                          ownersAdvance.outStandingAmount
                                        ) === 0
                                        ? "black"
                                        : "red"
                                      : "inherit",
                                }}
                              >
                                {ownersAdvance.outStandingAmount || 0}
                              </div>
                            </div>
                          </Table.Summary.Cell>
                        </Table.Summary.Row>
                      </>
                    );
                  }}
                />
              </div>
              <div>
                <h3>Advance Voucher</h3>
                <Table
                  className="tripRegister"
                  columns={columnsAdvanceVoucher}
                  rowKey="narration"
                  dataSource={advanceVoucher.voucherDetails}
                  pagination={false}
                  summary={(pageData) => {
                    return (
                      <>
                        <Table.Summary.Row className="tableSummary">
                          <Table.Summary.Cell colSpan={2}>
                            Total:
                          </Table.Summary.Cell>
                          <Table.Summary.Cell>
                            <div className="totalAmountWrapper">
                              <div>{advanceVoucher.total || 0}</div>
                            </div>
                          </Table.Summary.Cell>
                        </Table.Summary.Row>
                      </>
                    );
                  }}
                />
              </div>
            </Col>
            <Col xs={12} sm={12} md={12} lg={12} xl={12}>
              <div>
                <h3>
                  {(ownerDetails &&
                    ownerDetails.ownerName &&
                    `${ownerDetails.ownerName}'s`) ||
                    ""}{" "}
                  Expenses
                </h3>
                <Table
                  className="expenses"
                  columns={columnsExpenses}
                  rowKey="particulars"
                  dataSource={dataExpenses}
                  pagination={false}
                  summary={(pageData) => {
                    let totalBalance = 0;

                    pageData.forEach(({amount, particulars}) => {
                      if (amount) {
                        if (particulars === "Total Amount") {
                          totalBalance += +amount;
                        } else {
                          totalBalance -= +amount;
                        }
                      }
                    });

                    return (
                      <>
                        <Table.Summary.Row className="tableSummary">
                          <Table.Summary.Cell>
                            <div style={{padding: "0 16px"}}>Balance:</div>
                          </Table.Summary.Cell>
                          <Table.Summary.Cell>
                            <div className="totalAmountWrapper">
                              <div
                                style={{
                                  color:
                                    decimals(totalBalance) <= 0
                                      ? "red"
                                      : "inherit",
                                }}
                              >
                                {decimals(totalBalance)}
                              </div>
                            </div>
                          </Table.Summary.Cell>
                        </Table.Summary.Row>
                      </>
                    );
                  }}
                />
              </div>
            </Col>
          </Row>
        </div>
        <hr className="line"></hr>
        <div className="bankDetailsCard">
          <h3>Bank Account Details</h3>
          <Row className="cards">
            {bankAccDetails.map((accDetails, index) => (
              <Col
                key={index}
                className="cardone"
                xs={24}
                sm={24}
                md={12}
                lg={12}
                xl={12}
              >
                <Card style={{width: 330, marginBottom: "20px"}}>
                  <Row className="cardRow">
                    <div style={{width: "33%"}}>
                      {" "}
                      <p className="cardtitle">Bank Name</p>
                      <span className="cardpara">{accDetails.bankName}</span>
                    </div>
                    <div style={{width: "33%"}}>
                      {" "}
                      <p className="cardtitle">IFSC Code</p>{" "}
                      <span className="cardpara">{accDetails.ifscCode}</span>
                    </div>
                    <div style={{width: "33%"}}>
                      {" "}
                      <p className="cardtitle">Branch Name</p>
                      <span className="cardpara">{accDetails.branchName}</span>
                    </div>
                  </Row>
                  <Row className="cardRow">
                    <div style={{width: "50%"}}>
                      {" "}
                      <p className="cardtitle">Name</p>{" "}
                      <span className="cardpara">
                        {accDetails.accountHolderName}
                      </span>
                    </div>
                    <div style={{width: "50%"}}>
                      {" "}
                      <p className="cardtitle">A/C No</p>
                      <span className="cardpara">
                        {accDetails.accountNumber}
                      </span>
                    </div>
                  </Row>
                  <Row className="cardRow">
                    <p className="cardtitle">Phone Number</p>
                    <span className="cardphone">
                      {ownerDetails && ownerDetails.ownerPhone}
                    </span>
                  </Row>
                </Card>
              </Col>
            ))}
          </Row>
        </div>
        <Row>
          <Col
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
            style={{padding: "10px"}}
          ></Col>
          <Col
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
            style={{padding: "10px"}}
          >
            <div style={{display: "flex", float: "right"}}>
              <div style={{padding: "5px"}}>
                <Button
                  className="outlineBtn"
                  icon={
                    <img src={DownloadIcon} style={{paddingRight: "7px"}} />
                  }
                  onClick={handleDownload}
                >
                  Download
                </Button>
              </div>
              <div style={{padding: "5px"}}>
                <Button
                  className="outlineBtn"
                  icon={
                    <img src={PrintIcon} style={{paddingRight: "7px"}}></img>
                  }
                  onClick={handlePrint}
                >
                  Print
                </Button>
              </div>
            </div>
          </Col>
        </Row>
      </div>
      // </Spin>
    );
  }
}

export default PrintDownloadReport;
