/* eslint-disable no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable no-unused-expressions */
import React, {Component} from "react";
import {
  Input,
  Row,
  Col,
  Form,
  Select,
  Upload,
  Button,
  InputNumber,
  message,
  Spin,
  DatePicker,
} from "antd";
import axios from "axios";
import moment from "moment";
import {PaperClipOutlined} from "@ant-design/icons";
import Logo from "../../assests/images/trucklink.png";
import dateicon from "../../assests/images/dateicon.svg";
import {BASE_URL} from "../../constants/constants";
const {Option} = Select;
const addFile = {fontWeight: "600", color: " #01a6d0"};
const {Dragger} = Upload;
const dateFormatList = ["DD/MM/YYYY"];
const numInput = (event) => {
  const val = event.target.value;
  var digit = /^[0-9]*\.?[0-9]*$/.test(val);
  if (/^[0-9]*\.?[0-9]*$/.test(val) && event.key !== "Backspace") {
    var with2Decimals = val.toString().match(/^-?\d+(?:\.\d{0,1})?/);
    if (with2Decimals) {
      event.target.value = with2Decimals[0];
    }
  } else {
    return true;
  }
};
class vehicleinformation extends Component {
  constructor(props) {
    super(props);
    const {record, grDate} = this.props;
    const date = record.grDate;
    var GrDate = moment(date, "DD-MM-YYYY");
    const invDate =
      record.invoiceDate === undefined || record.invoiceDate === null
        ? ""
        : record.invoiceDate;

    var INVDate = invDate !== "" ? moment(invDate, "DD-MM-YYYY") : "";
    const {_id, vehicleType} = record;
    this.state = {
      loading: false,
      visible: false,
      fullrecord: record,
      materialDataSelect: [],
      loadLocationDataSelect: [],
      deliveryLocationDataSelect: [],
      AllVehicleNumber: [],
      challanId: _id,
      VehicleArray: [],
      Invoice: "",
      typeofTruck: vehicleType,
      commission: "",
      grDate: GrDate,
      vehicleDetails: "",
      invoiceDate: INVDate,
    };
  }

  formRef = React.createRef();
  getMaterialData = async () => {
    try {
      const response = await axios.get(`${BASE_URL}get-material`);
      const materialData = response.data.materials || "";
      const materialtypes = materialData.map(({_id, materialType}) => {
        const val = materialType.trimEnd();
        return (
          <Option key={_id} value={materialType}>
            {materialType}
          </Option>
        );
      });
      this.setState({
        materialDataSelect: materialtypes,
      });
    } catch (err) {
      console.log(err);
    }
  };
  getLoadLocationData = async () => {
    try {
      const response = await axios.get(`${BASE_URL}get-load-location`);
      const locationData = response.data.materials || "";
      const locations = locationData.map(({_id, location}) => {
        return (
          <Option key={_id} value={location}>
            {location}
          </Option>
        );
      });
      this.setState({
        loadLocationDataSelect: locations,
      });
    } catch (err) {
      console.log(err);
      message.error("Network Already, Please try again");
    }
  };
  getDeliveryLocationData = async () => {
    try {
      const response = await axios.get(`${BASE_URL}get-delivery-location`);
      const locationData = response.data.materials || "";
      const locations = locationData.map(({_id, location}) => {
        return (
          <Option key={_id} value={location}>
            {location}
          </Option>
        );
      });
      this.setState({
        deliveryLocationDataSelect: locations,
      });
    } catch (err) {
      console.log(err);
    }
  };
  getAllVehicleNumber = async () => {
    try {
      const response = await axios.get(`${BASE_URL}get-old-vehicle-numbers`);
      const vehicleData = response.data.data || "";
      const unique = [];

      vehicleData.map((x) =>
        unique.filter((a) => a._id == x._id && a.ownerId == x.ownerId).length >
        0
          ? null
          : unique.push(x)
      );

      const vehicleNo = unique.map(({_id, registrationNumber, ownerId}) => {
        let ownerIdData = "";
        if (ownerId && ownerId._id) {
          ownerIdData = ownerId._id;
        }
        const regData = registrationNumber + "-" + ownerIdData;
        return (
          <Option key={regData} value={regData}>
            {registrationNumber}
            {ownerId && ownerId.name ? " - " + ownerId.name.toUpperCase() : ""}
          </Option>
        );
      });
      this.setState({
        AllVehicleNumber: vehicleNo,
        VehicleArray: vehicleData,
      });
    } catch (err) {
      console.log(err);
      message.error("Network Failed, Please Try Again");
    }
  };

  componentDidMount() {
    this.getLoadLocationData();
    this.getDeliveryLocationData();
    this.getMaterialData();
    this.getAllVehicleNumber();
  }

  onFinish = async (ackDetails) => {
    const {
      fullrecord,
      invoicefilelink,
      typeofTruck,
      vehicleDetails,
    } = this.state;

    const {
      _id,
      commisionRate,
      invoiceProof,
      materialType,
      ownerId,
      ownerName,
      vehicleId,
      vehicleBank,
      ownerPhone,
    } = fullrecord;
    let invoicefileurl = "";
    if (invoicefilelink === "") {
      invoicefileurl = invoiceProof;
    } else {
      invoicefileurl = invoicefilelink;
    }
    const {
      bankTransfer,
      cash,
      deliveryLocation,
      deliveryNumber,
      diesel,
      grNumber,
      loadLocation,
      quantityInMetricTons,
      rate,
      shortage,
      vehicleNumber,
      commissionValue,
    } = ackDetails;
    const vehicleNoData = vehicleNumber.split("-");
    const date = ackDetails.grDate;
    var DateString = moment(date).format("DD/MM/YYYY");
    var startDate = moment(DateString, "DD/MM/YYYY");
    var grDate = startDate._i;
    const idate = ackDetails.invoiceDate;
    var iDateString = moment(idate).format("DD/MM/YYYY");
    var istartDate = moment(iDateString, "DD/MM/YYYY");
    var invoiceDate = istartDate._i;

    const totalExpenses = quantityInMetricTons * rate;
    const commissionTotals = (totalExpenses * commissionValue) / 100;
    const balances =
      totalExpenses -
      commissionTotals -
      diesel -
      cash -
      bankTransfer -
      shortage;
    try {
      await axios
        .put(`${BASE_URL}update-dispatch-challan/${_id}`, {
          invoiceDate: invoiceDate,
          balance: balances,
          grNumber: grNumber,
          deliveryLocation: deliveryLocation,
          bankTransfer: bankTransfer,
          cash: cash,
          deliveryNumber: deliveryNumber,
          diesel: diesel,
          loadLocation: loadLocation,
          quantityInMetricTons: quantityInMetricTons,
          rate: rate,
          vehicleNumber: vehicleNoData[0],
          grDate: grDate,
          commisionRate: commissionValue,
          commisionTotal: commissionTotals,
          totalExpense: totalExpenses,
          shortage: shortage,
          vehicleType: typeofTruck,
          invoiceProof: invoicefileurl,
          materialType: materialType,
          ownerId: vehicleDetails ? vehicleDetails.ownerId._id : ownerId,
          ownerName: vehicleDetails ? vehicleDetails.ownerId.name : ownerName,
          vehicleId: vehicleDetails ? vehicleDetails._id : vehicleId,
          ownerPhone: vehicleDetails
            ? vehicleDetails.ownerId.phoneNumber
            : ownerPhone,
          vehicleBank: vehicleDetails ? vehicleDetails.accountId : vehicleBank,
        })
        .then((data) => {
          message.success("Successfully Updated Recieve Register");
          this.props.parentFn();
        })
        .catch((error) => {
          const {response} = error;
          const {data} = response;
          const {message: msg} = data;
          message.error(msg);
        });
    } catch (err) {
      message.error("Delivery Number Already Exist");
    }
  };

  axiosFileUploadRequestInvoice = async (file) => {
    try {
      const formData = new FormData();
      formData.append("file", file.file);
      const config = {
        headers: {
          "content-type": "multipart/form-data",
        },
      };
      const response = await axios.post(
        `${BASE_URL}rc-upload`,
        formData,
        config
      );
      const {rcBookProof} = response.data;
      this.setState({invoicefilelink: rcBookProof});
      message.success("File uploaded successfully ");
    } catch (err) {
      message.error("Network Failed, Please Try Again");
    }
  };

  onReset = () => {
    this.formRef.current.resetFields();
  };

  getAllOwnerPhoneNumbers = async () => {
    try {
      this.setState({loading: true});
      const response = await axios.get(`${BASE_URL}get-owner-phoneNumbers`);
      const {ownerPhoneNumbers} = response.data;
      if (ownerPhoneNumbers && ownerPhoneNumbers.length > 0) {
        const ownerNumbers = ownerPhoneNumbers.map(({phoneNumber, _id}) => (
          <Option key={_id} value={_id}>
            {phoneNumber}
          </Option>
        ));
        this.setState({ownerNumbers, loading: false});
      }
    } catch (err) {
      console.error(err);
      this.setState({loading: false});
      message.error("Unable to fetch owner phone numbers");
    }
  };

  clearOnOwnerChange = () => {
    this.setState({
      accountInfo: [],
      ownerInfo: {},
      bankOptions: [],
      selectedBankDetails: {},
      isOwnerEnabled: false,
      isBankEnabled: false,
    });
  };

  handleOwnerSelection = async (e) => {
    try {
      this.clearOnOwnerChange();
      this.setState({loading: true});
      const response = await axios.get(
        `${BASE_URL}/get-owner-bank-details/${e}`
      );
      const {ownerDetails} = response.data;
      const {accountIds: accountInfo, ...ownerInfo} = ownerDetails
        ? ownerDetails[0]
        : {};
      let bankOptions = [];
      if (accountInfo && accountInfo.length > 0) {
        bankOptions = accountInfo.map(({_id, bankName}) => (
          <Option key={_id} value={_id}>
            {bankName}
          </Option>
        ));
      }
      this.setState({
        accountInfo,
        ownerInfo,
        bankOptions,
        isOwnerEnabled: true,
        loading: false,
      });
    } catch (err) {
      console.error(err);
      message.error("Unable to fetch bank details");
      this.setState({loading: false});
    }
  };

  handleAccountSelection = (e) => {
    try {
      const {accountInfo} = this.state;
      const bankDetails = accountInfo.filter(({_id}) => _id === e);
      this.setState({
        selectedBankDetails: bankDetails[0],
        isBankEnabled: true,
      });
    } catch (err) {
      message.error("Unable to change bank account");
    }
  };
  onChange = async (e) => {
    const data = e.split("-");
    try {
      const {VehicleArray} = this.state;
      const filteredTableData = VehicleArray.filter(
        ({registrationNumber, ownerId}) => {
          return registrationNumber == data[0] && ownerId._id == data[1];
        }
      );
      if (filteredTableData && filteredTableData.length > 0) {
        const typotruck = filteredTableData[0].truckType;
        // console.log(typotruck, "hihihi");
        const typeofTruck =
          typotruck && typotruck.length > 0
            ? typotruck
                .split(" ")
                .map((w) => w[0].toUpperCase() + w.substr(1).toLowerCase())
                .join(" ")
            : "";
        const commission = filteredTableData[0].commission;
        await this.setState({
          typeofTruck,
          vehicleDetails: filteredTableData[0],
        });
      }
    } catch (err) {
      console.log(err);
    }
  };

  render() {
    const {
      fileList,
      fullrecord,
      materialDataSelect,
      loadLocationDataSelect,
      deliveryLocationDataSelect,
      AllVehicleNumber,
      typeofTruck,
      grDate,
      invoiceDate,
    } = this.state;
    const {
      grNumber,
      shortage,
      deliveryLocation,
      bankTransfer,
      cash,
      deliveryNumber,
      diesel,
      loadLocation,
      quantityInMetricTons,
      rate,
      vehicleNumber,
      vehicleType,
      commisionRate,
      ownerName,
    } = fullrecord;
    return (
      <div>
        <Spin spinning={this.state.loading} delay={500}>
          <Form
            ref={this.formRef}
            name="vehicleinfo"
            className="vehicleinfoForm"
            autoComplete="off"
            onFinish={this.onFinish}
            initialValues={{
              grNumber,
              shortage,
              deliveryLocation,
              bankTransfer,
              cash,
              deliveryNumber,
              diesel,
              loadLocation,
              quantityInMetricTons,
              rate,
              vehicleNumber: vehicleNumber + " - " + ownerName,
              vehicleType,
              grDate: grDate,
              invoiceDate: invoiceDate,
              commissionValue: commisionRate,
            }}
          >
            <h2 style={{margin: "25px 30px 15px 30px"}}>Challan Details</h2>
            <Row style={{margin: "10px 22px"}}>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}}>GR Number</h3>
                <div>
                  <Form.Item name="grNumber">
                    <Input
                      onKeyUp={(e) => {
                        var start = e.target.selectionStart;
                        var end = e.target.selectionEnd;
                        e.target.value = e.target.value.toUpperCase();
                        e.target.setSelectionRange(start, end);
                      }}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  GR Date
                </h3>
                <div>
                  <Form.Item
                    name="grDate"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <DatePicker
                      suffixIcon={<img src={dateicon} alt="dateIcon" />}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={5} lg={5} xl={5}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Load Location
                </h3>
                <div>
                  <Form.Item
                    name="loadLocation"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Select
                      showSearch
                      placeholder="Select Load Location"
                      optionFilterProp="children"
                      // onChange={onChange}
                      // onFocus={onFocus}
                      // onBlur={onBlur}
                      // onSearch={onSearch}
                      filterOption={(input, option) =>
                        option.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {loadLocationDataSelect}
                    </Select>
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={5} lg={5} xl={5}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  To Deliver
                </h3>
                <div>
                  <Form.Item
                    name="deliveryLocation"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Select
                      showSearch
                      placeholder="Select Delivery Location"
                      optionFilterProp="children"
                      // onChange={onChange}
                      // onFocus={onFocus}
                      // onBlur={onBlur}
                      // onSearch={onSearch}
                      filterOption={(input, option) =>
                        option.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {deliveryLocationDataSelect}
                    </Select>
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={6} lg={6} xl={6}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Vehicle Number
                </h3>
                <div>
                  <Form.Item
                    name="vehicleNumber"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Select
                      showSearch
                      placeholder="Select Vehicle Number"
                      optionFilterProp="children"
                      onChange={this.onChange}
                      // onFocus={onFocus}
                      // onBlur={onBlur}
                      // onSearch={onSearch}
                      filterOption={(input, option) =>
                        option &&
                        option.children
                          .toString()
                          .toLowerCase()
                          .indexOf(input && input.toString().toLowerCase()) >= 0
                      }
                    >
                      {AllVehicleNumber}
                    </Select>
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Vehicle Type
                </h3>
                <div>
                  {/* <Form.Item
                    name="vehicleType"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  > */}
                  <Input disabled value={typeofTruck} />
                  {/* </Form.Item> */}
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Delivery Number
                </h3>
                <div>
                  <Form.Item
                    name="deliveryNumber"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input
                      onKeyUp={(e) => {
                        var start = e.target.selectionStart;
                        var end = e.target.selectionEnd;
                        e.target.value = e.target.value.toUpperCase();
                        e.target.setSelectionRange(start, end);
                      }}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Quantity (M/T)
                </h3>
                <div>
                  <Form.Item
                    name="quantityInMetricTons"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input
                      onKeyPress={(event) => {
                        if (
                          numInput(event) &&
                          !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
                          event.key !== "Backspace"
                        ) {
                          event.preventDefault();
                        }
                      }}
                      maxLength={10}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}}>Rate</h3>
                <div>
                  <Form.Item
                    name="rate"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input
                      onKeyPress={(event) => {
                        if (
                          numInput(event) &&
                          !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
                          event.key !== "Backspace"
                        ) {
                          event.preventDefault();
                        }
                      }}
                      maxLength={10}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}}>Invoice Date</h3>
                <div>
                  <Form.Item
                    name="invoiceDate"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <DatePicker format={dateFormatList} />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Commission %
                </h3>
                <div>
                  <Form.Item
                    name="commissionValue"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input
                      onKeyPress={(event) => {
                        if (
                          numInput(event) &&
                          !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
                          event.key !== "Backspace"
                        ) {
                          event.preventDefault();
                        }
                      }}
                      maxLength={10}
                    />
                  </Form.Item>
                </div>
              </Col>
            </Row>
            <Row
              style={{
                border: "0.5px dashed #979797",
                opacity: "0.40",
                margin: "20px 0 20px 0px",
              }}
            />
            <h2 style={{margin: "25px 30px 15px 30px", fontSize: "16px"}}>
              Load Trip Expenses Details
            </h2>
            <Row style={{margin: "10px 22px", lineHeight: "30px"}}>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Diesel
                </h3>
                <div>
                  <Form.Item
                    name="diesel"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input
                      onKeyPress={(event) => {
                        if (
                          numInput(event) &&
                          !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
                          event.key !== "Backspace"
                        ) {
                          event.preventDefault();
                        }
                      }}
                      maxLength={10}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Cash
                </h3>
                <div>
                  <Form.Item
                    name="cash"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input
                      onKeyPress={(event) => {
                        if (
                          numInput(event) &&
                          !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
                          event.key !== "Backspace"
                        ) {
                          event.preventDefault();
                        }
                      }}
                      maxLength={10}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Bank Transfer
                </h3>
                <div>
                  <Form.Item
                    name="bankTransfer"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input
                      onKeyPress={(event) => {
                        if (
                          numInput(event) &&
                          !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
                          event.key !== "Backspace"
                        ) {
                          event.preventDefault();
                        }
                      }}
                      maxLength={10}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                <h3 style={{marginLeft: "5px"}} className="require">
                  Shortage
                </h3>
                <div>
                  <Form.Item
                    name="shortage"
                    rules={[
                      {
                        required: true,
                        message: "This feild is required",
                      },
                    ]}
                  >
                    <Input
                      onKeyPress={(event) => {
                        if (
                          numInput(event) &&
                          !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
                          event.key !== "Backspace"
                        ) {
                          event.preventDefault();
                        }
                      }}
                      maxLength={10}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col
                xs={24}
                sm={24}
                md={8}
                lg={8}
                xl={8}
                style={{borderLeft: "1px solid #dfdfdf"}}
              >
                <h3 style={{paddingLeft: "4px"}}>Invoice</h3>
                <Form.Item>
                  <Dragger
                    name="file"
                    customRequest={this.axiosFileUploadRequestInvoice}
                    fileList={fileList}
                    accept=".pdf, .jpeg, .jpg, .png"
                    style={{width: "91%", height: "102px"}}
                  >
                    <PaperClipOutlined style={{fontSize: "20px"}} />
                    <a style={addFile}>Add File </a>
                    <span style={{color: "#a3a2a2"}}>or Drop files here</span>
                    <p style={{fontSize: "10px", color: "#858585"}}>
                      Only .PDF, .JPG and .PNG files allowed to upload
                    </p>
                  </Dragger>
                </Form.Item>
              </Col>
            </Row>

            <Row
              style={{
                border: "0.5px dashed #979797",
                opacity: "0.40",
                margin: "20px 0 20px 0px",
              }}
            />
            <Row>
              <Col lg={17}></Col>
              <Col lg={7}>
                <div
                  style={{padding: "10px", marginRight: "20px", float: "right"}}
                >
                  <Button className="savebtn" type="primary" htmlType="submit">
                    SAVE
                  </Button>
                </div>
                <div style={{padding: "10px", float: "right"}}>
                  <Button
                    type="primary"
                    className="cancelBtn"
                    onClick={() => {
                      document
                        .getElementsByClassName("ant-modal-close")[0]
                        .click();
                      this.onReset();
                    }}
                  >
                    CANCEL
                  </Button>
                </div>
              </Col>
            </Row>
          </Form>
        </Spin>
      </div>
    );
  }
}

export default vehicleinformation;
