import React, {Component} from "react";
import {Row, Col, Menu} from "antd";
import auth from "../../utils/auth";
import "./settingbreedcrumb.css";
class SettingBreedCrumb extends Component {
  constructor(props, a) {
    super(props);
    const {userData} = this.props;
    this.state = {
      current: null,
      userdetails: userData,
    };
  }

  handleConsigmentClick = (e) => {
    this.setState({
      current: e.key,
      activeItem: 1,
      activeState: false,
    });
  };

  youraccount = () => {
    this.props.history.push("/your-account");
  };

  useronboarding = () => {
    this.props.history.push("/user-onboarding");
  };

  render() {
    const url = this.props.history.location.pathname;
    const {userdetails} = this.state;
    return (
      <div>
        <Row className="consignmentList">
          <Col span={20}>
            <Menu
              onClick={this.handleConsigmentClick}
              selectedKeys={[this.state.current]}
              mode="horizontal"
            >
              <Menu.Item
                key="ownermaster"
                className={
                  url === "/your-account"
                    ? "consigmentActiveItem"
                    : "consigmentItem"
                }
                onClick={this.youraccount}
              >
                Your Account
              </Menu.Item>
              {userdetails && userdetails.roleName === "Admin" ? (
                <Menu.Item
                  key="truckmaster"
                  className={
                    url === "/user-onboarding"
                      ? "consigmentActiveItem"
                      : "consigmentItem"
                  }
                  onClick={this.useronboarding}
                >
                  User Onboarding
                </Menu.Item>
              ) : (
                ""
              )}
            </Menu>
          </Col>
        </Row>
      </div>
    );
  }
}

export default SettingBreedCrumb;
