import React, {Component} from "react";
import {Button, message} from "antd";
import jsPDF from "jspdf";
import "jspdf-autotable";
import Print from "../../../assests/images/print.svg";

class printvoucherdetails extends Component {
  constructor(props) {
    super(props);
  }
  handlePrint = (e) => {
    var totalPagesExp = "{total_pages_count_string}";
    try {
      const doc = new jsPDF("l", "mm", "a4");
      const items = [];
      const d = new Date();
      const m = d.getMonth() + 1;
      const day = d.getDate();
      const year = d.getFullYear();
      this.props.Data.map((d, index) => {
        items.push([
          index + 1,
          d.voucherNumber || "-",
          d.voucherDate || "-",
          d.ownerName || "-",
          d.vehicleNumber || "-",
          d.materialType || "-",
          d.narration || "-",
          d.modeOfPayment || "-",
          d.amount || "-",
        ]);
      });
      if (items.length === 0) {
        message.error("Please select voucher to download");
      } else {
        doc.autoTable({
          head: [
            [
              "Sl No",
              "Voucher Number",
              "Voucher Date",
              "Owner Name",
              "Truck Number",
              "Material Type",
              "Narration",
              "Mode Of Payment",
              "Amount",
            ],
          ],
          body: items,
          startY: 10,
          headStyles: {fontSize: 8, fontStyle: "normal", fillColor: "#44495b"},
          bodyStyles: {fontSize: 8, textAlign: "center"},
          didDrawPage: function (data) {
            // Header
            doc.setFontSize(10);
            doc.text("Voucher Details", data.settings.margin.left + 0, 5);
            doc.text("Date:-", data.settings.margin.left + 155, 5);
            doc.text(
              day + "/" + m + "/" + year,
              data.settings.margin.left + 170,
              5
            );

            // Footer
            var str = "Page " + doc.internal.getNumberOfPages();
            // Total page number plugin only available in jspdf v1.0+
            if (typeof doc.putTotalPages === "function") {
              str = str + " of " + totalPagesExp;
            }
            doc.setFontSize(10);

            // jsPDF 1.4+ uses getWidth, <1.4 uses .width
            var pageSize = doc.internal.pageSize;
            var pageHeight = pageSize.height
              ? pageSize.height
              : pageSize.getHeight();
            doc.text(str, data.settings.margin.left, pageHeight - 10);
          },
          margin: {top: 10},
        });
        if (typeof doc.putTotalPages === "function") {
          doc.putTotalPages(totalPagesExp);
        }
        doc.save("challanprint.pdf");
      }
    } catch (err) {
      message.error("Unable to Print");
    }
  };
  render() {
    return (
      <div>
        <Button
          className="button"
          onClick={this.handlePrint}
          icon={<img src={Print} style={{paddingRight: "10px"}} />}
        >
          PRINT
        </Button>
      </div>
    );
  }
}

export default printvoucherdetails;
