import React, {Component} from "react";
import {Button, Spin, Row, Col, Modal, message} from "antd";
import {FormOutlined} from "@ant-design/icons";
import HeaderBreedCrumb from "../../../../components/settingbreedcrumb/settingbreedcrumb";
import CurrentTime from "../../../../components/currenttime/currenttime";
import ViewSettings from "../viewaccout";
import EditSettings from "../editaccount";
import Editicon from "../../../../assests/images/edit.svg";
import ChangePassword from "../changepassword";
import axios from "axios";
import auth from "../../../../utils/auth";
import {BASE_URL} from "../../../../constants/constants";
import "./account.css";
class account extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const Det = auth.getUserData();
    const {_id} = Details;
    this.state = {
      visible: false,
      loading: false,
      viewVisible: true,
      userDetails: Details,
      userId: _id,
      userData: "",
      logId: Det._id,
    };
  }

  editinfo = () => {
    this.setState({
      viewVisible: false,
    });
  };
  canceleditinfo = () => {
    this.setState({
      viewVisible: true,
    });
  };
  showModal = () => {
    this.setState({
      visible: true,
    });
  };
  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };
  handleChangePassword = () => {
    this.setState({
      visible: false,
    });
  };
  gettableData = async () => {
    try {
      const {userId} = this.state;
      this.setState({loading: true});
      const response = await axios.get(`${BASE_URL}users/${userId}`);
      const userData = response.data.user || "";
      if (userData) {
        this.setState({
          userData: userData,
        });
      } else {
        this.setState({
          useData: "",
        });
      }
      this.setState({loading: false});
    } catch (err) {
      this.setState({loading: false});
      message.error("Error fetching data. Please try again later", 2);
    }
  };

  doSomethingBeforeUnload = async () => {
    await axios.put(`${BASE_URL}logout/${this.state.logId}`);
  };
  setupBeforeUnloadListener = async () => {
    console.log(this.state.userDetails, "confiff");
    window.addEventListener("beforeunload", async (ev) => {
      ev.preventDefault();
      return this.doSomethingBeforeUnload();
    });
  };

  componentDidMount() {
    this.gettableData();
    this.setupBeforeUnloadListener();
  }

  render() {
    const {viewVisible} = this.state;
    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Account and Settings
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Change Your Settings
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <HeaderBreedCrumb
            path="home"
            history={this.props.history}
            userData={this.state.userDetails}
          />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <div className="settings">
            <Modal
              title={
                <div>
                  <h3 style={{fontSize: "20px", margin: "0px"}}>
                    Change Password
                  </h3>
                  <p
                    style={{
                      fontSize: "12px",
                      fontFamily: "AvenirMedium",
                    }}
                  >
                    Enter User Details
                  </p>
                </div>
              }
              className="ModalLook"
              visible={this.state.visible}
              onOk={this.handleOk}
              destroyOnClose={true}
              onCancel={this.handleCancel}
              footer={null}
            >
              <div>
                <ChangePassword
                  parentFn={() => this.handleChangePassword()}
                  Data={this.state.userDetails}
                />
              </div>
            </Modal>
            <Spin spinning={this.state.loading}>
              {viewVisible === true ? (
                <div>
                  <Row>
                    <Col span={23}>
                      <h1>SETTINGS</h1>
                    </Col>
                    <Col
                      span={1}
                      onClick={() => this.editinfo()}
                      style={{textAlign: "right"}}
                    >
                      <img
                        src={Editicon}
                        style={{cursor: "pointer"}}
                        width="20px"
                      />
                    </Col>
                  </Row>
                  <Row
                    style={{
                      border: "0.5px dashed #979797",
                      opacity: "0.40",
                      margin: "20px 0 20px 0px",
                    }}
                  />
                  <ViewSettings />
                  <Row>
                    <Col span={24}>
                      <a onClick={this.showModal} className="resetPassword">
                        Reset Password
                      </a>
                    </Col>
                  </Row>
                </div>
              ) : (
                <div>
                  <Row>
                    <Col span={24}>
                      <h1>Edit Details</h1>
                    </Col>
                  </Row>
                  <Row
                    style={{
                      border: "0.5px dashed #979797",
                      opacity: "0.40",
                      margin: "20px 0 20px 0px",
                    }}
                  />
                  <EditSettings
                    parentFn={() => this.canceleditinfo()}
                    Data={this.state.userData}
                  />
                </div>
              )}
            </Spin>
          </div>
        </div>
      </div>
    );
  }
}

export default account;
