/* eslint-disable no-unused-vars */
/* eslint-disable no-const-assign */
/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from "react";
import {
  Table,
  Input,
  Button,
  Modal,
  Popconfirm,
  Checkbox,
  message,
  Spin,
  Row,
  Select,
  DatePicker,
  Col,
  Form,
  InputNumber,
} from "antd";
import moment from "moment";
import {RightOutlined, DownOutlined} from "@ant-design/icons";
import axios from "axios";
import {BASE_URL} from "../../../constants/constants";
import Editicon from "../../../assests/images/edit.svg";
import EditTableData from "./editabletable";
import PdfViewer from "../../../components/pdfviewer/pdfviewer";
import dateicon from "../../../assests/images/dateicon.svg";
import HeaderBreedCrumb from "../../../components/receivemenubreedcrumb/receivemenubreedcrumb";
import CurrentTime from "../../../components/currenttime/currenttime";
import auth from "../../../utils/auth";
import "./receiveregister";

const userDetails = auth.getAuthToken().userDetails;
const {Search} = Input;
const {RangePicker} = DatePicker;
const NullData = (tableText) => <span>{tableText || <p>&#8212;</p>}</span>;
const imageStyle = {
  height: "auto",
  maxWidth: "480px",
  minWidth: "200px",
  paddingBottom: 25,
};
const SELECTED_THRESHOLD = 0;
const iconStyle = {
  color: "#01a6d0",
  fontSize: "14px",
  width: "24px",
  height: "24px",
};
const dateFormatList = ["DD/MM/YYYY"];

const currentDate = moment(new Date(), dateFormatList[1]);

let timeout = null;
const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {},
  columnWidth: "10px",
  getCheckboxProps: (record) => ({
    disabled: record.name === "Disabled User",
    // Column configuration not to be checked
    name: record.name,
  }),
};

const EditableContext = React.createContext();

const EditableRow = ({index, ...props}) => {
  const [form] = Form.useForm();
  return (
    <Form form={form} component={false}>
      <EditableContext.Provider value={form}>
        <tr {...props} />
      </EditableContext.Provider>
    </Form>
  );
};
const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === "number" ? <InputNumber /> : <Input />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Please Input ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

class ReceiveReigsterDetails extends Component {
  state = {
    vidible: false,
    loading: false,
    doucmentvisible: false,
    editableVehicleDetails: null,
    fileurl: "",
    searchValue: "",
    searchResultArray: "",
    selectionType: Checkbox,
    dataSource: [],
    fileType: "",
    previewUrl: "",
    materialSelect: [],
    truckTypeStatus: [],
    startDate: "",
    endDate: "",
    editingKey: "",
    data: [],
  };
  customExpandIcon(props) {
    if (props.expanded) {
      return (
        <a
          style={{color: "black"}}
          onClick={(e) => {
            props.onExpand(props.record, e);
          }}
        >
          <DownOutlined style={iconStyle} className="customIcon" />
        </a>
      );
    }
    return (
      <a
        style={{color: "black"}}
        onClick={(e) => {
          props.onExpand(props.record, e);
        }}
      >
        <RightOutlined style={iconStyle} className="customIcon" />
      </a>
    );
  }
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancelEdit = () => {
    this.setState({
      editvisible: false,
    });
  };

  gettableData = async () => {
    try {
      this.setState({loading: true});
      const response = await axios.get(`${BASE_URL}get-receive-register`);
      const dispatchData = response.data.disptachData || "";
      if (dispatchData && dispatchData.length > 0) {
        this.setState({
          data: dispatchData.map((data, index) => {
            const date = data.grDate;
            var GrDate = moment(date, "DD-MM-YYYY");
            if (data.invoiceDate) {
              const date = data.invoiceDate;
              var InvoiceDate = moment(date, "DD-MM-YYYY");
            }
            return {
              ...data,
              slno: index + 1,
              commissionPercent: "@" + data.commisionRate + "%",
              grDateMom: GrDate,
              invoiceDateMoM: InvoiceDate,
            };
          }),
        });
      }
      this.setState({loading: false});
    } catch (err) {
      this.setState({loading: false});
      message.error("Error fetching data. Please try again later", 2);
    }
  };

  componentDidMount() {
    this.gettableData();
  }

  showmodals(e) {
    this.setState({
      editvisible: true,
      editableVehicleDetails: e,
    });
  }

  deleterecord(e) {}

  searchResult = (e) => {
    try {
      clearTimeout(timeout);
      let searchValue = e.target.value;
      timeout = setTimeout(() => {
        this.setState({searchValue});
        if (
          searchValue !== "" &&
          searchValue !== null &&
          searchValue !== undefined
        ) {
          searchValue = searchValue.toString().toUpperCase().trim();

          const newSearchedTableData = this.state.data.filter(
            ({vehicleNumber, deliveryNumber}) => {
              return (
                this.searchFn(searchValue, deliveryNumber) ||
                this.searchFn(searchValue, vehicleNumber)
              );
            }
          );
          this.setState({searchResultArray: newSearchedTableData});
        } else {
          this.setState({
            searchResultArray: [],
            printableArray: [],
            selectedRowKeys: [],
          });
        }
      }, 500);
    } catch (err) {
      console.error({err});
    }
  };

  searchFn = (search, value) => {
    if (value && search) {
      return value.toString().toUpperCase().includes(search);
    } else {
      return false;
    }
  };

  reloadAfterModalClose = () => {
    this.gettableData();
    this.setState({
      visible: false,
      editvisible: false,
      doucmentvisible: false,
    });
  };
  handleMaterialType = async (materialValue) => {
    // await this.getTableData();
    const obj = materialValue.toString();
    const matValue =
      Object.keys(materialValue).length > 0 ? obj.split(",") : [];
    this.setState({materialSelect: matValue});
    const {dataSource} = this.state;
    materialValue = materialValue.toString().toUpperCase().trim();
    if (matValue.length > 0 && dataSource.length > 0) {
      const filteredTableData = dataSource.filter(({materialType}) =>
        matValue.includes(materialType)
      );
      this.setState({dataSource: filteredTableData});
    }
  };
  handleTruckType = (truckValue) => {
    // await this.getTableData();
    const obj = truckValue.toString();
    const vehicleValue =
      Object.keys(truckValue).length > 0 ? obj.split(",") : [];
    this.setState({truckTypeStatus: vehicleValue});
    const {dataSource} = this.state;
    truckValue = vehicleValue.toString().toUpperCase().trim();
    if (vehicleValue.length > 0 && dataSource.length > 0) {
      const filteredTableData = dataSource.filter(({vehicleType}) =>
        vehicleValue.includes(vehicleType)
      );
      this.setState({dataSource: filteredTableData});
    }
  };

  onDateChange = async (dates) => {
    await this.gettableData();
    const {data} = this.state;
    const filterDateFormat = "YYYY-MM-DD";
    if (dates) {
      try {
        const [a, b] = dates;
        var st = (a && moment(a || "").format(filterDateFormat)) || "";
        var et = (b && moment(b || "").format(filterDateFormat)) || "";
        const filteredData = data.filter((dispatchData) => {
          const {grISODate} = dispatchData;
          var dt = moment(new Date(grISODate)).format(filterDateFormat);
          if (st && et) {
            return (
              moment(dt).isSameOrAfter(st, "day") &&
              moment(dt).isSameOrBefore(et, "day")
            );
          }
          if (st) {
            return moment(dt).isSameOrAfter(st, "day");
          }
          if (et) {
            return moment(dt).isSameOrBefore(et, "day");
          }
        });
        await this.setState({
          dataSource: filteredData,
          searchResultArray: filteredData,
          searchValue: st || et,
        });
      } catch (error) {
        console.log({error});
        message.error("Unable to fetch data");
      }
    } else {
      await this.setState({data: data, searchValue: ""});
    }
  };

  editModal = () => {
    this.setState({
      EditReciveregisterVisible: false,
    });
  };

  isEditing = (record) => record.key === this.state.editingKey;
  render() {
    const NullData = (tableText) => <span>{tableText || <p>&#8212;</p>}</span>;
    const columns = [
      {
        title: "Sl No",
        dataIndex: "slno",
        key: "name",
        render: NullData,
        fixed: "left",
      },
      {
        title: "GR No",
        dataIndex: "grNumber",
        key: "grNumber",
        render: NullData,
        editable: true,
      },
      {
        title: "GR Date",
        dataIndex: "grDate",
        key: "grDate",
        render: NullData,
        editable: true,
      },
      {
        title: "Truck No",
        dataIndex: "vehicleNumber",
        key: "vehicleNumber",
        editable: true,
        sorter: (a, b) =>
          `${a.registrationNumber}`.localeCompare(b.registrationNumber),
        render: (text) => (
          <span style={{textTransform: "uppercase"}}>{text}</span>
        ),
      },
      {
        title: "Invoice Date",
        dataIndex: "invoiceDate",
        key: "invoiceDate",
        render: NullData,
        editable: true,
      },
      {
        title: "From",
        dataIndex: "loadLocation",
        key: "loadLocation",
        render: NullData,
        editable: true,
      },
      {
        title: "Destination",
        dataIndex: "deliveryLocation",
        key: "deliveryLocation",
        render: NullData,
        editable: true,
      },
      {
        title: "Delivery No",
        dataIndex: "deliveryNumber",
        key: "deliveyNumber",
        render: NullData,
        editable: true,
      },
      {
        title: "Qty (MT)",
        dataIndex: "quantityInMetricTons",
        key: "qunantityInMetricTons",
        render: NullData,
        editable: true,
      },
      {
        title: "Rate",
        dataIndex: "rate",
        key: "rate",
        render: NullData,
        editable: true,
      },
      {
        title: "Total",
        dataIndex: "total",
        key: "total",
        render: NullData,
        editable: true,
      },
      {
        title: "Commission Rate",
        dataIndex: "commisionRate",
        key: "commissionRate",
        render: NullData,
        editable: true,
      },
      {
        title: "Commission Total",
        dataIndex: "commisionTotal",
        key: "commissionTotal",
        render: NullData,
        editable: true,
      },
      {
        title: "Diesel",
        dataIndex: "diesel",
        key: "diesel",
        render: NullData,
        editable: true,
      },
      {
        title: "Cash",
        dataIndex: "cash",
        key: "cash",
        render: NullData,
        editable: true,
      },
      {
        title: "Bank Transfer",
        dataIndex: "bankTransfer",
        key: "bankTransfer",
        render: NullData,
        editable: true,
      },
      {
        title: "Shortage",
        dataIndex: "shortage",
        key: "shortage",
        render: NullData,
        editable: true,
      },
      {
        title: "Balance",
        dataIndex: "balance",
        key: "balance",
        render: NullData,
        editable: true,
      },
      {
        title: "Action",
        dataIndex: "operation",
        fixed: "right",
        width: "140px",
        render: (_, record) => {
          const editable = this.isEditing(record);
          return editable ? (
            <div style={{display: "flex"}}>
              <div style={{margin: "5px"}}>
                <Button
                  type="primary"
                  className="savebtn"
                  onClick={() => {
                    this.post(record);
                  }}
                  style={{
                    fontSize: "10px",
                    height: "20px",
                    padding: "3px",
                    width: "50px",
                  }}
                >
                  POST
                </Button>
              </div>
              <div style={{margin: "5px"}}>
                <Button
                  type="primary"
                  className="cancelBtn"
                  onClick={() => {
                    this.cancel(record);
                  }}
                  style={{fontSize: "10px", height: "20px", padding: "3px"}}
                >
                  CANCEL
                </Button>
              </div>
            </div>
          ) : (
            <div style={{display: "flex"}}>
              <div style={{margin: "5px"}}>
                <img
                  src={Editicon}
                  style={{cursor: "pointer"}}
                  disabled={this.state.editingKey !== ""}
                  onClick={() => this.edit(record)}
                  width="20px"
                />
              </div>
              <div style={{margin: "5px"}}>
                <Button
                  type="primary"
                  className="cancelBtn"
                  onClick={() => {
                    this.post(record);
                  }}
                  style={{fontSize: "10px", height: "20px", padding: "3px"}}
                >
                  POST
                </Button>
              </div>
            </div>
          );
        },
      },
    ];
    const mergedColumns = columns.map((col) => {
      if (!col.editable) {
        return col;
      }

      return {
        ...col,
        onCell: (record) => ({
          record,
          inputType: col.dataIndex === "age" ? "number" : "text",
          dataIndex: col.dataIndex,
          title: col.title,
          editing: this.isEditing(record),
        }),
      };
    });

    const {
      fileType,
      previewUrl,
      data,
      searchResultArray,
      searchValue,
    } = this.state;
    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Receive Register
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Review/Update Register
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <HeaderBreedCrumb path="home" history={this.props.history} />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <Spin spinning={this.state.loading} delay={500}>
            <Modal
              visible={this.state.doucmentvisible}
              title={
                <div>
                  View Document
                  <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
                    RC Book View{" "}
                  </p>
                </div>
              }
              onCancel={() => this.setState({doucmentvisible: false})}
              className={`ant${fileType === "pdf" ? "PdfModal" : "ImageModal"}`}
              style={{width: "900px"}}
              okButtonProps={{style: {display: "none"}}}
            >
              {fileType === "pdf" && <PdfViewer url={previewUrl || ""} />}
              {fileType !== "pdf" && (
                <img src={previewUrl} alt="img" style={imageStyle} />
              )}
            </Modal>
            <div className="recive">
              <div style={{padding: "0px 0px 0px 20px"}}>
                <Row>
                  <Col
                    xs={12}
                    sm={12}
                    md={5}
                    lg={5}
                    xl={5}
                    style={{padding: "10px"}}
                  >
                    <h3 className="filterLabel" style={{borderRadius: "10px"}}>
                      Sort by Date Range
                    </h3>
                    <div style={{display: "flex"}}>
                      <RangePicker
                        format={dateFormatList}
                        onChange={(dates) => this.onDateChange(dates)}
                        allowEmpty={[true, true]}
                        suffixIcon={<img src={dateicon} alt="dateIcon" />}
                      />
                    </div>
                  </Col>
                  <Col
                    xs={12}
                    sm={12}
                    md={5}
                    lg={5}
                    xl={5}
                    style={{padding: "10px"}}
                  >
                    <h3 className="filterLabel">
                      Search By Delivery No or Truck No
                    </h3>
                    <div>
                      <Search
                        className="searchbtn"
                        onChange={this.searchResult}
                        style={{height: "30px !important"}}
                      />
                    </div>
                  </Col>
                </Row>
              </div>
              <div className="recieveregTable">
                <EditTableData
                  TableData={data}
                  DataValue={searchResultArray}
                  FindFilter={searchValue}
                />
              </div>
            </div>
          </Spin>
        </div>
      </div>
    );
  }
}

export default ReceiveReigsterDetails;
