/* eslint-disable no-unused-vars */
import React, {Component} from "react";
import {
  Input,
  Row,
  Col,
  Form,
  Select,
  Button,
  message,
  InputNumber,
  Spin,
} from "antd";
import {CloseCircleOutlined} from "@ant-design/icons";
import axios from "axios";
import {BASE_URL} from "../../../../constants/constants";
import "antd/dist/antd.css";
import "./vehicleOwnerDetail.css";

const {Option} = Select;

class AddvehicleiOwnerDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      visible: false,
      States: [],
      ownerId: "",
      phoneNumber: "",
      ownerCreated: false,
    };
  }
  formRef = React.createRef();

  onFinish = async (values) => {
    const {
      name,
      email,
      phoneNumber,
      panNumber,
      address,
      district,
      state,
      accountNumber,
      accountHolderName,
      ifscCode,
      bankName,
      branchName,
    } = values;
    if (!this.state.ownerCreated) {
      this.setState({
        ownerCreated: true,
      });
    } else {
      try {
        this.setState({loading: true});
        // const response = await axios.post(`${BASE_URL}create-owner`, {
        //   owner_details: {
        //     name: name,
        //     email: email,
        //     phoneNumber: phoneNumber,
        //     panNumber: panNumber,
        //     address: address,
        //     district: district,
        //     state: state,
        //   },

        // });
        // const ownerid = response.data.ownerId || "";
        // this.setState({
        //   ownerId: ownerid,
        //   phoneNumber: phoneNumber,
        // });
        // const BankDetails = [];
        // const bankDetails = values.bankDetails || null;
        // BankDetails.push({
        //   accountNumber: accountNumber,
        //   accountHolderName: accountHolderName,
        //   ifscCode: ifscCode,
        //   bankName: bankName,
        //   branchName: branchName,
        //   ownerId: this.state.ownerId,
        //   phoneNumber: this.state.phoneNumber,
        // });
        // if (bankDetails !== null) {
        //   for (const [index, value] of bankDetails.entries()) {
        //     BankDetails.push({
        //       accountNumber: value.accountNumber,
        //       accountHolderName: value.accountHolderName,
        //       ifscCode: value.ifscCode,
        //       bankName: value.bankName,
        //       branchName: value.branchName,
        //       ownerId: this.state.ownerId,
        //       phoneNumber: this.state.phoneNumber,
        //     });
        //   }
        // }
        const BankDetails = [];
        const bankDetails = values.bankDetails || null;
        BankDetails.push({
          accountNumber: accountNumber,
          accountHolderName: accountHolderName,
          ifscCode: ifscCode,
          bankName: bankName,
          branchName: branchName,
        });
        if (bankDetails !== null) {
          for (const [index, value] of bankDetails.entries()) {
            BankDetails.push({
              accountNumber: value.accountNumber,
              accountHolderName: value.accountHolderName,
              ifscCode: value.ifscCode,
              bankName: value.bankName,
              branchName: value.branchName,
            });
          }
        }
        // const res2 = await axios.post(
        //   `${BASE_URL}create-accounts-owner/${this.state.ownerId}`,
        //   BankDetails
        // );
        const response = await axios
          .post(`${BASE_URL}create-owner`, {
            owner_details: {
              name: name,
              email: email,
              phoneNumber: phoneNumber,
              panNumber: panNumber,
              address: address,
              district: district,
              state: state,
            },
            bank_details: BankDetails,
          })
          .then((data) => {
            message.success("Successfully added data");
            this.props.onUpdate();
            this.setState({loading: false});
            document.getElementsByClassName("ant-modal-close")[0].click();
          })
          .catch((error) => {
            this.setState({loading: false});
            const {response} = error;
            const {data} = response;
            const {message: msg} = data;
            message.error(msg);
          });
      } catch (err) {
        console.log(err);
        message.error("Owner already exist");
        this.setState({loading: false});
      }
    }
  };

  onReset = () => {
    this.formRef.current.resetFields();
  };

  getallStates = async (e) => {
    try {
      const response = await axios.get(`${BASE_URL}get-all-states`);
      const states = response.data.states || "";
      const allStates = states.map(({code, name}) => {
        return (
          <Option key={code} value={name}>
            {name}
          </Option>
        );
      });
      this.setState({
        States: allStates,
      });
    } catch (err) {
      console.log(err);
    }
  };

  componentDidMount() {
    this.getallStates();
  }

  render() {
    const {States} = this.state.States;
    return (
      <div>
        <Spin spinning={this.state.loading}>
          <Form
            ref={this.formRef}
            name="vehicleinfo"
            className="vehicleinfoForm"
            onFinish={this.onFinish}
            // autoComplete="off"
          >
            <div>
              <h2 style={{margin: "15px 30px"}}>Vehicle Owner Details</h2>
              <Row style={{margin: "10px 22px"}}>
                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 style={{marginLeft: "5px"}} className="require">
                    Owner Name
                  </h3>
                  <div>
                    <Form.Item
                      name="name"
                      rules={[{required: true, message: "required"}]}
                    >
                      <Input />
                    </Form.Item>
                  </div>
                </Col>

                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 style={{marginLeft: "5px"}}>Address</h3>
                  <div>
                    <Form.Item name="address">
                      <Input />
                    </Form.Item>
                  </div>
                </Col>

                <Col xs={12} sm={12} md={4} lg={4} xl={4}>
                  <h3 style={{marginLeft: "5px"}}>District</h3>
                  <div>
                    <Form.Item name="district">
                      <Input />
                    </Form.Item>
                  </div>
                </Col>
                <Col xs={12} sm={12} md={4} lg={4} xl={4}>
                  <h3 style={{marginLeft: "5px"}}>State</h3>
                  <div>
                    <Form.Item name="state">
                      <Select
                        showSearch
                        allowClear
                        placeholder="Select States"
                        optionFilterProp="children"
                        filterOption={(input, option) =>
                          option.children
                            .toLowerCase()
                            .indexOf(input.toLowerCase()) >= 0
                        }
                      >
                        {this.state.States}
                      </Select>
                    </Form.Item>
                  </div>
                </Col>

                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 style={{marginLeft: "5px"}}>PAN Card Number</h3>
                  <div>
                    <Form.Item name="panNumber">
                      <Input
                        onKeyUp={(e) => {
                          var start = e.target.selectionStart;
                          var end = e.target.selectionEnd;
                          e.target.value = e.target.value.toUpperCase();
                          e.target.setSelectionRange(start, end);
                        }}
                      />
                    </Form.Item>
                  </div>
                </Col>
                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 style={{marginLeft: "5px"}} className="require">
                    Mobile Number
                  </h3>
                  <div>
                    <Form.Item
                      name="phoneNumber"
                      validateStatus="error"
                      rules={[
                        {
                          required: true,
                          message: "invalid mobile number",
                        },
                        {min: 10, message: "Mobile No not less than 10 digit"},
                      ]}
                    >
                      <Input
                        onKeyPress={(event) => {
                          if (
                            !/^[0-9]+$/.test(event.key) &&
                            event.key !== "Backspace"
                          ) {
                            event.preventDefault();
                          }
                        }}
                        maxLength="10"
                      />
                    </Form.Item>
                  </div>
                </Col>

                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 style={{marginLeft: "5px"}}>Email ID</h3>
                  <div>
                    <Form.Item name="email">
                      <Input />
                    </Form.Item>
                  </div>
                </Col>
              </Row>
            </div>
            {this.state.ownerCreated && (
              <div>
                <Row
                  style={{
                    border: "0.5px dashed #979797",
                    opacity: "0.40",
                    margin: "20px 0 20px 0px",
                  }}
                />
                <h2 style={{margin: "15px 30px"}}>Bank Details</h2>
                <Row style={{margin: "10px 22px"}}>
                  <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                    <h3 style={{marginLeft: "5px"}} className="require">
                      Account Holders Name
                    </h3>
                    <div>
                      <Form.Item
                        name="accountHolderName"
                        rules={[{required: true, message: "required"}]}
                      >
                        <Input />
                      </Form.Item>
                    </div>
                  </Col>
                  <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                    <h3 style={{marginLeft: "5px"}} className="require">
                      BANK Name
                    </h3>
                    <div>
                      <Form.Item
                        name="bankName"
                        rules={[{required: true, message: "required"}]}
                      >
                        <Input />
                      </Form.Item>
                    </div>
                  </Col>
                  <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                    <h3 style={{marginLeft: "5px"}} className="require">
                      Account Number
                    </h3>
                    <div>
                      <Form.Item
                        name="accountNumber"
                        rules={[{required: true, message: "required"}]}
                      >
                        <Input
                          onKeyPress={(event) => {
                            if (
                              !/^[0-9]+$/.test(event.key) &&
                              event.key !== "Backspace"
                            ) {
                              event.preventDefault();
                            }
                          }}
                        />
                      </Form.Item>
                    </div>
                  </Col>
                  <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                    <h3 style={{marginLeft: "5px"}} className="require">
                      BANK Branch
                    </h3>
                    <div>
                      <Form.Item
                        name="branchName"
                        rules={[{required: true, message: "required"}]}
                      >
                        <Input />
                      </Form.Item>
                    </div>
                  </Col>
                  <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                    <h3 style={{marginLeft: "5px"}} className="require">
                      IFSC Code
                    </h3>
                    <div>
                      <Form.Item
                        name="ifscCode"
                        rules={[{required: true, message: "required"}]}
                      >
                        <Input
                          onKeyUp={(e) => {
                            var start = e.target.selectionStart;
                            var end = e.target.selectionEnd;
                            e.target.value = e.target.value.toUpperCase();
                            e.target.setSelectionRange(start, end);
                          }}
                        />
                      </Form.Item>
                    </div>
                  </Col>
                </Row>
                <Row
                  style={{
                    border: "0.5px dashed #979797",
                    opacity: "0.40",
                    margin: "20px 0 20px 0px",
                  }}
                />
                <Form.List name="bankDetails">
                  {(bankDetails, {add, remove, i}) => {
                    return (
                      <>
                        {bankDetails.map((field, index) => (
                          <div key={field.key}>
                            <Row style={{margin: "10px 22px"}}>
                              <Col xs={24} sm={24} md={21} lg={21} xl={21}>
                                <Row>
                                  <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                                    <h3
                                      style={{marginLeft: "5px"}}
                                      className="require"
                                    >
                                      Account Holders Name
                                    </h3>
                                    <div>
                                      <Form.Item
                                        name={[index, "accountHolderName"]}
                                        rules={[
                                          {
                                            required: true,
                                            message: "required",
                                          },
                                        ]}
                                      >
                                        <Input />
                                      </Form.Item>
                                    </div>
                                  </Col>
                                  <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                                    <h3
                                      style={{marginLeft: "5px"}}
                                      className="require"
                                    >
                                      Bank Name
                                    </h3>
                                    <div>
                                      <Form.Item
                                        name={[index, "bankName"]}
                                        rules={[
                                          {
                                            required: true,
                                            message: "required",
                                          },
                                        ]}
                                      >
                                        <Input />
                                      </Form.Item>
                                    </div>
                                  </Col>
                                  <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                                    <h3
                                      style={{marginLeft: "5px"}}
                                      className="require"
                                    >
                                      Account Number
                                    </h3>
                                    <div>
                                      <Form.Item
                                        name={[index, "accountNumber"]}
                                        rules={[
                                          {
                                            required: true,
                                            message: "required",
                                          },
                                        ]}
                                      >
                                        <Input />
                                      </Form.Item>
                                    </div>
                                  </Col>
                                  <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                                    <h3
                                      style={{marginLeft: "5px"}}
                                      className="require"
                                    >
                                      Bank Branch
                                    </h3>
                                    <div>
                                      <Form.Item
                                        name={[index, "branchName"]}
                                        rules={[
                                          {
                                            required: true,
                                            message: "required",
                                          },
                                        ]}
                                      >
                                        <Input />
                                      </Form.Item>
                                    </div>
                                  </Col>
                                  <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                                    <h3
                                      style={{marginLeft: "5px"}}
                                      className="require"
                                    >
                                      IFSC Code
                                    </h3>
                                    <div>
                                      <Form.Item
                                        name={[index, "ifscCode"]}
                                        rules={[
                                          {
                                            required: true,
                                            message: "required",
                                          },
                                        ]}
                                      >
                                        <Input />
                                      </Form.Item>
                                    </div>
                                  </Col>
                                </Row>
                              </Col>
                              <Col xs={24} sm={24} md={3} lg={3} xl={3}>
                                {i !== 0 && (
                                  <CloseCircleOutlined
                                    onClick={() => remove(index)}
                                    style={{
                                      fontSize: "28px",
                                      color: "#01a6d0",
                                    }}
                                  />
                                )}
                              </Col>
                            </Row>
                            <Row
                              style={{
                                border: "0.5px dashed #979797",
                                opacity: "0.40",
                                margin: "20px 0 20px 0px",
                              }}
                            />
                          </div>
                        ))}
                        <Row style={{margin: "10px 22px"}}>
                          <Col xs={12} sm={12} md={8} lg={7} xl={7}>
                            <Button
                              type="primary"
                              className="addbankBtn"
                              onClick={() => add()}
                            >
                              + Add Bank
                            </Button>
                          </Col>
                        </Row>
                      </>
                    );
                  }}
                </Form.List>
              </div>
            )}
            <Row>
              <Col lg={24}>
                <div
                  style={{
                    display: "flex",
                    float: "right",
                    marginBottom: "10px",
                  }}
                >
                  <div style={{padding: "10px"}}>
                    <Button
                      type="primary"
                      className="cancelBtn"
                      onClick={() => {
                        document
                          .getElementsByClassName("ant-modal-close")[0]
                          .click();
                        this.onReset();
                      }}
                    >
                      CANCEL
                    </Button>
                  </div>
                  <div style={{padding: "10px", margin: "0px 16px 0px 10px"}}>
                    <Button className="save" type="primary" htmlType="submit">
                      SAVE
                    </Button>
                  </div>
                </div>
              </Col>
            </Row>
          </Form>
        </Spin>
      </div>
    );
  }
}

export default AddvehicleiOwnerDetail;
