/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {Component} from "react";
import {
  // Table,
  Input,
  Button,
  Form,
  // Modal,
  // Popconfirm,
  // Checkbox,
  message,
  Row,
  Col,
  Spin,
} from "antd";
import axios from "axios";
import HeaderBreedCrumb from "../../../components/menubreedcrumb/menubreedcrumb";
import CurrentTime from "../../../components/currenttime/currenttime";
import {BASE_URL} from "../../../constants/constants";
class MasterData extends Component {
  state = {
    loading: false,
  };
  formRef = React.createRef();
  formReflocation = React.createRef();
  formRefdelivery = React.createRef();
  onFinish = async (values) => {
    const materialType = values.materialType;
    try {
      const response = await axios.post(`${BASE_URL}create-material`, {
        materialType: materialType,
      });
      const mess = response.data.message || "";
      message.success(mess);
    } catch (err) {
      console.log(err);
      message.error("Already Exist");
    }
    this.formRef.current.resetFields();
  };
  onLoadLocation = async (values) => {
    const loadLocation = values.loadLocation;
    try {
      const response = await axios.post(`${BASE_URL}create-load-location`, {
        location: loadLocation,
      });
      const mess = response.data.message || "";
      message.success(mess);
    } catch (err) {
      console.log(err);
      message.error("Already Exist");
    }
    this.formReflocation.current.resetFields();
  };
  onDeliveryLocation = async (values) => {
    const deliveryLocation = values.deliveryLocation;
    try {
      const response = await axios.post(`${BASE_URL}create-delivery-location`, {
        location: deliveryLocation,
      });
      const mess = response.data.message || "";
      message.success(mess);
    } catch (err) {
      console.log(err);
      message.error("Already Exist");
    }
    this.formRefdelivery.current.resetFields();
  };
  render() {
    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Master Data
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Enter the Master Data for your Opertion
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>
        <div>
          <HeaderBreedCrumb path="home" history={this.props.history} />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <Spin spinning={this.state.loading} delay={500}>
            <div>
              <div style={{padding: "30px 10px 50px 30px"}}>
                <h2 style={{fontWeight: "bold", padding: "0 10px"}}>
                  Master Data
                </h2>
                <Form
                  ref={this.formRef}
                  name="material"
                  className="vehicleinfoForm"
                  onFinish={this.onFinish}
                  autoCompleter="off"
                >
                  <Row style={{marginTop: "20px"}}>
                    <Col xs={24} sm={24} md={6} lg={6} xl={6}>
                      <h3 style={{fontWeight: "bold", paddingLeft: "5px"}}>
                        Material Type
                      </h3>
                      <div>
                        <Form.Item
                          name="materialType"
                          rules={[
                            {
                              message: "This feild is required",
                            },
                          ]}
                        >
                          <Input />
                        </Form.Item>
                      </div>
                    </Col>
                    <Col
                      xs={24}
                      sm={24}
                      md={6}
                      lg={6}
                      xl={6}
                      style={{margin: "18px 0px 0px 10px"}}
                    >
                      <Button
                        className="savebtn"
                        type="primary"
                        htmlType="submit"
                      >
                        ADD
                      </Button>
                    </Col>
                  </Row>
                </Form>
                <Form
                  ref={this.formReflocation}
                  name="load"
                  className="vehicleinfoForm"
                  onFinish={this.onLoadLocation}
                  autoCompleter="off"
                >
                  <Row style={{marginTop: "10px"}}>
                    <Col xs={24} sm={24} md={6} lg={6} xl={6}>
                      <h3 style={{fontWeight: "bold", paddingLeft: "5px"}}>
                        Load Location
                      </h3>
                      <div>
                        <Form.Item
                          name="loadLocation"
                          rules={[
                            {
                              message: "This feild is required",
                            },
                          ]}
                        >
                          <Input />
                        </Form.Item>
                      </div>
                    </Col>
                    <Col
                      xs={24}
                      sm={24}
                      md={6}
                      lg={6}
                      xl={6}
                      style={{margin: "18px 0px 0px 10px"}}
                    >
                      <Button
                        className="savebtn"
                        type="primary"
                        htmlType="submit"
                      >
                        ADD
                      </Button>
                    </Col>
                  </Row>
                </Form>
                <Form
                  ref={this.formRefdelivery}
                  name="delivery"
                  className="vehicleinfoForm"
                  onFinish={this.onDeliveryLocation}
                  autoCompleter="off"
                >
                  <Row style={{marginTop: "10px"}}>
                    <Col xs={24} sm={24} md={6} lg={6} xl={6}>
                      <h3 style={{fontWeight: "bold", paddingLeft: "5px"}}>
                        Delivery Location
                      </h3>
                      <div>
                        <Form.Item
                          name="deliveryLocation"
                          rules={[
                            {
                              message: "This feild is required",
                            },
                          ]}
                        >
                          <Input />
                        </Form.Item>
                      </div>
                    </Col>
                    <Col
                      xs={24}
                      sm={24}
                      md={6}
                      lg={6}
                      xl={6}
                      style={{margin: "18px 0px 0px 10px"}}
                    >
                      <Button
                        className="savebtn"
                        type="primary"
                        htmlType="submit"
                      >
                        ADD
                      </Button>
                    </Col>
                  </Row>
                </Form>
              </div>
            </div>
          </Spin>
        </div>
      </div>
    );
  }
}

export default MasterData;
