import React, {Component} from "react";
import {Document, Page, pdfjs} from "react-pdf";
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const allowCorsTemporary = "https://cors-anywhere.herokuapp.com/";

const fileurl = "";

export default class pdfviewer extends Component {
  constructor(props) {
    super(props);
    const {url} = this.props;
    this.state = {
      numPages: null,
      fileObject: {url: `${allowCorsTemporary}${url || fileurl}`},
      pageNumber: 1,
    };
  }

  onDocumentLoadSuccess = ({numPages}) => {
    this.setState({numPages});
  };

  render() {
    const {pageNumber, numPages, fileObject} = this.state;
    const pageRuler = `Page ${pageNumber} of ${numPages}`;

    return (
      <div className="pdfView">
        <Document file={fileObject} onLoadSuccess={this.onDocumentLoadSuccess}>
          {Array.from(new Array(numPages), (el, index) => (
            <Page key={`page_${index + 1}`} pageNumber={index + 1} />
          ))}
        </Document>
        <p>{pageRuler}</p>
      </div>
    );
  }
}
