import React, {useContext, useState, useEffect, useRef} from "react";
import {
  Input,
  Tooltip,
  Form,
  Table,
  Popconfirm,
  Row,
  Col,
  Spin,
  message,
  Button,
} from "antd";
import Editicon from "../../assests/images/edit.svg";
import axios from "axios";
import {BASE_URL} from "../../constants/constants";

import {CheckSquareFilled} from "@ant-design/icons";
import "./masterdata.css";

const {Search} = Input;
const EditableContext = React.createContext();
let timeout = null;
const EditableRow = ({index, ...props}) => {
  const [form] = Form.useForm();
  return (
    <Form form={form} component={false}>
      <EditableContext.Provider value={form}>
        <tr {...props} />
      </EditableContext.Provider>
    </Form>
  );
};

class Loadlocation extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      dataSource: [],
      editingKey: "",
    };

    this.materialtype = [
      {
        title: "",
        dataIndex: "location",
        width: "80%",
        editable: true,
      },

      {
        title: "",
        dataIndex: "operation",
        width: "20%",
        render: (text, record) => {
          const editable = this.isEditing(record);
          return (
            <div>
              {editable ? (
                <EditableContext.Consumer>
                  {(form) => (
                    <span>
                      <div onClick={() => this.handleSave(form, record._id)}>
                        <Tooltip title="Save">
                          <CheckSquareFilled className="saveicon" />
                        </Tooltip>
                      </div>

                      {/* <div onClick={() => this.handleCancel(record.key)}>
                        cancel
                      </div> */}
                    </span>
                  )}
                </EditableContext.Consumer>
              ) : (
                <Tooltip title="Edit">
                  <span>
                    <img
                      src={Editicon}
                      alt=""
                      style={{cursor: "pointer"}}
                      onClick={() => this.handleEdit(record._id)}
                    />
                  </span>
                </Tooltip>
              )}
            </div>
          );
        },
      },
    ];
  }
  formReflocation = React.createRef();
  gettableData = async () => {
    try {
      this.setState({loading: true});
      const response = await axios.get(`${BASE_URL}get-load-location`);
      const materials = response.data.materials || "";
      if (materials && materials.length > 0) {
        this.setState({
          dataSource: materials,
          loading: false,
        });
      } else {
        this.setState({
          dataSource: [],
          loading: false,
        });
      }
    } catch (err) {
      // console.log(err);
    }
  };

  componentDidMount() {
    this.gettableData();
  }
  handleSave = async (form, key) => {
    try {
      // const {dataSource} = this.state;
      const row = await form.validateFields();
      console.log(row, "rows", key);
      await axios
        .put(`${BASE_URL}update-load-location/${key}`, {
          location: row.location,
        })
        .then((data) => {
          message.success("Sucessfully updated load location");
          this.setState({editingKey: ""});
          this.gettableData();
        })
        .catch((error) => {
          const {response} = error;
          const {data} = response;
          const {message: msg} = data;
          message.error(msg);
        });
    } catch (err) {
      console.log(err);
    }
  };
  handleCancel = (key) => {
    this.setState({editingKey: ""});
  };

  isEditing = (record) => {
    return record._id === this.state.editingKey;
  };

  handleEdit = (key) => {
    this.setState({editingKey: key});
  };

  searchResult = (e) => {
    clearTimeout(timeout);
    let searchValue = e.target.value;
    timeout = setTimeout(() => {
      this.setState({searchValue});
      if (
        searchValue !== "" &&
        searchValue !== null &&
        searchValue !== undefined
      ) {
        searchValue = searchValue.toString().toUpperCase().trim();
        console.log(searchValue, "thisisdata", this.state.dataSource);
        const newSearchedTableData = this.state.dataSource.filter(
          ({location}) => {
            const loc = location || "";
            return loc.toString().toUpperCase().includes(searchValue);
          }
        );
        this.setState({searchResultArray: newSearchedTableData});
        console.log(this.state.searchResultArray, "thisisdata");
      } else {
        this.setState({
          searchResultArray: [],
          printableArray: [],
          selectedRowKeys: [],
        });
      }
    }, 500);
  };
  onLoadLocation = async (values) => {
    const loadLocation = values.loadLocation;
    try {
      const response = await axios.post(`${BASE_URL}create-load-location`, {
        location: loadLocation,
      });
      const mess = response.data.message || "";
      message.success(mess);
      this.gettableData();
    } catch (err) {
      console.log(err);
      message.error("Already Exist");
    }
    this.formReflocation.current.resetFields();
  };
  render() {
    // const {dataSource} = this.state;

    const EditableCell = ({
      editing,
      dataIndex,
      title,
      inputType,
      record,
      index,
      children,
      ...restProps
    }) => {
      return (
        <td {...restProps}>
          {editing ? (
            <Form.Item
              className="editbox"
              name={dataIndex}
              rules={[
                {
                  required: true,
                },
              ]}
              initialValue={record[dataIndex]}
            >
              <Input />
            </Form.Item>
          ) : (
            children
          )}
        </td>
      );
    };

    const components = {
      body: {
        row: EditableRow,
        cell: EditableCell,
      },
    };
    const materialtype = this.materialtype.map((col) => {
      if (!col.editable) {
        return col;
      }

      return {
        ...col,
        onCell: (record) => ({
          record,
          editable: col.editable,
          dataIndex: col.dataIndex,
          title: col.title,
          editing: this.isEditing(record),
        }),
      };
    });
    return (
      <div>
        <Form
          ref={this.formReflocation}
          name="load"
          onFinish={this.onLoadLocation}
          autoCompleter="off"
        >
          <Row>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <h3 className="typeTitle">Load Location</h3>
            </Col>
            <Col xs={24} sm={24} md={17} lg={17} xl={17}>
              <div className="material-type">
                <Form.Item
                  name="loadLocation"
                  rules={[
                    {
                      required: true,
                      message: "This feild is required",
                    },
                  ]}
                >
                  <Input />
                </Form.Item>
              </div>
            </Col>
            <Col xs={24} sm={24} md={4} lg={4} xl={4}>
              <Button className="addbtn" htmlType="submit">
                Add
              </Button>
            </Col>
          </Row>
        </Form>
        <Row className="master-data-maindiv">
          {" "}
          <Col xs={24} sm={24} md={24} lg={24} xl={24}>
            <Form name="material">
              <div style={{width: "100%"}}>
                <div className="searchdiv">
                  <Search
                    className="searchdata"
                    style={{width: "270px"}}
                    onChange={this.searchResult}
                    placeholder="Search"
                  />
                </div>
                <div className="materialTable">
                  <Table
                    components={components}
                    rowClassName={() => "editable-row"}
                    dataSource={
                      this.state.searchValue === "" ||
                      this.state.searchValue === null ||
                      this.state.searchValue === undefined
                        ? this.state.dataSource
                        : this.state.searchResultArray
                    }
                    columns={materialtype}
                    pagination={false}
                    scroll={{y: 240}}
                    rowKey="_id"
                  />
                </div>
              </div>
            </Form>
          </Col>
        </Row>
      </div>
    );
  }
}

export default Loadlocation;
