import React, {Component} from "react";
import "antd/dist/antd.css";
import {Layout, Avatar} from "antd";
import {
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  UserOutlined,
} from "@ant-design/icons";
import Logo from "../../assests/images/trucklink.png";
import SideMenu from "../menus/sidemenu/sidemenu";
import HeaderMenu from "../menus/headermenu/headermenu";
import UserImage from "../../assests/images/user.jpeg";
import auth from "./../../utils/auth";
import "./layouts.css";
const {Header, Sider, Content} = Layout;

class MainLayout extends Component {
  constructor(props) {
    super(props);
    this.userDetails = auth.getAuthToken().userDetails;
    this.state = {
      collapsed: false,
      vidible: false,
      collaspedwidth: "",
      sidebarWidth: "200",
      file: this.props,
    };
  }

  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };
  home = () => {
    this.props.history.push("/vehicle-onwer-details");
  };

  render() {
    const {firstName: fn, name: nm} = this.userDetails;
    const firstName = nm || fn || "";
    return (
      <Layout>
        <Sider
          trigger={null}
          className="siderbar"
          collapsible
          collapsed={this.state.collapsed}
          breakpoint="sm"
          width={this.state.sidebarWidth}
          collapsedWidth={this.state.collapsedWidth}
          onBreakpoint={(broken) => {
            if (broken === true) {
              this.setState({
                collapsed: true,
                collapsedWidth: "0",
                sidebarWidth: "80",
              });
            } else {
              this.setState({
                collapsedWidth: "80",
                sidebarWidth: "200",
              });
            }
          }}
        >
          <div className="layoutlogo" onClick={this.home}>
            <div>
              <img src={Logo} alt="logo" />
            </div>
            <div
              style={{
                fontFamily: "AvenirBold",
                color: "#fff",
                fontSize: "22px",
                padding: "4px",
              }}
            >
              <span>TRUCK</span>
              <span style={{fontFamily: "Avenir"}}>LINK</span>
            </div>
          </div>
          <div
            style={{
              display: "flex",
              background: "#262932",
              margin: "0px",
              padding: "4px",
            }}
          >
            <div>
              <span style={{margin: "10px"}}>
                <UserOutlined
                  src={UserImage}
                  style={{
                    margin: "5px 10px",
                    color: "#37c1d6",
                    fontSize: "18px",
                  }}
                />
              </span>
            </div>
            <div
              style={{
                color: "#37c1d6",
                fontSize: "12px",
                paddingTop: "8px",
                whiteSpace: "nowrap",
              }}
              className="proTitle"
            >
              Hello{" "}
              {firstName
                ? " " +
                  firstName.split("")[0].toUpperCase() +
                  firstName.substring(1)
                : ""}
            </div>
          </div>
          <SideMenu history={this.props.history} />
        </Sider>
        <Layout className="site-layout">
          <Header
            className="site-layout-background"
            style={{padding: 0, display: "flex"}}
          >
            {React.createElement(
              this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
              {
                className: "trigger",
                onClick: this.toggle,
              }
            )}
            <HeaderMenu history={this.props.history} />
          </Header>
          <Content>
            <this.props.component history={this.props.history} />
          </Content>
        </Layout>
      </Layout>
    );
  }
}

export default MainLayout;
