/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable no-unused-expressions */
import React, {Component} from "react";
import {
  Input,
  Row,
  Col,
  Form,
  Select,
  Upload,
  Button,
  InputNumber,
  message,
  Spin,
  Alert,
} from "antd";
import moment from "moment";
import axios from "axios";
import {PaperClipOutlined} from "@ant-design/icons";
import {BASE_URL} from "../../../../constants/constants";
import auth from "../../../../utils/auth";
import "antd/dist/antd.css";
import "./vehicleinformation.css";
const {Option} = Select;
const addFile = {fontWeight: "600", color: " #01a6d0"};
const {Dragger} = Upload;

class EditVehicleInformation extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const {record} = this.props;
    const {accountId: accountInformation,ownerId: ownerInformation,_id} = record;
    this.state = {
      loading: false,
      visible: false,
      fullrecord: record,
      fileList: [],
      selectedBankDetails: accountInformation[0] || {},
      ownerInfo: ownerInformation[0] || {},
      vehicleId: _id,
      ownerNumbers: [],
      bankOptions: [],
      accountInfo: [],
      rcBookProof: record ? record.rcBookProof : null,
      userDetails: Details,
      oldOwnerId: ownerInformation[0] ? ownerInformation[0]._id : "",
      TransferOwnerVehicle: [],
      transferDate: "",
      msg: "",
    };
  }

  formRef = React.createRef();

  componentDidMount() {
    
    this.getAllOwnerPhoneNumbers();
    this.checkOwnerTransfer();
  }
  checkOwnerTransfer = () => {
    const {vehicleId} = this.state;
    const {vehicleDetails} = this.state.ownerInfo;
    if (vehicleDetails) {
      const TransferOwnerVehicle = vehicleDetails.filter(
        ({vehicleIds}) => vehicleIds === vehicleId
      );
      if (TransferOwnerVehicle && TransferOwnerVehicle.length > 0) {
        const ownerTransferDate = TransferOwnerVehicle[0].ownerTransferDate;
        const date = moment(new Date(ownerTransferDate)).format("DD-MM-YYYY");
        this.setState({
          TransferOwnerVehicle,
          msg: `On this  ${date} you have transferred the vehicle ownership, hence only Recent owner data will be transferred while changing the Phone Number.`,
        });
      }
    }
  };
  getAllOwnerPhoneNumbers = async () => {
    try {
      this.setState({loading: true});
      const response = await axios.get(`${BASE_URL}get-owner-phoneNumbers`);
      const {ownerPhoneNumbers} = response.data;
      if (ownerPhoneNumbers && ownerPhoneNumbers.length > 0) {
        const ownerNumbers = ownerPhoneNumbers.map(
          ({phoneNumber, _id, name}) => (
            <Option key={_id} value={_id}>
              {phoneNumber}
              {name ? " - " + name.toUpperCase() : ""}
            </Option>
          )
        );
        this.setState({ownerNumbers, loading: false});
      }
    } catch (err) {
      console.error(err);
      this.setState({loading: false});
      message.error("Unable to fetch owner phone numbers");
    }
  };

  clearOnOwnerChange = () => {
    this.setState({
      accountInfo: [],
      ownerInfo: {},
      bankOptions: [],
      selectedBankDetails: {},
      isOwnerEnabled: false,
      isBankEnabled: false,
    });
  };

  handleOwnerSelection = async (e) => {
    try {
      this.clearOnOwnerChange();
      this.setState({loading: true});
      const response = await axios.get(
        `${BASE_URL}/get-owner-bank-details/${e}`
      );
      const {ownerDetails} = response.data;
      const {accountIds: accountInfo, ...ownerInfo} = ownerDetails
        ? ownerDetails[0]
        : {};
      let bankOptions = [];

      if (accountInfo && accountInfo.length > 0) {
        bankOptions = accountInfo.map(({_id, bankName, accountNumber}) => (
          <Option key={_id} value={_id}>
            {bankName}
            {accountNumber
              ? " - " + accountNumber.substr(accountNumber.length - 5)
              : ""}
          </Option>
        ));
      }
      this.setState({
        accountInfo,
        ownerInfo,
        bankOptions,
        isOwnerEnabled: true,
        loading: false,
      });
    } catch (err) {
      console.error(err);
      message.error("Unable to fetch bank details");
      this.setState({loading: false});
    }
  };
  handleAccountSelection = (e) => {
    try {
      const {accountInfo} = this.state;
      const bankDetails = accountInfo.filter(({_id}) => _id === e);
      this.setState({
        selectedBankDetails: bankDetails[0],
        isBankEnabled: true,
      });
    } catch (err) {
      message.error("Unable to change bank account");
    }
  };

  onFinish = async (vehicleDetails) => {
    try {
      this.setState({loading: true});
      const {
        ownerInfo,
        selectedBankDetails,
        vehicleId,
        rcBookProof,
        oldOwnerId,
      } = this.state;
      const {_id: ownerId} = ownerInfo;
      const {_id: accountId} = selectedBankDetails;
      
      if (accountId && ownerId) {
        const payload = {
          ownerId,
          accountId,
          ...vehicleDetails,
          rcBookProof,
          ownerName: ownerInfo ? ownerInfo.name : "",
        };
        
        await axios.put(
          `${BASE_URL}update-vehicle-details/${vehicleId}/${oldOwnerId}`,
          payload
        );
        if(this.props.onSearch()){
          this.props.onSearch();
        }
        message.success("Successfully updated details");
        this.props.onUpdate();

        this.setState({loading: false});
        this.props.parentFn();
      } else {
        this.setState({loading: false});
        message.error("Owner and account need to be selected");
      }
    } catch (err) {
      console.log("something went wrong, please try again");
    }
  };

  axiosFileUploadRequest = async (file) => {
    try {
      const formData = new FormData();
      formData.append("file", file.file);
      const config = {
        headers: {
          "content-type": "multipart/form-data",
        },
      };
      this.setState({
        loading: true,
      });
      const response = await axios.post(
        `${BASE_URL}rc-upload`,
        formData,
        config
      );

      const {rcBookProof} = response.data;
      this.setState({rcBookProof});
      this.setState({
        loading: false,
      });
      message.success("File uploaded successfully ");
    } catch (err) {
      message.error("Unable to upload a file.");
      console.log(err);
    }
  };

  onReset = () => {
    this.formRef.current.resetFields();
  };

  render() {
    const {
      selectedBankDetails,
      ownerInfo,
      fileList,
      fullrecord,
      ownerNumbers,
      bankOptions,
      userDetails,
      TransferOwnerVehicle,
      msg,
    } = this.state;
    const {
      registrationNumber,
      truckType,
      driverName,
      driverPhoneNumber,
      commission,
      phoneNumber,
    } = fullrecord;
    return (
      <div>
        <Spin spinning={this.state.loading}>
          <Form
            ref={this.formRef}
            name="vehicleinfo"
            className="vehicleinfoForm"
            onFinish={this.onFinish}
            autoComplete="off"
            initialValues={{
              registrationNumber,
              truckType,
              driverName,
              driverPhoneNumber,
              commission,
              phoneNumber,
            }}
          >
            <h2 style={{margin: "25px 30px 15px 30px"}}>Vehicle Details</h2>
            {userDetails && userDetails.roleName === "Admin" ? (
              <div style={{margin: "0px 10px 0px 10px"}}>
                {TransferOwnerVehicle && TransferOwnerVehicle.length > 0 ? (
                  <Alert message={msg} type="error" />
                ) : (
                  ""
                )}
              </div>
            ) : (
              ""
            )}
            <Row style={{margin: 0}}>
              <Col
                xs={24}
                sm={24}
                md={20}
                lg={16}
                xl={16}
                style={{margin: 0, padding: 0}}
              >
                <Row style={{margin: "10px 22px"}}>
                  <Col xs={24} sm={24} md={9} lg={8} xl={9}>
                    <h3 style={{marginLeft: "5px"}} className="require">
                      Vehicle Number
                    </h3>
                    <div>
                      <Form.Item
                        name="registrationNumber"
                        rules={[
                          {
                            required: true,
                            message: "required",
                          },
                        ]}
                      >
                        <Input
                          style={{textTransform: "uppercase"}}
                          onKeyUp={(e) => {
                            var start = e.target.selectionStart;
                            var end = e.target.selectionEnd;
                            e.target.value = e.target.value.toUpperCase();
                            e.target.setSelectionRange(start, end);
                          }}
                        />
                      </Form.Item>
                    </div>
                  </Col>

                  <Col xs={12} sm={12} md={6} lg={6} xl={6}>
                    <h3 style={{marginLeft: "5px"}} className="require">
                      Vehicle Type
                    </h3>
                    <div>
                      <Form.Item
                        name="truckType"
                        rules={[{required: true, message: "required"}]}
                      >
                        <Select defaultValue="Select Type">
                          <Option value="bulk">Bulk</Option>
                          <Option value="bag">Bag</Option>
                        </Select>
                      </Form.Item>
                    </div>
                  </Col>

                  <Col xs={12} sm={12} md={9} lg={9} xl={9}>
                    <h3 style={{marginLeft: "15px"}} className="require">
                      Owner Phone Number
                    </h3>
                    <div>
                      {userDetails && userDetails.roleName === "Admin" ? (
                        <Form.Item name="phoneNumber">
                          <Select
                            style={{width: "186px", marginLeft: "8px"}}
                            placeholder="Select owner phone"
                            onChange={this.handleOwnerSelection}
                            onDeselect={this.clearOnOwnerChange}
                            showSearch
                            showArrow={false}
                            allowClear
                            notFoundContent="No owner found with this phone number."
                            filterOption={(input, option) =>
                              option &&
                              option.children
                                .toString()
                                .toLowerCase()
                                .indexOf(
                                  input && input.toString().toLowerCase()
                                ) >= 0
                            }
                          >
                            {ownerNumbers}
                          </Select>
                        </Form.Item>
                      ) : (
                        <Input disabled value={ownerInfo.phoneNumber} />
                      )}
                    </div>
                  </Col>
                  <Col xs={12} sm={12} md={9} lg={9} xl={9}>
                    <h3 style={{marginLeft: "5px"}}>Driver Name</h3>
                    <div>
                      <Form.Item name="driverName">
                        <Input />
                      </Form.Item>
                    </div>
                  </Col>
                  <Col xs={12} sm={12} md={9} lg={9} xl={9}>
                    <h3 style={{marginLeft: "5px"}}>Driver Phone Number</h3>
                    <div>
                      <Form.Item
                        name="driverPhoneNumber"
                        rules={[
                          {
                            min: 10,
                            message: "Mobile No not less than 10 digit",
                          },
                        ]}
                      >
                        <Input
                          onKeyPress={(event) => {
                            if (
                              !/^[0-9]+$/.test(event.key) &&
                              event.key != "Backspace"
                            ) {
                              event.preventDefault();
                            }
                          }}
                          maxLength="10"
                        />
                      </Form.Item>
                    </div>
                  </Col>

                  <Col xs={12} sm={12} md={7} lg={7} xl={6}>
                    <h3 style={{marginLeft: "5px"}}>Commission %</h3>
                    <div>
                      <Form.Item
                        name="commission"
                        rules={[
                          {
                            required: true,
                            message: "Not valid Number",
                          },
                        ]}
                      >
                        <Input
                          onKeyPress={(event) => {
                            if (
                              !/^[0-9.]+$/.test(event.key) &&
                              event.key != "Backspace"
                            ) {
                              event.preventDefault();
                            }
                          }}
                          minlength="1"
                          maxlength="2"
                        />
                      </Form.Item>
                    </div>
                  </Col>
                </Row>
              </Col>
              <Col xs={24} sm={24} md={8} lg={8} xl={8}>
                <h3 style={{margin: "8px 30px 3px 5px"}}>RC Book</h3>
                <Form.Item>
                  <Dragger
                    name="file"
                    customRequest={this.axiosFileUploadRequest}
                    fileList={fileList}
                    accept=".pdf, .jpeg, .jpg, .png"
                    style={{width: "91%"}}
                  >
                    <PaperClipOutlined style={{fontSize: "20px"}} />
                    <a style={addFile}>Add File </a>
                    <span style={{color: "#a3a2a2"}}>or Drop files here</span>
                    <p style={{fontSize: "10px", color: "#858585"}}>
                      Only .PDF, .JPG and .PNG files allowed to upload
                    </p>
                  </Dragger>
                </Form.Item>
              </Col>
            </Row>

            <Row
              style={{
                border: "0.5px dashed #979797",
                opacity: "0.40",
                margin: "10px 0 10px 0px",
              }}
            />
            <div className="ownerform">
              <h2 style={{margin: "25px 30px 15px 30px"}}>
                Vehicle Owner Details
              </h2>
              <Row style={{margin: "10px 22px"}}>
                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 className="require">Owner Name</h3>
                  <div>
                    <Input disabled value={ownerInfo.name} />
                  </div>
                </Col>

                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3>Address</h3>
                  <div>
                    <Input disabled value={ownerInfo.address} />
                  </div>
                </Col>

                <Col xs={12} sm={12} md={4} lg={4} xl={4}>
                  <h3>District</h3>
                  <div>
                    <Input disabled value={ownerInfo.district} />
                  </div>
                </Col>
                <Col xs={12} sm={12} md={4} lg={4} xl={4}>
                  <h3>State</h3>
                  <div>
                    <Input disabled value={ownerInfo.state} />
                  </div>
                </Col>

                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 style={{marginTop: "12px"}}>PAN Card Number</h3>
                  <div>
                    <Input disabled value={ownerInfo.panNumber} />
                  </div>
                </Col>

                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 style={{marginTop: "12px"}}>Email ID</h3>
                  <div>
                    <Input disabled value={ownerInfo.email} />
                  </div>
                </Col>
                {userDetails && userDetails.roleName === "Admin" ? (
                  <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                    <h3
                      className="require"
                      style={{marginTop: 12, marginBottom: 3}}
                    >
                      Select Bank Account
                    </h3>
                    <div>
                      <Select
                        style={{width: "100%"}}
                        placeholder="Select owner bank account"
                        onChange={this.handleAccountSelection}
                        showSearch
                        showArrow
                        initialValue={null}
                        notFoundContent="No bank account found."
                        filterOption={(input, option) =>
                          option &&
                          option.children
                            .toString()
                            .toLowerCase()
                            .indexOf(input && input.toString().toLowerCase()) >=
                            0
                        }
                      >
                        {bankOptions}
                      </Select>
                    </div>
                  </Col>
                ) : (
                  ""
                )}
              </Row>
            </div>

            <Row
              style={{
                border: "0.5px dashed #979797",
                opacity: "0.40",
                margin: "20px 0 20px 0px",
              }}
            />
            <div className="bankform">
              <h2 style={{margin: "15px 30px"}}>Bank Details</h2>

              <Row style={{margin: "10px 25px"}}>
                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 className="require">Account Holders Name</h3>
                  <div>
                    <Input
                      disabled
                      value={selectedBankDetails.accountHolderName}
                    />
                  </div>
                </Col>

                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 className="require">Bank Name</h3>
                  <div>
                    <Input disabled value={selectedBankDetails.bankName} />
                  </div>
                </Col>
                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 className="require">Account Number</h3>
                  <div>
                    <Input disabled value={selectedBankDetails.accountNumber} />
                  </div>
                </Col>
                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 style={{marginTop: "12px"}} className="require">
                    Bank Branch
                  </h3>
                  <div>
                    <Input disabled value={selectedBankDetails.branchName} />
                  </div>
                </Col>
                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                  <h3 style={{marginTop: "12px"}} className="require">
                    IFSC Code
                  </h3>
                  <div>
                    <Input disabled value={selectedBankDetails.ifscCode} />
                  </div>
                </Col>
              </Row>
            </div>

            <Row
              style={{
                border: "0.5px dashed #979797",
                opacity: "0.40",
                margin: "20px 0 20px 0px",
              }}
            />
            <Row>
              <Col lg={17}>
                <p
                  style={{
                    color: "#989898",
                    fontSize: "10px",
                    padding: "20px",
                  }}
                >
                  Note*: Vehicle Owner details and Bank details can be edited
                  through Owner Master Tab
                </p>
              </Col>
              <Col lg={7}>
                <div
                  style={{padding: "10px", float: "right", marginRight: "20px"}}
                >
                  <Button className="savebtn" type="primary" htmlType="submit">
                    SAVE
                  </Button>
                </div>
                <div style={{padding: "10px", float: "right"}}>
                  <Button
                    type="primary"
                    className="cancelBtn"
                    onClick={() => {
                      this.onReset();
                      this.props.parentFn();
                    }}
                  >
                    CANCEL
                  </Button>
                </div>
              </Col>
            </Row>
          </Form>
        </Spin>
      </div>
    );
  }
}

export default EditVehicleInformation;
