import React, {Component} from "react";
import {
  Table,
  Input,
  Button,
  Modal,
  Popconfirm,
  Checkbox,
  message,
  Spin,
  Row,
  Select,
  DatePicker,
  Col,
} from "antd";
import moment from "moment";
import {RightOutlined, DownOutlined} from "@ant-design/icons";
import EditChallanDetails from "./editdispatchchallan";
import CurrentTime from "../../components/currenttime/currenttime";
import axios from "axios";
import {BASE_URL} from "../../constants/constants";
import Editicon from "../../assests/images/edit.svg";
import PdfViewer from "../../components/pdfviewer/pdfviewer";
import ViewDocument from "../../assests/images/documentview.svg";
import dateicon from "../../assests/images/dateicon.svg";
import HeaderBreedCrumb from "../../components/receivemenubreedcrumb/receivemenubreedcrumb";
import auth from "../../utils/auth";
import "./recieveregister.css";

const {Search} = Input;
const {RangePicker} = DatePicker;
const imageStyle = {
  height: "auto",
  maxWidth: "480px",
  minWidth: "200px",
  paddingBottom: 25,
};

const iconStyle = {
  color: "#01a6d0",
  fontSize: "14px",
  width: "24px",
  height: "24px",
};

const dateFormatList = ["DD/MM/YYYY"];

const {Option} = Select;
const NullData = (tableText) => <span>{tableText || <p>&#8212;</p>}</span>;
let timeout = null;

const buildQueryParams = (params) => {
  let queryParams = [];
  for (const param in params) {
    if (params[param]) {
      queryParams.push(`${param}=${params[param]}`);
    }
  }
  return queryParams.length ? `?${queryParams.join("&")}` : "";
};
class RecieveRegister extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const Det = auth.getUserData();
    this.state = {
      logId: Det._id,
      vidible: false,
      loading: false,
      doucmentvisible: false,
      editableVehicleDetails: null,
      filters: {
        startDate: null,
        endDate: null,
        searchTDNo: null,
      },
      fileurl: "",
      searchValue: "",
      searchStartDate: "",
      searchEndDate: "",
      searchResultArray: "",
      selectionType: Checkbox,
      dataSource: [],
      fileType: "",
      previewUrl: "",
      materialSelect: [],
      truckTypeStatus: [],
      startDate: "",
      endDate: "",
      materialDataSelect: [],
      truckDataSelect: [],
      selectRowArray: [],
      userDetails: Details,
      totalCount: "",
      page: 1,
      limit:50
    };
  }
  customExpandIcon(props) {
    if (props.expanded) {
      return (
        <a
          style={{color: "black"}}
          onClick={(e) => {
            props.onExpand(props.record, e);
          }}
        >
          <DownOutlined style={iconStyle} className="customIcon" />
        </a>
      );
    }
    return (
      <a
        style={{color: "black"}}
        onClick={(e) => {
          props.onExpand(props.record, e);
        }}
      >
        <RightOutlined style={iconStyle} className="customIcon" />
      </a>
    );
  }
  showModal = () => {
    this.setState({
      visible: true,
    });
  };
  showmodals = (e) => {
    this.setState({
      editvisible: true,
      editableVehicleDetails: e,
    });
  };
  handleOk = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };

  forceUpdate = () => {
    this.setState({
      editvisible: false,
    });
  };

  gettableData = async (data, page,limit) => {
    let queryParams = buildQueryParams(data);
    try {
      const pages = page ? page : 1;
      const limitData = limit ? limit : this.state.limit;
      this.setState({loading: true, page: pages});
      if (data) {
        const response = await axios.get(
          queryParams
            ? `${BASE_URL}get-receive-register${queryParams}&page=${pages}&limit=${limitData}`
            : `${BASE_URL}get-receive-register?page=${pages}&limit=${limitData}`
        );
        const dispatchData =
          response.data.dispatchData && response.data.dispatchData.length > 0
            ? response.data.dispatchData[0].data
            : "";
        const filterCount =
          response.data.dispatchData && response.data.dispatchData.length > 0
            ? response.data.dispatchData[0].count
            : "";
        if (dispatchData && dispatchData.length > 0) {
          const dataSource = dispatchData.map((data, index) => {
            const date = data.grDate;
            var GrDate = moment(date, "DD-MM-YYYY");
            if (data.invoiceDate) {
              const date = data.invoiceDate;
              var InvoiceDate = moment(date, "DD-MM-YYYY");
            }
            return {
              ...data,
              slno: index + 1,
              commissionPercent: "@" + data.commisionRate + "%",
              grDateMom: GrDate,
              invoiceDateMoM: InvoiceDate,
            };
          });
          this.setState({loading:false,dataSource, totalCount: filterCount});
        } else {
          this.setState({dataSource: ""});
        }
      } else {
        const response = await axios.get(
          `${BASE_URL}get-receive-register?page=${pages}&limit=${limitData}`
        );
        const dispatchData =
          response.data.dispatchData && response.data.dispatchData.length > 0
            ? response.data.dispatchData[0].data
            : "";
        const totalCount =
          response.data.dispatchData && response.data.dispatchData.length > 0
            ? response.data.dispatchData[0].count
            : "";
        if (dispatchData && dispatchData.length > 0) {
          const dataSource = dispatchData.map((data, index) => {
            const date = data.grDate;
            var GrDate = moment(date, "DD-MM-YYYY");
            if (data.invoiceDate) {
              const date = data.invoiceDate;
              var InvoiceDate = moment(date, "DD-MM-YYYY");
            }
            return {
              ...data,
              slno: index + 1,
              commissionPercent: "@" + data.commisionRate + "%",
              grDateMom: GrDate,
              invoiceDateMoM: InvoiceDate,
            };
          });

          this.setState({loading:false,dataSource, totalCount});
        } else {
          this.setState({dataSource: ""});
        }
        
      }
      setTimeout(() => {
        this.setState({loading: false});
      }, 700);
    } catch (err) {
      this.setState({loading: false});
      message.error(
        "No record available in Receive Register. Please post the record",
        2
      );
    }
  };

  getMaterialData = async () => {
    try {
      const response = await axios.get(`${BASE_URL}get-material`);
      const {materials: materialData} = response.data || "";
      if (materialData && materialData.length > 0) {
        const materialtypes = materialData.map(({_id, materialType}) => {
          const val = materialType ? materialType.trimEnd() : null;
          if (val) {
            return (
              <Option key={_id} value={materialType}>
                {materialType}
              </Option>
            );
          }
        });
        this.setState({
          materialDataSelect: materialtypes,
        });
      }
    } catch (err) {
      message.error("Network Failed, Please Try Again");
    }
  };
  doSomethingBeforeUnload = async () => {
    await axios.put(`${BASE_URL}logout/${this.state.logId}`);
  };
  setupBeforeUnloadListener = async () => {
    
    window.addEventListener("beforeunload", async (ev) => {
      ev.preventDefault();
      return this.doSomethingBeforeUnload();
    });
  };

  componentDidMount() {
    this.gettableData();
    this.getMaterialData();

    this.setupBeforeUnloadListener();
  }
  changePagination = async (e) => {
    try {
      this.setState({loading: true, page: e});
      this.gettableData(this.state.filters, e);
      // this.setState({loading: false});
    } catch (err) {
      console.log(err);
    }
  };

  changePaginationAll = async(currentpage,pageSize) =>{
    try{
      this.setState({loading: true, page: currentpage,limit: pageSize });
      this.gettableData(this.state.filters,currentpage,pageSize);

      // this.setState({loading: false});
    }catch(err){
      console.log(err)
    }
  }

  deleterecord(e) {}

  confirm = async (e) => {
    try {
      await axios.delete(`${BASE_URL}delete-dispatch-challan/${e._id}`);
      message.success("Successfully deleted vehicle");
      this.setState({loading: true});
      try {
        const response = await axios.get(`${BASE_URL}get-challan-data`);
        const dispatchData = response.data.disptachData || "";
        // if (dispatchData && dispatchData.length > 0) {
        this.setState({
          dataSource: dispatchData.map((data, index) => {
            return {...data, slno: index + 1};
          }),
        });
        // }
        this.setState({loading: false});
      } catch (err) {
        this.setState({loading: false});
        message.error("Error fetching data. Please try again later", 2);
      }
    } catch (err) {
      message.error("Unable to delete");
    }
  };

  searchResult = async (e) => {
    let searchValue = e.target.value;
    const filters = {...this.state.filters};
    if (searchValue != "") {
      filters.searchTDNo = searchValue;
    } else {
      filters.searchTDNo = null;
    }
    this.setState({
      filters,
    });
    await this.gettableData(filters);
  };

  searchFn = (search, value) => {
    if (value && search) {
      return value.toString().toUpperCase().includes(search);
    } else {
      return false;
    }
  };
  handleCancelEdit = () => {
    this.setState({
      editvisible: false,
    });
  };

  reloadAfterModalClose = () => {
    this.gettableData(this.state.filters);
    this.setState({
      visible: false,
      editvisible: false,
      doucmentvisible: false,
    });
  };
  // handleMaterialType = async (materialValue) => {
  //   await this.gettableData();
  //   const obj = materialValue.toString();

  //   materialValue = materialValue.toString().toUpperCase().trim();
  //   const matValue =
  //     Object.keys(materialValue).length > 0 ? obj.split(",") : [];
  //   this.setState({materialSelect: matValue});
  //   const {dataSource} = this.state;
  //   if (matValue.length > 0 && dataSource.length > 0) {
  //     const filteredTableData = dataSource.filter(({materialType}) =>
  //       materialValue.includes(materialType.toString().toUpperCase().trim())
  //     );
  //     this.setState({dataSource: filteredTableData});
  //   }
  // };
  // handleTruckType = async (truckValue) => {
  //   try {
  //     await this.gettableData();
  //     const obj = truckValue.toString().toUpperCase().trim();
  //     // vehicleValue = truckValue.toString().toUpperCase();
  //     const vehicleValue =
  //       Object.keys(truckValue).length > 0 ? obj.split(",") : [];
  //     this.setState({truckTypeStatus: vehicleValue});
  //     const {dataSource} = this.state;
  //     if (vehicleValue.length > 0 && dataSource.length > 0) {
  //       const filteredTableData = dataSource.filter(({vehicleType}) =>
  //         vehicleValue.includes(vehicleType.toString().toUpperCase())
  //       );
  //       this.setState({dataSource: filteredTableData});
  //     }
  //   } catch (err) {
  //     console.log(err);
  //     message.error("Unable to Filter");
  //   }
  // };

  onDateChange = async (dates) => {
    try {
      if (dates) {
        const [a, b] = dates;
        let startDate = "";
        let endDate = "";
        if (a) {
          startDate = new Date(a).getTime();
          const filters = {...this.state.filters};
          if (startDate != "") {
            filters.startDate = startDate;
          } else {
            filters.startDate = null;
          }
          await this.setState({
            filters,
          });
          // this.gettableData(filters);
        }
        if (b) {
          endDate = new Date(b).getTime();
          const filters = {...this.state.filters};
          if (endDate != "") {
            filters.endDate = endDate;
          } else {
            filters.endDate = null;
          }
          await this.setState({
            filters,
          });
          // this.gettableData(filters);
        }
        await this.gettableData(this.state.filters);
      } else {
        const filters = {...this.state.filters};
        filters.startDate = null;
        filters.endDate = null;
        this.setState({
          filters,
        });
        this.gettableData(filters);
      }
    } catch (error) {
      message.error("Unable to fetch data");
    }
  };

  viewDocments(e) {
    if (e.invoiceProof != null && e.invoiceProof != "") {
      let url = e.invoiceProof;
      let type = url ? url.split(".") : "";
      type = type[type.length - 1];
      this.setState({
        doucmentvisible: true,
        fileType: type,
        previewUrl: url,
      });
    } else {
      message.error("No Invoice Available", [0]);
    }
  }
  render() {
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({
          selectRowArray: selectedRows,
        });
      },
      columnWidth: "10px",
      getCheckboxProps: (record) => ({
        disabled: record.name === "Disabled User",
        name: record.name,
      }),
    };
    const columns = [
      {
        title: "Sl No",
        dataIndex: "slno",
        key: "name",
        render: NullData,
        fixed: "left",
        width: "60px",
        render: (value, item, index) => (this.state.page - 1) * this.state.limit + index + 1,
      },
      {
        title: "GR No",
        dataIndex: "grNumber",
        key: "grNumber",
        inputTypeValue: "Grno",
        width: "100px",
        render: (text, record) => (
          <span>{!record.grNumber ? <p>&#8212;</p> : record.grNumber}</span>
        ),
      },
      {
        title: "GR Date",
        dataIndex: "grDate",
        key: "grDate",
        inputTypeValue: "grDate",
        width: "80px",
      },
      {
        title: "Truck No",
        dataIndex: "vehicleNumber",
        key: "vehicleNumber",
        width: "100px",

        inputTypeValue: "vehicleNumber",
      },
      {
        title: "Invoice Date",
        dataIndex: "invoiceDate",
        key: "invoiceDate",
        width: "100px",
        sorter: (a, b) => `${a.invoiceDate}`.localeCompare(b.invoiceDate),
      },
      {
        title: "Invoice",
        dataIndex: "invoiceUpload",
        inputTypeValue: "invoiceuploadFile",
        key: "invoiceUpload",
        width: "140px",
        render: (text, record) => (
          <span>
            {!record.invoiceDate ? (
              <p>&#8212;</p>
            ) : (
              <span style={{textAlign: "left"}}>
                {record.invoiceProof ? (
                  <a>
                    <div style={{display: "flex"}}>
                      <div style={{margin: "5px"}}>
                        {record.invoiceProof && (
                          <a
                            download={`Rc-${record.registrationNumber}`}
                            href={record.invoiceProof}
                            target="_blank"
                          >
                            <img src={ViewDocument} width="18px" />
                          </a>
                        )}
                      </div>
                      {/* {!record.invoiceProof && <p>&#8212;</p>} */}
                      {record.invoiceProof && (
                        <div
                          style={{margin: "5px", color: "#60c4e0"}}
                          onClick={() => this.viewDocments(record)}
                        >
                          View Document
                        </div>
                      )}
                    </div>
                  </a>
                ) : (
                  <p>&#8212;</p>
                )}
              </span>
            )}
          </span>
        ),
      },
      {
        title: "From",
        dataIndex: "loadLocation",
        key: "loadLocation",
        inputTypeValue: "loadLocation",
        width: "80px",
      },
      {
        title: "Destination",
        dataIndex: "deliveryLocation",
        key: "deliveryLocation",
        inputTypeValue: "deliveryLocation",
        width: "80px",
      },
      {
        title: "Delivery No",
        dataIndex: "deliveryNumber",
        key: "deliveyNumber",
        render: NullData,
        width: "100px",
      },
      {
        title: "Qty (MT)",
        dataIndex: "quantityInMetricTons",
        key: "qunantityInMetricTons",
        width: "80px",
        render: (text, record) => (
          <span>
            {!record.quantityInMetricTons
              ? 0
              : record.quantityInMetricTons.toFixed(2)}
          </span>
        ),
      },
      {
        title: "Rate",
        dataIndex: "rate",
        key: "rate",
        width: "80px",
        render: (text, record) => (
          <span>{!record.rate ? 0 : record.rate.toFixed(2)}</span>
        ),
      },
      {
        title: "Total",
        dataIndex: "totalExpense",
        key: "total",
        width: "80px",
        render: (text, record) => (
          <span>
            {!record.totalExpense ? 0 : record.totalExpense.toFixed(2)}
          </span>
        ),
      },
      {
        title: "Commission",
        dataIndex: "commissionPercent",
        key: "commissionRate",
        inputTypeValue: "commissionRate",
        render: NullData,
        colSpan: 2,
        width: "60px",
      },
      {
        title: "Commission Total",
        dataIndex: "commisionTotal",
        key: "commissionTotal",
        inputTypeValue: "commissionTotal",
        width: "80px",
        render: (text, record) => (
          <span>
            {!record.commisionTotal ? 0 : record.commisionTotal.toFixed(2)}
          </span>
        ),
        colSpan: 0,
      },
      {
        title: "Diesel",
        dataIndex: "diesel",
        key: "diesel",
        width: "80px",
        render: (text, record) => (
          <span>{!record.diesel ? 0 : record.diesel.toFixed(2)}</span>
        ),
        inputTypeValue: "diesel",
      },
      {
        title: "Cash",
        dataIndex: "cash",
        key: "cash",
        width: "80px",
        render: (text, record) => (
          <span>{!record.cash ? 0 : record.cash.toFixed(2)}</span>
        ),
      },
      {
        title: "Bank Transfer",
        dataIndex: "bankTransfer",
        key: "bankTransfer",
        inputTypeValue: "bankTransfer",
        width: "100px",
        render: (text, record) => (
          <span>
            {!record.bankTransfer ? 0 : record.bankTransfer.toFixed(2)}
          </span>
        ),
      },
      {
        title: "Shortage",
        dataIndex: "shortage",
        key: "shortage",
        inputTypeValue: "shortage",
        width: "80px",
        render: (text, record) => (
          <span>
            {!record.invoiceDate
              ? "0"
              : record && record.shortage
              ? record.shortage.toFixed(2)
              : "0"}
          </span>
        ),
      },
      {
        title: "Balance",
        dataIndex: "balance",
        key: "balance",
        width: "100px",
        render: (text, record) => (
          <span>
            {record.balance > 0 ? (
              record.balance.toFixed(2)
            ) : (
              <span style={{color: "red"}}>
                {record && record.balance ? record.balance.toFixed(2) : "0"}
              </span>
            )}
          </span>
        ),
      },
      {
        title: "Action",
        dataIndex: "operation",
        fixed: "right",
        width: "80px",
        render: (_, record) => {
          return (
            <div style={{display: "flex"}}>
              <div style={{margin: "5px"}}>
                {this.state.userDetails &&
                this.state.userDetails.roleName === "Admin" ? (
                  <img
                    src={Editicon}
                    style={{cursor: "pointer"}}
                    onClick={() => this.showmodals(record)}
                    width="20px"
                  />
                ) : (
                  <object
                    data={Editicon}
                    type="image/svg+xml"
                    className="svgediticon"
                    style={{cursor: "pointer"}}
                    width="20px"
                  ></object>
                  // <img
                  //   src={Editicon}
                  //   style={{cursor: "pointer"}}
                  //   width="20px"
                  // />
                )}
              </div>
            </div>
          );
        },
      },
    ];

    const {
      editableVehicleDetails,
      fileType,
      previewUrl,
      materialDataSelect,
      dataSource,
      userDetails,
    } = this.state;
    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Receive Register
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Review/Update Register
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>

        <div>
          <HeaderBreedCrumb path="home" history={this.props.history} />
        </div>
        <div
          className="site-layout-background"
          style={{
            margin: "20px",
            minHeight: 280,
          }}
        >
          <Spin spinning={this.state.loading} delay={100}>
            <Modal
              title={
                <div>
                  Receive Register
                  <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
                    Update Register
                  </p>
                </div>
              }
              className="ModalLook"
              visible={this.state.editvisible}
              onOk={this.handleOk}
              onCancel={this.handleCancelEdit}
              destroyOnClose={true}
              footer={null}
            >
              <div>
                {editableVehicleDetails && (
                  <EditChallanDetails
                    record={editableVehicleDetails}
                    parentFn={() => this.reloadAfterModalClose()}
                  />
                )}
              </div>
            </Modal>
            <Modal
              visible={this.state.doucmentvisible}
              title={
                <div>
                  View Document
                  <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
                    Invoice View
                  </p>
                </div>
              }
              onCancel={() => this.setState({doucmentvisible: false})}
              className={`ant${fileType === "pdf" ? "PdfModal" : "ImageModal"}`}
              style={{width: "900px"}}
              okButtonProps={{style: {display: "none"}}}
            >
              {fileType === "pdf" && <PdfViewer url={previewUrl || ""} />}
              {fileType !== "pdf" && (
                <img src={previewUrl} alt="img" style={imageStyle} />
              )}
            </Modal>
            <div className="recive">
              <div style={{padding: "0px 0px 0px 20px"}}>
                <Row>
                  <Col
                    xs={12}
                    sm={12}
                    md={5}
                    lg={5}
                    xl={5}
                    style={{padding: "10px"}}
                  >
                    <h3 className="filterLabel" style={{borderRadius: "10px"}}>
                      Sort by Date Range
                    </h3>
                    <div style={{display: "flex"}}>
                      <RangePicker
                        format={dateFormatList}
                        onChange={(dates) => this.onDateChange(dates)}
                        allowEmpty={[true, true]}
                        suffixIcon={<img src={dateicon} alt="dateIcon" />}
                      />
                    </div>
                  </Col>
                  <Col
                    xs={12}
                    sm={12}
                    md={5}
                    lg={5}
                    xl={5}
                    style={{padding: "10px"}}
                  >
                    <h3 className="filterLabel">
                      Search By Delivery No or Truck No
                    </h3>
                    <div>
                      <Search
                        className="searchbtn"
                        onChange={this.searchResult}
                        style={{height: "30px !important"}}
                      />
                    </div>
                  </Col>
                </Row>
              </div>
              <div className="recieveregTable">
                <Table
                  columns={columns}
                  rowKey="_id"
                  dataSource={
                    this.state.searchValue === "" ||
                    this.state.searchValue === null
                      ? dataSource
                      : this.state.searchResultArray
                  }
                  scroll={{x: 1500}}
                  pagination={{
                    current: this.state.page,
                    defaultPageSize: 50,
                    showSizeChanger: true, 
                    onShowSizeChange: this.changePaginationAll,
                    total: this.state.totalCount,
                    onChange: this.changePagination,
                  }}
                />
              </div>
            </div>
          </Spin>
        </div>
      </div>
    );
  }
}

export default RecieveRegister;
