import React from "react";
import {TagOutlined} from "@ant-design/icons";
import Trips from "../../assests/images/trips.svg";
import Revenue from "../../assests/images/revenue.svg";
import Commission from "../../assests/images/comission.svg";
import Outstanding from "../../assests/images/outstanding.svg";
import {Statistic, Card, Row, Col, Button} from "antd";
const convertCurrency = (x) => {
  if (x) {
    x = x.toString();
    let lastThree = x.substring(x.length - 3);
    const otherNumbers = x.substring(0, x.length - 3);
    if (otherNumbers != "") lastThree = "," + lastThree;
    const res = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree;
    return res;
  }
};
const StatisticCard = ({data}) => {
  return (
    <Card bordered={false} style={{width: "100%", fontSize: 11}}>
      <Row>
        <Col span={20} lg={24} xl={20} className="monthly-earning-left">
          <Statistic
            title={data.label}
            value={convertCurrency(data.value)}
            prefix={data.prefix}
          />
        </Col>
        <Col span={4} lg={24} xl={4}>
          {data.label == "Total Trips" ? (
            <Button
              type="primary"
              className="card-icon-btn"
              shape="circle"
              icon={<img src={Trips} />}
            />
          ) : (
            ""
          )}
          {data.label == "Revenue" ? (
            <Button
              type="primary"
              className="card-icon-btn"
              shape="circle"
              icon={<img src={Revenue} />}
            />
          ) : (
            ""
          )}
          {data.label == "Commission" ? (
            <Button
              type="primary"
              className="card-icon-btn"
              shape="circle"
              icon={<img src={Commission} />}
            />
          ) : (
            ""
          )}
          {data.label == "Total Outstanding" ? (
            <Button
              type="primary"
              className="card-icon-btn"
              shape="circle"
              icon={<img src={Outstanding} />}
            />
          ) : (
            ""
          )}
        </Col>
      </Row>
    </Card>
  );
};

export default StatisticCard;
