import React, {Component} from "react";
import {
  Button,
  Spin,
  Table,
  Popconfirm,
  Row,
  Col,
  Modal,
  Input,
  Form,
  Select,
  message,
} from "antd";
import {FormOutlined} from "@ant-design/icons";
import auth from "../../../utils/auth";
import axios from "axios";
import {BASE_URL} from "../../../constants/constants";
import utils from "../../../utils/auth";

const {Option} = Select;

class EditAccount extends Component {
  constructor(props) {
    super(props);
    const {Data} = this.props;
    this.state = {
      userDetail: Data,
    };
  }
  gettableData = async () => {
    try {
      const {_id} = this.props.Data;
      this.setState({loading: true});
      const response = await axios.get(`${BASE_URL}users/${_id}`);
      const userData = response.data.user || "";
      if (userData) {
        this.setState({
          userDetail: userData,
        });
      } else {
        this.setState({
          userDetail: "",
        });
      }
      this.setState({loading: false});
    } catch (err) {
      this.setState({loading: false});
      message.error("Error fetching data. Please try again later", 2);
    }
  };
  componentDidMount() {
    this.gettableData();
  }
  onFinish = async (userDetails) => {
    const {_id} = this.props.Data;
    const {name, email, phoneNumber, roleName} = userDetails;

    try {
      await axios
        .put(`${BASE_URL}update-user/${_id}`, {
          name,
          email,
          phoneNumber,
          roleName,
        })
        .then((data) => {
          message.success("successfully updated a user");
          this.props.parentFn();
          this.setState({loading: false});
        })
        .catch((error) => {
          const {response} = error;
          const {data} = response;
          const {message: msg} = data;
          message.error(msg);
        });
      this.props.parentFn();
      setTimeout(() => {
        utils.removeToken();
        // window.location.replace("/");
        window.location.reload();
      }, 500);
    } catch (err) {
      console.log(err);
    }
  };
  onReset = () => {
    this.props.parentFn();
  };

  render() {
    const {name, email, phoneNumber, roleName} = this.state.userDetail;
    return (
      <div>
        <Form
          ref={this.formRef}
          name="editsettingInfo"
          className="editsettings"
          onFinish={this.onFinish}
          initialValues={{
            name,
            email,
            phoneNumber,
            roleName,
          }}
          autoComplete="off"
        >
          <Row style={{margin: "10px 22px"}}>
            <Col xs={24} sm={24} md={6} lg={6} xl={6}>
              <h3 style={{marginLeft: "5px"}}>Name</h3>
              <div>
                <Form.Item
                  name="name"
                  rules={[
                    {
                      required: true,
                      message: "This feild is required",
                    },
                  ]}
                >
                  <Input />
                </Form.Item>
              </div>
            </Col>

            <Col xs={24} sm={24} md={6} lg={6} xl={6}>
              <h3 style={{marginLeft: "5px"}} className="require">
                Email ID
              </h3>
              <div>
                <Form.Item
                  name="email"
                  rules={[
                    {
                      required: true,
                      message: "This feild is required",
                    },
                  ]}
                >
                  <Input />
                </Form.Item>
              </div>
            </Col>

            <Col xs={24} sm={24} md={6} lg={6} xl={6}>
              <h3 style={{marginLeft: "5px"}} className="require">
                Phone Number
              </h3>
              <div>
                <Form.Item
                  name="phoneNumber"
                  rules={[
                    {
                      required: true,
                      message: "This feild is required",
                    },
                    {
                      min: 10,
                      message: "Mobile No not less than 10 digit",
                    },
                  ]}
                >
                  <Input
                    onKeyPress={(event) => {
                      if (
                        !/^[0-9]*\.?[0-9]*$/.test(event.key) &&
                        event.key !== "Backspace"
                      ) {
                        event.preventDefault();
                      }
                    }}
                    maxLength={10}
                    minLength={10}
                  />
                </Form.Item>
              </div>
            </Col>
            <Col xs={24} sm={24} md={6} lg={6} xl={6}>
              <h3 style={{marginLeft: "5px"}}>User Role</h3>
              <div>
                <Form.Item
                  name="roleName"
                  rules={[
                    {
                      required: true,
                      message: "This feild is required",
                    },
                  ]}
                >
                  <Input disabled />
                </Form.Item>
              </div>
            </Col>
          </Row>

          <Row
            style={{
              border: "0.5px dashed #979797",
              opacity: "0.40",
              margin: "20px 0 20px 0px",
            }}
          />
          <Row>
            <Col lg={17}></Col>
            <Col lg={7}>
              <div
                style={{
                  padding: "10px",
                  marginRight: "20px",
                  float: "right",
                }}
              >
                <Button className="savebtn" type="primary" htmlType="submit">
                  SAVE
                </Button>
              </div>
              <div style={{padding: "10px", float: "right"}}>
                <Button
                  type="primary"
                  className="cancelBtn"
                  onClick={() => {
                    this.onReset();
                  }}
                >
                  CANCEL
                </Button>
              </div>
            </Col>
          </Row>
        </Form>
      </div>
    );
  }
}

export default EditAccount;
