import React, {Component} from "react";
import {Row, Col, Menu} from "antd";
import "./reportbreedcrumb.css";

class ReportMenuBreedCrumb extends Component {
  constructor(props, a) {
    super(props);
    const {userData} = this.props;
    this.state = {
      current: null,
      userdetails: userData,
    };
  }

  handleConsigmentClick = (e) => {
    this.setState({
      current: e.key,
      activeItem: 1,
      activeState: false,
    });
  };

  report = () => {
    this.props.history.push("/report");
  };

  reportDashboard = () => {
    this.props.history.push("/report-dashboard");
  };

  render() {
    const url = this.props.history.location.pathname;
    const {userdetails} = this.state;
    return (
      <div>
        <Row className="consignmentList">
          <Col span={20}>
            <Menu
              onClick={this.handleConsigmentClick}
              selectedKeys={[this.state.current]}
              mode="horizontal"
            >
              {userdetails && userdetails.roleName === "Admin" ? (
                <Menu.Item
                  key="truckmaster"
                  className={
                    url === "/report-dashboard"
                      ? "consigmentActiveItem"
                      : "consigmentItem"
                  }
                  onClick={this.reportDashboard}
                >
                  Visual Dashboard
                </Menu.Item>
              ) : (
                ""
              )}
              <Menu.Item
                key="ownermaster"
                className={
                  url === "/report" ? "consigmentActiveItem" : "consigmentItem"
                }
                onClick={this.report}
              >
                Report Dashboard
              </Menu.Item>
            </Menu>
          </Col>
        </Row>
      </div>
    );
  }
}

export default ReportMenuBreedCrumb;
