import React from "react";
import {
  Input,
  Button,
  Form,
  Table,
  Row,
  Col,
  Select,
  Spin,
  message,
  List,
} from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import {
  getAllDeliveryNumbers,
  createBillRegister,
  updateBillRegister,
  getBillRegisterDataById,
} from "./billRegisterAPI";

import InfiniteScroll from "react-infinite-scroller";

import "../recoveryRegister/recoveryRegister.css";

const { Search } = Input;
const { Option } = Select;
const numInput = (event) => {
  const val = event.target.value;
  // var digit = /^[0-9]*\.?[0-9]*$/.test(val);
  if (/^[0-9]*\.?[0-9]*$/.test(val) && event.key !== "Backspace") {
    var with2Decimals = val.toString().match(/^-?\d+(?:\.\d{0,1})?/);
    if (with2Decimals) {
      event.target.value = with2Decimals[0];
    }
  } else {
    return true;
  }
};
function handleChange(value) {
  console.log(`selected ${value}`);
}

class BillForm extends React.Component {
  constructor(props) {
    super(props);
  }

  state = {
    allDeliveryNumbers: [],
    deliveryNumbersList: [],
    allDerivedDOList: [],
    derivedDOList: [],
    originalSearchText: "",
    derivedSearchText: "",
    derivedSearch: [],
    removedDoList: [],
    loadingoutside: false,
    loading: false,
    hasMore: true,
    page: 1,
    limit: 300,
    data: [],
  };

  materialtype = [
    {
      title: "",
      dataIndex: "deliveryNumber",
      width: "70%",
      editable: true,
    },

    {
      title: "",
      dataIndex: "operation",
      width: "30%",
      render: (text, record) => {
        return (
          <div>
            <Button
              type="primary"
              className="addBtn"
              onClick={() => this.addDerivedDoList(record)}
              disabled={this.state.derivedDOList.length >= 50}
            >
              ADD
            </Button>
          </div>
        );
      },
    },
  ];

  loadLocation = [
    {
      title: "",
      dataIndex: "deliveryNumber",
      width: "70%",
    },
    {
      title: "",
      dataIndex: "operation",
      width: "30%",
      render: (text, record) => {
        return (
          <div>
            <Button
              type="primary"
              className="rmoveBtn"
              onClick={() => this.removeDerivedDoList(record)}
            >
              REMOVE
            </Button>
          </div>
        );
      },
    },
  ];

  addDerivedDoList = (data) => {
    const allDerivedDOList = [...this.state.derivedDOList, data];
    const deliveryNumbersList = this.state.deliveryNumbersList.filter(
      (item) => item._id !== data._id
    );

    this.setState({
      deliveryNumbersList: deliveryNumbersList,
      allDerivedDOList,
      derivedDOList: allDerivedDOList,
      removedDoList: [
        ...this.state.removedDoList.filter((id) => id !== data._id),
      ],
    });
  };
  handleInfiniteOnLoad = async () => {
    await this.setState({
      loading: true,
    });
    let { page, limit } = this.state;
    page = page + 1;
    limit = limit + 300;
    let { deliveryNumbersList } = this.state;
    getAllDeliveryNumbers(page, limit).then((res) => {
      deliveryNumbersList = deliveryNumbersList.concat(res);
      this.setState({
        deliveryNumbersList,
        loading: false,
        page,
        limit,
      });
    });
  };

  removeDerivedDoList = (data) => {
    const allDerivedDOList = this.state.derivedDOList.filter(
      (item) => item._id !== data._id
    );
    const deliveryNumbersList = [...this.state.deliveryNumbersList, data];

    this.setState({
      deliveryNumbersList: deliveryNumbersList,
      allDerivedDOList,
      derivedDOList: allDerivedDOList,
      removedDoList: [...this.state.removedDoList, data._id.toString()],
    });
  };

  onFinish = async (values) => {
    let payload = {};

    if (this.props.editMode) {
      payload = {
        billType: values.billType,
        tax: values.tax,
        remarks: values.remarks,
        valueRaised: values.valueRaised,
        // challan: this.state.allDerivedDOList.map((item) => item._id),
        challan:
          this.state.allDerivedDOList.length > 0
            ? this.state.allDerivedDOList
            : [
                ...this.props.data.challan.filter(
                  (item) => !this.state.removedDoList.includes(item)
                ),
              ],
      };
    } else {
      payload = {
        billType: values.billType,
        tax: values.tax,
        remarks: values.remarks,
        valueRaised: values.valueRaised,
        challan: this.state.allDerivedDOList.map((item) => item._id),
      };
    }

    const savePayload = {
      ...payload,
      billNumber: values.billNumber,
    };

    const updatePayload = {
      ...payload,
      billNumber: values.billNumber,
    };

    this.setState({
      loadingoutside: true,
    });

    const response = this.props.editMode
      ? await updateBillRegister(
          this.props.data && this.props.data._id,
          updatePayload
        )
      : await createBillRegister(savePayload);

    if (response && response.message) {
      if (this.props.onSearch()) {
        this.props.onSearch();
      }

      message.success(
        `Bill register ${
          this.props.editMode ? "updated" : "created"
        } successfully`
      );
      this.props.onSave();
    }

    this.setState({
      loadingoutside: false,
    });
  };

  handleGetAllDeliveryNumbers = async () => {
    try {
      this.setState({
        loading: true,
        loadingoutside: true,
      });
      const response = await getAllDeliveryNumbers();
      const slicedResponse = response.filter(
        (item) => this.props.linkedChallans.indexOf(item._id) === -1
      );

      const allDeliveryNumbers = this.props.editMode
        ? slicedResponse.filter(
            (item) => !this.props.data.challan.includes(item._id)
          )
        : slicedResponse;
      const allDerivedDOList = this.props.editMode
        ? response.filter((item) => this.props.data.challan.includes(item._id))
        : [];

      this.setState({
        allDeliveryNumbers: allDeliveryNumbers,
        deliveryNumbersList: allDeliveryNumbers,
        allDerivedDOList: allDerivedDOList,
        // derivedDOList: allDerivedDOList,
        loading: false,
        loadingoutside: false,
      });
    } catch (err) {
      console.log(err);
    }
  };

  async getBillRegisterById() {
    this.setState({ loadingoutside: true });
    let response;
    if (this.props.editMode) {
      response = await getBillRegisterDataById(this.props.data._id);
    }

    let doList = [];
    response.challan.map((item) => {
      doList.push({ _id: item._id, deliveryNumber: item.deliveryNumber });
    });

    const final_res = await this.setState({
      derivedDOList: doList,
      derivedSearch: doList,
    });
    if (final_res) {
      this.setState({ loadingoutside: false });
    }
  }

  componentDidMount() {
    this.handleGetAllDeliveryNumbers();
    if (this.props.editMode) {
      this.getBillRegisterById();
    }
  }

  handleSaveRecoveryCode = () => {};

  searchResult = async (searchText, type) => {
    await this.setState({
      loading: true,
    });
    // console.log(searchText, "searchtext");
    // const response = await getAllDeliveryNumbers(searchText);
    getAllDeliveryNumbers(searchText).then((res) => {
      res = res.filter(
        (ar) => !this.state.derivedDOList.find((rm) => rm._id === ar._id)
      );
      this.setState({
        deliveryNumbersList: res,
        loading: false,
      });
    });

    this.setState({
      [type === "original"
        ? "originalSearchText"
        : "derivedSearchText"]: searchText,
    });
  };

  searchResultDoList = async (searchText, type) => {
    await this.setState({
      loading: true,
    });

    const { derivedDOList, derivedSearch } = this.state;
    const searchData = derivedSearch.filter((item) =>
      item.deliveryNumber.match(searchText)
    );

    await this.setState({
      derivedDOList:
        searchData.length == 0
          ? []
          : searchData == ""
          ? derivedDOList
          : searchData,
      loading: false,
    });
  };

  render() {
    const locale = {
      emptyText: (
        <div className="ant-empty ant-empty-normal">
          <span>
            <p>
              <ExclamationCircleOutlined style={{ color: "red" }} />
              <span style={{ padding: "5px", textTransform: "initial" }}>
                Please, add DO number.
              </span>
            </p>
          </span>
        </div>
      ),
    };
    const {
      deliveryNumbersList,
      originalSearchText,
      derivedSearchText,
      derivedDOList,
      loadingoutside,
      loading,
    } = this.state;

    return (
      <Spin spinning={loadingoutside} delay={100}>
        <div style={{ padding: "30px 10px 30px 30px" }}>
          <h2 style={{ fontWeight: "bold" }}>Link Code</h2>
          {/* <Row style={{marginTop: "20px"}}> */}
          <Form
            name="material"
            onFinish={this.onFinish}
            initialValues={this.props.data}
          >
            <Row>
              <Col xs={24} sm={24} md={7} lg={7} xl={7}>
                <div>
                  <h3 className="title">Bill No.</h3>
                  <Form.Item
                    name="billNumber"
                    validateFirst
                    rules={[
                      {
                        message: "This feild is required",
                        required: true,
                      },
                      {
                        pattern: new RegExp(/^(?=.*[0-9])(?=.*)([0-9]+)$/),
                        message: "Invalid Bill Number",
                      },
                      {
                        pattern: new RegExp("^.{1,8}$"),
                        message: "Bill Number cannot exceed 8 digits",
                      },
                      {
                        max: 8,
                        message: "Bill No Not more than 8 digit",
                      },
                    ]}
                  >
                    <Input
                      onKeyPress={(event) => {
                        if (
                          !/^[0-9]$/.test(event.key) &&
                          event.key !== "Backspace"
                        ) {
                          event.preventDefault();
                        }
                      }}
                      maxLength={8}
                    />
                    {/* <Input disabled={this.props.editMode} maxLength="6" /> */}
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={6} lg={6} xl={6} offset={1}>
                <div>
                  <h3 className="title">Bill Type</h3>
                  <Form.Item
                    name="billType"
                    rules={[
                      {
                        message: "This feild is required",
                        required: true,
                      },
                    ]}
                  >
                    <Select placeholder="Select" onChange={handleChange}>
                      <Option value="Physical">Physical</Option>
                      <Option value="Epod">Epod</Option>
                      <Option value="Incentive">Incentive</Option>
                    </Select>
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={6} lg={6} xl={6} offset={1}>
                <div>
                  <h3 className="title">Value Raised</h3>
                  <Form.Item
                    name="valueRaised"
                    rules={[
                      {
                        message: "This feild is required",
                        required: true,
                      },
                      {
                        pattern: new RegExp(/^\d{1,8}(\.\d{1,2})?$/),
                        message: "Only 8 digit with upto 2 decimal allowed",
                      },
                      // {
                      //   max: 8,
                      //   message: "Bill No Not more than 8 digit",
                      // },
                    ]}
                  >
                    <Input
                      type="number"
                      onKeyPress={(event) => {
                        if (numInput(event)) {
                          event.preventDefault();
                        }
                      }}
                      maxLength={11}
                    />
                  </Form.Item>
                </div>
              </Col>
            </Row>
            <Row>
              <Col xs={24} sm={24} md={7} lg={7} xl={7}>
                <div>
                  <h3 className="title"> Tax</h3>
                  <Form.Item
                    name="tax"
                    rules={[
                      {
                        message: "This feild is required",
                        required: false,
                      },
                      // {
                      //   pattern: new RegExp(
                      //     /^\s*(\d+(\.\d{1,2})?|\.\d{1,2})\s*$/
                      //   ),
                      //   message: "Only decimals upto 2 digits are allowed",
                      // },
                      {
                        pattern: new RegExp(/^\d{1,8}(\.\d{1,2})?$/),
                        message: "Only 8 digit with 2 decimal allowed",
                      },
                    ]}
                  >
                    <Input
                      type="number"
                      onKeyPress={(event) => {
                        if (numInput(event)) {
                          event.preventDefault();
                        }
                      }}
                      maxLength={11}
                    />
                  </Form.Item>
                </div>
              </Col>
              <Col xs={24} sm={24} md={13} lg={13} xl={13} offset={1}>
                <div>
                  <h3 className="title"> Remarks</h3>
                  <Form.Item
                    name="remarks"
                    rules={[
                      {
                        message: "This feild is required",
                        required: false,
                      },
                      {
                        pattern: /^[a-zA-Z0-9 ]+$/i,
                        message: "only alphanumeric allowed",
                      },
                    ]}
                  >
                    <Input
                      onKeyPress={function (event) {
                        var test = event.target.value
                        if (/[^a-zA-Z0-9 ]/.test(test)) {
                          event.preventDefault()
                        }
                      }}/>
                  </Form.Item>
                </div>
              </Col>
            </Row>
            <Row style={{ margin: "0 0 30px 0" }}>
              <Col xs={24} sm={24} md={8} lg={8} xl={8}>
                <h3 className="title">Add DO number to Bill</h3>
                <div className="master-data-maindiv">
                  <div style={{ width: "100%" }}>
                    <div className="searchdiv">
                      <Search
                        className="searchdata"
                        // style={{width: "270px"}}
                        onChange={(e) =>
                          this.searchResult(e.target.value, "original")
                        }
                        placeholder="Search"
                        // value={originalSearchText}
                      />
                    </div>
                    <div className="materialTable">
                      <div className="demo-infinite-container">
                        <InfiniteScroll
                          initialLoad={false}
                          pageStart={0}
                          loadMore={this.handleInfiniteOnLoad}
                          hasMore={!this.state.loading && this.state.hasMore}
                          useWindow={false}
                        >
                          <List
                            dataSource={deliveryNumbersList.filter((item) =>
                              item.deliveryNumber.includes(originalSearchText)
                            )}
                            renderItem={(item) => (
                              <List.Item
                                actions={[
                                  <Button
                                    type="primary"
                                    className="addBtn"
                                    onClick={() => this.addDerivedDoList(item)}
                                  >
                                    ADD
                                  </Button>,
                                ]}
                              >
                                <List.Item.Meta title={item.deliveryNumber} />
                              </List.Item>
                            )}
                          >
                            {this.state.loading && this.state.hasMore && (
                              <div className="demo-loading-container">
                                <Spin />
                              </div>
                            )}
                          </List>
                        </InfiniteScroll>
                      </div>
                      {/* <Table
                        rowClassName={() => "editable-row"}
                        dataSource={deliveryNumbersList.filter((item) =>
                          item.deliveryNumber.includes(originalSearchText)
                        )}
                        columns={this.materialtype}
                        pagination={false}
                        scroll={{y: 270}}
                        rowKey="name"
                      /> */}
                    </div>
                  </div>
                </div>
              </Col>
              <Col xs={24} sm={24} md={8} lg={8} xl={8} offset={2}>
                <h3 className="title">DO List</h3>
                <div className="master-data-maindiv">
                  <div style={{ width: "100%" }}>
                    <div className="searchdiv">
                      <Search
                        className="searchdata"
                        // style={{width: "270px"}}
                        onChange={(e) =>
                          this.searchResultDoList(e.target.value, "derived")
                        }
                        placeholder="Search"
                        // value={derivedSearchText}
                      />
                    </div>
                    <div className="materialTable">
                      <Table
                        rowClassName={() => "editable-row"}
                        dataSource={derivedDOList.filter((item) =>
                          item.deliveryNumber.includes(derivedSearchText)
                        )}
                        columns={this.loadLocation}
                        pagination={false}
                        scroll={{ y: 270 }}
                        rowKey="age"
                        locale={locale}
                      />
                    </div>
                  </div>
                </div>
              </Col>
            </Row>
            <Row>
              <Col lg={24} style={{ margin: "10px 0px" }}>
                <div
                  style={{
                    padding: "10px",
                    float: "right",
                    margin: "0px 23px 0px 10px",
                  }}
                >
                  <Button className="save" type="primary" htmlType="submit">
                    {`${this.props.editMode ? "UPDATE" : "SAVE"}`}
                  </Button>
                </div>
                <div style={{ padding: "10px", float: "right" }}>
                  <Button
                    type="primary"
                    className="cancelBtn"
                    onClick={() => this.props.onCancel()}
                  >
                    CANCEL
                  </Button>
                </div>
              </Col>
            </Row>
          </Form>
          {/* </Row> */}
        </div>
      </Spin>
    );
  }
}

export default BillForm;
