import React, {useState, useEffect} from "react";
import {
  Table,
  Input,
  Popconfirm,
  Form,
  Button,
  DatePicker,
  Col,
  Row,
  Spin,
  message,
} from "antd";
import moment from "moment";
import {FormOutlined} from "@ant-design/icons";
import Deleteicon from "../../../assests/images/delete.svg";
import Addtruckicon from "../../../assests/images/addtruckicon.svg";
import axios from "axios";
import {BASE_URL} from "../../../constants/constants";
import dateicon from "../../../assests/images/dateicon.svg";
import auth from "../../../utils/auth";
import "./cashBook.css";

const userDetails = auth.getAuthToken().userDetails;
const dateFormat = "DD/MM/YYYY";
const currentYear = moment().year();
const currentMonth = moment().month() + 1;
let defDateRange = moment(`${currentMonth}/1/${currentYear}`);

let selectedDate = null;

const onDateChange = (date, dateString) => (selectedDate = dateString);

const numInput = (event) => {
  try {
    const {target, which, keyCode} = event;
    const {value} = target || {};
    const charCode = which ? which : keyCode;
    if (charCode !== 46 && charCode > 31 && (charCode < 48 || charCode > 57))
      return true;
    if (value && value.indexOf(".") > -1 && value.split(".")[1].length > 1)
      return true;
    return false;
  } catch (err) {
    return true;
  }
};

const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  let inputNode = null;
  if (inputType === "number") {
    inputNode = (
      <Input
        onKeyPress={(event) => {
          if (numInput(event)) {
            event.preventDefault();
          }
        }}
        defaultValue={0}
        maxLength="13"
      />
    );
  }
  if (dataIndex === "entryDate") {
    inputNode = (
      <DatePicker
        onChange={onDateChange}
        format={[dateFormat]}
        allowClear={false}
        defaultPickerValue={defDateRange}
        suffixIcon={<img src={dateicon} alt="dateIcon" />}
      />
    );
  }
  if (dataIndex === "narration") {
    inputNode = <Input />;
  }
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex === "entryDate" ? "entDate" : dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const EditableCashBookTable = () => {
  const [form] = Form.useForm();
  const [data, setData] = useState("");
  const [selectedYear, setYear] = useState(currentYear);
  const [selectedMonth, setMonth] = useState(currentMonth);
  const [UpdateRecord, setUpdateRecord] = useState("");
  const [editingKey, setEditingKey] = useState("");
  const [allAmounts, setAllAmounts] = useState("");
  const [isLoading, setLoader] = useState(false);

  const isEditing = (record) => record._id === editingKey;

  const gettableData = async () => {
    try {
      setLoader(true);
      const response = await axios.get(
        `${BASE_URL}get-cash-book-by-month/${selectedMonth}/${selectedYear}`
      );
      const {cashBookEntries, amounts: cashBookAmount} = response.data || {};
      setAllAmounts(cashBookAmount);
      let dataSource = [];
      dataSource = cashBookEntries.map((data, index) => {
        const {entryDate: date, credit, debit} = data;
        const eDate = moment(date, "DD/MM/YYYY");
        const creditVal = credit.toFixed(2);
        const debitVal = debit.toFixed(2);
        return {
          ...data,
          slno: index + 1,
          entDate: eDate,
          creditVal,
          debitVal,
        };
      });
      const {previousMonthOutstandingAmount: pa} = cashBookAmount;
      const amt = Number(pa);
      let prevMonthDate = null;
      let pmd = moment(`1/${selectedMonth}/${selectedYear}`, dateFormat)
        .subtract(1, "months")
        .format(dateFormat);
      // if (pMonth) {
      //   prevMonthDate = `01/${
      //     pMonth < 9 ? `0${pMonth}` : pMonth
      //     }/${selectedYear}`;
      // }
      let dt = [];
      dt = [
        ...dataSource,
        {
          slno: dataSource.length + 1,
          entryDate: pmd || prevMonthDate,
          narration: "Previous month balance",
          debitVal: amt < 0 ? Math.abs(amt) + ".00" : "0.00",
          creditVal: amt > 0 ? Math.abs(amt) + ".00" : "0.00",
          noEdit: true,
        },
      ];
      setData(dt);
      setLoader(false);
    } catch (err) {
      console.log(err);
      setLoader(false);
    }
  };

  useEffect(() => {
    if (!data) {
      gettableData();
    }
  });

  const edit = (record) => {
    form.setFieldsValue({
      entryDate: "",
      narration: "",
      debit: "",
      credit: "",
      entryDateObj: "",
      ...record,
    });
    setEditingKey(record._id);
    setUpdateRecord(record);
  };

  const save = async (key) => {
    try {
      const row = await form.validateFields();
      const id = UpdateRecord._id;
      const {entDate, narration, debitVal, creditVal} = row;
      if (
        (Number(creditVal || 0) === 0 && Number(debitVal || 0) > 0) ||
        (Number(debitVal || 0) === 0 && Number(creditVal || 0) > 0)
      ) {
        let dt = null;
        if (!selectedDate) {
          const DateString = moment(entDate).format("DD/MM/YYYY");
          const startDate = moment(DateString, "DD/MM/YYYY");
          dt = startDate._i;
        }
        try {
          await axios.put(`${BASE_URL}update-cash-book/${id}`, {
            entryDate: selectedDate || dt,
            debit: Number(debitVal || 0),
            credit: Number(creditVal || 0),
            narration,
          });
          selectedDate = null;
          message.success("Successfully Added Cashbook details");
          setData(null);
          setEditingKey("");
        } catch (err) {
          console.log(err);
        }
      } else {
        message.error("Enter either debit or credit");
      }
    } catch (errInfo) {
      console.error({errInfo});
    }
  };

  const createCashBook = async () => {
    try {
      const row = await form.validateFields();
      const {narration, creditVal, debitVal} = row;
      if (
        (Number(creditVal || 0) === 0 && Number(debitVal || 0) > 0) ||
        (Number(debitVal || 0) === 0 && Number(creditVal || 0) > 0)
      ) {
        try {
          await axios.post(`${BASE_URL}create-cash-book`, {
            entryDate: selectedDate,
            debit: debitVal ? Number(debitVal) : 0,
            credit: creditVal ? Number(creditVal) : 0,
            narration,
          });
          message.success("Successfully Added Cashbook details");
          setEditingKey("");
          setData(null);
          selectedDate = null;
        } catch (err) {
          console.log(err);
        }
      } else {
        message.error("Enter either debit or credit");
      }
    } catch (errInfo) {
      // console.log("Validate Failed:", errInfo);
    }
  };

  const Cancel = (key) => {
    const dataSource = [...data];
    const dataof = dataSource.filter((item) => item.key !== key);
    setData(dataof);
    setEditingKey("");
  };

  const handleCancel = () => {
    setEditingKey("");
  };

  const confirmDelete = async (record) => {
    try {
      const {_id} = record;
      await axios.delete(`${BASE_URL}delete-cash-book/${_id}`);
      message.success("Deleted one entry successfully.");
      setData(null);
    } catch (err) {
      console.log(err);
      message.error("Network Error, Please Try Again");
    }
  };

  const handleAdd = () => {
    const newData = {
      entryDate: "",
      narration: "",
      debit: "",
      credit: "",
      debitVal: "",
      creditVal: "",
      entDate: "",
      entryDateObj: "",
      _id: 1,
      key: 1,
    };
    if (data && data.length > 0) {
      const datasource = [newData, ...data];
      setData(datasource);
    } else {
      const datasource = [newData];
      setData(datasource);
    }
    form.setFieldsValue({
      entryDate: "",
      narration: "",
      debit: "",
      credit: "",
      entryDateObj: "",
      debitVal: "0",
      creditVal: "0",
      entDate: "",
      _id: 1,
    });
    setEditingKey(1);
  };

  const columns = [
    {
      title: "Sl No",
      dataIndex: "slno",
      key: "name",
      width: "8%",
      render: (index, record) => <span>{record.slno}</span>,
    },
    {
      title: "Date",
      dataIndex: "entryDate",
      key: "entryDate",
      editable: true,
      width: "15%",
    },
    {
      title: "Narration",
      dataIndex: "narration",
      key: "narration",
      editable: true,
      width: "33%",
      render: (index, record) => (
        <span style={{textTransform: "capitalize"}}>{record.narration}</span>
      ),
    },
    {
      title: "Debit (Dr)",
      dataIndex: "debitVal",
      inputType: "number",
      key: "debitVal",
      editable: true,
      width: "16%",
    },
    {
      title: "Credit (Cr)",
      dataIndex: "creditVal",
      inputType: "number",
      key: "creditVal",
      editable: true,
      width: "17%",
    },
    {
      title: "",
      dataIndex: "",
      width: "12%",
      render: (_, record) => {
        const editable = isEditing(record);
        if (record.noEdit) {
          return <span />;
        }
        return editable ? (
          <span>
            {record.key === 1 ? (
              <div style={{display: "flex"}}>
                <div>
                  <Button
                    type="primary"
                    className="saveCashbook"
                    onClick={() => createCashBook(record.key)}
                  >
                    SAVE
                  </Button>
                </div>
                <div>
                  <Button
                    type="primary"
                    className="cancelCashbook"
                    onClick={() => Cancel(record.key)}
                  >
                    CANCEL
                  </Button>
                </div>
              </div>
            ) : (
              <div style={{display: "flex"}}>
                <div>
                  <Button
                    type="primary"
                    className="saveCashbook"
                    onClick={() => save(record.key)}
                  >
                    SAVE
                  </Button>
                </div>
                <div>
                  <Button
                    type="primary"
                    className="cancelCashbook"
                    onClick={handleCancel}
                  >
                    CANCEL
                  </Button>
                </div>
              </div>
            )}
          </span>
        ) : (
          <div style={{display: "flex", alignItems: "center"}}>
            <Button
              type="primary"
              className="cancelBtn"
              disabled={editingKey !== ""}
              onClick={() => edit(record)}
              style={{fontSize: "10px", height: "24px", padding: "2px"}}
            >
              <FormOutlined
                style={{fontSize: "11px", margin: "3px 2px 3px 4px"}}
              />
              <span style={{margin: "0px 4px 0px 4px"}}>EDIT</span>
            </Button>
            <div style={{margin: "0 5px"}}>
              {userDetails && userDetails.roleName === "Admin" ? (
                <Popconfirm
                  title="Are you sure?"
                  onConfirm={() => confirmDelete(record)}
                  okText="Yes"
                  cancelText="No"
                >
                  <img src={Deleteicon} style={{cursor: "pointer"}} alt="del" />
                </Popconfirm>
              ) : (
                ""
              )}
            </div>
          </div>
        );
      },
    },
  ];

  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: col.inputType,
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });

  const disabledDate = (current) => {
    // Can not select days before today and today
    return current && current > moment().endOf("day");
  };

  const onMonthChange = async (date, dateString) => {
    const [yr, mnt] = dateString.split("-");
    defDateRange = moment(`${mnt}/1/${yr}`);
    setEditingKey("");
    setYear(yr);
    setMonth(mnt);
    setData(null);
  };

  const {
    monthlyTotalDebit,
    monthlyTotalCredit,
    monthlyOutstanding,
    totalDebit,
    totalCredit,
    outstandingAmount,
  } = allAmounts;

  return (
    <Spin spinning={isLoading}>
      <div className="cashBookContainer">
        <div className="cashBookHeader">
          <h2>Daily Cash Book</h2>
          <Col xs={12} sm={12} md={9} lg={9} xl={9}>
            <div style={{display: "flex", float: "right"}}>
              <div style={{padding: "0px 10px 10px 10px"}}>
                <h3 className="cashbookTitle">Filter By Month</h3>
                <DatePicker
                  style={{width: "150px", height: "30px"}}
                  onChange={onMonthChange}
                  picker="month"
                  allowClear={false}
                  disabledDate={disabledDate}
                  defaultValue={moment()}
                  suffixIcon={<img src={dateicon} alt="dateIcon" />}
                />
              </div>
              <div style={{padding: "18px 10px 10px 10px"}}>
                <Button
                  type="primary"
                  className="addEntry"
                  icon={
                    <img
                      src={Addtruckicon}
                      style={{marginRight: "6px"}}
                      alt="truckIcon"
                    />
                  }
                  onClick={handleAdd}
                  disabled={editingKey !== ""}
                >
                  ADD ENTRY
                </Button>
              </div>
            </div>
          </Col>
        </div>
        <Form form={form} component={false}>
          <Table
            components={{
              body: {
                cell: EditableCell,
              },
            }}
            dataSource={data}
            columns={mergedColumns}
            rowClassName="editable-row"
            className="cashBookTable"
            pagination={false}
            rowKey="_id"
          />

          <Row className="balanceRow">
            <Col className="colOne"></Col>

            <Col className="colTwo">
              <span className="balanceTitle">Current Month Balance</span>
            </Col>
            <Col className="colDebit">
              <span className="balanceText">
                {monthlyTotalDebit || "00.00"}
              </span>
            </Col>
            <Col className="colCredit">
              <span className="balanceText">
                {monthlyTotalCredit || "00.00"}
              </span>
            </Col>
            <Col className="colOutstanding">
              <span
                className="balanceText"
                style={monthlyOutstanding < 0 ? {color: "red"} : {}}
              >
                {monthlyOutstanding || "00.00"}
              </span>
            </Col>
          </Row>
        </Form>
      </div>
    </Spin>
  );
};

export default EditableCashBookTable;
