import React, {Component} from "react";
import {Button, message, DatePicker} from "antd";
import * as XLSX from "xlsx";
import DownloadIcon from "../../assests/images/download.svg";

class DownloadBillRegister extends Component {
  constructor(props) {
    super(props);
  }
  onDownload = () => {
    const selectedData = this.props.data.map((d, index) => ({
      "Sl No": index + 1,
      "Bill No": d.billNumber,
      "Bill Type": d.billType,
      "Value Raised": d.valueRaised,
      "Value Recieved": d.valueReceived,
      Tax: d.tax,
      Difference: d.difference,
      Remarks: d.remarks,
    }));

    if (selectedData.length === 0) {
      message.error("Please select bill(s) to download");
    } else {
      const ws = XLSX.utils.json_to_sheet(selectedData);
      const wb = {
        Sheets: {bill_register: ws},
        SheetNames: ["bill_register"],
      };
      const excelBuffer = XLSX.write(wb, {bookType: "xlsx", type: "array"});
      const data = new Blob([excelBuffer], {
        type:
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8",
      });
      var url = window.URL.createObjectURL(data);
      window.open(url, "_blank");
    }
  };
  render() {
    return (
      <Button
        className="outlineBtn"
        onClick={this.onDownload}
        icon={<img src={DownloadIcon} style={{paddingRight: "7px"}} />}
      >
        DOWNLOAD
      </Button>
    );
  }
}

export default DownloadBillRegister;
