import React, {Component} from "react";
import {Row, Col, Menu} from "antd";
import "./receivemenubreedcrumb.css";

class MenuBreedCrumb extends Component {
  constructor(props, a) {
    super(props);
    const {userData} = this.props;
    this.state = {
      current: null,
      userdetails: userData,
    };
  }

  handleConsigmentClick = (e) => {
    this.setState({
      current: e.key,
      activeItem: 1,
      activeState: false,
    });
  };

  acknowledgement = () => {
    this.props.history.push("/owner-advance");
  };

  receiveregister = () => {
    this.props.history.push("/voucher-book");
  };
  cashbook = () => {
    this.props.history.push("/daily-cash-book");
  };
  recoveryRegister = () => {
    this.props.history.push("/recovery-register");
  };
  billRegister = () => {
    this.props.history.push("/bill-register");
  };
  render() {
    const url = this.props.history.location.pathname;
    const {userdetails} = this.state;
    return (
      <div>
        <Row className="consignmentList">
          <Col span={20}>
            <Menu
              onClick={this.handleConsigmentClick}
              selectedKeys={[this.state.current]}
              mode="horizontal"
            >
              <Menu.Item
                key="ownermaster"
                className={
                  url === "/owner-advance"
                    ? "consigmentActiveItem"
                    : "consigmentItem"
                }
                onClick={this.acknowledgement}
              >
                Owner Advance
              </Menu.Item>
              <Menu.Item
                key="voucher-book"
                className={
                  url === "/voucher-book"
                    ? "consigmentActiveItem"
                    : "consigmentItem"
                }
                onClick={this.receiveregister}
              >
                Voucher Book
              </Menu.Item>
              <Menu.Item
                key="daily-cash-book"
                className={
                  url === "/daily-cash-book"
                    ? "consigmentActiveItem"
                    : "consigmentItem"
                }
                onClick={this.cashbook}
              >
                Daily Cash Book
              </Menu.Item>
              {userdetails && userdetails.roleName === "Admin" ? (
                <Menu.Item
                  key="recovery-register"
                  className={
                    url === "/recovery-register"
                      ? "consigmentActiveItem"
                      : "consigmentItem"
                  }
                  onClick={this.recoveryRegister}
                >
                  Recovery Register
                </Menu.Item>
              ) : (
                ""
              )}

              {userdetails && userdetails.roleName === "Admin" ? (
                <Menu.Item
                  key="bill-register"
                  className={
                    url === "/bill-register"
                      ? "consigmentActiveItem"
                      : "consigmentItem"
                  }
                  onClick={this.billRegister}
                >
                  Bill Register
                </Menu.Item>
              ) : (
                ""
              )}
            </Menu>
          </Col>
        </Row>
      </div>
    );
  }
}

export default MenuBreedCrumb;
