import React, {Component} from "react";
import {
  Table,
  Input,
  Button,
  Modal,
  Popconfirm,
  Checkbox,
  message,
  Spin,
  Row,
  Select,
  DatePicker,
  Col,
  Upload,
  Form,
} from "antd";
import moment from "moment";
import {RightOutlined, DownOutlined, UploadOutlined} from "@ant-design/icons";
import AddChallanDetails from "./adddispatchcallan";
import EditChallanDetails from "./editdispatchchallan";
import PrintChallan from "./printDispatchChallan";
import CurrentTime from "../../components/currenttime/currenttime";
import axios from "axios";
import {BASE_URL} from "../../constants/constants";
import Editicon from "../../assests/images/edit.svg";
import ChallanIcon from "../../assests/images/challan.svg";
import PrintIcon from "../../assests/images/print.svg";
import Deleteicon from "../../assests/images/delete.svg";
import DownloadIcon from "../../assests/images/download.svg";
import dateicon from "../../assests/images/dateicon.svg";
import Uploadicon from "../../assests/images/upload.svg";
import auth from "../../utils/auth";
import * as XLSX from "xlsx";
import "./dispatch.css";
const {Search} = Input;
const {RangePicker} = DatePicker;

const SELECTED_THRESHOLD = 0;
const iconStyle = {
  color: "#01a6d0",
  fontSize: "14px",
  width: "24px",
  height: "24px",
};
const dateFormat = ["DD/MM/YYYY"];

const {Option} = Select;
const NullData = (tableText) => <span>{tableText || <p>&#8212;</p>}</span>;
let timeout = null;
function capitalizeFirstLetter(word) {
  return word.charAt(0).toUpperCase() + word.slice(1);
}
const buildQueryParams = (params) => {
  let queryParams = [];
  for (const param in params) {
    if (params[param]) {
      queryParams.push(`${param}=${params[param]}`);
    }
  }
  return queryParams.length ? `?${queryParams.join("&")}` : "";
};

class DispatchChallan extends Component {
  constructor(props) {
    super(props);
    const Details = auth.getAuthToken().userDetails;
    const Det = auth.getUserData();
    this.state = {
      vidible: false,
      loading: false,
      doucmentvisible: false,
      editableVehicleDetails: null,
      fileurl: "",
      searchValue: "",
      searchResultArray: "",
      selectionType: Checkbox,
      dataSource: [],
      fileType: "",
      previewUrl: "",
      materialSelect: [],
      truckTypeStatus: [],
      startDate: "",
      endDate: "",
      materialDataSelect: [],
      truckDataSelect: [],
      selectRowArray: [],
      userDetails: Details,
      downloadVisible: false,
      totalCount: "",
      page: 1,
      limit:50,
      logId: Det._id,
      AlldispatchData: [],
      downloadLoading: false,
      filters: {
        startDate: null,
        endDate: null,
        searchTDNo: null,
        vehicleType: null,
        materialType: null,
      },
    };
  }
  customExpandIcon(props) {
    if (props.expanded) {
      return (
        <a
          style={{color: "black"}}
          onClick={(e) => {
            props.onExpand(props.record, e);
          }}
        >
          <DownOutlined style={iconStyle} className="customIcon" />
        </a>
      );
    }
    return (
      <a
        style={{color: "black"}}
        onClick={(e) => {
          props.onExpand(props.record, e);
        }}
      >
        <RightOutlined style={iconStyle} className="customIcon" />
      </a>
    );
  }
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });
  };
  handleModalOk = (e) => {
    this.setState({
      downloadVisible: false,
    });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };
  handleModalCancel = (e) => {
    this.setState({
      downloadVisible: false,
    });
  };
  handleCancelEdit = () => {
    this.setState({
      editvisible: false,
    });
  };
  getAllData = async () => {
    const data = this.state.filters;
    try {
      const responseAll = await axios.post(`${BASE_URL}get-all-challan-data`, {
        materialType: data.materialType ? data.materialType : null,
        vehicleType: data.vehicleType ? data.vehicleType : null,
        startDate: data.startDate ? data.startDate : null,
        endDate: data.endDate ? data.endDate : null,
        searchTDNo: data.searchTDNo ? data.searchTDNo : "",
      });
      const dispatchDataAll = responseAll.data.disptachData || "";
      this.setState({
        AlldispatchData: dispatchDataAll,
      });
    } catch (err) {}
  };

  gettableData = async (data, page,limit) => {
    try {
      const pages = page ? page : 1;
      const limitData = limit ? limit : this.state.limit; 
      this.setState({loading: true, page: pages}); 
      if (data) {
        const response = await axios.post(
          `${BASE_URL}get-challan-data?page=${pages}&limit=${limitData}`,
          {
            materialType: data.materialType ? data.materialType : null,
            vehicleType: data.vehicleType ? data.vehicleType : null,
            startDate: data.startDate ? data.startDate : null,
            endDate: data.endDate ? data.endDate : null,
            searchTDNo: data.searchTDNo ? data.searchTDNo : "",
          }
        );
        const dispatchData = response.data.disptachData[0] || "";
        if (dispatchData) {
          this.setState({
            loading:false,
            dataSource: dispatchData.data,
            totalCount: dispatchData.count,
          });
        } else {
          this.setState({
            dataSource: "",
          });
        }
      } else {
        const response = await axios.post(
          `${BASE_URL}get-challan-data?page=${pages}&limit=${limitData}`
        );

        const dispatchData = response.data.disptachData[0] || "";
        if (dispatchData) {
          this.setState({
            loading:false,
            dataSource: dispatchData.data,
            totalCount: dispatchData.count,
          });
        }
      }

      this.setState({loading: false});
      // const {searchValue} = this.state;
      // if (
      //   searchValue !== "" &&
      //   searchValue !== null &&
      //   searchValue !== undefined
      // ) {
      //   const searchVal = searchValue.toString().toUpperCase().trim();
      //   const newSearchedTableData = this.state.dataSource.filter(
      //     ({vehicleNumber, deliveryNumber}) => {
      //       return (
      //         this.searchFn(searchVal, deliveryNumber) ||
      //         this.searchFn(searchVal, vehicleNumber)
      //       );
      //     }
      //   );
      //   this.setState({searchResultArray: newSearchedTableData});
      // }
    } catch (err) {
      this.setState({loading: false});
      message.error("Error fetching data. Please try again later", 2);
    }
  };

  getMaterialData = async () => {
    try {
      const response = await axios.get(`${BASE_URL}get-material`);
      const {materials: materialData} = response.data || "";
      if (materialData && materialData.length > 0) {
        const materialtypes = materialData.map(({_id, materialType}) => {
          const val = materialType ? materialType.trimEnd() : null;
          if (val) {
            return (
              <Option key={_id} value={materialType}>
                {materialType}
              </Option>
            );
          }
        });
        this.setState({
          materialDataSelect: materialtypes,
        });
      }
    } catch (err) {
      console.log(err);
      message.error("Network Failed, Please Try Again");
    }
  };
  doSomethingBeforeUnload = async () => {
    await axios.put(`${BASE_URL}logout/${this.state.logId}`);
  };
  setupBeforeUnloadListener = async () => {
    window.addEventListener("beforeunload", async (ev) => {
      ev.preventDefault();
      return this.doSomethingBeforeUnload();
    });
  };

  componentDidMount() {
    this.gettableData();
    this.getMaterialData();
    this.setupBeforeUnloadListener();
    // this.getAllData();
  }

  showmodals(e) {
    this.setState({
      editvisible: true,
      editableVehicleDetails: e,
    });
  }

  deleterecord(e) {}

  confirm = async (e) => {
    try {
      await axios.delete(`${BASE_URL}delete-dispatch-challan/${e._id}`);
      message.success("Successfully deleted vehicle");
      this.setState({loading: true});
      try {
        await this.gettableData(this.state.filters);
        // const response = await axios.get(`${BASE_URL}get-challan-data`);
        // const dispatchData = response.data.disptachData || "";
        // // if (dispatchData && dispatchData.length > 0) {
        // this.setState({
        //   dataSource: dispatchData.map((data, index) => {
        //     return {...data, slno: index + 1};
        //   }),
        // });
        // }
        this.setState({loading: false});
      } catch (err) {
        this.setState({loading: false});
        message.error("Error fetching data. Please try again later", 2);
      }
    } catch (err) {
      message.error("Unable to delete");
    }
  };

  searchResult = async (e) => {
    let searchValue = e.target.value;
    const filters = {...this.state.filters};
    if (searchValue != "") {
      filters.searchTDNo = searchValue;
    } else {
      filters.searchTDNo = null;
    }
    this.setState({
      filters,
    });
    await this.gettableData(filters);
  };

  searchFn = (search, value) => {
    if (value && search) {
      return value.toString().toUpperCase().includes(search);
    } else {
      return false;
    }
  };

  reloadAfterModalClose = () => {
    this.gettableData(this.state.filters);
    this.setState({
      visible: false,
      editvisible: false,
      doucmentvisible: false,
    });
  };
  handleMaterialType = async (materialValue) => {
    try {
      if (materialValue && materialValue.length > 0) {
        const obj = materialValue.toString();
        materialValue = materialValue.toString().toUpperCase().trim();
        const matValue =
          Object.keys(materialValue).length > 0 ? obj.split(",") : [];
        const filters = {...this.state.filters};
        filters.materialType = matValue;
        await this.setState({
          filters,
        });
        this.setState({materialSelect: matValue});
        await this.gettableData(this.state.filters);
      } else {
        const filters = {...this.state.filters};
        filters.materialType = null;
        await this.setState({
          filters,
        });
        this.setState({materialSelect: []});
        await this.gettableData(filters);
      }
    } catch (err) {
      console.log(err);
      message.error("Unable to Filter");
    }
  };
  handleTruckType = async (truckValue) => {
    try {
      if (truckValue && truckValue.length > 0) {
        const value = truckValue.toString().toLowerCase().trim();
        const obj = value
          .split(" ")
          .map((w) => w[0].toUpperCase() + w.substr(1).toLowerCase())
          .join(" ");
        const vehicleValue =
          Object.keys(truckValue).length > 0 ? obj.split(",") : [];

        const filters = {...this.state.filters};
        filters.vehicleType = vehicleValue;
        await this.setState({
          filters,
        });
        await this.gettableData(filters);
        this.setState({truckTypeStatus: vehicleValue});
      } else {
        const filters = {...this.state.filters};
        filters.vehicleType = null;
        await this.setState({
          filters,
        });
        await this.gettableData(filters);
        this.setState({truckTypeStatus: []});
      }
    } catch (err) {
      console.log(err);
      message.error("Unable to Filter");
    }
  };

  onModalDownload = () => {
    if (this.state.selectRowArray && this.state.selectRowArray.length > 0) {
      this.setState({
        downloadVisible: true,
      });
    } else {
      message.error("Please select Challan to download");
    }
  };

  axiosFileUploadRequest = async (file) => {
    try {
      const formData = new FormData();
      formData.append("file", file.file);
      const config = {
        headers: {
          "content-type": "multipart/form-data",
        },
      };
      const response = await axios
        .post(`${BASE_URL}dispatch-upload-challan`, formData, config)
        .then((data) => {
          message.success("Sucessfully Uploaded");
          this.gettableData();
        })
        .catch((error) => {
          const {response} = error;
          const {data} = response;
          const {message: msg} = data;
          message.error(msg);
        });
    } catch (err) {
      message.error("Network Failed, Please Try Again");
    }
  };

  onModalFinish = async (e) => {
    const {challanType} = e;
    this.setState({loading:true}) 
    await this.getAllData();
    if (challanType == "selectedpage") {
      try {
        const selectedData = this.state.AlldispatchData.filter((d) => {
          if (this.state.selectRowArray.includes(d._id)) {
            return d;
          }
        }).map((d, index) => {
          return {
            "S.No": index + 1,
            id: d._id,
            materialType: d.materialType || "-",
            grNumber: d.grNumber || "-",
            grDate: d.grDate || "-",
            loadLocation: d.loadLocation || "-",
            deliveryLocation: d.deliveryLocation || "-",
            vehicleNumber: d.vehicleNumber || "-",
            vehicleType: d.vehicleType || "-",
            deliveryNumber: d.deliveryNumber || "-",
            quantityInMetricTons: d.quantityInMetricTons || "0",
            rate: d.rate || "0",
            diesel: d.diesel || "0",
            cash: d.cash || "0",
            bankTransfer: d.bankTransfer || "0",
          };
        });

        if (selectedData.length === 0) {
          message.error("Please select Challan to download");
        } else {
          const ws = XLSX.utils.json_to_sheet(selectedData);
          const wb = {
            Sheets: {vehicle_owner_details: ws},
            SheetNames: ["vehicle_owner_details"],
          };
          const excelBuffer = XLSX.write(wb, {bookType: "xlsx", type: "array"});
          const data = new Blob([excelBuffer], {
            type:
              "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8",
          });
          var url = window.URL.createObjectURL(data);
          window.open(url, "_blank");
          this.setState({loading:false})
        }
        this.setState({
          loading:false,
          downloadVisible: false,
        });
      } catch (error) {
        message.error("Unable to Download");
      }
    }
    if (challanType == "allchallans") {
      try {
        
        // const response = await axios.post(`${BASE_URL}get-all-challan-data`);
        const data = this.state.filters;
        const response = await axios.post(`${BASE_URL}get-all-challan-data`, {
          materialType: data.materialType ? data.materialType : null,
          vehicleType: data.vehicleType ? data.vehicleType : null,
          startDate: data.startDate ? data.startDate : null,
          endDate: data.endDate ? data.endDate : null,
          searchTDNo: data.searchTDNo ? data.searchTDNo : "",
        });

        const dispatchData = response.data.disptachData || "";
        const selectedData = dispatchData.map((d, index) => {
          return {
            "S.No": index + 1,
            id: d._id,
            "Material Type": d.materialType || "-",
            "GR Number": d.grNumber || "-",
            "GR Date": d.grDate || "-",
            "Load Location": d.loadLocation || "-",
            "Deliver To": d.deliveryLocation || "-",
            "Vehicle Number": d.vehicleNumber || "-",
            "Vehicle Type": d.vehicleType || "-",
            "Delivery Number": d.deliveryNumber || "-",
            "Qty (MT)": d.quantityInMetricTons || "0",
            Rate: d.rate || "0",
            Diesel: d.diesel || "0",
            Cash: d.cash || "0",
            "Bank Transfer": d.bankTransfer || "0",
          };
        });
        if (selectedData.length === 0) {
          message.error("Please select Challan to download");
        } else {
          const ws = XLSX.utils.json_to_sheet(selectedData);
          const wb = {
            Sheets: {vehicle_owner_details: ws},
            SheetNames: ["vehicle_owner_details"],
          };
          const excelBuffer = XLSX.write(wb, {bookType: "xlsx", type: "array"});
          const data = new Blob([excelBuffer], {
            type:
              "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8",
          });
          var url = window.URL.createObjectURL(data);
          window.open(url, "_blank");
        }
        this.setState({
          loading:false,
          downloadVisible: false,
        });
      } catch (error) {
        message.error("Unable to Download");
      }
    }
  };

  onDownload = () => {
    try {
      const selectedData = this.state.selectRowArray.map((d, index) => {
        return {
          "S.No": index + 1,
          "Material Type": d.materialType || "-",
          "GR Number": d.grNumber || "-",
          "GR Date": d.grDate || "-",
          "Load Location": d.loadLocation || "-",
          "Deliver To": d.deliveryLocation || "-",
          "Vehicle Number": d.vehicleNumber || "-",
          "Vehicle Type": d.vehicleType || "-",
          "Delivery Number": d.deliveryNumber || "-",
          "Qty (MT)": d.quantityInMetricTons || "0",
          Rate: d.rate || "0",
          Diesel: d.diesel || "0",
          Cash: d.cash || "0",
          "Bank Transfer": d.bankTransfer || "0",
        };
      });
      if (selectedData.length === 0) {
        message.error("Please select Challan to download");
      } else {
        const ws = XLSX.utils.json_to_sheet(selectedData);
        const wb = {
          Sheets: {vehicle_owner_details: ws},
          SheetNames: ["vehicle_owner_details"],
        };
        const excelBuffer = XLSX.write(wb, {bookType: "xlsx", type: "array"});
        const data = new Blob([excelBuffer], {
          type:
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8",
        });
        var url = window.URL.createObjectURL(data);
        window.open(url, "_blank");
      }
    } catch (error) {
      message.error("Unable to Print");
    }
  };

  onDateChange = async (dates) => {
    try {
      if (dates) {
        const [a, b] = dates;
        let startDate = "";
        let endDate = "";
        if (a) {
          startDate = new Date(a).getTime();
          const filters = {...this.state.filters};
          if (startDate != "") {
            filters.startDate = startDate;
          } else {
            filters.startDate = null;
          }
          await this.setState({
            filters,
          });
          // this.gettableData(filters);
        }
        if (b) {
          endDate = new Date(b).getTime();
          const filters = {...this.state.filters};
          if (endDate != "") {
            filters.endDate = endDate;
          } else {
            filters.endDate = null;
          }
          await this.setState({
            filters,
          });
          // this.gettableData(filters);
        }
        await this.gettableData(this.state.filters);
      } else {
        const filters = {...this.state.filters};
        filters.startDate = null;
        filters.endDate = null;
        this.setState({
          filters,
        });
        this.gettableData(filters);
      }
    } catch (error) {
      message.error("Unable to fetch data");
    }
  };

  changePagination = async (e) => {
    try {
      this.setState({loading: true, page: e});
      this.gettableData(this.state.filters, e);

      // this.setState({loading: false});
    } catch (err) {
      console.log(err);
    }
  };

  changePaginationAll = async(currentpage,pageSize) =>{
    try{
      this.setState({loading: true, page: currentpage,limit: pageSize });
      this.gettableData(this.state.filters,currentpage,pageSize);

      // this.setState({loading: false});
    }catch(err){
      console.log(err)
    }
  }

  viewDocments(e) {
    if (e.invoiceProof != null && e.invoiceProof != "") {
      let url = e.invoiceProof;
      let type = url ? url.split(".") : "";
      type = type[type.length - 1];
      this.setState({
        doucmentvisible: true,
        fileType: type,
        previewUrl: url,
      });
    } else {
      message.error("No Invoice Available", [0]);
    }
  }
  render() {
    const {selectRowArray} = this.state;
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const selectDataValue =
          selectRowArray && selectRowArray.length > 0 ? selectRowArray : [];
        this.setState({
          selectRowArray: selectedRowKeys,
        });
      },
      columnWidth: "10px",
      getCheckboxProps: (record) => ({
        disabled: record.name === "Disabled User",
        name: record.name,
      }),
    };
    const columns = [
      {
        title: "Sl No",
        dataIndex: "slno",
        key: "name",
        render: (value, item, index) => (this.state.page - 1) * this.state.limit + index + 1,
        // render: (text, record) => (
        //   <span>{this.state.dataSource.indexOf(record) + 1}</span>
        // ),
      },
      {
        title: "Material Type",
        dataIndex: "materialType",
        key: "materialType",
        render: (text, record) => (
          <span>
            {!record.materialType
              ? "0"
              : record.materialType
                  .split(" ")
                  .filter((w) => w.trim() != "")
                  .map(
                    (w) =>
                      w.split("")[0].toUpperCase() + w.substr(1).toLowerCase()
                  )
                  .join(" ")}
          </span>
        ),
      },
      {
        title: "GR Number",
        dataIndex: "grNumber",
        key: "grNumber",
        render: (text, record) => (
          <span>
            {record && (record.grNumber === null || record.grNumber === "") ? (
              text
            ) : (
              <p>&#8212;</p>
            )}
          </span>
        ),
      },
      {
        title: "GR Date",
        dataIndex: "grDate",
        key: "grDate",
      },
      {
        title: "Load Location",
        dataIndex: "loadLocation",
        key: "loadLocation",
      },
      {
        title: "Deliver To",
        dataIndex: "deliveryLocation",
        key: "deliverLocation",
      },
      {
        title: "Vehicle Number",
        dataIndex: "vehicleNumber",
        key: "vehicleNumber",
        sorter: (a, b) => `${a.vehicleNumber}`.localeCompare(b.vehicleNumber),
        render: (text) => (
          <span style={{textTransform: "uppercase"}}>{text}</span>
        ),
      },
      {
        title: "Vehicle Type",
        dataIndex: "vehicleType",
        render: (text) => (
          <span style={{textTransform: "capitalize"}}>{text}</span>
        ),
      },
      {
        title: "Delivery Number",
        dataIndex: "deliveryNumber",
        key: "deliveryNumber",
      },
      {
        title: "Qty (MT)",
        dataIndex: "quantityInMetricTons",
        key: "qunantityInMetricTons",
        render: (text, record) => (
          <span>
            {!record.quantityInMetricTons
              ? "0"
              : record.quantityInMetricTons.toFixed(2)}
          </span>
        ),
      },
      {
        title: "Rate",
        dataIndex: "rate",
        key: "rate",
        render: (text, record) => (
          <span>{!record.rate ? "0" : record.rate.toFixed(2)}</span>
        ),
      },
      {
        title: "Diesel",
        dataIndex: "diesel",
        key: "Diesel",
        render: (text, record) => (
          <span>{!record.diesel ? "0" : record.diesel.toFixed(2)}</span>
        ),
      },
      {
        title: "Cash",
        dataIndex: "cash",
        key: "cash",
        render: (text, record) => (
          <span>{!record.cash ? "0" : record.cash.toFixed(2)}</span>
        ),
      },
      {
        title: "Bank Transfer",
        dataIndex: "bankTransfer",
        key: "bankTransfer",
        render: (text, record) => (
          <span>
            {!record.bankTransfer ? "0" : record.bankTransfer.toFixed(2)}
          </span>
        ),
      },
      {
        title: "Action",
        dataIndex: "",
        key: "x",
        render: (text, record) => (
          <div style={{display: "flex"}}>
            <div style={{margin: "5px"}}>
              <img
                src={Editicon}
                style={{cursor: "pointer"}}
                onClick={() => this.showmodals(record)}
                width="15px"
              />
            </div>
            {userDetails && userDetails.roleName === "Admin" ? (
              <div style={{margin: "5px"}}>
                <Popconfirm
                  title="Are you sure?"
                  onConfirm={() => this.confirm(record)}
                  onCancel={this.cancel}
                  okText="Yes"
                  cancelText="No"
                >
                  <a href="#">
                    <img
                      src={Deleteicon}
                      style={{cursor: "pointer"}}
                      width="15px"
                    />
                  </a>
                </Popconfirm>
              </div>
            ) : (
              ""
            )}
          </div>
        ),
      },
    ];

    const {
      editableVehicleDetails,
      fileType,
      previewUrl,
      materialDataSelect,
      dataSource,
      userDetails,
    } = this.state;
    return (
      <div>
        <div
          style={{
            display: "flex",
            background: "#fff",
            height: "60px",
            marginTop: "2px",
            padding: "4px 10px 10px 30px",
          }}
        >
          <div style={{marginTop: "4px"}}>
            <h3
              style={{
                fontSize: "18px",
                fontFamily: "MyriadProBold",
                margin: "0px",
              }}
            >
              Dispatch Challan
            </h3>
            <p style={{fontSize: "12px", fontFamily: "MyriadProBold"}}>
              Create and View Dispatch Challan
            </p>
          </div>
          <div style={{position: "absolute", right: 40}}>
            <CurrentTime />
          </div>
        </div>

        <div
          className="site-layout-background"
          style={{
            margin: "40px 20px",
            minHeight: 280,
          }}
        >
          <Spin spinning={this.state.loading}>
            <div className="dispatchChallan">
              <Modal
                title={
                  <div>
                    <h3 style={{fontSize: "20px", margin: "0px"}}>
                      Dispatch Challan
                    </h3>
                    <p
                      style={{
                        fontSize: "12px",
                        fontFamily: "MyriadProBold",
                      }}
                    >
                      Enter Dispatch Challan Details
                    </p>
                  </div>
                }
                className="ModalLook"
                visible={this.state.visible}
                onOk={this.handleOk}
                destroyOnClose={true}
                onCancel={this.handleCancel}
                footer={null}
              >
                <div>
                  <AddChallanDetails
                    parentFn={() => this.reloadAfterModalClose()}
                  />
                </div>
              </Modal>
              
              <Modal
                title={
                  <div>
                    <h3 style={{fontSize: "20px", margin: "0px"}}>
                      Download Dispatch Challan
                    </h3>
                    <p
                      style={{
                        fontSize: "12px",
                        fontFamily: "MyriadProBold",
                      }}
                    >
                      Download Dispatch Challan Details
                    </p>
                  </div>
                }
                // className="ModalLook"
                visible={this.state.downloadVisible}
                onOk={this.handleModalOk}
                destroyOnClose={true}
                onCancel={this.handleModalCancel}
                footer={null}
              >
                <div>
                  <Form
                    ref={this.formRef}
                    name="material"
                    onFinish={this.onModalFinish}
                    autoCompleter="off"
                    style={{margin: "30px 30px 60px 30px"}}
                  >
                    <Row>
                      <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                        <h3 className="typeTitle">Download Type</h3>
                      </Col>

                      <Col xs={24} sm={24} md={17} lg={17} xl={17}>
                        <div className="material-type">
                          <Form.Item
                            name="challanType"
                            rules={[
                              {
                                required: true,
                                message: "This feild is required",
                              },
                            ]}
                          >
                            <Select placeholder="Select challan to download">
                              <Select.Option value="selectedpage">
                                Selected Page
                              </Select.Option>
                              <Select.Option value="allchallans">
                                All Challans
                              </Select.Option>
                            </Select>
                          </Form.Item>
                        </div>
                      </Col>
                      <Col xs={24} sm={24} md={4} lg={4} xl={4}>
                        <Button className="addbtn" htmlType="submit">
                          Download
                        </Button>
                      </Col>
                    </Row>
                  </Form>
                </div>
              </Modal>
              <Modal
                title={
                  <div>
                    <h3 style={{fontSize: "20px", margin: "0px"}}>
                      Dispatch Challan
                    </h3>
                    <p
                      style={{
                        fontSize: "12px",
                        fontFamily: "MyriadProBold",
                      }}
                    >
                      Enter Dispatch Challan Details
                    </p>
                  </div>
                }
                className="ModalLook"
                visible={this.state.editvisible}
                onOk={this.handleOk}
                onCancel={this.handleCancelEdit}
                destroyOnClose={true}
                footer={null}
              >
                <div>
                  {editableVehicleDetails && (
                    <EditChallanDetails
                      record={editableVehicleDetails}
                      parentFn={() => this.reloadAfterModalClose()}
                    />
                  )}
                </div>
              </Modal>
              <div>
                <Row>
                  <Col
                    xs={12}
                    sm={12}
                    md={3}
                    lg={3}
                    xl={3}
                    style={{padding: "10px 8px"}}
                  >
                    <h3 className="filterLabel">Material Type</h3>
                    <div>
                      <Select
                        placeholder="Select All"
                        mode="multiple"
                        style={{
                          width: "100%",
                          overflowY: "auto tabindex=0",
                        }}
                        maxTagPlaceholder={`+ ${
                          this.state.materialSelect.length - SELECTED_THRESHOLD
                        } Selected`}
                        maxTagCount={SELECTED_THRESHOLD}
                        onChange={this.handleMaterialType}
                        tabIndex="0"
                        showArrow
                      >
                        {materialDataSelect}
                      </Select>
                    </div>
                  </Col>
                  <Col
                    xs={12}
                    sm={12}
                    md={3}
                    lg={3}
                    xl={3}
                    style={{padding: "10px 8px"}}
                  >
                    <h3 className="filterLabel">Truck Type</h3>
                    <div>
                      <Select
                        mode="multiple"
                        placeholder="Select All"
                        style={{width: "100%"}}
                        maxTagPlaceholder={`+ ${
                          this.state.truckTypeStatus.length - SELECTED_THRESHOLD
                        } Selected`}
                        maxTagCount={SELECTED_THRESHOLD}
                        onChange={this.handleTruckType}
                        showArrow
                      >
                        <option value="Bag">Bag</option>
                        <option value="Bulk">Bulk</option>
                      </Select>
                    </div>
                  </Col>
                  <Col
                    xs={12}
                    sm={12}
                    md={4}
                    lg={4}
                    xl={4}
                    style={{padding: "10px 8px"}}
                  >
                    <h3 className="filterLabel">Sort by Date Range</h3>
                    <div style={{display: "flex"}}>
                      <RangePicker
                        format={dateFormat}
                        onChange={(dates) => this.onDateChange(dates)}
                        allowEmpty={[true, true]}
                        suffixIcon={<img src={dateicon} alt="dateIcon" />}
                      />
                    </div>
                  </Col>
                  <Col
                    xs={12}
                    sm={12}
                    md={5}
                    lg={5}
                    xl={5}
                    style={{padding: "10px 8px"}}
                  >
                    <h3 className="filterLabel">
                      Search by Delivery or Vehicle Number
                    </h3>
                    <div>
                      <Search
                        className="searchbtn"
                        onChange={this.searchResult}
                        style={{height: "30px !important"}}
                      />
                    </div>
                  </Col>
                  <Col
                    xs={12}
                    sm={12}
                    md={9}
                    lg={9}
                    xl={9}
                    style={{padding: "10px", marginTop: "10px"}}
                  >
                    <div style={{display: "flex", float: "right"}}>
                      <div style={{padding: "13px 4px"}}>
                        <Upload
                          name="file"
                          customRequest={this.axiosFileUploadRequest}
                          accept=".xlsx"
                          showUploadList="false"
                          fileList=""
                          // className={Styles.growerUpload}
                        >
                          <Button
                            className="outlineBtn"
                            icon={<img src={Uploadicon} />}
                          >
                            {/* <UploadOutlined
                              style={{fontSize: "20px", color: "#01a6d0"}}
                            /> */}
                          </Button>
                        </Upload>
                      </div>
                      <div style={{padding: "13px 4px"}}>
                        <Button
                          className="outlineBtn"
                          onClick={() => {
                            this.onModalDownload();
                          }}
                          icon={<img src={DownloadIcon} />}
                        >
                          {/* DOWNLOAD */}
                        </Button>
                      </div>
                      <div style={{padding: "13px 4px"}}>
                        <PrintChallan Data={this.state.selectRowArray} />
                      </div>
                      <div style={{padding: "13px 0"}}>
                        <Button
                          type="primary"
                          onClick={this.showModal}
                          icon={
                            <img
                              src={ChallanIcon}
                              style={{paddingRight: "7px"}}
                            />
                          }
                        >
                          CREATE CHALLAN
                        </Button>
                      </div>
                    </div>
                  </Col>
                </Row>
              </div>

              <div className="dispatchChallanTable">
                <Table
                  columns={columns}
                  rowKey="_id"
                  dataSource={
                    this.state.searchValue === "" ||
                    this.state.searchValue === null
                      ? dataSource
                      : this.state.searchResultArray
                  }
                  rowSelection={{
                    type: this.state.selectionType,
                    ...rowSelection,
                  }}
                  expandIcon={(props) => this.customExpandIcon(props)}
                  pagination={{
                    current: this.state.page,
                    defaultPageSize: 50,
                    showSizeChanger: true, 
                    onShowSizeChange: this.changePaginationAll,
                    total: this.state.totalCount,
                    onChange: this.changePagination,
                  }}
                />
              </div>
            </div>
          </Spin>
        </div>
      </div>
    );
  }
}

export default DispatchChallan;
